import {Map, List, fromJS} from '@the-grid/immutable'
// import {fromTo} from '@the-grid/multiverse-ui'

let _defaults = {
  show: true,
  active: false,
  canDeactivate: true,
  icon: '+', // 'live_help',
  activeIcon: 'x',
  pop: 4,
  appButton: undefined,
  overlayProps: Map({show: true}),
  onClick: undefined,
  actionList: undefined,
  chroma: {type: 'light'},
  'v-position': 'bottom',
  'h-position': 'right',

  // @TODO: req from scroll store?
  // align offset from element
  // should be element, or querySelector first selection
  alignWith: undefined,

  // @TODO: shadowed-goo, @see theme/gooey/svg
  // 'gooey', 'springs'
  theme: 'springs',
  labelAsTip: false,
  loading: false,
}

export default {
  _defaults: {
    cache: false,
    get() {
      return _defaults
    },
  },

  className: {default: ''},
  active: {default: _defaults.active},
  show: {default: false},
  canDeactivate: {default: _defaults.canDeactivate},
  chroma: {default: _defaults.chroma},
  icon: {default: _defaults.icon},
  activeIcon: {default: _defaults.activeIcon},
  pop: {default: _defaults.pop},
  overlayProps: {default: _defaults.overlayProps},
  appButton: {
    set(appButton) {
      if (!appButton) return undefined
      if (!appButton.toJS) appButton = fromJS(appButton)
      return {appButton: appButton.merge({
        isAppButton: true,
        id: 'app-button',
        key: 'app-button',
      })}
    },
  },
  onClick: undefined,
  _actionList: undefined,
  actionList: {
    deps: ['_actionList','appButton'],
    get(_actionList, appButton) {
      if (!appButton) return _actionList
      if (!_actionList) return List.of(appButton)
      return _actionList.push(appButton)
    },
    set(_actionList) {
      return {_actionList}
    },
  },
  'v-position': undefined,
  'h-position': undefined,
  theme: undefined,
  labelAsTip: undefined,
  loading: undefined,
}



