import React from 'react'
import Component from '@the-grid/ui-component-deprecated'
import LogoLoader from '@the-grid/ui-logo-loader'

let cancelScrollEvent = function(e) {
  e.stopImmediatePropagation()
  e.preventDefault()
  e.returnValue = false
  return false
}

let addScrollEventListener = function(elem, handler) {
  elem.addEventListener('wheel', handler, false)
}

let removeScrollEventListener = function(elem, handler) {
  elem.removeEventListener('wheel', handler, false)
}

class IFrame extends Component {

  static displayName = 'IFrame'

  static propTypes = {
    url: React.PropTypes.string.isRequired,
    show: React.PropTypes.bool,
  }

  static defaultProps = {
    url: 'http://about:blank',
    show: true,
    loadingStyle: {},
    iframeStyle: {},
    sandbox: 'allow-scripts',
  }

  state = {
    loading: false,
  }

  scrollLock() {
    addScrollEventListener(this.wrap_el, this.scrollHandler)
  }

  scrollRelease(elem) {
    removeScrollEventListener(this.wrap_el, this.scrollHandler)
  }

  scrollHandler = (e) => {
    var elem = this.wrap_el
    var scrollTop = elem.scrollTop
    var scrollHeight = elem.scrollHeight
    var height = elem.clientHeight
    var wheelDelta = e.deltaY
    var isDeltaPositive = wheelDelta > 0

    if (isDeltaPositive && wheelDelta > scrollHeight - height - scrollTop) {
      elem.scrollTop = scrollHeight
      return cancelScrollEvent(e)
    }
    else if (!isDeltaPositive && -wheelDelta > scrollTop) {
      elem.scrollTop = 0
      return cancelScrollEvent(e)
    }
  }

  componentDidMount() {
    this.didLoad()
    this.startLoading()
    this.timeOut = setTimeout(this.didLoad, 666)
   // this.iframe_el.contentWindow.document.addEventListener('DOMContentLoaded', this.didLoad)
    // this.scrollLock()
  }

  componentWillReceiveProps(nextProps) {
    // if (this.props.url != nextProps.url) {
    //  this.startLoading()
    // }
  }

  componentWillUnmount() {
    // this.iframe_el.contentWindow.document.removeEventListener('DOMContentLoaded', this.didLoad)
    clearTimeout(this.timeOut)
    this.scrollRelease()
    this.wrap_el = undefined
    this.iframe_el = undefined
  }

  startLoading() {
    this.setState({
      loading: true,
    })
  }

  didLoad = () => {
    this.setState({
      loading: false,
    })
  }

  getStyle() {
    let defaultStyle = {
      position: 'absolute',
      top: '0px',
      bottom: '0px',
      left: '0px',
      right: '0px',
      zIndex: '50',
      border: '0px',
    }

    let style =  Object.assign({}, this.props.style || defaultStyle)
    style['display'] = (this.props.show) ? 'block' : 'none'
    return style
  }

  getDefaultIframeStyle() {
    return {
      position: 'absolute',
      width: '100%',
      height: '100%',
      border: '0px',
    }
  }

  renderLoader() {
    let loadingStyle = Object.assign({}, this.props.loadingStyle)
    loadingStyle['display'] = (this.props.show) ? 'block' : 'none'
    return (
      <div style={{position: 'absolute', left: 0, right: 0, bottom: 0, top: 0}}>
        <div className="color-bg" style={{opacity: .5, position: 'absolute', left: 0, right: 0, bottom: 0, top: 0}} />
        <LogoLoader />
      </div>
    )
  }

  wrap_el = undefined
  wrap_ref = (el) => {
    this.wrap_el = el
  }

  iframe_el = undefined
  iframe_ref = (el) => {
    this.iframe_el = el
  }

  render() {
    let styles = this.getStyle()
    let url = this.props.url
    // url = ''

    let iframeStyle = Object.assign({}, this.getDefaultIframeStyle(), this.props.iframeStyle)
    iframeStyle['display'] = (this.props.show) ? 'block' : 'none'

    return (
      <div ref={this.wrap_ref} style={styles} className={this.props.className}>
        <iframe ref={this.iframe_ref}
          style={iframeStyle}
          src={url}
          frameBorder="0"
          sandbox={this.props.sandbox}
          allowFullScreen />
        {(this.state.loading) ? this.renderLoader() : null}
      </div>
    )
  }
}

export default IFrame



