import {React, PropTypes, raf} from '@the-grid/deps'
import {installStore} from '@the-grid/cymatics'
import ScrollStore from './ScrollStore'

let count = 0

class ScrollContext extends React.Component {

  static displayName = 'ScrollContext'

  static propTypes = {
    target: PropTypes.string.isRequired,
    children: PropTypes.any.isRequired,
  }

  static defaultProps = {
    target: 'document',
  }

  componentWillMount() {
    this.store.silently( 'update-props', this.props )
    this.store.execute( 'listenIfNeeded' )
  }

  componentDidMount() {
    raf(() => {
      this.store.execute( 'listenIfNeeded' )
    })
  }

  componentWillReceiveProps(props) {
    if (props.target !== this.props.target) this.store.silently( 'update-props', props )
    // if (props.shouldUpdateProps) this.store.silently( 'update-props', props )
  }

  // componentWillRecieveProps(props) {
  componentDidUpdate() {
    this.store.execute( 'listenIfNeeded', this.props.target )
  }

  componentWillUnmount() {
    this.store.silently( 'teardown' )
  }

  render() {
    return this.props.children
  }
}

export default installStore( ScrollContext, 'scroll', (scrollContext) => {
  return new ScrollStore(scrollContext)
})


