import {Component, PropTypes} from 'react'
import {cloneComponentProps} from './enhancer'
import Styleverse from './Styleverse'

export default function enhancePlugin(configOrComposedComponent, config) {
  if (typeof configOrComposedComponent !== 'function') {
    const newConfig = {...config, ...configOrComposedComponent}
    return function(configOrComponent) {
      return enhancePlugin(configOrComponent, newConfig)
    }
  }

  // let {
  //   //namespace
  // } = config

  const component = configOrComposedComponent
  let ComposedComponent = component

  // Handle stateless components
  if (!ComposedComponent.render && !ComposedComponent.prototype.render) {
    ComposedComponent = class extends Component {
      render() {
        return component(this.props, this.context)
      }
    }
    ComposedComponent.displayName = component.displayName || component.name
  }

  class Enhancer extends ComposedComponent {
    constructor(...args) {
      super(...args)

      // this.state = this.state || {}
      // this.state._radiumStyleState = {}

      if (this.mountCSS) this.mountCSS = this.mountCSS.bind(this)
      if (this.unmountCSS) this.unmountCSS = this.unmountCSS.bind(this)
      // if (this.mountCSS && !this.unmountCSS) throw new Error("...")
    }

    // TODO
    // - willMount or DidMount????????
    componentWillMount() {
      if (super.componentWillMount) super.componentWillMount()
      const styleverse = this.context.styleverse
      if (this.mountCSS) styleverse.mountCSS(this.mountCSS())
    }

    componentWillUpdate(...args) {
      if (super.componentWillUpdate) super.componentWillUpdate(...args)
    }

    componentWillUnmount() {
      if (super.componentWillUnmount) super.componentWillUnmount()
      const styleverse = this.context.styleverse
      if (this.unmountCSS) styleverse.unmountCSS(this.unmountCSS())
    }

    getChildContext() {
      const superChildContext = super.getChildContext ?
        super.getChildContext() :
        {}

      const childContext = {...superChildContext}

      const styleStore = this.props.styleStore
        ? this.props.styleStore
        : this.context.styleStore
          ? this.context.styleStore
          : new Styleverse.StyleStoreStruct({})

      childContext.styleStore = this.updateStyleStore
        ? this.updateStyleStore(styleStore)
        : styleStore

      return childContext
    }

    // render() {
    //  const renderedElement = super.render();
    //  return resolveStyles(this, renderedElement, currentConfig);
    // }
  }

  // Class inheritance uses Object.create and because of __proto__ issues
  // with IE <10 any static properties of the superclass aren't inherited and
  // so need to be manually populated.
  // See http://babeljs.io/docs/advanced/caveats/#classes-10-and-below-
  cloneComponentProps(component, Enhancer)

  if (process.env.NODE_ENV !== 'production') {
    // This also fixes React Hot Loader by exposing the original components top
    // level prototype methods on the enhanced prototype as discussed in
    // https://github.com/FormidableLabs/radium/issues/219.
    cloneComponentProps(ComposedComponent.prototype, Enhancer.prototype)
  }

  // if (Enhancer.propTypes && Enhancer.propTypes.style) {
  //  Enhancer.propTypes = {
  //    ...Enhancer.propTypes,
  //    style: PropTypes.oneOfType([
  //      PropTypes.array,
  //      PropTypes.object
  //    ])
  //  };
  // }

  Enhancer._isEnhancedStyleversePlugin = true

  Enhancer.displayName =
    component.displayName ||
    component.name ||
    'StyleversePluginComponent'

  Enhancer.contextTypes = {
    ...Enhancer.contextTypes,
    styleverse: PropTypes.object,
    styleStore: PropTypes.object,
  }

  Enhancer.childContextTypes = {
    ...Enhancer.childContextTypes,
    styleStore: PropTypes.object,
  }

  return Enhancer
}



