import React, {PropTypes} from 'inferno-compat'
import Component from '@the-grid/ui-component-deprecated'
import Ribbon from '@the-grid/ui-ribbon'
let displayName = 'PricingItem'

class PricingItem extends Component {
  static displayName = displayName
  render() {
    let {
      item,
      id,
    } = this.props
    let title = item.get('title')
    let price = item.get('price')
    let description = item.get('description')
    let isFeatured = item.get('isFeatured')
    let features = item.get('features')
    let action = item.get('action')

    let className = 'pricing-item'
    if (isFeatured) className += ' featured'

    let featureNodes = []
    for (let i = 0; i < features.size; i++) {
      let feature = features.get(i)
      featureNodes.push(<li className="pricing-item_feature" key={'feature-' + i}>{feature}</li>)
    }

    return (
      <div id={id} key={id} className={className}>

        {isFeatured ? <Ribbon>Most Popular</Ribbon> : null}

        <h3 className="pricing-item_title">{title}</h3>
        <div className="pricing-item_price">
          <span className="pricing-item_currency">{price.get('currency')}</span>
          <span className="pricing-item_amount">{price.get('amount')}</span>
          <span className="pricing-item_period">{price.get('period')}</span>
        </div>
        <p className="pricing-item_description">{description}</p>
        <ul className="pricing-item_features">
          {featureNodes}
        </ul>
        <button className="pricing-item_action" onClick={action.get('onClick')}>{action.get('label')}</button>
      </div>
    )
  }
}

// PricingItem.propTypes = {
//   item: PropTypes.iterable.isRequired
// }

export default PricingItem



