import React from 'react'
import {SVGGradientMap} from '../components'

// TODO: make proper!
var isSafari
var ua = navigator.userAgent.toLowerCase();
if (ua.indexOf('safari') != -1) {
  if (ua.indexOf('chrome') > -1) {
    isSafari = false // Chrome
  } else {
    isSafari = true // Safari
  }
}

let keyVals = {

  "box-sh--l": {
    deps: ['styleStore.chroma.type'],
    get: function (type) {
      let style
      if (type === 'dark') style = {'box-shadow': `0 10px 30px hsla(0, 0%, 0%, 0.7), 0 6px 10px hsla(0, 0%, 0%, 1)`}
      style =                      {'box-shadow': `0 10px 30px hsla(0, 0%, 0%, 0.19), 0 6px 10px hsla(0, 0%, 0%, 0.23)`}
      return {style}
    }
  },

  "box-sh--m": {
    deps: ['styleStore.chroma.type'],
    get: function (type) {
      let style
      if (type === 'dark') style = {'box-shadow': `0 1px 6px hsla(0, 0%, 0%, .2), 0 1px 4px hsla(0, 0%, 0%, 0.3)`}
      style =                      {'box-shadow': `0 1px 6px hsla(0, 0%, 0%, 0.12), 0 1px 4px hsla(0, 0%, 0%, 0.12)`}
      return {style}
    }
  },

  "chroma--box-pop": {
    deps: ['styleStore.chroma.palette'],
    get: function (palette) {
      return {
        style: {
          'background-color': palette('boxPop'),
          'color': palette('boxPopText'),
        }
      }
    }
  },

  "chroma--box-pop2": {
    deps: ['styleStore.chroma.palette'],
    get: function (palette) {
      return {
        style: {
          'background-color': palette('boxPop2'),
          'color': palette('boxPop2Text'),
        }
      }
    }
  },

  'chroma--loading-text': {
    deps: ['styleStore.chroma.palette','styleStore.chroma.chromaId'],
    get: function (palette, chromaId) {
      return {
        style: {
          'animation-duration': '2s',
          'animation-fill-mode': 'forwards',
          'animation-iteration-count': 'infinite',
          'animation-name': `${chromaId}-loading-text`,
          'animation-timing-function': 'linear',
          '-webkit-animation-delay': '2s', /* Safari 4.0 - 8.0 */
          'animation-delay': '2s',
        },
        css: `
          @keyframes ${chromaId}-loading-text {
            0%{
              color: ${palette('textPop')};
            }
            ${100 * 1/4}% {
              color: ${palette('textPop2')};
            }
            ${100 * 3/4}% {
              color: ${palette('text')};
            }
            100%{
              color: ${palette('textPop')};
            }
          }
        `
      }
    }
  },

  //'chroma--loading-text-pop': (get) => {
  //  return {
  //    'animation-duration': '2s',
  //    'animation-fill-mode': 'forwards',
  //    'animation-iteration-count': 'infinite',
  //    'animation-name': `${key}-loading-bg-pop`,
  //    'animation-timing-function': 'linear',
  //    'background': palette('box'),
  //    'background-size': '100%',
  //    CSS: `
  //      @keyframes ${key}-loading-bg-pop {
  //        0%{
  //          color: ${ palette('selection') };
  //        }
  //        ${100 * 1/4}% {
  //          color: ${ palette('boxPop') };
  //        }
  //        ${100 * 3/4}% {
  //          color: ${ palette('boxPop2') };
  //        }
  //        100%{
  //          color: ${ palette('selection') };
  //        }
  //      }
  //    `
  //  }
  //},

  'chroma--loading-bg-pop': {
    deps: ['styleStore.chroma.palette','styleStore.chroma.chromaId'],
    get: function (palette, chromaId) {
      return {
        style: {
          'animation-duration': '2s',
          'animation-fill-mode': 'forwards',
          'animation-iteration-count': 'infinite',
          'animation-name': `${chromaId}-loading-bg-pop`,
          'animation-timing-function': 'linear',
          'background': palette('box'),
          //'color': palette('text'),
          'background-size': '100%',
          '-webkit-animation-delay': '1.2s', /* Safari 4.0 - 8.0 */
          'animation-delay': '1.2s',
        },
      css: `
        @keyframes ${chromaId}-loading-bg-pop {
          0%{
            background: ${ palette('selection') };
          }
          ${100 * 1/4}% {
            background: ${ palette('boxPop') };
          }
          ${100 * 3/4}% {
            background: ${ palette('boxPop2') };
          }
          100%{
            background: ${ palette('selection') };
          }
        }
      `
        //css: `
        //  @keyframes ${chromaId}-loading-bg-pop {
        //    0%{
        //      background: ${ palette('selection') };
        //      color: ${ palette('text') };
        //    }
        //    ${100 * 1/4}% {
        //      background: ${ palette('boxPop') };
        //      color: ${ palette('boxPopText') };
        //    }
        //    ${100 * 3/4}% {
        //      background: ${ palette('boxPop2') };
        //      color: ${ palette('boxPop2Text') };
        //    }
        //    100%{
        //      background: ${ palette('selection') };
        //      color: ${ palette('text') };
        //    }
        //  }
        //`
      }
    }
  },

  'chroma--inverse-loading-bg-pop': {
    deps: ['styleStore.chroma.palette','styleStore.chroma.chromaId'],
    get: function (palette, chromaId) {
      return {
        style: {
          'animation-duration': '2s',
          'animation-fill-mode': 'forwards',
          'animation-iteration-count': 'infinite',
          'animation-name': `${chromaId}-inverse-loading-bg-pop`,
          'animation-timing-function': 'linear',
          'background': palette('box',false,'inverse'),
          'background-size': '100%',
        },
        css: `
          @keyframes ${chromaId}-inverse-loading-bg-pop {
            0%{
              background: ${ palette('selection',false,'inverse') };
            }
            ${100 * 1/4}% {
              background: ${ palette('boxPop',false,'inverse') };
            }
            ${100 * 3/4}% {
              background: ${ palette('boxPop2',false,'inverse') };
            }
            100%{
              background: ${ palette('selection',false,'inverse') };
            }
          }
        `
      }
    }
  },

  'chroma--loading-bg': {
    deps: ['styleStore.chroma.palette','styleStore.chroma.chromaId'],
    get: function (palette, chromaId) {
      return {
        style: {
          'animation-duration': '2s',
          'animation-fill-mode': 'forwards',
          'animation-iteration-count': 'infinite',
          'animation-name': `${chromaId}-loadingShimmer-1`,
          'animation-timing-function': 'linear',
          'background': palette('box'),
          'background-size': '100%',
        },
        css: `
          @keyframes ${chromaId}-loadingShimmer-1 {
            0%{
              background: ${palette('box')};
            }
            ${100 * 1/3}% {
              background: ${ palette('box',true).clone().mix(palette('bg',true),.5).hslString() };
            }
            ${100 * 2/3}% {
              background: ${ palette('box',true).clone().mix(palette('selection',true),.3).hslString() };
            }
            100%{
              background: ${ palette('box') };
            }
          }
        `
      }
    }
  },


  // ------------------------------------------------

  'chroma-filter--gradient-map': {
    // TODO remove mountCount
    deps: ['styleStore.chroma.gradientMap','styleStore.chroma.chromaId'],//,'styleStore.chroma.mountCount'],
    get: function (gradientMap, chromaId ) { //mountCount) {
      let id = chromaId + '-gradient-map'
      let {colors} = gradientMap
      if (isSafari) {
        return {
          style: {
            '-webkit-filter': 'contrast(.5) brightness(0.4) !important',
            'filter': 'contrast(.5) brightness(0.4) !important',
            'opacity': '.3 !important'
          }
        }
      }
      return {
        style: {
          '-webkit-filter': `url("#${id}")`,
                  'filter': `url("#${id}")`,
          'backface-visibility': 'hidden'
        },
        component: <SVGGradientMap id={id} colors={colors} />,
      }
    }
  },


  'chroma-filter--gradient-map-dark-demph': {
    // TODO remove mountCount
    deps: ['styleStore.chroma.gradientMapDarkDemph','styleStore.chroma.chromaId'],//,'styleStore.chroma.mountCount'],
    get: function (gradientMap, chromaId) {//, mountCount) {
      let id = chromaId + '-gradient-map-dark-demph'
      let {colors} = gradientMap
      if (isSafari) {
        return {
          style: {
            '-webkit-filter': 'contrast(.5) brightness(0.3)',
            'filter': 'contrast(.5) brightness(0.4)',
            'opacity': '.25 !important'
          }
        }
      }
      return {
        style: {
          '-webkit-filter': `url("#${id}")`,
                  'filter': `url("#${id}")`,
          'backface-visibility': 'hidden'
        },
        component: <SVGGradientMap id={id} colors={colors} />,
      }
    }
  },

}

export default keyVals



