import React from 'react'
import PeopleMount from './people-mount'
import {fromJS} from '@the-grid/immutable'
import {Story} from '@the-grid/ui-story-machine'

class People extends Story {
  storyName = 'people'
  willMount() {
    if (this.focused) return
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'update', this.UI.FAB)
    this.contextRequest('FAF', 'update', this.UI.FAF)
    this.contextRequest('FAN', 'update', this.UI.FAN)
  }
  display(children) {
    return React.createElement(PeopleMount, {key: 'people-mount'}, children)
  }
  UI = {
    FAB: fromJS({
      show: true,
      active: true,
      icon: '+',
      // chroma: {type: 'dark', typeIndex: 0},
      actionList: [
        {
          label: 'Add collaborator',
          // icon: 'color_lens',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Collaborators or switch to an account with permisssion.`)
          },
        },
        {
          label: 'Add member',
          // icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Members or switch to an account with permisssion.`)
          },
        },
        {
          label: 'Send message',
          // icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to Messages from this site or switch to an account with permisssion.`)
          },
        },
      ],
    }),
    FAF: fromJS({
      show: true,
      hideOnScroll: true,
      chroma: {type: 'light', typeIndex: 1},
      active: 3,
      actionList: [
        {
          label: 'newsfeed',
          key: 'newsfeed',
          willActivate: () => {
            alert('show newsfeed')
          },
        },
        {
          label: 'sites',
          key: 'sites',
          willActivate: () => {
            this.contextRequest('storyline', 'to-story','create')
          },
        },
        {
          label: 'thegrid.io',
          // key: 'current-site',
          willActivate: () => {
            this.contextRequest('storyline', 'to-story','on-the-grid')
          },
        },
        {
          label: 'people',
          // key: 'design',
          willActivate: () => {
            // this.execute('start-story','create')
          },
        },
      ],
    }),
    FAN: fromJS({
      hideOnScroll: false,
      show: false,
      chroma: {type: 'dark', typeIndex: 1},
    }),
  }
}

export default People



