import React, {PropTypes} from 'inferno-compat'
import {Style} from '@the-grid/styleverse'
import Component from '@the-grid/ui-component-deprecated'

let themes = {
  'sonam': {
    props: {perf: true},
    styles: `
      :scope {
        fnt-sz -- txt
        fnt -- txt2
        clr -- chroma-text

        .pricing_bg {
          bg-clr -- chroma-bg
        }

        .pricing-item {

          margin -- 1em
          padding -- 2em
          cursor -- default
          //
          border-radius -- 10px
          background -- chroma-box
          box-shadow -- 0 5px 20px hsla(0, 0%, 0%, 0.05), 0 15px 30px -10px hsla(0, 0%, 0%, 0.3)
          transition -- background 0.3s

          .pricing-item_title {
            fnt-sz -- d-s
            width -- 100%
            margin -- 0 0 0.25em
            padding -- 0 0 0.5em
            fnt -- d2
            //
            border-bottom -- 3px solid chroma-line
          }

          .pricing-item_price {
            pad-t -- 1em
            pad-b -- 1em
            fnt-sz -- txt-l
            fnt -- d
            //
            clr -- chroma-text-pop
          }

          .pricing-item_description {
            pad-t -- 1em
            pad-b -- 1em
            fnt -- txt2
          }

          .pricing-item_features {
            margin -- 0
            padding -- 1em 1.25em 2em
            txt-aln -- center

            li {
              pad-t -- .25em
              pad-b -- .25em
              o -- .614
            }
          }

          .pricing-item_action {
            cursor -- pointer
            fnt-sz -- txt
            margin-top -- auto
            padding -- 0.75em 2em
            border-radius -- 5px
            bg-clr -- chroma-box-pop
            clr -- chroma-box-pop-text
            transition -- background 0.3s
          }
        }
      }

    `,
  },


  'jinpa': {
    props: {perf: true},
    styles: `
      :scope {
        fnt-sz -- txt
        fnt -- txt2
        clr -- chroma-text

        .pricing_bg {
          bg-clr -- chroma-box
        }

        .pricing-item {

          margin -- 0em
          padding -- 2em
          cursor -- default
          //
          border-radius -- 0px
          background -- chroma-box

          border -- 1px solid chroma-line

          transition -- background 0.3s

          &:nth-child(2) {
            border-right -- none
            border-left -- none
          }

          .pricing-item_title {
            fnt-sz -- d-s
            width -- 100%
            margin -- 0 0 0.25em
            padding -- 0 0 0.5em
            fnt -- d2
            //
          }

          .pricing-item_price {
            pos -- r
            pad-t -- 1em
            pad-b -- 1em
            mar-t -- 2em
            mar-b -- 2em

            fnt-sz -- txt-l
            fnt -- d

            w -- 100px
            h -- 100px
            mar-r -- auto
            mar-l -- auto
            border-radius -- 50%
            bg-clr -- chroma-box-pop2
            clr -- chroma-box-pop2-text
            text-align -- center
            line-height -- 100px
            padding -- 0


            .pricing-item_period {
              fnt-sz -- txt-s
              o -- .25
              pos -- a
              b -- -20
              l -- 0
              r -- 0
            }
          }

          .pricing-item_description {
            pad-t -- 1em
            pad-b -- 1em
            fnt -- txt2
          }

          .pricing-item_features {
            margin -- 0
            padding -- 1em 1.25em 2em
            txt-aln -- center

            li {
              pad-t -- .25em
              pad-b -- .25em
              o -- .614
            }
          }

          .pricing-item_action {
            cursor -- pointer
            fnt-sz -- txt
            margin-top -- auto
            padding -- 0.75em 2em
            border-radius -- 5px
            bg-clr -- chroma-box-pop
            clr -- chroma-box-pop-text
            transition -- background 0.3s
          }
        }
      }

    `,
  },


  'quip': {
    props: {
      perf: true,
      featuredOffset: '0em',
    },
    styles: `
      :scope {
        fnt-sz -- txt
        fnt -- txt2
        clr -- chroma-text


        .pricing_bg {
          bg-clr -- chroma-bg
        }

        .pricing-item {

          &:hover {
            bg-clr -- white !important
            li {
              bg-clr -- chroma-box !important
              &:nth-child(odd) {
                bg-color -- chroma-zebra-stripe !important
              }
            }
          }

          mar -- 0em
          mar-t -- 2em
          mar-b -- 2em
          padding -- 2em
          cursor -- default
          //
          border-radius -- 2px
          bg-clr -- chroma-box

          //border -- 0px solid chroma-line

          box-sh -- m

          transition -- background 0.3s


          &.featured {
            mar-t -- props.featuredOffset
            mar-b -- props.featuredOffset
            z-idx -- 1
            box-sh -- l
          }

          .pricing-item_title {
            cursor -- pointer
            fnt-sz -- d-m
            width -- 100%
            margin -- 0 0 0.25em
            padding -- 0 0 0.5em
            fnt -- d2
            clr -- chroma-title2
            //
          }
          &.featured .pricing-item_title {
            clr -- chroma-title
          }

          .pricing-item_price {
            mar-t -- auto
            order -- 100
            pad-t -- 1em
            pad-b -- 1em
            fnt-sz -- txt-l
            fnt -- d
            //
            clr -- chroma-text-pop
          }
          &.featured .pricing-item_price {
            clr -- chroma-text-pop2
          }

          .pricing-item_period {
            fnt-sz -- txt-s
            mar-l -- .25em
            o -- .6
          }

          .pricing-item_description {
            pad-t -- .75em
            pad-b -- .75em
            fnt -- txt
            clr -- chroma-text2
            mar-b -- auto
          }

          .pricing-item_features {
            margin -- 0
            mar-t -- 1em
            mar-b -- 1em
            txt-aln -- center

            li {
              pad-t -- .75em
              pad-b -- .75em
              mar -- 0
              mar-l -- -2em
              mar-r -- -2em
              clr -- chroma-meta
              box-sh -- inset 0 -1px 1px hsla(0,0%,0%,.1)

              transition -- background 0.3s

              &:nth-child(odd) {
                bg-color -- chroma-zebra-stripe
              }

              &:last-child {
                border-b -- none
              }
            }
          }
          &.featured .pricing-item_features {
            mar-t -- 1em
          }

          .pricing-item_action {
            cursor -- pointer
            fnt-sz -- txt
            order -- 1000
            margin-top -- initial
            padding -- 1em 2em
            //border-radius -- 5px
            bg-clr -- chroma-box-pop2
            clr -- chroma-box-pop2-text
            transition -- background 0.3s
          }
          &.featured .pricing-item_action {
            bg-clr -- chroma-box-pop
            clr -- chroma-box-pop-text
          }
        }
      }
    `,
  },

}


function Styles(props) {
  let {
    theme,
    scopeId,
  } = props
  let scope = '#' + scopeId
  if (!theme) theme = 'quip'
  theme = themes[theme]
  return Inferno.createElement('span',null, [
    Inferno.createElement(Style, {scope},`
      :scope {

        padding -- 4em 0
        position -- relative

        d -- flex
        flex-direction -- column
        flx-aln -- mid
        flx-jst -- mid

        .pricing_table {
        	d -- flex
        	flex-wrap -- wrap
        	justify-content -- center
        	width -- 100%
          margin -- 0 auto 3em
        }

        .pricing_bg {
          pos -- a
          t -- 0
          r -- 0
          b -- 0
          l -- 0
        }

        .pricing-item {
        	pos -- relative
        	d -- flex
        	flex-direction -- column
        	align-items -- stretch
        	text-align -- center
        	flex -- 0 1 330px
        }

        .pricing-item_features {
        	text-align -- left
        }

        .pricing-item_action {
        	color -- inherit
        	border -- none
        	bg -- none

          &:focus {
          	outline -- none
          }
        }
      }
    `),
    Inferno.createElement(Style, Object.assign({scope}, theme.props), theme.styles),
  ])
}


export default Styles


