import {React, PropTypes, raf} from '@the-grid/deps'
import {installStore} from '@the-grid/cymatics'
import ViewportStore from './ViewportStore'

let count = 0

class ViewportContext extends React.Component {

  static displayName = 'ViewportContext'

  static propTypes = {
    children: PropTypes.object.isRequired,
  }

  componentWillMount() {
    // this.store.silently( 'update-props', this.props )
    this.store.execute( 'listenIfNeeded' )
  }

  componentWillUnmount() {
    this.store.silently( 'teardown' )
  }

  render() {
    return this.props.children
  }
}

export default installStore( ViewportContext, 'viewport', (component) => {
  return new ViewportStore(component)
})


