import {Store} from '@the-grid/cymatics'
import BaseStruct from './Struct'
let initialState = new BaseStruct()

class BaseStore extends Store {
  name = 'styleverse-base'
  deferCommit = false
  state = initialState

  actions = {

    'update-props': (state, props) => {
      // nothing for now
      return state
    },

    'teardown': () => {
      return initialState
    },


  }
}

export default BaseStore



