let DEFAULT_THROTTLE_OPTIONS = {
  leading: 0, // 0 === wait for delay, 1 === wait for raf, 2 === before raf
  delay: 0,
}
function throttle(callback, options) {
  if (!options) {
    options = DEFAULT_THROTTLE_OPTIONS
  }
  else {
    options = Object.assign({},DEFAULT_THROTTLE_OPTIONS,options)
  }
  let {
    leading,
    delay
  } = options

  if (leading === true) leading = 1

  let requestId = null
  let startTime = null
  let is_leading = false
  let args_to_apply = undefined

  let execution_count = 0
  let request_count = 0
  let immediate = false
  let leadingDone_request
  function later() {
    //console.log('..later',{immediate,request_count,execution_count})
    if (immediate) {
      immediate = false
      let args = args_to_apply
      args_to_apply = undefined
      execution_count++
      //console.log('...lead!!!')
      raf.cancel(leadingDone_request)
      leadingDone_request = raf(leadingDone)
      return callback(...args)
    }

    if (delay && startTime) {
      let elapsed = Date.now() - startTime
      if (elapsed < delay) {
        requestId = raf(later)
        raf.cancel(leadingDone_request)
        leadingDone_request = raf(leadingDone)
        return
      }
    }

    startTime = null
    requestId = null
    let args = args_to_apply
    args_to_apply = undefined
    let results
    if (execution_count !== request_count) {
      execution_count++
      //console.log('exec!!!!')
      results = callback(...args)
    }
    raf.cancel(leadingDone_request)
    leadingDone_request = raf(leadingDone)
    return results
  }

  function leadingDone() {
    //console.log('leadingDone')
    is_leading = false
  }
  function throttled(...args) {
    //console.log('throttled()')
    args_to_apply = args
    if (execution_count === request_count) request_count++
    if (requestId === null) {
      if (leading && startTime === null && !is_leading) {
        is_leading = true
        immediate = true
        //console.log('lead...')
        if (leading === 2) {
          later()
        } else {
          raf(later)
        }
      }
      if (delay && startTime === null) startTime = Date.now()
      //console.log('raf(later)..')
      requestId = raf(later)
    }
    raf.cancel(leadingDone_request)
    leadingDone_request = raf(leadingDone)
  }
  function tick() {

  }
  function cancel() {
    if (requestId) {
      raf.cancel(requestId)
      requestId = null
    }
    if (leadingDone_request) {
      raf.cancel(leadingDone_request)
      leadingDone_request = null
    }
    immediate = false
    startTime = null
    args_to_apply = undefined
    is_leading = false
    execution_count = 0
    request_count = 0
  }
  throttled.cancel = cancel
  return throttled
}

let DEFAULT_DEBOUNCE_OPTIONS = {
  leading: false,
  delay: 0,
}
function debounce(callback, options) {
  if (!options) {
    options = DEFAULT_DEBOUNCE_OPTIONS
  }
  else {
    options = Object.assign({},DEFAULT_DEBOUNCE_OPTIONS,options)
  }
  let {
    leading,
    delay
  } = options
  let requestId = null
  let end_request = null
  let startTime
  let args_to_apply
  function debounced(...args) {
    args_to_apply = args
    if (requestId === null) {
      if (leading) later(args)
    } else {
      raf.cancel(requestId)
    }
    if (delay) startTime = Date.now()
    requestId = raf(later(args))
  }
  function later(args) {
    return function() {
      if (delay) {
        let elapsed = Date.now() - startTime
        if (elapsed < delay) {
          requestId = raf(later(args))
          return
        }
      }
      requestId = null
      raf.cancel(end_request)
      end_request = raf(end)
    }
  }
  function end() {
    if (!requestId) {
      let args = args_to_apply
      end_request = undefined
      args_to_apply = undefined
      return callback(...args)
    }
  }
  function cancel() {
    if (requestId) raf.cancel(requestId)
    if (end_request) raf.cancel(end_request)
    startTime = null
    requestId = null
    end_request = null
    args_to_apply = undefined
  }
  debounced.cancel = cancel
  return debounced
}

raf.throttle = throttle
raf.debounce = debounce
export {throttle, debounce}


