import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import designable from '@the-grid/ui-designable'


class SectionDual extends React.Component {
  static displayName = 'SectionDual'
  render() {
    let {id, className, ...props} = this.props
    this.id = id
    this.className = className
    return React.createElement('section', {...props, id, className}, this.renderChildren())
  }
  renderChildren() {
    return [
      (
        <div className="layout-context" key="context">
          {this.props.children[0]}
        </div>
      ),
      (
        <div className="layout-subject" key="subject">
          {this.props.children[1]}
        </div>
      )
    ]
  }
}

