import {Struct, Map, List, fromJS} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
import SemanticSpace from './semantic-space-struct'

class SemanticSpaceStore extends Store {
  name = 'semantic-space'
  deferCommit = true

  initialState = Map.EMPTY
  state = this.initialState

  shouldCommit(state) {
    return !!state.get('_vectors')
  }

  willMount(component) {
    this.execute('replace-state', component.props.space)
  }
  // willGetProps(component) {
  //   this.execute('replace-state', component.props.space)
  // }
  willUnmount() {
    this.state = this.initialState
  }


  // mountedStoriesById = {}
  // registerMountedStory = (id, story) => {
  //   this.mountedStoriesById[id] = story
  // }
  // unregisterMountedStory = (id) => {
  //   delete this.mountedStoriesById[id]
  // }

}

function make(component) {
  // TODO pool & recycle
  return new SemanticSpaceStore({component})
}

SemanticSpaceStore.make = make

export default SemanticSpaceStore



