import {Store} from '@the-grid/cymatics'
import {Map} from '@the-grid/immutable'

class Story extends Store {
  name = 'story'
  initialState = Map()
  state = this.initialState
  storyline = undefined
  id = undefined
  depth = undefined
  focused = undefined

  // internal
  _willMount(component) {
    var {
      storyline,
    } = component.context.stores
    if (!storyline) throw new Error('Story Store !storyline?')
    var {
      id,
      depth,
      focused,
    } = component.props
    if (!id) throw new Error('Story Store !id?')
    if (typeof depth !== 'number') throw new Error('Story Store !depth?')
    this.storyline = storyline
    this.id = id
    this.depth = undefined
    this.focused = focused
    storyline.registerMountedStory(id, this)
  }
  _willUnmount() {
    this.storyline.unregisterMountedStory(this.id)
    this.state = this.initialState
    this.id = undefined
    this.depth = undefined
    this.storyline = undefined
    this.focused = undefined
  }

  // Story Interface
  willMount(component) {
    return true
  }
  focus() {
    return true
  }
  display(children) {
    return children
  }
  willUnmount() {
    return true
  }
}

export default Story



