import {Struct, Map} from '@the-grid/immutable'


let initialState

const makeStyleStoreStruct = function(plugins) {
  /*
    StyleSheeet plugins are executed bottom up in plugin component hiearchy.
    Plugin components higher in hierachy provide context needed for child components.
    Child components have higher priority StyleSheet execution.
  */

  const {
    namesWithParser,
    namesWithComponent,
    parsers,
  } = plugins

  const keyValParsers = []
  const keyParsers = []
  const valParsers = []
  const renderParsers = []

  for (let i = 0, len = namesWithParser.length; i < len; i++) {
    const key = namesWithParser[i]
    const keyValParser = parsers[key].keyVal
    const keyParser = parsers[key].key
    const valParser = parsers[key].val
    const renderParser = parsers[key].render
    if (keyValParser) keyValParsers.push(keyValParser)
    if (keyParser) keyParsers.push(keyParser)
    if (valParser) valParsers.push(valParser)
    if (renderParser) renderParsers.push(renderParser)
  }

  const schema = {
    isReady: {
      cache: false,
      params: ['_'],
      get() {
        return !this.equals(initialState)
      },
    },
    meta: {default: Map()},
  }
  for (let i = 0, len = namesWithComponent.length; i < len; i++) {
    schema[namesWithComponent[i]] = undefined
  }

  let StyleStoreStruct = Struct(schema)
  initialState = new StyleStoreStruct()
  StyleStoreStruct.initialState = initialState
  // StyleStoreStruct
  return StyleStoreStruct
}

export default makeStyleStoreStruct



