// console.log('/////')
import Immutable from 'immutable';

// enhance Map, Record, etc
// Immutable.Collection.Keyed = enhanceKeyed(Immutable.Collection.Keyed)

var Map = Immutable.Map,
    List = Immutable.List,
    Seq = Immutable.Seq,
    Set = Immutable.Set,
    Stack = Immutable.Stack,
    Iterable = Immutable.Iterable,
    OrderedMap = Immutable.OrderedMap,
    OrderedSet = Immutable.OrderedSet;


Immutable.OMap = OrderedMap;
Immutable.OSet = OrderedSet;

Map.EMPTY = Map();
List.EMPTY = List();
Seq.EMPTY = Seq();
Set.EMPTY = Set();
Stack.EMPTY = Stack();
Iterable.EMPTY = Iterable();
Immutable.OMap.EMPTY = Immutable.OMap();
Immutable.OSet.EMPTY = Immutable.OSet();
console.log('Immutable', Immutable);
export default Immutable;


