import React from 'react'
import {installStore} from '@the-grid/cymatics'
import FAFStore from './faf-store'
import FAF from '../faf'

let count = 0
class FAFContext extends React.Component {
  static displayName = 'FAFContext'
  _id = 'faf-context-' + count++
  componentWillMount() {
    this.store.willMount()
  }
  componentWillUnmount() {
    this.store.willUnmount()
  }
  shouldComponentUpdate() {
    return false
  }
  render() {
    let id = this.props.id || this._id
    return (
      <div id={id} className="faf-context">
        {this.props.children}
        <FAF key={id} id={id} />
      </div>
    )
  }
}
FAFContext = installStore(FAFContext, 'FAF', FAFStore)
export default FAFContext



