import React from 'react'
import {Style} from '@the-grid/styleverse'
import {installStore} from '@the-grid/cymatics'
import FANStore from './fan-store'
import FAN from '../fan'

let count = 0
class FANContext extends React.Component {
  static displayName = 'FANContext'
  _id = 'fan-context-' + count++
  componentWillMount() {
    this.store.willMount()
  }
  componentWillUnmount() {
    this.store.willUnmount()
  }
  shouldComponentUpdate() {
    return false
  }
  render() {
    let id = this.props.id || this._id
    return (
      <div id={id} className="fan-context">
        {this.props.children}
        <FAN key={id} id={id} />
      </div>
    )
  }
}
FANContext = installStore(FANContext, 'FAN', FANStore)
export default FANContext


