import React from 'react';
import {raf} from '@the-grid/deps'
import {Store} from '@the-grid/cymatics'
import SiteStruct from './site-struct'

let sectionIndex = -1

let getSectionId = (section) => {
  let id = section.itemsToRenderIds.length ? section.itemsToRenderIds[0] : ''
  return 'section-' + section.componentId + id
}

let initialState = new SiteStruct()

class SiteStore extends Store {

  // store config
  name = 'site'
  state = initialState
  deferCommit = false

  // store lifecycle
  handleContextStores({storyline}) {
    this.storyline = storyline
  }
  willMount(obj) {
    if (obj) this.request('merge', obj)
    //if (payload) this.request('set', 'payload', payload)
    if (this.storyline) this.storyline.request('connect', this.handleStoryline)
  }
  willGetProps(obj) {
    if (obj) this.request('merge', obj)
    //if (payload) this.request('set', 'payload', payload)
  }
  willUnmount() {
    if (this.storyline) this.storyline.request('disconnect', this.handleStoryline)
    this.storyline = undefined
  }

  // store implementation
  httpRequest = null
  storyline = undefined

  handleStoryline = (store) => {
    let currentAppPayload = this.state.get('appPayload')
    let newAppPayload = store.get('payload')
    if (newAppPayload && !store.get('payload').equals(currentAppPayload)) {
      this.store.request('set', 'appPayload', newAppPayload)
    }
  }

  onNextPageLoad = () => {
    //raf(()=>{
    this.execute('handle-next-page-load')
    //})
  }

  actions = {

    'getStore': (state) => {
      return state
    },

    'load-next-page': (state) => {
      let url = state.get('nextPageUrl')
      if (!url) return state
      if (url.indexOf('.html') === -1) {
        if (url.charAt(0) === '/') url = url.slice(1);
        if (url.indexOf('/') === url.length - 1) url = url.slice(0, -1);
        url = url + '/index.html';
      }

      this.httpRequest = new XMLHttpRequest();
      this.httpRequest.onreadystatechange = this.onNextPageLoad
      this.httpRequest.open('GET', url);
      this.httpRequest.send();

      return state
    },

    'handle-next-page-load': (state) => {
      let httpRequest = this.httpRequest;
      let newSections
      if (httpRequest.readyState === XMLHttpRequest.DONE) {
        if (httpRequest.status === 200) {
          let regex = new RegExp('<script id="pageReactInitStateScript">(.*?)</script>');
          let scriptContent = regex.exec(httpRequest.responseText);
          let initStateScript = scriptContent[1];

          initStateScript = initStateScript.substring("window.pageReactInitState ='".length, initStateScript.length);
          initStateScript = JSON.parse(initStateScript);

          // TODO - remove hack to inject pricing table. Inject thru Ed structured post.
          initStateScript.sections.push(
            {
              componentId: 'PricingTable',
              itemsToRenderIds: ['pricing-table'],
              contracts: {}
            }
          )

          state = state.set('payload', initStateScript)

        } else {
          // TODO - handle failing request
        }
      }

      return state
    },

    'open-article': (state, article) => {
      return state
    },

    'set-app-payload': (state, appPayload) => {
      let newState = state.set('appPayload', appPayload)
      return newState
    },

  }

}

export default SiteStore




