import {Struct, Map, List, fromJS} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
import Story from '../story'

var EMPTY_LIST = List()
var EMPTY_MAP = Map()

class StorylineStore extends Store {
  name = 'storyline'
  deferCommit = true

  StateStruct = Struct({
    data: {default: Map()},
    current: undefined,
    params: {default: EMPTY_MAP},
    path: {
      default: null,
      set(path) {
        if (!path) return {path: null}
        if (path.startsWith('.')) return {path}

        return {path: './' + path}
      },
    },
    pathNames: {
      cache: 1,
      deps: ['path'],
      get(path) {
        if (!path) return []
        return path.split('/')
      },
    },
    focusedName: {
      cache: 1,
      deps: ['pathNames'],
      get(pathNames) {
        return pathNames[pathNames.length - 1]
      },
    },
    storyIds: {
      cache: 1,
      deps: ['pathNames'],
      get(pathNames) {
        if (!pathNames.length) return []
        var ids = []
        for (var depth = 0; depth < pathNames.length; depth++) {
          ids.push(pathNames[depth] + '__' + depth)
        }
        return ids
      },
    },
    focusedStoryId: {
      cache: 1,
      deps: ['storyIds'],
      get(storyIds) {
        return storyIds[storyIds.length - 1]
      },
    },

    // TODO - TEMPORARY, just to switch sections
    _payload: undefined,
    payload: {
      get() {
        return this.get('_payload')
      },
      set(payload) {
        let that = this
        that = that.set('_payload', fromJS(payload))
        return that
      },
    },
  })
  initialState = new this.StateStruct()
  state = this.initialState

  shouldCommit(state) {
    return !!state.get('path')
  }

  willMount(component) {
    var stories = component.props.stories
    if (stories) this.registerStories(stories)
  }
  willUnmount() {
    this.state = this.initialState
  }

  registeredStories = {
    // default root story, override if necessary
    '.': Story,
  }
  storyPool = {}
  registerStory = (name, story) => {
    this.registeredStories[name] = story
  }
  registerStories = (stories) => {
    this.registeredStories = Object.assign(this.registeredStories, stories)
  }

  installStory = (name, component) => {
    var RegisteredStory = this.registeredStories[name]
    if (!RegisteredStory) throw new Error('App does not have registeredStories[name] for name = ' + name)

    // TODO: !!! POOL & RECYCLE !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    return new RegisteredStory()
  }

  mountedStoriesById = {}
  registerMountedStory = (id, story) => {
    this.mountedStoriesById[id] = story
  }
  unregisterMountedStory = (id) => {
    delete this.mountedStoriesById[id]
  }

  actions = {
    'push-story': (state, name, data) => {

      var path = state.get('path')
      if (path && path.endsWith('/' + name)) {
        return state
      }
      path = (!!path) ? path + '/' + name : name
      if (data) {
        return state.merge({
          path,
          params: state.get('params').merge({
            [name]: data,
          }),
        })
      }
      return state.set('path', path)
    },
    'add-story': (state, name, data) => {
      var path = state.get('path')
      if (path) {
        if (path.endsWith('/' + name)) return state
        if (path.includes('/' + name + '/')) return state
      }
      path = (!!path) ? path + '/' + name : name
      if (data) {
        return state.merge({
          path,
          params: state.get('params').merge({
            [name]: data,
          }),
        })
      }
      return state.set('path', path)
    },
    'pop-story': (state) => {
      var path = state.get('path').split('/')
      var name = path.pop()
      var params = state.get('params').delete(name)
      path = path.join('/')
      return state.merge({
        path,
        params,
      })
    },
    'replace-story': (state, name, data) => {
      return this.actions['push-story'](
        this.actions['pop-story'](state),
        name,
        data
      )
    },
    'from-story': (state, name, data) => {
      var path = state.get('path')
      if (path) {
        if (path.endsWith('/' + name)) return state
        if (path.includes('/' + name + '/')) return state
      }
      return this.actions['to-story'](state, name, data)
    },
    'to-story': (state, name, data) => {
      // pops all stories until story with name found
      // pushes new story at root if not found in path
      var focusedName = state.get('focusedName')

      if (focusedName) {
        if (focusedName === name) return state
        while (focusedName && focusedName !== '.' && focusedName !== name) {
          state = this.actions['pop-story'](state)
          focusedName = state.get('focusedName')
        }
      }
      return this.actions['push-story'](state, name, data)
    },
    'open': (state, path, params = EMPTY_MAP) => {
      return state.merge({
        path,
        params,
      })
    },
    'register-story': (state, name, Story) => {
      this.registerStory(name, Story)
      return state
    },

    // TODO: TEMPORARY
    'set-payload': (state, payload) => {
      let newState = state.set('payload', payload)
      return newState
    },
  }
}

export default StorylineStore



