import React from 'react'
import designable from '@the-grid/ui-designable'
import mapStyles from './styles'

var designConfig = {
  style: `
    :scope {
      bg-clr -- chroma-box
      h--100%
      w--100%
    }
  `,
  designverse: {
    'filter--gmap': {
      style: `
        :scope {
          chroma-filter--gradient-map
        }
      `
    },
  },
  sharedCSS: `
    /*
      9/16*100 = 56.25
      16/9*100 = 177.77
    */
  `
}

class Map extends React.Component {
  static displayName = 'Map'

  get_id() {
    if (this.props.id) return this.props.id
    return this._id
  }

  get_className() {
    let {className} = this.props
    if (!className) className = ''
    className += ' map component--map'
    return className
  }

  componentDidMount() {
    var lat = this.props.lat
    var lng = this.props.lng

    var mapOptions = {
        // How zoomed in you want the map to start at (always required)
        zoom: 11,
        // The latitude and longitude to center the map (always required)
        center: new google.maps.LatLng(lat, lng), // New York
        styles: mapStyles[this.props.mapStyle]
    }

    var map = new google.maps.Map(this.divmap, mapOptions)
    var marker = new google.maps.Marker({
        position: new google.maps.LatLng(lat, lng),
        map: map,
        title: 'Hotel name here'
    })

    google.maps.event.addDomListener(window, 'resize', function() {
      var center = map.getCenter()
      google.maps.event.trigger(map, 'resize')
      map.setCenter(center)
    })
  }

  render() {
    // getId('Header-bg')
    this.id = this.get_id()
    this.className = this.get_className()
    var style = this.props.style

    return (
      <div key={this.id} id={this.id} className={this.className} style={style} ref={ (divmap) => { this.divmap = divmap } }>
        {this.renderChildren()}
      </div>
    )
  }

  renderChildren() {
    return []
  }

}
export default designable(Map, designConfig)



