import {
  React,
  PropTypes,
} from '@the-grid/deps'
import {installStore} from '@the-grid/cymatics'
import PluginStore from './Store'

let mountCount = 0

class ChromaContext extends React.Component {
  static displayName = 'Styleverse_ChromaContext'

  static propTypes = {
    colors: PropTypes.any, // array of css strings, or List of same
    type: PropTypes.string,
    typeIndex: PropTypes.number,
    isRoot: PropTypes.bool,
    // renderCSS: PropTypes.bool,
    randomIfNeeded: PropTypes.bool,
  }

  static defaultProps = {
    // renderCSS: false,
    randomIfNeeded: false,
  }

  updateProps = (props) => {
    let {
      colors, // array of css strings, or List of same
      type,
      typeIndex,
      isRoot,
      randomIfNeeded,
      mountCount
    } = props
    let p = {}
    if (mountCount) p.mountCount = mountCount
    if (colors) p.colors = colors
    if (type) p.type = type
    if (typeIndex !== undefined) p.typeIndex = typeIndex
    if (isRoot !== undefined) p.isRoot = isRoot
    if (randomIfNeeded !== undefined) p.randomIfNeeded = randomIfNeeded
    this.store.execute('update-props', p)
  }

  componentWillMount() {
    mountCount++
    this.updateProps(Object.assign({mountCount}, this.props))
  }

  componentWillReceiveProps(nextProps) {
    this.updateProps(nextProps)
  }

  componentWillUnmount() {
    this.store.silently('teardown')
  }

  render() {
    return this.props.children
  }
}

ChromaContext = installStore(ChromaContext, 'styleverse-chroma', (componentInstance) => {
  return new PluginStore(componentInstance)
})

export default ChromaContext



