import React from 'react'
import {Style} from 'styleverse'


let shared = `
  .Ribbon {
    position: absolute;
    backface-visibility: hidden;
    transform: translate3d(0px,0px,0px);
    top: -2px;
    right: -2px;
    width: 8em;
    height: 8em;
    overflow: hidden;
    line-height: 1em;
  }
  .Ribbon-text {
    position: relative;
    padding: .75em 0;
    perspective: 1000;
    backface-visibility: hidden;
    transform: translate3d(0px,0px,0px) rotate(45deg);
    left: -1em;
    top: 2em;
    width: 12em;
    white-space: nowrap;
    transition: background-color 1s;
    text-align: center;
  }
  .Ribbon-text:before, .Ribbon-text:after {
    content: "";
    position: absolute;
    bottom: -2px;
  }
  .Ribbon-text:before {
    left: 0;
  }
  .Ribbon-text:after {
    right: 0;
  }​
`

function renderScoped(scope) {
  return (
    <Style key={"scoped-ribbon-style"} scope={scope}>{`
      :scope {

        fnt-sz -- txt-xs
        fnt -- txt2
        fnt-wght -- b

        .Ribbon-text {
          //txt-sh -- 0px 1px 0px chroma-selection-more-opaque
          box-sh -- 0px 0px 2px rgba(0,0,0,0.3)
          clr -- chroma-title
          bg-clr -- chroma-selection
        }
        .Ribbon-text:before, .Ribbon-text:after {
          bdr-t -- 2px solid chroma-box-pop
          bdr-l --  2px solid transparent
          bdr-r -- 2px solid transparent
        }
      }
    `}</Style>
  )
}

var ribbonStyle = {
  shared,
  renderScoped
}

export default ribbonStyle
export {
  shared,
  renderScoped
}

