import penner from './penner';
import bound from './bound';

export default function(i, i1, i2, o1, o2, pennerName='linear', boundName='extend') {
  let boundMethod = bound[boundName];
  let pennerMethod = penner[pennerName];
  if (!boundMethod) {
    throw new Error(`Polator: bound method ${boundName} not found`);
  }
  if (!pennerMethod) {
    throw new Error(`Polator: penner method ${pennerName} not found`);
  }
  return boundMethod(i, i1, i2, o1, o2, pennerMethod);
};


