import React from 'react'
import Component from '@the-grid/ui-component-deprecated'
import {Style} from '@the-grid/styleverse'
import Device from '../Device'

class DeviceIcons extends Component {
  static displayName = "DeviceIcons"

  render() {

    let {
      selectable
    } = this.props

    return (
      <div {...this.elProps({className: "device-icons"})}>
        <Style perf={true}>{`

          .device-icons {
            pos -- a
            l -- 1goldx
            r -- 1goldx
            b -- 0
            h -- 2goldy

            d -- flx
            flx-dir -- row
            flx-jst -- mid
            flx-aln -- mid
          }
          .device-icon {
            pos -- relative
            d -- flx
            flx-dir -- row
            flx-jst -- mid
            flx-aln -- mid

            h -- 100%

            fnt-sz -- txt-l
            clr -- chroma-dark-meta
            pad-l -- 1.5em
            pad-r -- 1.5em
            o--.88
          }
          .device-icon:hover {
            clr -- chroma-dark-text-pop2
          }

          .device-icon:after {
            content -- ''
            d --block
            pos -- absolute
            h -- 4
            w -- 100%
            b -- 0
          }
          .device-icon:hover:after {
            bg-clr -- chroma-dark-text
          }
          .device-icon.selected {
            clr -- chroma-dark-text-pop
            o--1
          }
          .device-icon.selected:after {
            bg-clr -- chroma-dark-box-pop2
          }

        `}</Style>
      {
        Device.types.map( (item) => {
          let type = item
          let iconText = (type === 'ipad')
            ? 'tablet_mac'
          : (type === 'macbook')
            ? 'laptop'
          : (type === 'full')
            ? 'zoom_out_map'
            : 'phone_iphone'

          let className = selectable.isItemSelected(item) ? 'selected' : ''
          className += ' device-icon'

          return (
            <a onClick={ selectable.itemToggler(item)} className={ className } key={type} >
              <i className="material-icons">{iconText}</i>
            </a>
          )

        })
      }
      </div>
    )

  }

}

let {PropTypes} = React

DeviceIcons.propTypes = {
  selectable: PropTypes.object.isRequired,
}

export default DeviceIcons


