import React from 'react'
import {Style} from '@the-grid/styleverse'

const _ = function (val, width) {
  val = Number(val)
  return val * width / 100
}

let deviceSpecs = {

  full: {
    ratio: 1 / 2,
    screenWidthScaler: 98,
    screenHeightScaler: (100 * 16 / 9) - 6,
  },

  macbook: {
    ratio: 100 / 69,
    screenWidthScaler: 95.56,
    screenHeightScaler: 59.55,
  },

  ipad: {
    ratio: 100 / 124,
    screenWidthScaler: 86,
    screenHeightScaler: 107,
  },

  iphone: {
    ratio: 100 / 205.2,
    screenWidthScaler: 95.56,
    screenHeightScaler: 159.64,
  },
}

const computeLayout = function (type, width, height) {

  let boundingRatio = width / height

  let {
    ratio,
    screenWidthScaler,
    screenHeightScaler,
  } = deviceSpecs[type]

  // if bounds are wider ratio than device's ratio, size by height
  if (boundingRatio > ratio) {
    width = ratio * height
  }
  // otherwise just size by width
  else {
    height = width / ratio
  }

  return {
    type,
    width,
    height,
    screenWidth: _(screenWidthScaler, width),
    screenHeight: _(screenHeightScaler, width),
  }

}

const renderStyle = function (component, {width, height, screenWidth, screenHeight, type}) {

  let {sel} = component

  //width = 100

  let p = {
    perf: true,
    key: 'styles-for-type--' + type,
    type,
    width,
    screenWidth,
    screenHeight,
    w: width + 'px',
    h: height + 'px',
    _: function (val) {
      return _(val, width) + 'px'
    },

  }

  let deviceStyle

  if (type === 'macbook') {
  //if (type) {
    deviceStyle = (<Style {...p}>{`

      ${sel('pca-hold')} {
        pos -- r
        w -- props.w
        h -- props.h
        mar -- 0 props._(12.5)
      }
      ${sel('pca-main')} {
        d -- block
        pos -- relative
        w -- props.w
        h -- props._(64)
        bdr-t-l-rad -- props._(3)
        bdr-t-r-rad -- props._(3)

        bg-clr -- chroma-inverse-box
        bdr -- props._(3) solid chroma-meta
        box-sh -- inset 0 0 1px black
      }

      // camera light
      ${sel('pca-main')}:after {
        content -- ''
        pos -- absolute
        t -- props._(-1.75)
        l -- 50%
        tf -- translate(-50%,0)
        w -- 3
        h -- 3
        bdr-rad -- 100%

        bg-clr -- chroma-selection
      }

      ${sel('pca-inner')} {
        pos -- r
        d -- block
        ovf -- auto
        w -- 100%
        h -- 100%

        box-sh -- 0 0 0 1px chroma-dark-meta
      }
      ${sel('pca-sub')} {
        d -- block
        txt-aln -- center
        pos -- relative
        h -- props._(3)
        tf -- translateY( props._(-1.375) )
      }

      // bottom of labtop
      ${sel('pca-top')} {
        pos -- absolute
        b -- 0
        l -- props._(-9)
        d -- block
        w -- props._(118)
        h -- props._(3)
        bdr-b-l-rad -- props._(11)
        bdr-b-r-rad -- props._(11)

        o -- 1
        z-idx -- 2
        bdr-t -- solid props._(.333333) white
        bg-clr -- chroma-meta
        box-sh -- 0 props._(1.5) props._(2) hsla(0,0%,0%,.3), inset 0 props._(2) props._(3.5) chroma-box
      }

      // bottom inset thingy
      ${sel('pca-bot')} {
        d -- block
        w -- props._(40)
        h -- props._(1)
        pos -- absolute
        t -- 0
        l -- 50%

        bdr-rad -- 0 0 props._(45) props._(45)
        z-idx -- 2
        tf -- translate(-50%,0)

        bg-clr -- chroma-meta
        box-sh -- inset 0 props._(.5) props._(2) chroma-line
        bdr-b -- solid 1px white
        o -- 1
      }


    `}</Style>)
  }

  if (type === 'ipad') {
    deviceStyle = (<Style {...p}>{`

      ${sel('pca-hold')} {
        pos -- r
        w -- props.w
        h -- props.h
      }
      ${sel('pca-main')} {
        d -- b
        pos -- r
        w -- props.w
        h -- props.h

        bdr-rad -- props._(5)
        bdr   -- props._(7) solid
        bdr-b -- props._(10) solid

        bdr-clr -- chroma-box
        bg-clr -- chroma-meta
        box-sh -- 0 0 0 props._(.5) white, 0 8px 21px hsla(0,0%,0%,.3), inset 0 0 1px chroma-dark-meta
      }

      //
      ${sel('pca-main')}:after {
        content -- ''
        pos -- a
        t -- props._(-4)
        l -- 50%
        tf -- translate(-50%,0)
        w -- props._(1.1)
        h -- props._(1.1)

        bdr-rad -- 100%

        bg-clr -- chroma-selection
      }

      ${sel('pca-inner')} {
        pos -- r
        d -- block
        ovf -- auto
        w -- 100%
        h -- 100%

        box-sh -- 0 0 0 1px white
      }
      ${sel('pca-sub')} {
        d -- block
        txt-aln -- center
        pos -- absolute
        h -- 0
        l -- 0
        r -- 0
        b -- 0
      }

      // home button
      ${sel('pca-sub')} ${sel('pca-bot')} {
        d -- block
        bdr-rad -- 100%
        w -- props._(6)
        h -- props._(6)
        mar -- props._(-2) auto 0 auto
        bg-clr -- transparent
        z-idx -- 2
        tf -- translate(0,-100%)

        box-sh -- 0 0 0 props._(1) chroma-box-pop
      }
      ${sel('pca-hold')}:hover ${sel('pca-bot')} {
        box-sh -- 0 0 0 props._(.9) chroma-box-pop
        bg-clr -- white
      }

    `}</Style>)
  }



  if (type === 'full') {
    deviceStyle = (<Style {...p}>{`

      ${sel('pca-hold')} {
        pos -- r
        w -- props.w
        h -- props.h
      }
      ${sel('pca-main')} {
        d -- b
        pos -- r
        w -- props.w
        h -- props.h

        bdr-rad -- props._(2)
        bdr-t   -- props._(4) solid
        bdr-b -- props._(2) solid

        bdr-clr -- chroma-box
        bg-clr -- chroma-meta
        box-sh -- 0 0 0 props._(.5) white, 0 8px 21px hsla(0,0%,0%,.3), inset 0 0 1px chroma-dark-meta
      }

      // light
      ${sel('pca-main')}:after {
        content -- ''
        pos -- a
        t -- props._(-2)
        l -- props._(2)
        tf -- translate(0,-50%)
        w -- props._(1.1)
        h -- props._(1.1)

        bdr-rad -- 100%

        bg-clr -- chroma-box-pop
      }

      ${sel('pca-main')}:before {
        content -- ''
        pos -- a
        t -- props._(-2)
        l -- props._(5)
        tf -- translate(0,-50%)
        w -- props._(1.1)
        h -- props._(1.1)

        bdr-rad -- 100%

        bg-clr -- chroma-box-pop2
      }

      ${sel('pca-inner')} {
        pos -- r
        d -- block
        ovf -- auto
        w -- 100%
        h -- 100%

        box-sh -- 0 0 0 1px white
      }
      ${sel('pca-sub')} {
        d -- block
        txt-aln -- center
        pos -- absolute
        h -- 0
        l -- 0
        r -- 0
        b -- 0
      }

      // home button
      ${sel('pca-sub')} ${sel('pca-bot')} {
        d -- block
        bdr-rad -- 100%
        w -- props._(6)
        h -- props._(6)
        mar -- props._(-2) auto 0 auto
        bg-clr -- transparent
        z-idx -- 2
        tf -- translate(0,-100%)

        box-sh -- 0 0 0 props._(1) chroma-box-pop

        o--0
      }
      ${sel('pca-hold')}:hover ${sel('pca-bot')} {
        box-sh -- 0 0 0 props._(.9) chroma-box-pop
        bg-clr -- white
      }

    `}</Style>)
  }


  if (type === 'iphone') {
    deviceStyle = (<Style {...p}>{`

      ${sel('pca-hold')} {
        pos -- r
        w -- props.w
        h -- props._(205.2)
      }
      ${sel('pca-main')} {
        d -- b
        pos -- r
        w -- props.w
        h -- props._(205.2)

        bdr-rad -- props._(11.3)
        bdr   -- props._(3) solid
        bdr-t -- props._(23) solid
        bdr-b -- props._(23.6) solid

        bdr-clr -- chroma-box
        bg-clr -- chroma-meta
        box-sh -- 0 0 0 props._(.5) white, 0 8px 21px hsla(0,0%,0%,.3), inset 0 0 1px chroma-dark-meta
      }

      // iphone camera hole
      ${sel('pca-main')}:after {
        content -- ''
        pos -- a
        t -- props._(-13)
        l -- 50%
        tf -- translate(-50%,0)
        w -- props._(3)
        h -- props._(3)

        bdr-rad -- 100%
        tf -- translate( props._(-18) ,0)

        bg-clr -- chroma-selection
      }


      // iphone speaker line
      ${sel('pca-main')}:before {
        content -- ""
        pos -- absolute
        t -- props._(-5)
        l -- 50%
        o -- 1
        w -- props._(20)
        h -- props._(1)
        tf -- translate( props._(-9) , props._(-7.5) )
        bdr-rad -- props._(1)

        bg-clr -- chroma-line
      }
      ${sel('pca-inner')} {
        pos -- r
        d -- block
        ovf -- auto
        w -- 100%
        h -- 100%

        box-sh -- 0 0 0 1px white
      }
      ${sel('pca-sub')} {
        d -- block
        txt-aln -- center
        pos -- absolute
        h -- 0
        l -- 0
        r -- 0
        b -- 0
      }

      // home button
      ${sel('pca-bot')} {
        d -- block
        bdr-rad -- 100%
        w -- props._(14)
        h -- props._(14)
        mar -- 0 auto
        bg-clr -- transparent
        z-idx -- 2
        tf -- translate(0, props._(-18.8) )

        box-sh -- 0 0 0 props._(1.1) chroma-box-pop
      }
      ${sel('pca-hold')}:hover ${sel('pca-bot')} {
        box-sh -- 0 0 0 props._(1) chroma-box-pop
        bg-clr -- white
      }

    `}</Style>)
  }

  return (

    <span>
      {deviceStyle}
      <Style perf={true}>{`
        ${sel()} {
          pos -- a
          w -- 100%
          h -- 100%
          d -- flx
          flx-aln -- mid
          flx-jst -- mid
        }
        ${sel('pca-hold')} {
          trans -- all .3s ease-out
        }
        ${sel('pca-main')} {
          trans -- all .3s ease-out
        }
        ${sel('pca-main')}:after {
          trans -- all .3s ease-out
        }
        // iphone speaker line
        ${sel('pca-main')}:before {
          trans -- all .3s ease-out
        }
        ${sel('pca-inner')} {
          trans -- all .3s ease-out
        }
        ${sel('pca-sub')} {
          trans -- all .3s ease-out
        }
        // home button
        ${sel('pca-sub')} ${sel('pca-bot')} {
          trans -- all .3s ease-out
        }
      `}</Style>
    </span>

  )

}

export {computeLayout, renderStyle}


