import React from 'inferno-compat'
import Styleverse from '@the-grid/styleverse'
//import {LayoutverseRoot} from '@the-grid/layoutverse'
import {SpringsContext} from '@the-grid/ui-sprung'
import FAB from '@the-grid/ui-fab'
import FAF from '@the-grid/ui-faf'
import FAN from '@the-grid/ui-fan'
import {Storyline} from '@the-grid/ui-story-machine'
import stories from '../stories'

class App extends React.Component {
  shouldComponentUpdate() {return false}
  render() {
    var {children, styleContext} = this.props
    return (
      <SpringsContext key="springs" id="root-springs">
        <Styleverse key="styleverse" styleContext={styleContext}>
          <FAF key="faf"><FAB key="fab"><FAN key="fan">
            <Storyline key="storyline" stories={stories}>
              {children}
            </Storyline>
          </FAN></FAB></FAF>
        </Styleverse>
      </SpringsContext>
    )
  }
}
export default App



