import React from 'react'
import {palettes,raf} from '@the-grid/deps'
import {Style, StyleContext} from '@the-grid/styleverse'
import {connectState} from '@the-grid/cymatics'
function NOOP(){}
var shownSheet = (
  <Style id="popup-shown-styles" key="popup-shown-styles">{`
    html, body {
      overflow -- hidden !important

    }

  `}</Style>
)

var sheet = (

  <Style id="popup-styles" key="popup-styles">{`
    .story-popup {
      &.shown {
        o -- 1 !important
        viz -- visible !important
        tf -- translateY(0) !important
        z-index -- 800
      }
      &:not(.shown) {
        o -- 0 !important
        viz -- hidden !important
        tf -- translateY(-3em)
        z-index -- -800
      }
      bf-viz -- hidden
      will-change -- transform, opacity
      transition -- transform .3s, opacity .3s


      //viz -- hidden
      pos -- fixed
      l -- 0
      t -- 0
      b -- 0
      r -- 0
      overflow-y -- scroll


      txt-aln -- left
      fnt -- txt
      clr -- chroma-text2
      h2 {
        clr -- chroma-title
        fnt -- d2
      }


      .bg {
        pos -- a
        l -- 0
        t -- 0
        b -- 0
        r -- 0
        bg-clr -- chroma-dark-bg
        o -- .8
      }
      .content {
        > * {
          mar-b -- 1em
        }
        pos -- a
        l -- 0
        t -- 0
        min-h -- 100vh
        r -- 0
        box-sh -- m
        bg-clr -- chroma-box
        mar-l -- 1goldx
        mar-r -- 2goldx
        mar-t -- 1goldy
        min-h -- 2goldy
        pad-l -- .5goldx
        pad-r -- .75goldx
        pad-t -- .5goldy
        pad-b -- .75goldy
      }

    }
  `}</Style>

)

class Popup extends React.Component {
  static displayName = 'ColorPanel'
  static defaultProps = {
    children: null,
  }
  hasShown = false
  state = {shown: false, storylinePath: ''}
  componentDidMount() {
    var that = this
    setTimeout(function(){
      raf(function(){
        that.setState({shown: true},NOOP)
      })
    }, 180)
    // this.el.addEventListener('mouseleave', this.finish)
  }
  finish = () => {
    // this.el.removeEventListener('mouseleave', this.finish)
  }
  componentWillUnmount() {
    //this.el.removeEventListener('mouseleave', this.finish)
  }

  render() {
    // console.log("///////")
    var props = this.props
    let className = "story-popup "
    let isShown
    if (!this.hasShown) {
      isShown = this.state.shown
      if (isShown) this.hasShown = true
    } else {
      isShown = this.state.storylinePath.endsWith('/on-the-grid')
    }
    if (isShown) className += 'shown'
    return (
      <div>
        {this.props.children}
      <div style={{opacity:0, transform: 'translateY(-3em)'}} className={className} ref={(el) => {
        if (this.el) return
        this.el = el
      }}>
        {sheet}
        {isShown ? shownSheet : null}
        <div className="bg"></div>
        <div className="content">
          <h2>Welcome to an exclusive sneak peak of v3!</h2>
          <p>{`The website is the app is the website. On v3, this is how you will orchestrate your beautiful slices of the Internet in real-time.`}</p>
        <p>{`Click around, tweak the colors of the site, though you'll lack many permissions, you'll get taste of what's ahead & maybe even find a goose egg. With your support we will be rolling this out, first to Lifetime Members, then to all. Stay tuned, more juicy details coming soon...`}</p>
        </div>

      </div>
      </div>
    )
  }
}

export default connectState( Popup, ['storyline.path'], (storylinePath) => {
  return {storylinePath}
})



