if (DEBUG) {
  let LOG = (that, ...messages) => {
    messages = [
      '%c %s',
      'background: hsl(190,60%,70%); color: hsl(0,00%,0%)',
      `stores/virtualizer ${that.instance_index}`
    ].concat(messages).concat([
      JSON.stringify({
        count: that.state.get('count'),
        width: that.state.get('width'),
        _measuredHeightsByIndex: that._measuredHeightsByIndex,
        _measuredTopsByIndex: that._measuredTopsByIndex,
        _indexAtPositionCache: that._indexAtPositionCache,
        layout: that.state.get('layout')
      })
    ])
    console.log(...messages)
  }
  class RowVirtualizer_DEBUG extends RowVirtualizer {
    constructor() {
      super()
      for (let i=0;i<LOG_METHODS.length; i++) {
        let name = LOG_METHODS[i]
        let method = this[name]
        this[name] = (...args) => {
          if (PERF_METHODS.includes(name)) console.time(`${name}(${args})`)
          //LOG(this,`${name}(`,...args,')')
          let val = method.apply(this,args)
          LOG(this,`${name}(`,...args,')', val)
          //LOG(this,`${name}()`, val)
          if (PERF_METHODS.includes(name)) console.timeEnd(`${name}(${args})`)
          return val
        }
      }
    }
  }

  RowVirtualizer = RowVirtualizer_DEBUG
}

export default RowVirtualizer
