import React from 'react'
import Icon from '@the-grid/ui-icon'

var el = React.createElement

export default function(_props) {
  // TODO!!!!!!
  var {className, label, icon, ...props} = _props
  className += ' cta'
  let children = []
  if (icon) children.push( el(Icon, {key: 'icon'}, icon) )
  if (label) children.push(el('span', {key: 'label', className: 'label'}, label))
  return el('button', {...props, className}, children)
}


