import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
let id = 'action-list-static-style'
export default (
  <Style key={id} id={id} fabSize={60 + 'px'} linkHeight={90 + 'px'} >
    {`
      #fab {
        .action-list {
          pos -- absolute
          min-w -- props.fabSize
          t -- 0
        }
        .action-link {
          fnt -- d2
          fnt-sz -- props.fabSize
          ln-h -- props.linkHeight
          h -- props.linkHeight
          whs -- nw
          pos -- absolute
          t -- 0
          l -- 0
          tf-origin -- 0% 50%
          txt-aln -- left
        }
      }
    `}
  </Style>
)


