import React from 'react'
import DesignBody from './design-body'
import DesignResizer from './design-resizer'
import {palettes, raf} from '@the-grid/deps'
import {fromJS, Map} from '@the-grid/immutable'
import {Story} from '@the-grid/ui-story-machine'

class Design extends Story {
  storyName = 'design'
  state = Map({
    resizeState: 'zoom_out'
  })
  willMount() {
    if (this.focused) return
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'update', this.UI.FAB)
    this.contextRequest('FAF', 'update', this.UI.FAF)
    this.contextRequest('FAN', 'update', this.UI.FAN)
  }
  display(children) {
    return React.createElement(DesignResizer, {key: 'designResizer'}, children)
  }
  UI = {
    FAB: fromJS({
      show: true,
      active: true,
      canDeactivate: true,
      //icon: '+',
      //active: true,
      activeIcon: 'x',
      icon: '+',
      onClick: () => {
        let that = this
        raf(function(){
          that.contextRequest('storyline', 'to-story', 'view-site')
        })
        return false
      },
      appButton: {
        icon: 'arrow_back',
        show: true,
        //activeIcon: 'arrow_back',
        onClick: () => {
          let that = this
          raf(function(){
            that.contextRequest('storyline', 'pop-story')
          })
          return false
        },
      },

      //activeIcon: 'arrow_back',
      //onClick: () => {
      //  raf(()=> {
      //    this.req('app','start-story','on-the-grid')
      //  })
      //},
      chroma: {type: 'dark', typeIndex: 1},
      //appButton: presets.appButton.viewSite.call(this),
      actionList: [
        {
          label: 'Color',
          //icon: 'color_lens',
          onClick: () => {
            //this.req('styleverse', 'merge', {styleContext: {chroma: {colors: palettes.getRandom(), type: 'natural'}}})
            setTimeout(()=>{
              // this.req('AppBody', 'add-render', 'right-panel', function() {
              //   return <ColorPanel key="color-panel" />
              // })
              this.contextRequest('storyline', 'push-story', 'color')

            },1)
          }
        },
        {
          label: 'Typography',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to Typography or switch to an account with permisssion.`)
          }
        },
        {
          label: 'Layout',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to Layout or switch to an account with permisssion.`)
            //this.contextRequest('storyline', 'set-payload', {sectionRandomIndex: Math.random()})
            // this.actions['set-payload'](this.state, {sectionRandomIndex: Math.random()})
          }
        },
      ]
    }),
    FAF: fromJS({
      show: false,
      //hideOnScroll: true,
      chroma: {type: 'light', typeIndex: 1},
      //active: 2,
      actionList: [
        {
          //label: 'newsfeed',
          icon: 'weekend',
          key: 'newsfeed',
          onClick: () => {
            alert('show newsfeed')
          }
        },

        {
          //label: 'sites',
          icon: 'apps',
          key: 'sites',
          onClick: () => {
            this.contextRequest('storyline', 'replace-story','sites')
          }
        },

      ],
      actionList2: [
        {
          src: 'https://s.gravatar.com/avatar/631d7f473dcf141a6a809de74f776259?d=https%3A%2F%2Fpassport.thegrid.io%2Fprofile-fallback.png',
          key: 'account',
          willActivate: () => {
            alert('show account')
          }
        },
        {
         label: '$500',
         key: 'wallet',
         willActivate: () => {
           alert('show wallet')
         }
        },
      ],
    }),
    FAN: fromJS({
      hideOnScroll: false,
      className: 'align--c',
      show: true,
      chroma: {type: 'dark', typeIndex: 1},
      active: 0,
      actionList: [
        //{
        //  icon: 'arrow_back',
        //  willActivate: () => {
        //    raf(()=> {
        //      this.req('app','start-story','on-the-grid')
        //    })
        //  },
        //},
          {
            icon: 'zoom_out',
            willActivate: () => {
              this.execute('set', 'resizeState', 'zoom_out')
            }
          },
        {
          icon: 'phone_iphone',
          willActivate: () => {
            this.execute('set', 'resizeState', 'iphone')
          }
        },
        {
          icon: 'tablet_mac',
          willActivate: () => {
            this.execute('set', 'resizeState', 'ipad')
          }
        },
        {
          icon: 'laptop',
          willActivate: () => {
            this.execute('set', 'resizeState', 'macbook')
          }
        },


        //{
        //  icon: 'zoom_out',
        //  willActivate: () => {
        //    var key = 'design/zoom_browser'
        //    this.AppBody.request('merge',{key, render: () => {
        //      return React.createElement(DesignBody, {key:'DesignBody', deviceType: 'full'})
        //    }})
        //  }
        //}
      ],
      //actionList2: [
      //  {
      //    icon: 'color_lens',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //  {
      //    icon: 'shuffle',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //  {
      //    icon: 'settings',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //],
    })
  }
}

export default Design


