const valParser = function(key, val) {
  // {n}goldx{div?}

  if (val.indexOf('layout.') === 0) {
    // TODO: need @Peter! ... temporary hack for executing prop functions until we get proper parser
    return {
      deps: ['styleStore.' + val],
      get(propVal) {
        if (typeof propVal === 'number') return propVal + 'px'
        return propVal
      },
    }
  }



  // {n}goldx{div?}
  if (val.includes('goldx')) {
    let parts = val.split('goldx')
    return {
      deps: ['styleStore.layout.goldX'],
      get(goldX) {
        return goldX(Number(parts[0]), Number(parts[1])) + 'px'
      },
    }
  }
  // {n}goldy{div?}
  if (val.includes('goldy')) {
    let parts = val.split('goldy')
    return {
      deps: ['styleStore.layout.goldY'],
      get(goldY) {
        return goldY(Number(parts[0]), Number(parts[1])) + 'px'
      },
    }
  }

  let test
  test = 'vw'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      // let dep = 'styleStore.layout.'+test
      return {
        deps: ['styleStore.layout.' + test],
        get(f) {
          return f(n, divs) + 'px'
        },
      }
    }
    return undefined
  }

  test = 'vh'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      return {
        deps: ['styleStore.layout.' + test],
        get(func) {
          return func(n, divs) + 'px'
        },
      }
    }
    return undefined
  }

  test = 'nw'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      return {
        deps: ['styleStore.layout.' + test],
        get(func) {
          return func(n, divs) + 'px'
        },
      }
    }
    return undefined
  }

  test = 'nh'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      return {
        deps: ['styleStore.layout.' + test],
        get(func) {
          return func(n, divs) + 'px'
        },
      }
    }
    return undefined
  }

  test = 'pw'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      return {
        deps: ['styleStore.layout.' + test],
        get(func) {
          return func(n, divs) + 'px'
        },
      }
    }
    return undefined
  }

  test = 'ph'
  if (val.includes(test)) {
    let [n,divs] = val.split(test)
    let match = false
    n = Number(n)
    divs = !divs ? 100 : Number(divs)
    if (!isNaN(n) && !isNaN(divs)) {
      return {
        deps: ['styleStore.layout.' + test],
        get(func) {
          return func(n, divs) + 'px'
        },
      }
    }
    return undefined
  }


  // layout-...
  if (val.indexOf('layout-') === 0) {
    return {
      deps: ['styleStore.layout'],
      get(layout) {
        let layoutVal = layout.get(val.substr('layout-'.length))
        if (layoutVal === undefined) throw new Error('Styleverse Layout valPlugin can\'t recognize val: ' + val)
        return layoutVal + 'px'
      },
    }
  }

  return undefined
}

export default valParser



