import React, {PropTypes} from 'react'
import {linkEvent} from 'inferno'
import {StyleContext} from '@the-grid/styleverse'
import FABChromaStyle from './fab-chroma-style'
import FABStaticStyle from './fab-static-style'
import ActionList from '../action-list'
import SprungStyle from '../theme/spring/fab-sprung-style'
//import GooeyStyle from '../theme/gooey'
import {onClick, onMouseUp, onMouseDown_0} from '../helpers/events'
// import GooeyStyle, {gooeyStyleGenerator, gooeyStyleGenerator2} from '../theme/gooey'
// import ReactTooltip from 'react-hint'
// import ReactTooltip from '../helpers/hint'

var renderedSprungStyle
function renderSprungStyle() {
  if (!renderedSprungStyle) renderedSprungStyle = <SprungStyle key="sprung-style" />
  return renderedSprungStyle
}
function renderThemeStyle(theme) {
  // if (theme === 'gooey') return (state) => gooeyStyleGenerator(state)
  // if (theme === 'gooey2') return (state) => gooeyStyleGenerator2(state)
  // if (theme === 'gooey2') return (state) => {
  //   debugger
  // }
  //if (theme === 'gooey' || theme === 'gooey2') return GooeyStyle
  return renderSprungStyle()
}

// import IconLineMorpher from '../icon-line-morpher'
import ActionButton from '../action-button'

let mountCount = 0

class FAB extends React.Component {
  static displayName = 'FAB'
  static contextTypes = {
    request: PropTypes.func.isRequired,
  }
  /*
  static propTypes = {
    name: string
    icon: PropTypes.string,
    label: PropTypes.string,
    chromaType: PropTypes.string,
    actionList: Immutable.PropTypes.list.isRequired,
    onClick: PropTypes.func,
    active: PropTypes.bool.isRequired,
    show: PropTypes.bool.isRequired,
    pop: PropTypes.number.isRequired,
  }
  */
  state = {
    mouseDown: false,
    FABState: undefined,
  }
  prevMouseDownIndex = -1
  mouseDownIndex = -1
  hasShown = false
  // onLeave = (targetIndex) => {
  //  this.mouseDownIndex = targetIndex
  //  this.setState({mouseDown: true})
  // }
  // captureClick = (e) => {
  //  e.stopPropagation()
  // }
  get(key, fallback) {
    let FABState = this.state.FABState
    if (!FABState) return fallback
    let val = FABState.get(key, fallback)
    return val
  }
  updateFABState = (store) => {
    let FABState = store.state
    let prev = this.state.FABState
    // if (prev) {
    //  if (FABState.equals(prev)) return
    // }
    this.setState({FABState}, _noop)
  }
  mountIndex = -1
  componentWillMount() {
    this.mountIndex = mountCount
    mountCount++
    // TODO: auto unmount lower index?
    if (mountCount > 1) throw new Error('Only one FAB can be mounted at a time')
    this.hasShown = false
    this.context.request('FAB', 'connect', this.updateFABState)
    var that = this
    this.onMouseUp = function() {
      onMouseUp(that)
    }
    document.addEventListener('mouseup',this.onMouseUp)
  }
  componentWillUnmount() {
    mountCount--
    this.hasShown = false
    this.context.request('FAB', 'disconnect', this.updateFABState)
    document.removeEventListener('mouseup',this.onMouseUp)
    this.onMouseUp = undefined
    // if (this.showTimeout) clearTimeout(this.showTimeout)
  }
  componentDidMount() { this.didRender() }
  componentDidUpdate() { this.didRender() }
  didRender() {
    this.prevMouseDownIndex = -1
  }

  getDestructure() {
    var show = this.get('show')

    // @NOTE: any reason not to use the var ^?
    if (this.get('show')) this.hasShown = true

    var active = this.get('active')
    var chroma = this.get('chroma')
    var pop = this.get('pop')

    // var shape = this.get('shape')
    // var showLabel = this.get('showLabel')
    var labelAsTip = this.get('labelAsTip')
    var loading = this.get('loading')

    return {show, active, chroma, pop, loading, labelAsTip}
  }

  getClassName({active, loading, pop}) {
    let className = this.props.className || ''
    className += ` pop-` + pop
    if (active) className += ' is-active'
    if (loading) className += ' loading'

    return className
  }

  renderTipIfNeeded(labelAsTip, children) {
    // if (labelAsTip) {
    //   children.push(<ReactTooltip />)
    //   // children.push(styles)
    // }
  }

  render() {
    let {
      mouseDownIndex,
      prevMouseDownIndex,
    } = this

    let id = 'fab'
    let scope = '#' + id

    var {
      show,
      active,
      chroma,
      pop,
      loading,
      labelAsTip,
    } = this.getDestructure()
    var className = this.getClassName({show, active, chroma, pop, loading}) + ' ' + this.get('className')

    let children
    if (!this.hasShown) children = null
    else {
      children = [
        renderThemeStyle(),
        FABChromaStyle,
        FABStaticStyle,
        this.renderOverlay(id),
        this.renderActionList(id, active, show),
        this.renderActionButton(id, active),
      ]
      this.renderTipIfNeeded(labelAsTip, children)
    }
    return <StyleContext chroma={chroma}><div id={id} className={className} style={{visibility: 'hidden'}}>{children}</div></StyleContext>
  }

  renderActionButton(id, active) {
    let icon = (active) ? this.get('activeIcon') : this.get('icon')
    let label = this.get('label')
    return (
      <ActionButton key="FAB"
        id={ id + '_action-button' }
        active={ active }
        className="FAB_action-button"
        icon={ icon }
        label={ label }
        onClick={ linkEvent(this, onClick) }
        onMouseDown={ linkEvent(this, onMouseDown_0) }
       />
    )
  }
  renderOverlay(id) {
    let overlayProps = this.get('overlayProps')
    // if (!overlayProps) return null
    // return null
    return (
      <div key="overlay" id={id + '-active-bg-mask'}>
        <div id={id + '-active-bg'} onClick={linkEvent(this, onClick)} />
      </div>
    )
  }
  renderActionList(id, active, show) {
    let actionList = this.get('actionList')
    if (!actionList || actionList.size <= 1) return null
    let actionListProps = {
      key: 'action-list',
      id: id + '-action-list',
      list: actionList,
      show,
      active,
      //type, // TODO
    }
    return (
      <ActionList {...actionListProps} />
    )
  }
}

export default FAB



