import React from 'react'
import gooeySvg from './gooey-svg'
// import gooeySvg1 from './gooey-svg'
// import gooeySvg2 from './gooey-svg-2'
import gooeyStyleGenerator from './gooey-list'
import gooeyStyleGenerator2 from './gooey-list-2'


// var gooeySvg = (<span>{gooeySvg1}{gooeySvg2}</span>)
var gooeyFilter = gooeySvg
export {gooeySvg, gooeyStyleGenerator, gooeyStyleGenerator2}

// var styles = gooeyStyleGenerator()
// var styledStyles = styles.split('\n')
// console.error({gooeySvg, styles, gooeyStyleGenerator})
// console.debug({styledStyles})
// <pre><code>{styles}</code></pre>
export default (state) => {
  var styles = gooeyStyleGenerator(state)
  if (di.browserIsLowPerf) gooeyFilter = null

  // + Math.random(0, 500)
  return (
    <span>
      <style key={'fab-gooey'}>{styles}</style>
      {gooeyFilter}
    </span>
  )
}



