import React from 'react'

if (!G) var G = {debug: false}

let count = 0

let maxRecursions = 2

// let globalIgnoredDiffPaths = ['props.routes', 'props.location', 'props.route', 'props.children', 'props.perf']
let globalIgnoredDiffPaths =  ['props.routes', 'props.route', 'props.children', 'props.perf', 'props.debug']
// let globalIgnoredDiffPaths = []

let setStateHmmmm = function(partialState, callback) {
  this.updater.enqueueSetState(this, partialState)
  if (callback) {
    this.updater.enqueueCallback(this, callback, 'setState')
  }
}

class Component extends React.Component {

  constructor(...args) {

    super(...args)

    // id selector helpers
    this._instanceCount = count
    count++

    let prefix = this.constructor.displayName
    if (!prefix) prefix = 'Component'
    prefix += '-'

    this.id = prefix + this._instanceCount
    this.getId = (postfix) => {
      let id = this.props.id || this.id
      if (postfix) return id + '-' + postfix
      return id
    }
    // if (!this.$) {
    //   this.$ = (postfix) => {
    //      return '#' + this.getId(postfix)
    //    }
    // }
    this.sel = (postfix) => {
      return '#' + this.getId(postfix)
    }

    // debugging
    this.debug = false

    this._log = (...messages) => {
      const name = this.constructor.displayName || this.constructor.name
      messages = [`DEBUG #${this._instanceCount - 1}/${count} x${this.renderCount} ${name}`].concat(messages)
      if ( messages[messages.length - 1].css ) {
        messages = ['%c %s', messages.pop().css].concat(messages)
      }
      messages.push({id: this.id})
      /*eslint-disable */
      console.log(...messages)
      /*eslint-enable */
    }
    this._warn = (...messages) => {
      /*eslint-disable */
      console.warn(...[`DEBUG ${this.constructor.name} ::`].concat(messages))
      /*eslint-enable */
    }

    this.perfLog = (...messages) => {
      // messages.push({props, nextProps, state:this.state, nextState})
      messages.push({css: 'background: hsl(0,50%,50%); color: hsl(0,00%,100%)'})
      messages = ['shouldComponentUpdate()'].concat(messages)
      this._log(...messages)
    }

    // setTimeout(()=>{
    //  this._log('INIT')
    // },1)

    // perf ignore diffPaths
    // this.perfIgnoredPaths = ['props...']


    // perf flags

    this.FORCE_UPDATE = false
    this.perfCheckState = false
    this.perfCheckFunctions = false
    this.perfIgnorePropChanges = false


    // wrap setState

    let setState = this.setState
    this.setState = (...args) => {

      // if not checking state, force rerender on setState
      if (!this.perfCheckState) {
        this.FORCE_UPDATE = true
        return setState.apply(this,args)
      }

      let [newState] = args
      let oldState = this.state
      let keys = Object.keys(newState)

      let log = undefined
      if (this.debug || G.debug) {
        log = this.perfLog
      }

      // check if new (differing) state is actually set
      for (let i = 0; i < keys.length; i++) {
        let key = keys[i]
        let newVal = newState[key]
        let oldVal = oldState[key]
        let stateValsDoDiffer = this.doDiffer(newVal, oldVal, 0, 'state.' + key, log)
        if (stateValsDoDiffer) {
          if (log) this._log('FORCE_UPDATE: setState {' + key + '} VALS DO DIFFER **/')
          this.FORCE_UPDATE = true
          return setStateHmmmm.apply(this,args)
        }
      }

      // if stateVals do not differ, do nothing :)
      return
    }

    // let componentWillMount = this.componentWillMount
    // this.componentWillMount = (...args) => {
    //  this.FORCE_UPDATE = false
    //  return componentWillMount.apply(this,args)
    // }
    // let componentWillReceiveProps = this.componentWillReceiveProps
    // this.componentWillReceiveProps = (...args) => {
    //  this.FORCE_UPDATE = false
    //  return componentWillReceiveProps.apply(this,args)
    // }


    // perf - renderCount
    this.renderCount = 0
    let render = this.render
    this.render = (...args) => {
      this.FORCE_UPDATE = false
      this.renderCount++
      return render.apply(this,args)
    }
  }

  render() {
    return this.props.children
  }

  elProps(obj = {}) {
    if (obj.id) throw new Error('elProps({id}), id is already provided automatically in ' + this)
    let rootProps = Object.assign({id: this.getId()}, obj)
    let className = obj.className || ''
    className += ' '
    className += this.props.className || ''
    let style = Object.assign({}, obj.style, this.props.style)
    if (className !== ' ') rootProps.className = className
    if (Object.keys(style).length !== 0) rootProps.style = style

    if (this.props.onClick) {
      if (rootProps.onClick) throw new Error(this.constructor.name + '::elProps() onClick is being passed down through props!')
      rootProps.onClick = this.props.onClick
    }
    return rootProps
  }

  doDiffer(prev, next, recursions = 0, path = '', log) {

    if (recursions > maxRecursions) {
      if (log) log(`!IMMUTABLE & RECURSIONS > ${maxRecursions}`, {path})
      return false
    }

    if (this.constructor.perfProps && path.indexOf('props.') === 0) {
      if (!this.constructor.perfProps.includes( path.split('.')[1] )) {
        if (log) log('IGNORED: NOT IN constructor.perfProps', {path: path, prev: prev, next: next})
        return false
      }
      // else check if it differs!
    }
    else if (globalIgnoredDiffPaths.includes(path)) {
      if (log) log('IGNORED', {path: path, prev: prev, next: next})
      return false
    }

    if (this.perfIgnoredPaths) {
      if (this.perfIgnoredPaths.includes(path)) return false
    }

    let nextType = typeof next
    if (next === null || next === undefined) nextType = 'falsy'
    let prevType = typeof prev
    if (prev === null || prev === undefined) prevType = 'falsy'
    if (nextType !== prevType) {
      if (log) log('TYPES !==', {path, prevType, nextType})
      return true
    }

    if (prevType === 'falsy' || nextType === 'falsy') {
      if (prev === next) {
        return false
      }
      if (log) log('null !==', {path, prev, next})
      return true
    }

    // check immutables
    else if (next.equals && next.toJS) {
      if (!next.equals(prev)) {
        if (log) {
          log('IMMUTABLES !equals', {path, prev: prev.toJS(), next: next.toJS()})
          log('IMMUTABLES !equals, INSPECTING JS OBJ NOW....', {path, prev: prev.toJS(), next: next.toJS()})
          this.doDiffer(prev.toJS(), next.toJS(), recursions + 1, path, log)
        }
        return true
      }
      return false
    }

    else if (nextType === 'function') {
      if (!this.perfCheckFunctions || next === prev) {
        return false
      }
      if (log) log('FUNCs !==', {path})
      return true
    }
    else if (nextType === 'number' || nextType === 'string' || nextType === 'boolean') {
      if (next === prev) {
        return false
      }
      if (log) log('Num/String/Boolean !==', {path, prev, next})
      return true
    }
    else if (next instanceof Array) {
      if (next.length !== prev.length) {
        if (log) log('Array lengths !==', {path})
        return true
      }
      let a = 0
      while (a < next.length) {
        let doDiffer = this.doDiffer(prev[a], next[a], recursions + 1, path + '[' + a + ']', log)
        if (doDiffer) return true
        a++
      }
      // console.log('shouldComponentUpdate: ?? Array', prev, next)
    }
    else if (nextType === 'object') {

      let nextKeys = Object.keys(next)
      let nextKeysString = nextKeys.join(',')
      let prevKeys = Object.keys(prev)
      let prevKeysString = prevKeys.join(',')

      if (nextKeysString !== prevKeysString) {
        if (log) log('Object.keys !==', {path, prevKeysString, nextKeysString})
        return true
      }

      if (nextKeys.length === 0 && prevKeys.length == 0) {
        return false
      }

      let k = 0
      while (k < nextKeys.length) {
        let key = nextKeys[k]
        k++

        let valsDoDiffer = this.doDiffer(prev[key], next[key], recursions + 1, path + '.' + key, log)
        if (valsDoDiffer) return true
      }
      // if (log) log('?? Object', {key, path}, prev, next)

    }
    else {
      if (log) log('shouldComponentUpdate: ???????', {path, prev, next, prevType, nextType})
    }

    return false
  }


  shouldComponentUpdate(nextProps, nextState) {

    let FORCE_UPDATE = this.FORCE_UPDATE
    this.FORCE_UPDATE = false

    if (this.props.perf === false || this.perf === false) return true

    let props = this.props
    let log = undefined
    if (this.debug || G.debug) log = this.perfLog
    // this._log(this.props,nextProps)


    // component is forced updated

    if (FORCE_UPDATE) {
      if (log) {
        this._log('this.FORCE_UPDATE from state change, INPSECTING STATE ANYWAY....', {state: this.state, nextState})
        // let debugCheckStatesDoDiffer = this.doDiffer(this.state, nextState, 0, 'state', log)
        // if (!debugCheckStatesDoDiffer) log('this.perfCheckState=false STATE === NEXTSTATE', {state: this.state, nextState})
      }
      return true
    }


    // check if next props differ

    let propsDoDiffer = false
    if (!this.perfIgnorePropChanges) propsDoDiffer = this.doDiffer(props, nextProps, 0, 'props', log)
    if (propsDoDiffer) {
      // if (log) this._log('shouldComponentUpdate: PROPS DIFFER')
      return true
    }


    // if (!this.perfCheckState) return false
    // let statesDoDiffer = this.doDiffer(this.state, nextState, 0, 'state', log)
    // if (statesDoDiffer) {
    //  //if (log) this._log('shouldComponentUpdate: STATES DIFFER **/')
    //  return true
    // }


    if (log) this._log('shouldComponentUpdate()', {css: 'background: hsl(90,50%,50%); color: hsl(0,00%,100%)'})


    // do not update

    return false
  }

}

export default Component
export {Component}



