import React from 'react'
import ColorPanel from './color-panel'
import {palettes, raf} from '@the-grid/deps'
import {Story} from '@the-grid/ui-story-machine'
import {fromJS} from '@the-grid/immutable'

class Color extends Story {
  storyName = 'color'
  willMount() {
    if (this.focused) return
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'merge', this.UI.FAB)
    this.contextRequest('FAF', 'merge', this.UI.FAF)
  }
  display(children) {
    return React.createElement(ColorPanel, {key: 'color-panel', onSelect: this.setColors}, children)
  }
  setColors = (colors) => {
    this.contextRequest('styleverse', 'merge', {styleContext: {chroma: {colors, type: 'natural'}}})
  }
  UI = {
    FAF: fromJS({
      show: false,
    }),
    FAB: fromJS({
      // show: true,
      // chroma: {type: 'dark', typeIndex: 0},
      active: true,
      activeIcon: 'x',
      icon: '+',
      onClick: () => {
        let that = this
        raf(function(){
          that.contextRequest('storyline', 'to-story', 'view-site')
        })
        return false
      },
      appButton: {
        icon: 'arrow_back',
        show: true,
        //activeIcon: 'arrow_back',
        onClick: () => {
          let that = this
          raf(function(){
            that.contextRequest('storyline', 'to-story', 'design')
          })
          return false
        },
      },
      //icon: 'arrow_back',
      //activeIcon: 'arrow_back',
      //onClick: () => {
      //  let that = this
      //  raf(function(){
      //    that.contextRequest('storyline', 'to-story', 'design')
      //  })
      //  return false
      //},
      actionList: [
        {
          icon: 'shuffle',
          onClick: () => {
            this.setColors(palettes.getRandom())
            return false
          },
        },
      ],
    }),
  }
}

export default Color



