import {Map} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
import {palettes} from '@the-grid/deps'
import PluginStruct from './Struct'
let initialState = new PluginStruct()

class ChromaStore extends Store {
  debug = false
  name = 'styleverse-chroma'
  deferCommit = false
  state = initialState
  isReady = false

  shouldCommit() {
    return this.isReady
  }

  contextStores = {
    'styleverse-chroma': (chromaStore) => {
      this.parentChroma = chromaStore
      this.execute('update-parent', chromaStore.state)
    },
  }

  parentChroma = undefined

  actions = {

    'update-props': (state, props) => {
      this.isReady = true
      return state.set('props', Map(props))
    },

    'update-parent': (state, parentState) => {
      return state.set('parent', parentState)
    },

    'teardown': () => {
      this.parentChroma = undefined
      this.isReady = false
      return initialState
    },
  }
}

export default ChromaStore



