import React from 'react'
// import {cloneComponentProps} from 'lib/cloneComponentProps'
import {cloneComponentProps} from '@the-grid/deps'
import {Struct} from '@the-grid/immutable'

/**
 * TODO:
 * - options param? {cache:..}
 * - or, use lowest cache val of deps???
 */
let connectState = function(Component, deps, callback, o) {
  let method = (o && o.method) ? o.method : 'setState'

  // Unpack deps
  let storeNames = []
  let depStructSchema = {}
  // let storeDepMap = {}

  /**
   * loop through our dependencies
   * get the store name out
   *
   * map to an object to use in a struct
   *
   * @example
   * app.propertyName
   * storeName = app
   */
  deps.forEach((depPath) => {
    let firstDotIndex = depPath.indexOf('.')
    let hasDot = (firstDotIndex === -1)
    let storeName = (hasDot)
      ? depPath
      : depPath.substr(0, firstDotIndex)

    if (!storeNames.includes(storeName)) {
      storeNames.push(storeName)
      depStructSchema[storeName] = undefined
    }

    // if (!storeDepMap[storeName]) storeDepMap[storeName] = []
    // let storePath = (hasDot)
    //   ? depPath.substr( firstDotIndex + 1, depPath.length )
    //   : '*'
    // storeDepMap[storeName].push( storePath )
  })

  const needsDebounce = storeNames.length > 1


  // Finalize dynamically generated Struct
  let getKey = 0

  depStructSchema['stateValues'] = {
    deps,
    get(...args) {
      getKey++
      let stateValues = callback(...args)
      return {stateValues, getKey}
    },
  }
  const DepStruct = Struct(depStructSchema)
  const initialDepStruct = new DepStruct({})


  // Extend passsed Component
  if (Component._hasContextualConnectedState)
    throw new Error('contextual.connectState can only be applied once to a component')

  // TODO: allow multiple connects? hmm...
  class Connected extends Component {

    constructor(...args) {
      super(...args)

      this._depStruct = initialDepStruct
      this._lastDepGetKey = 0
      this._depStruct_toMerge = undefined

      if (!this.state) this.state = {}

      this._setContextState = (name, state) => {
        if (!this._depStruct_toMerge) this._depStruct_toMerge = {}
        this._depStruct_toMerge[name] = state
        return this._commitContextStates()

        // if (!needsDebounce) return this._commitContextStates()
        //
        // raf.cancel(this._commitContextStates)
        // return raf(this._commitContextStates)

        // return this._commitContextStatesDebounced()
      }

      this._commitContextStates = () => {
        let nextDepStruct = this._depStruct.merge(this._depStruct_toMerge)

        this._depStruct_toMerge = undefined

        let {stateValues, getKey} = nextDepStruct.get('stateValues')
        if (getKey === this._lastDepGetKey) return

        // TODO
        // let shouldSetState = false
        // for (let key in stateValues) {
        //  let val = stateValues[key]
        //  ...
        // }

        this._lastDepGetKey = getKey
        this._depStruct = nextDepStruct

        if (stateValues === undefined) throw new Error('connectState cannot return undefined, if you dont want to updateState then return null')
        if (stateValues === null) return
        this[method](stateValues)
      }

      // TODO DEFER?
      // this._commitContextStatesDebounced = debounce( this._commitContextStates, 16 )
      // this._commitContextStatesDebounced = this._commitContextStates

      this._handleConnectedStore = (store) => {
        let storeName = store.name
        let storeState = store.state

        let currentStoreState = this._depStruct.get(storeName)
        if (storeState === currentStoreState) return
        if (currentStoreState && storeState.equals(currentStoreState)) return

        this._setContextState(storeName, storeState)
      }
    }

    componentWillMount() {
      if (super.componentWillMount) super.componentWillMount()

      // if store installed locally
      let request = this.request
      // else use context.request
      if (!request) request = this.context.request

      storeNames.forEach((storeName) => {
        request(storeName, 'connect', this._handleConnectedStore)
      })
    }

    render() {
      if (super.render) return super.render()
      if (this.props.children && this.props.children.length > 1)
        return (<div>{this.props.children}</div>)
      return this.props.children
    }

    componentWillUnmount() {
      if (super.componentWillUnmount) super.componentWillUnmount()

      // if store installed locally
      let request = this.request
      // else use context.request
      if (!request) request = this.context.request

      storeNames.forEach((storeName) => {
        request(storeName, 'disconnect', this._handleConnectedStore)
      })
    }
  }


  // b/c cross-browser & hot-reload issues
  cloneComponentProps(Component, Connected)

  Connected._hasContextualConnectedState = true

  Connected.displayName =
    Component.displayName ||
    Component.name ||
    'ContextualConnected'

  Connected.contextTypes = {
    ...Connected.contextTypes,
    request: React.PropTypes.func,
    __contextual_names__: React.PropTypes.array,
  }

  return Connected
}

export default connectState



