import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
export default (
  <Style id="fab-chroma-style" key="fab-chroma-style"  perfFrame={true}>
  {`
    #fab {

      ::selection {
        background -- none !important
        color -- inherit !important
      }



      #app-button, .action-button.bg--none {
        background -- transparent !important
        box-sh -- none !important
      }

      &.fab-bg--none {
        #fab_action-button, #fab-active-bg {
          background -- transparent !important
          box-sh -- none !important
          stroke -- chroma-text-pop2
        }
      }


      .action-button {
        bg-clr -- chroma-box
        clr -- chroma-text-pop
        box-sh -- l

        .action-label {
          clr -- chroma-title2
        }

        trans -- background-color .5s

        .icon-line, .icon-svg-part {
          trans -- background-color .5s, fill .5s
        }
        .icon-renderer svg polyline {
          trans -- stroke .5s
        }

        .icon-line, .icon-svg-part {
          fill -- chroma-text-pop2
          bg-clr -- chroma-text-pop2
        }
        .icon-renderer svg polyline {
          stroke -- chroma-text-pop2
        }
      }
      .action-button:hover {
        bg-clr -- chroma-inverse-box
        clr -- chroma-inverse-text-pop

        .action-label {
          clr -- chroma-inverse-text-pop2
        }
      }

      // TODO
      &:not(.fab-bg--none) #fab_action-button {
        bg-clr -- chroma-box-pop
        clr -- chroma-box-pop-text
        box-sh -- l

        .icon-line, .icon-svg-part {
          fill -- chroma-box-pop-text
          bg-clr -- chroma-box-pop-text
        }
        .icon-renderer svg polyline {
          stroke -- chroma-box-pop-text
        }

        &:hover {
          bg-clr -- chroma-box-pop2
          clr -- chroma-box
          .icon-line {
            bg-clr -- chroma-box
          }
        }
      }
      &.pop-1 #fab_action-button {
        bg-clr -- chroma-line
        clr -- chroma-meta
        box-sh -- m

        .icon-line, .icon-svg-part {
          fill -- chroma-meta
          bg-clr -- chroma-meta
        }
        .icon-renderer svg polyline {
          stroke -- meta
        }

        &:hover {
          bg-clr -- chroma-inverse-box
          clr -- chroma-inverse-title

          .icon-line, .icon-svg-part {
            fill -- chroma-inverse-title
            bg-clr -- chroma-inverse-title
          }
          .icon-renderer svg polyline {
            stroke -- chroma-inverse-title
          }
        }
      }
      &.loading #fab_action-button {
        chroma--loading-bg
      }

      // active-bg
      // =======================

      #fab-active-bg {
        bg-clr -- chroma-box-pop
      }
      &.is-active #fab-active-bg {
        bg-clr -- chroma-bg
      }

      // chroma overrides
      .chroma--flashing-pop.action-button {
        chroma--loading-bg-pop
      }

    }
  `}
  </Style>
)



