import Color from '../Color'
import polator from '../polator'
import basesFromPalette from '../helper/basesFromPalette'
import {Synthesizer} from '../helper/Synthesizer'
import Solver from './solver'

import _ from '../_'
let {nextFrom, scoredQuery, nearestNum, nearestCircularDistributed} = _

class ChromeSolver extends Solver {

  solve(options = {}) {
    let iconSelected
    let iconOver
    let result
    let colors = super.solve(options)
    let {
      base,
      title,
      bg,
      box,
      text,
      meta,
      metaHover,
      boxPop,
      boxPopText,
      boxPopTextHover,
      textPop,
      textShadow,
      palette,
      type,
      colorsToString,
    } = colors

    let isLight
    let isDark
    let lightnessClass
    let boxShadow
    if (colors.box.light()) {
      isLight = true
      isDark = false
      lightnessClass = 'is-light'
      boxShadow = Color('hsla(0,0%,0%,.1)')
    } else {
      isLight = false
      isDark = true
      lightnessClass = 'is-dark'
      boxShadow = Color('hsla(0,0%,0%,.3)')
    }

    let title2
    if (colors.title.length === 1) {
      title2 = colors.title[0]
    } else {
      title2 = colors.title[1]
    }

    let textPop2
    if (colors.textPop.length === 1) {
      textPop2 = colors.textPop[0]
    } else {
      textPop2 = colors.textPop[1]
    }

    let zebraStripe = colors.box.clone().mix(colors.meta,.05)
    let line = colors.meta.clone().mix(colors.box).mix(colors.box)

    let mediaBorder = {
      css: `1px solid ${line.hslString()}`,
      width: 1,
      type: 'solid',
      color: line,
    }

    let b
    let w
    if (isLight) {
      b = 'rgba(0, 0, 0, 0.2)'
      w = 'rgba(255, 255, 255, 0.1)'
    } else {
      b = 'rgba(0, 0, 0, 0.4)'
      w = 'rgba(255, 255, 255, 0.1)'
    }
    let mediaShadowColor = Color(b)
    let mediaInsetColor = Color(w)
    let mediaShadows = [
      {
        css: `0 1px 2px ${b}`,
        x: 0,
        y: 1,
        blur: 2,
        color: mediaShadowColor,
        inset: false,
      },
      {
        css: `inset 0 1px 1px ${w}`,
        x: 0,
        y: 1,
        blur: 1,
        color: mediaInsetColor,
        inset: true,
      },
    ]

    let icon =
    iconSelected =
    iconOver = textPop[0]

    let sliderTrack = line
    // sliderTrackShadows: []
    // sliderLabel sames as title
    // sliderBall  same as boxpop & boxpop2

    let boxPopByVibrance = scoredQuery([].concat(boxPop).concat(palette), function(c) {
      if (c.hslString() === box.hslString()) { return undefined }
      let score = c.chromaDistance() * -1
      // if box.isVibrant()
      //  score *= -1 if c.hue() - box.hue() < 20
      return score
    })

    let synthesizer = new Synthesizer()

    // create ::Selection bg & text colors
    // - synthesize vibrant if needed
    // - if box is dark, must be dark, etc

    let possibleSelectionsUnfiltered = []
    for (let i = 0; i < boxPopByVibrance.length; i++) {
      let bpop = boxPopByVibrance[i]
      if (!bpop.isVibrant()) {
        possibleSelectionsUnfiltered.push(synthesizer.vibrant(bpop))
      } else {
        possibleSelectionsUnfiltered.push(bpop)
      }
    }
    // for (let c of boxPopByVibrance) {
    //   if (!c.isVibrant()) {
    //     possibleSelectionsUnfiltered.push(synthesizer.vibrant(c));
    //   } else {
    //     possibleSelectionsUnfiltered.push(c);
    //   }
    // }

    let possibleSelections = possibleSelectionsUnfiltered.filter(function(c) {
      if (box.dark()) {
        return !!c.dark()
      }
      if (box.light()) {
        return !!c.light()
      }
    })
    if (possibleSelections.length === 0) {
      possibleSelections = possibleSelectionsUnfiltered
    }
    let selection = possibleSelections[0].clone()

    if (colorsToString) {
      if (!['rgbString','hslString','hexString'].includes(colorsToString)) {
        colorsToString = 'rgbString'
      }
    }
    let put = function(_c) {
      if (colorsToString) {
        if (_c[colorsToString]) return _c[colorsToString]()
      }
      return _c
    }

    return result = {

      type, // dark, light, vibrant, user
      isLight,
      isDark,
      lightnessClass, // "is-light" or "is-dark"

      base: put(base),

      bg: put(bg),

      box: put(box),
      boxShadow: put(boxShadow),

      boxPop: put(boxPop[0]),
      boxPopText: put(boxPop[0].whiteOrBlack()),
      boxPop2: boxPop[1] ? put(boxPop[1]) : put(boxPop[0]),
      boxPop2Text: boxPop[1] ? put(boxPop[1].whiteOrBlack()) : put(boxPop[0].whiteOrBlack()),
      boxPopTextHover: put(boxPopTextHover[0]),
      boxPop2TextHover: boxPopTextHover[1] ? put(boxPopTextHover[1]) : put(boxPopTextHover[0]),

      boxPopMostVibrant: put(boxPopByVibrance[0]),
      boxPopMostVibrantText: put(boxPopByVibrance[0].whiteOrBlack()),

      selectionText: put(colors.box.whiteOrBlack()),
      selection: put(selection.alpha(.5)),

      header: put(colors.box.clone().mix(colors.bg,.3)),

      zebraStripe: put(zebraStripe),
      line: put(line),

      mediaBorder: put(mediaBorder),
      mediaShadowColor: put(mediaShadowColor),
      mediaInsetColor: put(mediaInsetColor),
      mediaShadows: put(mediaShadows),

      title: put(title[0]), // [format]()
      title2: put(title2),

      subTitle: put(meta),

      text: put(text[0]),
      text2: text[1] ? put(text[1]) : put(text[0]),
      textShadow: put(textShadow),
      textShadowProps: {
        color: put(textShadow),
        x: 0,
        y: 1,
        blur: 0,
      },

      textPop: put(textPop[0]),
      textPop2: put(textPop2),

      meta: put(meta),
      metaHover: put(metaHover),

      icon: put(icon),
      iconSelected: put(iconSelected),
      iconOver: put(iconOver),

      palette,
    }
  }
}

export default ChromeSolver



