import React from 'react'
import {installStore} from '@the-grid/cymatics'
import SemanticSpaceStore from '../semantic-space/semantic-space-store'

var mountCount = 0

class SemanticDisplay extends React.Component {
  static displayName = 'SemanticDisplay'
  static defaultProps = {
    children: null,
    space: null,
    id: null,
    className: '',
  }
  state = {
    rendered: null,
  }
  componentWillMount() {
    if (!this.props.space) throw new Error('SemanticSpace requires props.space')
    this.store.willMount(this)
    this.id = 'semantic-display-' + mountCount
    mountCount++
    this.store.execute('connect', this.onStoreUpdate)
  }
  forceUpdate = false
  shouldComponentUpdate(nextProps) {
    if (this.forceUpdate) {
      this.forceUpdate = false
      return true
    }
    if (nextProps.space) {
      return !nextProps.space.equals(this.props.space)
    }
  }
  // TODO componentWillReceiveProps
  componentWillUnmount() {
    this.forceUpdate = false
    this.store.willUnmount(this)
    mountCount--
    this.store.execute('disconnect', this.onStoreUpdate)
    this.store = undefined
  }
  lastStoreState = undefined
  onStoreUpdate = (store) => {
    if (!this.lastStoreState) {
      this.lastStoreState = store.state
    }
    else {
      if (this.lastStoreState === store.state || this.lastStoreState.equals(store.state)) return
      this.lastStoreState = store.state
    }
    this.forceUpdate = true
    this.setState({rendered: this.store.get('rendered')})
  }
  render() {
    return (
      <div id={this.props.id || this.id} className={'semantic-display ' + this.props.className} key="semantic-display-el">
        {this.props.children || null}
        {this.state.rendered}
      </div>
    )
  }
}
export default installStore(SemanticDisplay, 'semantic-space', function(component) {
  return SemanticSpaceStore.make(component)
})



