import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
export default (
  <Style id="fan-chroma-style" key="fan-chroma-style"  perfFrame={true}>
  {`
    // TODO
    #fan {
      box-sh -- m
    }
    #fan {
      .fan_bg {
        o -- 1
        bg-clr -- chroma-box
        trans -- background-color .5s
      }
      //&:hover {
      //  .fan_bg {
      //    o -- 1
      //  }
      //}

      ::selection {
        background -- none !important
      }

      .fan_action-item {
        clr -- chroma-inverse-bg
        .icon {
          clr -- chroma-meta
        }
        .label {
          o -- .6
        }
        &:hover {
          clr -- chroma-text
          &:not(.is-inset):after {
            bg -- chroma-meta
          }
          .icon {
            clr -- chroma-text2
          }
          .label {
            o -- 1
          }
        }
        &.active {
          clr -- chroma-text-pop
          &:not(.is-inset):after {
            bg -- chroma-box-pop
          }
          .icon {
            clr -- chroma-title
          }
          .label {
            o -- 1
          }
        }
        &.is-text {
          clr -- chroma-text !important
          .label {
            o -- 1
          }
        }
        &.is-inset {
          clr -- chroma-box-pop-text
          bg -- chroma-box-pop
          .icon {
            clr -- chroma-inverse-box
          }
          .label {
            clr -- chroma-box-pop-text
            o -- 1
          }
          &:hover {
            clr -- chroma-box-pop2-text
            bg -- chroma-box-pop2
            .icon {
              clr -- chroma-title
            }
            .label {
              clr -- chroma-box-pop2-text
              o -- 1
            }
          }
        }
      }

    }
  `}
  </Style>
)


