import {React, Style} from '../../../deps'

let chromaStyles = {

  default: (scope) => (
    <Style key='chromaStyles-default' perfFrame={true} perf={true} scope={scope}>
      {`

        //section:scope {
        //
        //  @? chroma.type === 'light' {
        //
        //    border-t -- 1px solid rgba(255,255,255,.7)
        //
        //  }
        //
        //}

        :scope {

          clr -- chroma-text

          h1 {
            clr -- chroma-title
          }

          h2 {
            clr -- chroma-title2
          }

          h3 {
            clr -- chroma-title2
          }

          .meta {
            clr -- chroma-meta
          }

          // ctas

          .cta {
            txt-sh--n
            box-sh -- m

            trans-- color .3s, background-color .3s

            & {
              bg-clr -- chroma-box-pop

              > * {
                clr -- chroma-box-pop-text
              }
            }

            &:nth-child(even) {
              bg-clr -- chroma-box-pop2

              > * {
                clr -- chroma-box-pop2-text
              }
            }

            &:hover {
              bg-clr -- chroma-selection-more-opaque

              > * {
                clr -- chroma-text
              }

            }

            &:focus {
              otln -- n
            }
          }

          // links

          a, a:visited {
            clr -- chroma-text-pop
          }
          a:nth-child(even), a:nth-child(even):visited {
            clr -- chroma-text-pop2
          }

          .layout > *:nth-child(even) {
            a {
              clr -- chroma-text-pop2
            }
            a:visited {
              clr -- chroma-text-pop2
            }
            a:nth-child(even) {
              clr -- chroma-text-pop
            }
            a:nth-child(even):visited {
              clr -- chroma-text-pop
            }
          }

          ::selection {
            bg-color -- chroma-selection
            color -- chroma-selection-text
            text-shadow -- none
          }
        }
      `}
    </Style>
  )

}

export default chromaStyles


