import React from 'react'
import {installStore, connectState} from '@the-grid/cymatics'
import OverlayStore from './OverlayStore'

// <Overlay />

class Overlay extends React.Component {
  static displayName = 'Overlay'
  render() {
    return (
      <div>
        {this.props.children}
        <OverlayRenderer />
      </div>
    )
  }
}

Overlay = installStore( Overlay, 'overlay', (component) => {
  return new OverlayStore(component)
})

// <OverlayRenderer />

class OverlayRenderer extends React.Component {
  static displayName = 'OverlayRenderer'
  static contextTypes = {
    request: React.PropTypes.func.isRequired,
  }
  static rootStyle = {
    zIndex: 1000,
    position: 'fixed',
    top: 0,
    left: 0,
    backfaceVisibility: 'hidden',
  }
  state = {
    overlay: undefined,
  }

  componentWillUpdate(nextProps, nextState) {
    let overlayState = nextState.overlay
    if (!overlayState) return
    let show = overlayState.get('show')
    if (!show) return
    this.context.request('FAB', 'update', {
      defaultChromaType: 'dark',
      show: true,
      icon: 'x',
      'v-position': 'top',
      onClick: () => {
        this.context.request('FAB', 'use-prev', {active: false})
        this.context.request('overlay', 'hide')
      }
    })
  }
  render() {
    let overlayState = this.state.overlay
    if (!overlayState) return null
    let show = overlayState.get('show')
    return (
      <div id="overlay-container" style={OverlayRenderer.rootStyle}>
        <div id="overlay-renderer" style={{position: 'absolute', top:0, left: 0, width: '100vw', maxHeight: '100vh', backfaceVisibility: 'hidden', visibility: 'hidden', overflowY: 'scroll'}}>
          {overlayState.get('sprungSheet')}
          {overlayState.get('render')}
        </div>
      </div>
    )
  }
}

OverlayRenderer = connectState( OverlayRenderer, ['overlay'], (overlay) => {
  return {overlay}
})

export default Overlay


