import imgfloURL from 'imgflo-url'

const imgfloConfig = {
  'server': 'https://imgflo.herokuapp.com/',
  'key': '5d24a8ac9b4ac51',
  'label': 'TheGrid Webapp production',
  'secret': '6c1480fefb5a273',
}

const imgflo = {

  url: function(url) {
    return imgfloURL(imgfloConfig, 'passthrough', {input: url})
  },

  ext: function(operation = 'passthrough', obj, format = 'jpg') {
    return imgfloURL(imgfloConfig, operation, obj, format)
  },

}

export default imgflo



