import React, {PropTypes} from 'react'
import ResizeContext from '@the-grid/ui-resize-context'

let count = 0

export default class ResizeRenderer extends ResizeContext {
  static displayName = 'ResizeRenderer'

  state = {
    resize: undefined,
  }

  onResize = (store) => {
    this.setState({resize: store.state})
  }

  renderChildren(children) {
    if (typeof children === 'function') return children(this.store.state, this.store)
    return children
  }
}


