import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
export default (
  <Style id={'fab-static-style'} key="fab-static-style"
    fabSize={60 + 'px'} fabSizeHalf={60 / 2 + 'px'}>
    {`
      #fab {
        o -- 1
        d -- block
        min-w -- props.fabSize
        //h -- props.fabSize
        pos -- fixed
        t -- 0
        l -- 0
        bf-viz -- hidden
        z-idx -- 2001
        box-szg -- border-box


        > * {
          bf-viz -- hidden
        }

        // TYPOGRAPHY?
        fnt--d3

        .action-button {

          pos -- a
          t -- 0
          r -- 0
          tf-origin -- calc(100% - props.fabSizeHalf ) 50%
          min-w -- props.fabSize
          h -- props.fabSize
          bdr-rad -- props.fabSizeHalf

          .action-icon {
            w -- props.fabSize
            h -- props.fabSize
            ln-h -- props.fabSize
            txt-aln -- center
          }
        }

        #fab-active-bg {
          bf-viz -- hidden
          pos -- fixed
          bdr-rad -- 50%
          t -- 0
          l -- 0
          o -- 1
          tf-origin -- 50% 50%
          trans -- background-color .3s, opacity .1s
        }

        #fab-active-bg-mask {
          bf-viz -- hidden
          pos -- fixed
          t -- 0
          l -- 0
          ovf -- hidden
          // box-sh -- inset 0px 1px 4px 0px rgba(0,0,0,0.5)
        }
      }
    `}
  </Style>
)


