import React from 'react'
import {Style} from '@the-grid/styleverse'
import FABStore from './fab-store'
import {installStore} from '@the-grid/cymatics'
import FAB from '../fab'
//import {gooeySvg} from '../theme/gooey'

let count = 0
class FABContext extends React.Component {
  static displayName = 'FABContext'
  FAB_renders = undefined
  _id = 'fab-' + count++
  componentWillMount() {
    this.store.willMount()
  }
  componentWillUnmount() {
    this.store.willUnmount()
  }
  shouldComponentUpdate() {
    return false
  }
  render() {
    let id = this.props.id || this._id
    // {gooeySvg}
    return (
      <div id={id} className="fab-context">
        {this.props.children}
        <FAB key={id} id={id} />
      </div>
    )
  }
}
FABContext = installStore(FABContext, 'FAB', FABStore)
export default FABContext



