import React from 'react'
import {Style} from '@the-grid/styleverse'
import InlineSVG from '@the-grid/ui-inline-svg'

const el = React.createElement

const icon_lines_types = ['+','*','x','-']

const icon_svg_types = {
  'facebook': {
    type: 'svg',
    rendered: (
      <svg x="0px" y="0px" viewBox="0 0 24 24" style={{
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
      }}>
        <path className="icon-svg-part" d={
          `M13.9,24V13.1h3.7l0.5-4.3h-4.2V6.1C13.9,4.8,14.2,4,16,4l2.3,0V0.2C17.8,0.1,16.5,0,14.9,0c-3.3,0-5.5,2-5.5,5.6
        	v3.1H5.8v4.3h3.7V24H13.9z`
        } />
      </svg>
    ),
  },
  'twitter': {
    type: 'svg',
    rendered: (
      <svg x="0px" y="0px" viewBox="0 0 24 24" style={{
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
      }}>
        <path className="icon-svg-part" d={
          `M7.5,21.8c9.1,0,14-7.5,14-14c0-0.2,0-0.4,0-0.6c1-0.7,1.8-1.6,2.5-2.5c-0.9,0.4-1.9,0.7-2.8,0.8c1-0.6,1.8-1.6,2.2-2.7
          c-1,0.6-2,1-3.1,1.2c-1.9-2-5-2.1-7-0.2c-1.3,1.2-1.8,3-1.4,4.7c-4-0.2-7.6-2.1-10.1-5.1C0.4,5.4,1,8.3,3.2,9.7
          C2.4,9.7,1.6,9.5,1,9.1c0,0,0,0,0,0.1c0,2.3,1.7,4.4,4,4.8c-0.7,0.2-1.5,0.2-2.2,0.1c0.6,2,2.5,3.4,4.6,3.4
          c-1.7,1.4-3.9,2.1-6.1,2.1c-0.4,0-0.8,0-1.2-0.1C2.3,21,4.9,21.8,7.5,21.8`
        } />
      </svg>
    ),
  },
  'pinterest': {
    type: 'svg',
    rendered: (
      <svg x="0px" y="0px" viewBox="0 0 24 24" style={{
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
      }}>
        <path className="icon-svg-part" d={
          `M12,0C5.4,0,0,5.4,0,12c0,5.1,3.2,9.4,7.6,11.2c-0.1-0.9-0.2-2.4,0-3.4c0.2-0.9,1.4-6,1.4-6S8.7,13,8.7,12
			    c0-1.7,1-2.9,2.2-2.9c1,0,1.5,0.8,1.5,1.7c0,1-0.7,2.6-1,4c-0.3,1.2,0.6,2.2,1.8,2.2c2.1,0,3.8-2.2,3.8-5.5c0-2.9-2.1-4.9-5-4.9
			    c-3.4,0-5.4,2.6-5.4,5.2c0,1,0.4,2.1,0.9,2.7c0.1,0.1,0.1,0.2,0.1,0.3c-0.1,0.4-0.3,1.2-0.3,1.4c-0.1,0.2-0.2,0.3-0.4,0.2
          c-1.5-0.7-2.4-2.9-2.4-4.6c0-3.8,2.8-7.3,7.9-7.3c4.2,0,7.4,3,7.4,6.9c0,4.1-2.6,7.5-6.2,7.5c-1.2,0-2.4-0.6-2.8-1.4
          c0,0-0.6,2.3-0.7,2.9c-0.3,1-1,2.3-1.5,3.1C9.6,23.8,10.8,24,12,24c6.6,0,12-5.4,12-12C24,5.4,18.6,0,12,0z`
        } />
      </svg>
    ),
  },
}

const types = Object.keys(icon_lines_types)
  .concat(Object.keys(icon_svg_types))



var cache = {}

function get_icon_type(icon) {
  if (icon_lines_types.includes(icon)) return 'lines'
  if (icon_svg_types[icon]) return 'svg'
  if (icon.endsWith('.svg')) return 'svgurl'
  return 'font'
}

const renderer_by_type = {

  lines(_, icon, props) {
    let {
      iconWeight,
      iconSize,
    } = props
    var cache_key = icon + ':' + iconWeight + ',' + iconSize
    if (cache[cache_key]) return cache[cache_key]
    let partStyle = {
      position: 'absolute',
      width: iconWeight,
      height: iconSize,
      transformOrigin: '50%',
      backfaceVisibility: 'hidden',
      top: `calc(50% - ${iconSize / 2}px)`,
      left: `calc(50% - ${iconWeight / 2}px)`,
    }
    cache[cache_key] = [
        (<div key={'icon-line-0'} style={partStyle} className="icon-line icon-line-0" />),
        (<div key={'icon-line-1'} style={partStyle} className="icon-line icon-line-1" />),
        (<div key={'icon-line-2'} style={partStyle} className="icon-line icon-line-2" />),
    ]
    return cache[cache_key]
  },

  svg(_, icon) {
    if (cache[icon]) return cache[icon]
    var key = 'icon-svg--' + icon
    cache[icon] = el('div',
      {
        key,
        className: key + ' icon-renderer icon-svg',
        style: {
          position: 'absolute',
          // width: 24,
          // height: 24,
          width: '1em',
          height: '1em',
          transformOrigin: '50%',
          backfaceVisibility: 'hidden',
          // top: `calc(50% - ${24 / 2}px)`,
          // left: `calc(50% - ${24 / 2}px)`,
          top: `calc(50% - .5em)`,
          left: `calc(50% - .5em)`,
        },
      },
      icon_svg_types[icon].rendered
    )
    return cache[icon]
  },

  svgurl(_, icon) {
    if (cache[icon]) return cache[icon]
    var key = 'icon-svgurl--' + icon
    cache[icon] = el('div',
      {
        key,
        className: key + ' icon-renderer icon-svgurl',
        style: {
          position: 'absolute',
          width: '100%',
          height: '100%',
          // width: 24,
          // height: 24,
          transformOrigin: '50%',
          backfaceVisibility: 'hidden',
          top: 0,
          left: 0,
          //top: `calc(50% - ${24 / 2}px)`,
          //left: `calc(50% - ${24 / 2}px)`,
        },
      },
      el(InlineSVG, {src: icon, cacheGetRequests: true})
    )
    return cache[icon]
  },

  font(_, icon) {
    if (cache[icon]) return cache[icon]
    var key = 'icon-font--' + icon
    cache[icon] = el('i',
      {
        key,
        className: key + ' material-icons icon-renderer icon-font',
        style: {
          position: 'absolute',
          // width: 24,
          // height: 24,
          width: '1em',
          height: '1em',
          fontSize: 'inherit',
          transformOrigin: '50%',
          backfaceVisibility: 'hidden',
          // top: `calc(50% - ${24 / 2}px)`,
          // left: `calc(50% - ${24 / 2}px)`,
          top: `calc(50% - .5em)`,
          left: `calc(50% - .5em)`,
        },
      },
      icon === 'share'
        ? 'reply'
        : icon
    )
    return cache[icon]
  },

}

function renderer(id, icon, props) {
  if (!icon) return null
  return renderer_by_type[get_icon_type(icon)](id, icon, props)
}

export default renderer
export {
  renderer,
  get_icon_type,
}



