class FAN extends React.Component {
  static displayName = 'FAN'
  static contextTypes = {
    request: PropTypes.func.isRequired,
  }
  /*
  static propTypes = {
    name: string
    icon: PropTypes.string,
    label: PropTypes.string,
    chromaType: PropTypes.string,
    actionList: Immutable.PropTypes.list.isRequired,
    onClick: PropTypes.func,
    active: PropTypes.bool.isRequired,
    show: PropTypes.bool.isRequired,
    pop: PropTypes.number.isRequired,
  }
  */
  state = {
    // mouseDown: false,
    FANState: undefined,
  }
  // prevMouseDownIndex = -1
  // mouseDownIndex = -1
  hasShown = false
  // onLeave = (targetIndex) => {
  //  this.mouseDownIndex = targetIndex
  //  this.setState({mouseDown: true})
  // }
  // captureClick = (e) => {
  //  e.stopPropagation()
  // }
  get(key, fallback) {
    let FANState = this.state.FANState
    if (!FANState) return fallback
    let val = FANState.get(key)
    if (val === undefined) return fallback
    return val
  }
  updateFANState = (store) => {
    let FANState = store.state
    let prev = this.state.FANState
    if (prev) {
      if (FANState.equals(prev)) return
    }
    this.setState({FANState})
  }
  mountIndex = -1
  componentWillMount() {
    this.mountIndex = mountCount
    mountCount++
    // TODO: auto unmount lower index?
    if (mountCount > 1) throw new Error('Only one FAN can be mounted at a time')
    this.hasShown = false
    this.context.request('FAN', 'connect', this.updateFANState)
    var that = this
    // this.onMouseUp = function(){
    //  onMouseUp(that)
    // }
    // document.addEventListener('mouseup',this.onMouseUp)
  }
  componentWillUnmount() {
    mountCount--
    this.hasShown = false
    this.context.request('FAN', 'disconnect', this.updateFANState)
    // document.removeEventListener('mouseup',this.onMouseUp)
    // this.onMouseUp = undefined
  }
  componentDidMount() { this.didRender() }
  componentDidUpdate() { this.didRender() }
  didRender() {
    // this.prevMouseDownIndex = -1
  }
  render() {
    // let {
    //  mouseDownIndex,
    //  prevMouseDownIndex,
    // } = this
    if (!this.hasShown && this.state.FANState === undefined) return null
    var show = this.get('show')
    if (this.get('show')) this.hasShown = true
    if (!this.hasShown) return null

    let id = 'fan'
    let scope = '#' + id
    var active = this.get('active')
    var chroma = this.get('chroma')
    var pop = this.get('pop')
    var render = this.get('render')
    var size = this.get('size')

    let className = this.get('className')
    if (this.props.className) className += this.props.className
    className += ` pop-` + pop
    className += ' size--' + size
    if (active !== false) className += ' is-active'
    let content = []
    let child
    child = this.renderActionList(this.get('actionList'), id, active, show)
    if (child) content.push(child)
    child = this.renderActionList(this.get('actionList2'), id + '-secondary-', active, show)
    if (child) content.push(child)
    child = render ? render() : null
    if (child) content.push(child)
    if (chroma && chroma.toJS) chroma = chroma.toJS()
    return (
      <div id={id} className={className} style={{display: 'none'}}>
        <div id={id + '-bg'} className={'fan_bg'} />
        <StyleContext key="chroma-context" chroma={chroma}>
          {FANChromaStyle}
        </StyleContext>
        {renderSprungStyle()}
        {FANStaticStyle}
        <div className="fan_content" key="fan_content">
          {content}
        </div>
      </div>
    )
  }
  // renderOverlay(id) {
  //  let overlayProps = this.get('overlayProps')
  //  if (!overlayProps) return null
  //  return null
  //  return (
  //    <div key="overlay" id={id+'-active-bg-mask'}>
  //      <div id={id+'-active-bg'} onClick={linkEvent(this, onClick)} />
  //    </div>
  //  )
  // }

  onActionItemClick = (key) => {
    this.context.request('FAN', 'activate', key)
  }
  renderActionList(actionList, id, active, show) {
    if (!actionList) return null
    var l = actionList.size
    if (!l) return null
    var children = [], action, id, key, label, isActive, className, onItemClick, icon, src
    for (var i = 0; i < l; i++) {
      action = actionList.get(i)
      id = 'fan_action-item-' + i
      key = action.get('key', i)
      isActive = active === key
      className = action.get('className', '')
      className += ' fan_action-item'
      onItemClick = action.get('onClick')
      if (!onItemClick) {
        if (isActive) {
          className += ' active'
        }
        else {
          onItemClick = this.onActionItemClick.bind(this, key)
        }
      }
      //if (isActive) {
      //  onItemClick = null
      //  className += ' active'
      //}
      //else {
      //  onItemClick = this.onActionItemClick.bind(this, key)
      //}
      label = action.get('label', null)
      if (label) label = <div className="fan_action-item_label label">{label}</div>
      icon = action.get('icon', null)
      if (icon) icon = <Icon className="fan_action-item_icon">{icon}</Icon>
      src = action.get('src', null)
      if (src) src = <div className="fan_action-item_src" style={{background: `url(${src})`, backgroundSize: 'cover'}} />

      children.push(
        <div id={id} key={'action-item-' + key} className={className} onClick={onItemClick}>
          {icon}
          {label}
          {src}
        </div>
      )
    }
    return (
      <div className="fan_action-list">
        {children}
      </div>
    )
    // let actionListProps = {
    //  key: 'action-list',
    //  id: id+'-action-list',
    //  list: actionList,
    //  show,
    //  active,
    //  //type, // TODO
    // }
    // return (
    //  <ActionList {...actionListProps} />
    // )
  }
  _renderActionList(id, active, show) {

  }
}


export default FAN



