import React from 'react'
import {installStore} from '@the-grid/cymatics'
import StyleStore from './StyleStore'

class StyleStoreContext extends React.Component {

  static displayName = 'Styleverse_StyleStoreContext'

  static propTypes = {
    ROOT: React.PropTypes.bool,
    children: React.PropTypes.object.isRequired,
  }

  static contextTypes = {
    styleverse: React.PropTypes.object.isRequired,
  }

  componentWillMount() {
    this.store.execute('setup', this.context.styleverse, this.props.ROOT)
  }
  componentWillUnmount() {
    this.store.silently('teardown')
  }

  render() {
    return this.props.children
  }
}

StyleStoreContext = installStore(StyleStoreContext, 'styleStore', (componentInstance) => {
  return StyleStore.getInstance(componentInstance)
})

export default StyleStoreContext



