console.log('@the-grid/ui-pricing-table')
import React, {PropTypes} from 'inferno-compat'
import {Style} from '@the-grid/styleverse'
import PricingItem from './pricing-item'
import Styles from './Styles'

let displayName = 'PricingTable'
let RIBBON_STYLE

function PricingTable(props) {
  let {
    id,
    className,
    items,
    theme,
    styleverse,
    style,
    before,
    after,
  } = props

  if (!id) throw new Error('PricingTable requires props.id')
  if (!className) className = ''
  className += ' pricing'
  if (!style) style = null

  let hasFeatured = false
  let itemRenders = []
  let itemId
  for (let i = 0; i < items.size; i++) {
    let item = items.get(i)
    if (!hasFeatured) hasFeatured = item.get('isFeatured')
    itemId = id + '-item-' + i
    itemRenders.push(
      <PricingItem id={itemId} key={itemId} item={item} />
    )
  }

  let children = []
  if (before) children.push(before)
  children.push(<Styles theme={theme} scopeId={id} />)
  children.push(<div className="pricing_bg" />)
  children.push(<div id={id + '-table'} className="pricing_table">{itemRenders}</div>)
  if (hasFeatured) {
    if (!RIBBON_STYLE) {
      RIBBON_STYLE = (
        <Style scope={'#' + id}>{`
        :scope {
          .Ribbon {
            pos -- a
            bf-viz--hidden
            tf--translate3d(0px,0px,0px)
            t -- -2px
            r -- -2px
            w -- 8em
            h -- 8em
            ovf -- hidden
            line-h -- 1em
            fnt-sz -- txt-xs
          }
          .Ribbon-text {
            pos -- r
            pad -- .75em 0
            perspective -- 1000
            bf-viz--hidden
            tf -- translate3d(0px,0px,0px) rotate(45deg)
            l -- -1em
            t -- 2em
            w -- 12em
            whs--nw
            fnt -- txt2
            fnt-wght -- b
            txt-aln -- center
            //txt-sh -- 0px 1px 0px chroma-selection-more-opaque
            box-sh -- 0px 0px 2px rgba(0,0,0,0.3)
            clr -- chroma-title2
            bg-clr -- chroma-selection
            trans-- background-color 1s
          }
          .Ribbon-text:before, .Ribbon-text:after {
            content -- ""
            bdr-t -- 2px solid chroma-box-pop
            bdr-l --  2px solid transparent
            bdr-r -- 2px solid transparent
            pos -- a
            b -- -2px
          }
          .Ribbon-text:before {
            l -- 0
          }
          .Ribbon-text:after {
            r -- 0
          }​
        }
        `}</Style>
      )
    }
    children.push(RIBBON_STYLE)
  }
  if (after) children.push(after)
  return <section id={id} className={className} style={style}>{children}</section>
}

PricingTable.displayName = displayName
export default PricingTable


