import {Store} from '@the-grid/cymatics'
import {Map} from '@the-grid/immutable'
import Springs from '../springs'

var initalState = Map({tick: 0})
class SpringsStore extends Store {
  name = 'springs'
  state = initalState
  deferCommit = false

  springs = undefined
  on_style_listeners = []
  on_state_will_update_listeners = []
  on_did_update_listeners = []

  // shouldCommit(state) {
  //   return false
  // }

  actions = {
    'setup': (state, {id}) => {
      var springs = new Springs({id})
      this.springs = springs
      return state
    },
    'set-spring': (state, springId, config) => {
      this.springs.set(springId, config)
      this.springs.commitAsync()
      return state
    },
    'set-state': (state, key, val) => {
      this.springs.setState(key, val)
      this.springs.commitAsync()
      return state
    },
    'on-state-will-update': (state, callback) => {
      if (this.on_state_will_update_listeners.includes(callback)) return
      this.on_state_will_update_listeners.push(callback)
      return state
    },
    'off-state-will-update': (state, callback) => {
      let idx = this.on_state_will_update_listeners.indexOf(callback)
      if (idx === -1) return
      this.on_state_will_update_listeners.splice(idx, 1)
      return state
    },
    'on-did-update': (state, callback) => {
      this.springs.addListener(callback)
      return state
    },
    'off-did-update': (state, callback) => {
      this.springs.removeListener(callback)
      return state
    },
    'on-style': (state, callback) => {
      if (this.on_style_listeners.includes(callback)) return
      this.on_style_listeners.push(callback)
      return state
    },
    'off-will-update': (state, callback) => {
      let idx = this.on_style_listeners.indexOf(callback)
      if (idx === -1) return
      this.on_style_listeners.splice(idx, 1)
      return state
    },
    'teardown': (state) => {
      this.on_style_listeners = undefined
      this.on_state_will_update_listeners = undefined
      this.on_did_update_listeners = undefined
      this.springs.reset()
      this.springs = undefined
      this.state = undefined
      return state
    },
  }


}
export default SpringsStore



