import React from 'react'
import {cloneComponentProps} from '@the-grid/deps'
import {Struct, Map, fromJS} from '@the-grid/immutable'
import {classObj} from '@the-grid/util-classname'
import {Style, StyleContext} from '@the-grid/styleverse'

var schema = {

  'style': undefined,

  'renderStyles': {
    params: ['id', 'className'],
    deps: ['style','designverse','designBranches'],
    get(
      id, className,
      style, designverse, designBranches
    ) {
      var branch, pathName, path,
        children = []
      if (style) children.push(<Style key={'base-style'} scope={'#' + id} >{style}</Style>)

      if (designBranches) {
        for (var i = 0; i < designBranches.length; i++) {
          branch = designBranches[i]
          pathName = classObj.get(className, branch)
          if (!pathName) continue
          path = branch+'--'+pathName
          style = designverse.getIn([path, 'style'])
          if (!style) continue
          children.push(<Style key={path} scope={'#' + id} >{style}</Style>)
        }
      }

      if (!children.length) return null
      return children
    }
  },

  'designBranches': undefined,

  'designverse': {
    set(designverse) {
      var paths = Object.keys(designverse)
      var designBranches = []
      var branch
      for (var i = 0; i < paths.length; i++) {
        branch = paths[i].split('--')[0]
        if (designBranches.includes(branch)) continue
        designBranches.push(branch)
      }
      return {designverse: fromJS(designverse), designBranches}
    }
  },
}

/**
 * essentially connects the store to the context
 * and points requests to the right place
 *
 */
let enhance = function(ComponentToEnhance, _designConfig) {


  var
    // each Component class gets own Designer Struct for cache reset purposes
    DesignerStruct = undefined,
    // each component of class shares designer struct
    designer = undefined,
    // displayName required
    displayName = ComponentToEnhance.displayName,
    mountCount = 0

  var {sharedCSS, ...designConfig} = _designConfig

  var hasDesigner = !!Object.keys(designConfig).length

  function getDesigner() {
    if (!DesignerStruct) DesignerStruct = Struct(schema)
    if (!designer) designer = new DesignerStruct(designConfig)
    return designer
  }

  if (!displayName) throw new Error('designable requires Component.displayName ' + Component + ', designConfig=' + JSON.stringify(designConfig))


  // @TODO: @autobind
  class Enhanced extends ComponentToEnhance {
          //
    constructor(...args) {
      super(...args)

      // TODO!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! improve rendering pipeline with GOM or something
      if (hasDesigner) {
        var super_renderChildren = this.renderChildren
        if (!super_renderChildren) throw new Error('designable requires component.renderChildren ' + displayName)
        // wrap renderChildren method
        this.renderChildren = () => {
          // TODO: remove dep on this id & className
          let {id, className} = this
          if (!id || !className) throw new Error('TEMP HACK: designable requires this.className && this.id ' + displayName)
          // render the children
          let children = super_renderChildren.call(this)
          if (!children || !(children instanceof Array)) throw new Error("designable renderChildren must return array")
          // render the styles
          let styles = getDesigner().renderStyles(id, className)
          if (styles) return styles.concat(children)
          return children
        }
      }

    }

    componentWillMount(...args) {
      if (mountCount === 0) {
        if (sharedCSS) this.context.request('styleverse','add-css','component--' + displayName, sharedCSS)
      }
      mountCount++
      this._id = displayName + '-' + mountCount
      if (super.componentWillMount) super.componentWillMount(...args)
    }

    componentWillUnmount(...args) {
      mountCount--
      if (mountCount === 0) {
        if (hasDesigner && DesignerStruct) DesignerStruct.cacheReset()
      }
      if (super.componentWillUnmount) super.componentWillUnmount(...args)
    }

    /*
    getChildContext() {
      const superChildContext = super.getChildContext ?
        super.getChildContext() :
        {}
      let childContext = {...superChildContext}
      childContext.request = this.request
      return childContext
    }
    */
  }

  // b/c cross-browser & hot-reload issues
  cloneComponentProps(ComponentToEnhance, Enhanced)

  Enhanced._isDesignableEnhanced = true

  if (!Enhanced.contextTypes || !Enhanced.contextTypes.request) {
    Enhanced.contextTypes = {
      ...Enhanced.contextTypes,
      request: React.PropTypes.func.isRequired,
    }
  }

  /*
  Enhanced.childContextTypes = {
    ...Enhanced.childContextTypes,
    stores: React.PropTypes.object,
    request: React.PropTypes.func,
    __contextual_names__: React.PropTypes.array,
  }
  */

  return Enhanced
}

export default enhance



