//import {Struct} from '@the-grid/immutable'
import propNames from './propNames'
import keyVals from './keyVals'
import notPixelKeys from './notPixelKeys'
import valNames from './valNames'

let BaseParser = {

  keyVal: function (keyVal) {
    let style = keyVals[keyVal]
    if (!style) return undefined
    return {style}
  },

  //key: function (key, val) {
  //  let keyParts = key.split('-')
  //  if (!propNames[keyParts[0]] && keyParts[0] !== '') return undefined
  //  return keyParts.map(function (part) {
  //    if (part === '') return ''
  //    let namePart = propNames[part]
  //    if (!namePart) throw new Error('Styleverse Base keyPlugin could not finish key: '+key +' part:'+part)
  //    return namePart
  //  }).join('-')
  //},

  key: function (key, val) {
    let keyParts = key.split('-')
    //if (!propNames[keyParts[0]] && keyParts[0] !== '') return undefined
    let result = ''
    let part
    let len = keyParts.length
    for (let i = 0; i < len; i++) {
      part = keyParts[i]
      if (part === '') {
        result += ''
      }
      let namePart = propNames[part]
      if (!namePart) {
        result += part // do not throw!!!!!
      } else {
        result += namePart
      }
      if (i !== len - 1) result += '-'
    }
    return result
  },

  val: function (key, val) {
    let num = Number(val)
    if (!isNaN(num)) {
      if (notPixelKeys.includes(key)) return num
      return val + 'px'
    }
    if (valNames[val]) return valNames[val]
    return val
    //if (val.endsWith('px')) return val
    //if (val.endsWith('em')) return val
    //if (val.endsWith('rem')) return val
    //if (val.endsWith('vw')) return val
    //if (val.endsWith('vh')) return val
    //return valName
  },

  render: function (selector, obj) {
    let css = selector
    let additionalCSS = ''

    css += ' {\n'

    let keys = Object.keys(obj)
    for (let i = 0; i < keys.length; i++) {
      let key = keys[i]
      if (key === 'CSS') {
        if (obj[key]) additionalCSS += obj[key]
        continue
      }
      css += key
      css += ': '
      css += obj[key]
      css += ';\n'
    }
    css += '}\n'

    if (additionalCSS) {
      if (selector.includes(',')) {
        // TODO, make sure selector with commas works here...
        throw new Error("Styleverse doesnt handle selectors with commans plus like this yet...", {selector,additionalCSS})
        let selParts = selector.split(',')
        for (let i = 0; i < selParts.length; i++) {
          let selPart = selParts[i]
          css += additionalCSS.replace( /\&/g, selPart)
        }
      }
      else {
        // replace & selector with selector

        if (additionalCSS.includes('&')) additionalCSS = additionalCSS.replace( /\&/g, selector)
        css += additionalCSS + '\n'
      }

    }


    return {child: css, obj: {}}

  },

}

export default BaseParser



