const PrefixerParser = {
  ruleset(selector, rulesObj, prerenderHelper, struct) {
    const prefixed = {}
    const transform = rulesObj.transform
    if (transform) {
      // prefixed['-o-transform'] = transform
      prefixed['-ms-transform'] = transform
      prefixed['-moz-transform'] = transform
      prefixed['-webkit-transform'] = transform
    }

    const display = rulesObj.display
    if (display === 'flex') {
      prefixed[' display'] = '-webkit-flex'
    }

    let flexProp
    let val

    flexProp = 'flex-direction'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'justify-content'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'align-items'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'align-content'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'align-self'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex-wrap'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex-flow'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex-grow'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex-shrink'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'flex-basis'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    flexProp = 'order'
    val = rulesObj[flexProp]
    if (val) prefixed['-webkit-'+flexProp] = val

    if (Object.keys(prefixed)) return Object.assign(prefixed, rulesObj)
    return rulesObj
  },
}

export default PrefixerParser



