import React from 'react'
import Device from '../Device'
import IFrame from '@the-grid/ui-iframe'

let DeviceIFrame = function (props) {

  let {
    url,
    ...DeviceProps
  } = props

  return (
    <Device {...DeviceProps}>{ ({type, screenWidth}) => {
      let pixelWidth
      if (type === 'iphone') {
        pixelWidth = 480
      }
      else if (type === 'macbook') {
        pixelWidth = 1440
      }
      else if (type === 'ipad') {
        pixelWidth = 760
      }
      else if (type === 'full') {
        pixelWidth = 2000
      }
      else {
        throw new Error("Device.IFrame doesn't recognize type: " + type)
      }
      let percentSize = (pixelWidth / screenWidth) * 100 + '%'
      let scale = screenWidth / pixelWidth
      let iframeStyle = {
        transformOrigin: '0% 0%',
        width: percentSize,
        height: percentSize,
        transform: `scale(${scale})`
      }
      return (
        <IFrame url={url} iframeStyle={iframeStyle} />
      )
    }}
    </Device>
  )

}

let {PropTypes} = React

DeviceIFrame.propTypes = {
  url: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
}

export default DeviceIFrame


