window.renderVectors = function(vectors, props) {
  var space = new SemanticSpace({vectors: fromJS(vectors), components})
  if (props) {
    return <SemanticDisplay key="SemanticDisplay" space={space} {...props}/>
  }
  return <SemanticDisplay key="SemanticDisplay" space={space}/>
}

window.mountApp = function(vectors, targetId = 'app') {
  let styleContext = window.ROOT_STYLE_CONTEXT
  // let initState = base64.decode(window.pageReactInitState);
  // initState = JSON.parse(initState);
  let initState = window.pageReactInitState
  window.initState = initState


  // ????????????????????????????????????????????????????????????????????????????
  window.theGridSiteType = 'permalink'
  // routes.initState = initState;
  // ReactDOM.render(<Router history={browserHistory}  onUpdate={() => { if (typeof window !== 'undefined') window.scrollTo(0, 0)}} routes={routes} />, document.getElementById('app'));   hihkjhjh
  return ReactDOM.render(<App key="app">{renderVectors(vectors)}</App>, document.getElementById(targetId))
}

function checkVECTORS() {
  if (window.VECTORS) {
    mountApp(window.VECTORS)
  }
  else {
    raf(checkVECTORS)
  }
}
checkVECTORS()



