import React from 'react'
import {Map, List, Struct} from '@the-grid/immutable'
import {StyleContext} from '@the-grid/styleverse'

// var EMPTY_OBJ = {}

// TODO: id, key...  path???
var SemanticVector = Struct({

  ___id: undefined,
  id: {
    set(___id) {
      return {___id}
    },
    cache: false,
  //  instanceCache: true,
    deps: ['___id', 'parentId', 'semanticRole', 'semanticIndex'],
    get(_id, parentId, semanticRole, semanticIndex) {
      if (_id) return _id
      if (!parentId) return undefined
      _id = parentId
      if (semanticRole) _id += '__' + semanticRole

      if (semanticIndex) _id += '_' + semanticIndex
      return _id
    }
  },

  parentId: undefined,

  _styleContext: null,
  styleContext: {
    set(_styleContext) {
      return {_styleContext}
    },
    cache: true,
    cacheInstance: true,
    deps: ['_styleContext'],
    get(_styleContext) {
      if (!_styleContext) return null
      return _styleContext.toJS()
    },
  },

  $class: undefined,

  semanticRole: 'subject',
    // set internally

  semanticIndex: undefined,
    // set internally
    // if context semanticIndex === 0
    // all subjects semanticIndex >= 1

  semanticKey: undefined,
    // set internally when subjects are keyed not indexed

  components: {
    set(components) {
      // merge to immutable
      return {components: Map(components)}
    },
  },
    // local component registry

  component: {
    set(component) {
      if (!component.includes('.')) return {component}
      var [component, ..._component_className] = component.split('.')
      return {component, _component_className: _component_className.join(' ')}
    },
  },

  _component_className: '',
    // string

  props: undefined, // {
//     set(props) {
//       // merge to immutable
//       return {props}
//     }
//   },

  props_to_render: {
    cache: 1,
    instanceCache: true,
    deps: ['props', 'id',
      'semanticRole', 'semanticIndex', 'semanticKey',
      'component', '_component_className'
    ],
    get(
      props, id,
      semanticRole, semanticIndex, semanticKey,
      component, _component_className) {
      // injects:
      // - props.id if not set
      // - props.key if not set
      // - props.semanticRole
      // - props.semanticIndex
      // - className `semantic-role--{}`
      // - className `semantic-index--{}`
      // - className `component--{}`

      var key = id ? id : semanticRole + '-' + semanticIndex

      let className = 'semantic-role--' + semanticRole
      className += ' component--' + component
      if (_component_className) className += ' ' + _component_className
      if (semanticIndex !== undefined) {
        className += ' semantic-index--' +  semanticIndex
      }
      if (semanticKey !== undefined) {
        className += ' semantic-key--' +  semanticKey
      }
      if (!props) {
        return {
          id,
          key,
          className,
          semanticRole,
          semanticIndex,
          semanticKey,
        }
      }
      var props_to_render = {}
      if (props.toJS && props.forEach) {
        props.forEach(function(val, key) {
          // TODO!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
          if (key === 'style') {
            if (val.toJS) val = val.toJS()
          }
          props_to_render[key] = val
        })
      }
      if (!props_to_render.id && id) props_to_render.id = id
      if (!props_to_render.key) props_to_render.key = key
      props_to_render.semanticRole = semanticRole
      props_to_render.semanticIndex = semanticIndex
      props_to_render.semanticKey = semanticKey
      props_to_render.className = (!!props_to_render.className)
        ? className + ' ' + props_to_render.className
        : className
      return props_to_render
    },
  },

  render: {
    cache: false,
    instanceCache: false,
    params: ['components_inherited'], // passed in component registry
    deps: [
      'components',
      'component','props_to_render',
      'renderContext','renderSubjects','renderCover', //'actions',
      'styleContext',
    ],
    get(
        components_inherited,
        components,
        c, p,
        renderContext, renderSubjects, renderCover, //actions,
        styleContext,
    ) {
      var Component
      if (!components_inherited) components_inherited = Map.EMPTY
      components = components
        ? components_inherited.merge(components)
        : components_inherited
      Component = components.get(c)
      if (!Component) throw new Error('SemanticVector: cant find component ' + c)

      var renderedContext = renderContext(components)
      var renderedCover = renderCover(components)
      var renderedSubjects = renderSubjects(components)


      var keyedChildren
      if (renderedSubjects && !(renderedSubjects instanceof Array)) {
        keyedChildren = renderedSubjects
        renderedSubjects = null
        p = Object.assign({keyedChildren}, p)
      }
      else {
              // NOTE: must clone props?
        p = Object.assign({}, p)
      }



      // NOTE: if no context, cover or subjects, then no children
      if (!renderedContext && !renderedSubjects && !renderedCover && !keyedChildren) return React.createElement(Component, p)

      // NOTE: if context or subjects, then children[0] is context or null
      var children = []
      if (renderedCover) children.push(renderedCover)
      if (renderedContext) children.push(renderedContext)
      if (renderedSubjects) children = children.concat(renderedSubjects)

      children = React.createElement(Component, p, children)

      // should not leave render footprint...
      if (styleContext) {

        // NOTE: must clone styleContext props!
        styleContext = Object.assign({},styleContext)
        if (p.key) styleContext.key = p.key + '-style-context'
        children = React.createElement(StyleContext, styleContext, children)
      }
      return children
    },
  },

  _context: undefined,
  context: {
    set(context) {
      context = make(context, {semanticRole: 'context', parentId: this.get('id')})
      return {_context: context}
    },
    deps: ['_context'],
    cache: false,
    get(_context) {
      return _context
    },
  },
  renderContext: {
    cache: 1,
    instanceCache: true,
    params: ['components'], // passed in component registry
    deps: ['context'],
    get(components, context) {
      if (context) return context.render(components)
      return null
    },
  },

  _cover: undefined,
  cover: {
    set(cover) {
      cover = make(cover, {semanticRole: 'cover', semanticIndex: 0, parentId: this.get('id')})
      return {_cover: cover}
    },
    deps: ['_cover'],
    cache: false,
    get(_cover) {
      return _cover
    },
  },
  renderCover: {
    cache: 1,
    instanceCache: true,
    params: ['components'], // passed in component registry
    deps: ['cover'],
    get(components, cover) {
      if (cover) return cover.render(components)
      return null
    },
  },

  subjectsType: undefined,
  subjectsKeys: undefined,
  _subjects: undefined,
  subjects: {
    set(subjects) {
      if (!subjects) return undefined
      var
        parentId = this.get('id'),
        i,
        l,
        keys,
        key,
        subject,
        subjectsKeys,
        isImmutable = !!subjects.toJS && !!subjects.equals,
        subjectsType = subjects.push ? 'indexed' : 'keyed',
        _subjects

      // keyed
      if (subjectsType === 'keyed') {
        keys = isImmutable
          ? subjects.keySeq().toArray()
          : Object.keys(subjects)
        l = keys.length
        if (!l) return undefined
        _subjects = {}
        subjectsKeys = []
        for (i = 0; i < l; i++) {
          key = keys[i]
          subject = isImmutable ? subjects.get(key) : subjects[key]
          if (!subject) continue
          subjectsKeys.push(key)

          // collection of subjects at key
          if (subject.push) {
            _subjects[key] = subject.map(function(sub, ii){
              return make(sub, {semanticRole: 'subject', semanticKey: key, semanticIndex: `${i+1}_${ii+1}`, parentId})
            })
          }
          // single subject at key
          else {
            _subjects[key] = make(subject, {semanticRole: 'subject', semanticIndex: i+1, semanticKey: key, parentId})
          }
        }
      }

      // indexed
      else {
        l = isImmutable ? subjects.size : subjects.length
        _subjects = []
        if (!l) return undefined
        for (i = 0; i < l; i++) {
          subject = isImmutable ? subjects.get(i) : subjects[i]
          _subjects.push(make(subject, {semanticRole: 'subject', semanticIndex: i+1, parentId}))
        }
        subjectsKeys = 0
      }

      return {_subjects, subjectsType, subjectsKeys} // NOTE: uses .merge which converts to immutable List
    },
    deps: ['_subjects'],
    cache: 1,
    instanceCache: true,
    get(_subjects) {
      return _subjects
    },
  },

  renderSubjects: {
    cache: 1,
    instanceCache: true,
    params: ['components'], // passed in component registry
    deps: ['subjects', 'subjectsType', 'subjectsKeys'],
    get(components, subjects, subjectsType, keys) {
      if (!subjects || !subjects.size) return null

      var
        i,
        key,
        children,
        subject

      // keyed
      if (subjectsType === 'keyed') {
        children = {}
        subject
        for (i = 0; i < keys.size; i++) {
          key = keys.get(i)
          subject = subjects.get(key)
          // collection of subjects at key
          if (subject.push) {
            if (!subject.size && !subject.length) continue
            children[key] = []
            subject.forEach(function(sub, i){
              children[key].push( sub.render(components) )
            })
          }
          // single subject at key
          else {
            children[key] = subject.render(components)
          }
        }

        return children
      }

      // indexed
      else {
        children = []
        subject
        for (i = 0; i < subjects.size; i++) {
          subject = subjects.get(i)
          children.push(subject.render(components))
        }
        return children
      }

    },
  },

  _actions: undefined,
  actions: {
    set(actions) {
      // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
      throw new Error('SemanticVector: ACTIONS NOT REAY')

      if (!actions) return undefined
      var l, i, action
      var _actions = []
      var isImmutable = !!actions.toJS && !!actions.equals
      l = isImmutable ? actions.size : actions.length
      if (!l) return undefined
      for (i = 0; i < l; i++) {
        action = isImmutable ? actions.get(i) : actions[i]

        // TODO???
        //_actions.push(make(action), {semanticRole: 'action', semanticIndex: i, parentId: this.get('id')})

      }
      return {_actions} // NOTE: uses .merge which converts to immutable List
    },
    deps: ['_actions'],
    cache: 1,
    instanceCache: true,
    get(_actions) {
      return _actions
    },
  },
})

var EMPTY = new SemanticVector()

function make(o, config) {
  // if (semanticRole === 'context') semanticIndex = undefined
  if (o.merge && o.toJS && o.equals) {
    if (config) o = o.merge(config)
    if (o.get && o.get('StructType') === SemanticVector) {
      return o
    }
    return EMPTY.merge(o)
  }
  if (!config) return new SemanticVector(o)
  return new SemanticVector(Object.assign({},o,config))
}
function makeList(a) {
  if (!a) return List.EMPTY
  var isImmutable = (a.equals && a.hashCode)
  var l = isImmutable ? a.size : a.length
  if (!l) return List.EMPTY
  var vectors, vector, i
  vectors = []
  for (i = 0; i < l; i++) {
    vector = isImmutable ? a.get(i) : a[i]
    vectors.push(make(vector, {semanticRole: 'subject', semanticIndex: i + 1}))
  }
  return List(vectors)
}
SemanticVector.make = make
SemanticVector.makeList = makeList

export default SemanticVector



