import Color from '../Color'
import polator from '../polator'
import buildPalette from '../helper/paletteBuilder'
import basesFromPalette from '../helper/basesFromPalette'
import _ from '../_'
// import filters from "../filters";
// Synthesizer = require("./helper/Synthesizer").Synthesizer

let {
  nextFrom,
  scoredQuery,
  nearestNum,
  nearestCircularDistributed,
} = _
let defaultOptions = {
  format: 'hsl', // hslString, rgb, rgbString
  hueTargets: null, // [280, 60]
  hueCount: null, // 360
  hueOffset: 0,
}
  // lightest: {h:0,s:0,l:100}
  // darkest: {h:0,s:0,l:0}

class Solver {

  constructor(inputColors, options = {}) {
    if (!inputColors) throw new Error('Chromatose: new Solver(inputColors, options) requires inputColors')
    this.palette = buildPalette(inputColors)
    this.possibleBases = basesFromPalette(this.palette, options)
  }

  solve(options = {}) {
    let {palette} = this
    options = Object.assign({}, defaultOptions, options)
    let {
      format,
      hueTargets,
      hueCount,
      hueOffset,
      base,
      type,
    } = options
    if (!!base) {
      if (typeof base === 'string') {
        base = Color(base)
      } else if (base.hsl && !!base.chromaDistance) {
        base = base
      } else {
        let baseIndex = base.index
        if (typeof baseIndex !== 'number') baseIndex = 0
        let baseType = base.type
        if (!baseType || baseType === 'natural') {
          baseType = 'all'
        }
        if (this.possibleBases[baseType] && this.possibleBases[baseType][baseIndex]) {
          base = this.possibleBases[baseType][baseIndex]
        }
        else if (baseType === 'natural-inverse') {
          base = this.possibleBases['all'][0]
          if (base.light()) {
            base = this.possibleBases['dark'][baseIndex]
          }
          else {
            base = this.possibleBases['light'][baseIndex]
          }
        }
        else {
          base = Color(baseType)
        }
        if (!base) return null
      }
    }

    if (!base) {
      base = this.possibleBases.all[0]
    }

    let {h,s,l} = base.hsl()

    // Box Background Color
    // ------------------------------

    let box_h
    // closest target hue based on input hue
    if (hueTargets) {
      box_h = nearestNum(h, hueTargets)
    } else if (hueCount) {
      box_h = nearestCircularDistributed(h, hueCount, 360, hueOffset)
    }

    // if h is 18 and s is 71
    let _isVibrant
    let isDark
    let isLight
    let box
    if (base.isVibrant()) {
      _isVibrant = true
      box = base.clone()
      box.alpha(1)
    }
    // dark
    else if (base.dark()) { // (l <= 35) or (l <= 50 and s <= 15)
      isDark = true
      if (base.hsl().l > 15) {
        box = base.darkMutedClone()
      } else {
        box = base.clone()
      }

    // light
    } else {
      isLight = true
      if (base.hsl().l < 95) {
        box = base.lightMutedClone()
      } else {
        box = base.clone()
      }
    }


    // Text Color
    // ------------------------------
    let titleHSL
    let title
    let textHSL
    let text
    let boxPop
    let boxHSL
    let textPop
    if (_isVibrant) {
      if (box.dark()) {
        title = []

        titleHSL = palette.byLuminosity[0].hsl()
        titleHSL.s = Math.min(25, titleHSL.s)
        titleHSL.l = Math.max(88, titleHSL.l)
        title.push(Color(titleHSL))

        titleHSL = palette.byLuminosity[1].hsl()
        titleHSL.s = Math.min(33, titleHSL.s)
        titleHSL.l = Math.max(80, titleHSL.l)
        title.push(Color(titleHSL))

        title.push(Color('black'))

        // text = Color "white"
        textHSL = palette.byLuminosity[0].hsl()
        textHSL.l = Math.max(95, textHSL.l)
        text = [Color(textHSL)]

        boxPop = scoredQuery(palette, function(c) {
          if (c === base) { return undefined }
          // return undefined unless c.isBrand()
          // unless c.isVibrant()
          //  return undefined if c.contrast(box) < 1.5
          let score = 0
          // score -= .5 if c is textPop[0]
          // score += 1.5 if c.isVibrant()
          score += 1 - (1 / (c.contrast(box)))
          score += 1 / Math.max(1,c.chromaDistance())
          return score
        })

        boxHSL = box.hsl()

        // avoid color discord
        for (let i = 0; i < boxPop.length; i++) {
          // if Math.abs(box.hsl().l - clone.hsl().l) <= 15
          //  if boxPop.length > 1
          //    boxPop.splice(i,1)
          //    continue
          let bp = boxPop[i]
          if (bp.chromaDistance() <= 90) {
            let clone = bp.clone()
            clone.saturation(boxHSL.s)
            if (Math.abs(boxHSL.l - clone.hsl().l) < 10) {
              clone.lightness(boxHSL.l * 1.6)
            }
            boxPop.splice(i,1,clone)
          }
        }
      } else {
        title = []
        titleHSL = palette.byLuminosity[palette.byLuminosity.length - 1].hsl()
        titleHSL.s = Math.min(18, titleHSL.s)
        titleHSL.l = Math.min(22, titleHSL.l)
        title.push(Color(titleHSL))

        titleHSL = palette.byLuminosity[palette.byLuminosity.length - 2].hsl()
        titleHSL.s = Math.min(22, titleHSL.s)
        titleHSL.l = Math.min(27, titleHSL.l)
        title.push(Color(titleHSL))

        title.push(Color('white'))

        textHSL = palette.byLuminosity[palette.byLuminosity.length - 1].hsl()
        textHSL.l = Math.min(15, textHSL.l)
        text = [Color(textHSL)]
        // text = Color "black"

        boxPop = scoredQuery(palette, function(c) {
          if (c === base) { return undefined }
          // return undefined unless c.isBrand()
          // unless c.isVibrant()
          //  return undefined if c.contrast(box) < 1.5
          let score = 0
          // score -= .5 if c is textPop[0]
          // score += 1.5 if c.isVibrant()
          score += 1 - (1 / (c.contrast(box)))
          score += 1 / Math.max(1,c.chromaDistance())
          return score
        })
        // boxPop.push Color 'black'

        boxHSL = box.hsl()

        // avoid color discord
        for (let i = 0; i < boxPop.length; i++) {
          let bp = boxPop[i]
          if (bp.chromaDistance() <= 90) {
            // avoid color discord
            let clone = bp.clone()
            clone.saturation(boxHSL.s)
            if (Math.abs(boxHSL.l - clone.hsl().l) < 10) {
              clone.lightness((boxHSL.l * 3) / 4)
            }
            boxPop.splice(i,1,clone)
          }
        }
      }
    }

    else if (box.dark()) {
      title = scoredQuery(palette, function(c) {
        if (c.hsl().l > 95) { return undefined }
        let score = 0
        if (c.isReddish()) { score -= 1 } // subtle red sucks
        score += c.luminosity()
        score += 1 / Math.max(1,c.chromaDistance())
        return score
      },
        {max: 1, clone: true}
      )

      let toConcat = []
      for (let i = 0; i < title.length; i++) {
        let title_c = title[i]
        let {l} = title_c.hsl()
        title_c.lightness(Math.max(Math.min(75, l),90))
        // low
        toConcat.push(title_c.clone().mix(box,.3))
      }
      title = title.concat(toConcat)

      // high
      title.push(Color('white'))

      textHSL = palette.byLuminosity[palette.byLuminosity.length - 1].hsl()
      textHSL.l = Math.max(Math.min(90, textHSL.l),95)
      text = [Color(textHSL)]
      text.push(text[0].clone().mix(box, .3))
      // text.push Color('white')

      textPop = scoredQuery(palette, function(c) {
        if (c === base) { return undefined }
        if (c === text[0]) { return undefined }
        if (!c.isBrand()) { return undefined }
        if (!c.light() || !c.isVibrant()) {
          if (c.contrast(text[0]) < 1.5) { return undefined }
        }
        if (c.contrast(box) < 2) { return undefined }
        let score = 0
        if (c.light()) { score += 1 }
        if (c.isVibrant()) { score += 1.5 }
        score += 1 - (1 / ((c.contrast(box) - 2.2) + 1))
        score += 1 / Math.max(1,c.chromaDistance())
        return score
      })

      boxPop = scoredQuery(palette, function(c) {
        if (c === base) { return undefined }
        if (!c.isBrand()) { return undefined }

        let score = 0

        let ccd = c.chromaDistance()
        // unless c.isVibrant()
        //  return undefined if c.contrast(box) < 1.5
        if (ccd > 100) {
          if (c.contrast(box) < 1.5) { return undefined }
        } else {
          let bonus = polator(ccd, 0, 100, 2, .1)
          score += bonus
        }

        score += 1 - (1 / (c.contrast(box)))

        if (c === textPop[0]) {
          score *= .5
        }

        return score
      })

      // add the most ing muted boxPop if one or less
      if (boxPop.length <= 1) {
        boxPop = boxPop.concat(scoredQuery(palette, function(c) {
          if (c.isBrand()) { return undefined }
          let score = 0
          score += 1 - (1 / (c.contrast(box)))
          return score
        }
        , {max: 2}))
      }
      // add synthetic vibrants
      for (let j = 0; j < palette.dormantHues.length; j++) {
        let dhue_ = palette.dormantHues[j]
        s = Math.max(Math.min(60,box.hsl().s),75)
        let vsynth_ = Color({h: dhue_, s, l: 55})
        boxPop.push(vsynth_)
      }
      // add synthetic light
      // lsynth = vsynth.clone()
      // lsynth.mix(Color("white"))
      // boxPop.push lsynth
      // TODO: check for boxPop discords
      // if boxPop?.chromaDistance() > 120

      // if textPop.length is 0
      let boxS = box.hsl().s
      for (let u = 0; u < boxPop.length; u++) {
        let bxp_c = boxPop[u].clone()
        if (bxp_c.isYellowish()) {
          bxp_c.lighten(.3)
        }
        if (bxp_c.isVibrant()) {
          if (boxS >= 25) {
            bxp_c.saturation(boxS)
          } else {
            bxp_c.desaturate(.1)
          }
        }
        if (bxp_c.dark()) {
          bxp_c.lighten(.2)
        }
        textPop.push(bxp_c)
      }

      // ensure textPop colors are legible on dark by mixing with white when needed
      for (let q = 0; q < textPop.length; q++) {
        let tp_c_ = textPop[q]
        let ccd_ = tp_c_.chromaDistance()
        if (ccd_ <= 100) {
          let whitenAmount = polator(ccd_, 0, 100, .05, .4)
          let newColor = tp_c_.clone()
          newColor.mix(Color('white'), whitenAmount)
          textPop.splice(q,1,newColor)
        }
      }


      // textPop = {
      //  h:box_h,
      //  s:s + 40,
      //  l:box_l + 40 # tone-on-tone
      // }
    }
    else {
      title = scoredQuery(palette.byLuminosity, function(c) {
        if (c.hsl().l <= 10) { return undefined }
        let score = 0
        if (c.isReddish()) { score -= 1 } // subtle red sucks
        score -= c.luminosity()
        score += 1 / Math.max(1,c.chromaDistance())
        return score
      }
      , {max: 1, clone: true})
      // if base.hslString() is "hsl(38, 39%, 62%)"

      let toConcat_ = []
      for (let ii = 0; ii < title.length; ii++) {
        let titleColor = title[ii]
        let {l} = titleColor.hsl()
        titleColor.lightness(Math.min(Math.max(40, l),20))
        // low
        toConcat_.push(titleColor.clone().mix(box,.3))
      }
      title = title.concat(toConcat_)
      // high
      title.push(Color('black'))

      textHSL = palette.byLuminosity[palette.byLuminosity.length - 1].hsl()
      textHSL.l = Math.min(15, textHSL.l)
      text = [Color(textHSL)]
      text.push(text[0].clone().mix(box,.5))

      // TODO pic, mixed text or original base based on
      textPop = scoredQuery(palette, function(c) {
        if (c === base) { return undefined }
        if (!c.isBrand()) { return undefined }
        if (c.contrast(text[0]) < 1.5) { return undefined }
        if (c.contrast(box) < 2) { return undefined }

        let score = 0

        let ccd = c.chromaDistance()
        if (ccd <= 100) {
          let bonus = polator(ccd, 0, 100, 2, .1)
          score += bonus
        }
        score += 1 - (1 / (c.contrast(box)))
        return score
      })

      boxPop = scoredQuery(palette, function(c) {
        if (c === base) { return undefined }
        if (!c.isBrand()) { return undefined }

        let score = 0

        let ccd = c.chromaDistance()
        // unless c.isVibrant()
        //  return undefined if c.contrast(box) < 1.5
        if (ccd > 100) {
          if (c.contrast(box) < 1.5) { return undefined }
        } else {
          let bonus = polator(ccd, 0, 100, 2, .1)
          score += bonus
        }

        score += 1 - (1 / (c.contrast(box)))

        if (c === textPop[0]) {
          score *= .5
        }

        return score
      })

      // add the most ing muted boxPop if one or less
      if (boxPop.length <= 1) {
        boxPop = boxPop.concat(scoredQuery(palette, function(c) {
          if (c.isBrand()) { return undefined }
          let score = 1 - (1 / (c.contrast(box)))
          return score
        }
        , {max: 2}))
      }

      // add synthetic vibrant
      for (let j = 0; j < palette.dormantHues.length; j++) {
        let dhue = palette.dormantHues[j]
        s = Math.max(Math.min(60,box.hsl().s),75)
        let vsynth = Color({h: dhue, s, l: 55})
        boxPop.push(vsynth)
      }
      // add synthetic light
      // lsynth = vsynth.clone()
      // lsynth.mix(Color("black"))
      // boxPop.push lsynth
      // TODO: check for boxPop discords
      // if boxPop?.chromaDistance() > 120

      // if textPop.length is 0
      let boxS_ = box.hsl().s
      for (let _ix = 0; _ix < boxPop.length; _ix++) {
        let bp_c = boxPop[_ix].clone()
        if (bp_c.isYellowish()) {
          bp_c.darken(.3)
        }
        if (bp_c.isVibrant()) {
          if (boxS_ >= 25) {
            bp_c.saturation(boxS_)
          } else {
            bp_c.desaturate(.1)
          }
        }
        if (bp_c.light()) {
          bp_c.darken(.2)
        }
        textPop.push(bp_c)
      }

      // ensure textPop colors are legible on light by mixing with black when needed
      for (let t = 0; t < textPop.length; t++) {
        let tp_c = textPop[t]
        if (tp_c.isVibrant()) {
          let newColor = tp_c.clone()
          newColor.mix(Color('black'),.15)
          textPop.splice(t,1,newColor)
        }
      }
    }

    let meta
    let tpop1
    let meta1
    let tpop2
    let meta2
    if (_isVibrant) {
      if (base.light()) {
        tpop1 = base.clone().mix(Color('black'),.5)
        meta1 = base.clone().mix(Color('black'),.3)
        tpop2 = base.clone()
        meta2 = base.clone()
      } else {
        tpop1 = base.clone().mix(Color('white'),.75)
        meta1 = base.clone().mix(Color('white'),.4)
        tpop2 = base.clone()
        meta2 = base.clone()
      }
      if (box.contrast(tpop1) > box.contrast(tpop2)) {
        textPop = [tpop1]
      } else {
        textPop = [tpop2]
      }
    } else {
      meta1 = text[0].clone()
      meta1.mix(box)
      meta1.mix(box,.4)
      meta2 = base.clone()
    }
    if (box.contrast(meta1) > box.contrast(meta2)) {
      meta = meta1
    } else {
      meta = meta2
    }

    let textShadow
    if (box.light()) {
      textShadow = box.clone().lighten(.3).alpha(.5)
    } else {
      textShadow = box.clone().darken(.5).alpha(.5)
    }

    let bg
    // canvas
    if (isDark) {
      // for dark solutions, bg is derived from varying box lightness
      let lit = box.hsl().l
      let newLit
      if (lit > 4) {
        newLit = lit - polator(lit, 0, 100, 3, 0)
        bg = box.clone().lightness(newLit)
      }
      else {
        newLit = 7
        bg = box.clone().lightness(newLit)
      }
    }
    else if (isLight) {
      // for light solutions, base is used for bg get a little dormant hues out
      bg = base.clone().mix(Color('white'))
      bg = Color('hsl(0,0%,50%)').mix(bg,.9).mix(box,.4)
    }
    else {
      // for vibrant solutions, bg is from box
      let {h,s,l} = box.hsl()
      bg = Color({h, s: s - 10, l: l - 4})
      // if (box.dark()) {
      //
      // }
      // else {
      //  bg = Color({h, s: s-5, l: l+4})
      // }
    }


    title = title.concat(textPop)

    let metaHover
    if (meta.light()) {
      metaHover = meta.clone().darken(.15)
    } else {
      metaHover = meta.clone().lighten(.15)
    }

    if (!boxPop || boxPop.length === 0) {
      if (base.light()) {
        boxPop = [Color('black')]
      }
      if (!boxPop) {
        boxPop = [Color('white')]
      }
    }

    let boxPopText = boxPop.map(bpop => bpop.whiteOrBlack())

    let boxPopTextHover = boxPopText.map(function(bpopText) {
      let bpopTextHover = bpopText.clone()
      if (bpopTextHover.light()) {
        return bpopTextHover.darken(.2)
      } else {
        return bpopTextHover.lighten(.2)
      }
    })

    // TODO - define a standard output for colors generator that would cover all
    // UI needs.
    let result = {
      base,
      title, // [format]()
      bg, //
      box, // TODO - not currenlty an array, should it be? [format]()
      text, // array
      meta, // single color
      metaHover,
      boxPop, // [format]()
      boxPopText,
      boxPopTextHover,
      textPop, // [format]()
      textShadow,
      palette,
      type, // dark, light, vibrant, user
    }

    // result.filters = filters(result);
    return result
  }
}

export default Solver



