import React from 'react'
import Struct from './Struct'

//let mountCountById = {}
let mountCount = 0

class SVGGradientMap extends React.Component {
  constructor(...args) {
    super(...args)
    this.state = {
      struct: undefined,
    }
  }

  //renderable = true
  componentWillMount() {
    let id = this.props.id
    mountCount++
    // if (!mountCountById[id]) {
    //   mountCountById[id] = 1
    // }
    // else {
    //   mountCountById[id]++
    //   this.renderable = false
    // }
    this.setState({struct: new Struct(Object.assign({},this.props,{id:this.props.id + '-' + mountCount}))})
  }

  componentWillReceiveProps(props) {
    const struct = this.state.struct.merge(props)
    if (struct.equals(this.state.struct)) return
    this.setState({struct})
  }

  render() {
    return this.state.struct.get('render')
  }

}

const {PropTypes} = React
SVGGradientMap.propTypes = {
  id: PropTypes.string.isRequired,
  colors: PropTypes.array.isRequired,
  stops: PropTypes.array,
}

export default SVGGradientMap



