import Color from "../Color";

class Synthesizer {

  constructor() {
    this.config = {
      vibrant: {
        l_max: 60,
        l_min: 40,
        s: 88 // TODO: make function?
      }
    }
    this
  }

  vibrant(color) {
    if (color.isVibrant()) return color.clone()
    let {h,s,l} = color.hsl()
    l = Math.min(Math.max(l, this.config.vibrant.l_min), this.config.vibrant.l_max)
    s = this.config.vibrant.s
    return Color({h, s, l})
  }

  dark(color) {
    let {h,s,l} = color.hsl()
    l = Math.min(l, 15)
    s = Math.min(s+8, 20)
    return Color({h,s,l})
  }

  light(color) {
    let {h,s,l} = color.hsl()
    l = Math.max(l, 95)
    s = Math.min(s+8, 20)
    return Color({h,s,l})
  }
}

export { Synthesizer };


