import _ from "../_"
import Color from "../Color"
import {Synthesizer} from "./Synthesizer"
let {scoredQuery} = _

const synthesizer = new Synthesizer()
function basesFromPalette(palette, o = {}) {

  const {
    singleBase,
  } = o

  const bases = {
    palette,
    all: [],
    dark: [],
    light: [],
    vibrant: [],
    '!dark': [],
    '!light': [],
    '!vibrant': [],
  }

  const addDark = function(color) {
    bases.all.push(color)
    bases.dark.push(color)
    bases['!vibrant'].push(color)
    return bases['!light'].push(color)
  }

  const addVibrant = function(color) {
    bases.all.push(color)
    bases.vibrant.push(color)
    bases['!dark'].push(color)
    return bases['!light'].push(color)
  }

  const addLight = function(color) {
    bases.all.push(color)
    bases.light.push(color)
    bases['!vibrant'].push(color)
    return bases['!dark'].push(color)
  }

  // TODO
  // @carl, Why do we need singleBase?
  // - @D4
  const sbc = singleBase
  if (!!sbc) {
    //single base is use to force chromatose to use a single base.
    bases.all.push(sbc)
    if (sbc.dark()) { bases.dark.push(sbc) }
    if (sbc.light()) { bases.light.push(sbc) }
    if (sbc.isVibrant()) { bases.vibrant.push(sbc) }
    return bases
  }

  let colorKeys = palette
  var mostVibrantColor
  var leastChromaDist = 1000
  for (var i = 0, len = colorKeys.length; i < len; i++) {
    let color = colorKeys[i]
    let chromaDist = color.chromaDistance()
    if (chromaDist < leastChromaDist) {
      mostVibrantColor = color
      leastChromaDist = chromaDist
    }
    if (color.isVibrant()) {
      addVibrant(color)
    } else if (color.dark()) {
      if (bases.dark.length < 3) {
        addDark(color)
      }
    } else if (color.light()) {
      if (bases.light.length < 3) {
        addLight(color)
      }
    }
  }

  if (!bases.vibrant.length) {
    addVibrant(synthesizer.vibrant(mostVibrantColor))
  }

  if (bases.dark.length < 3) {
    let q = scoredQuery(palette, function(c) {
      if (!c.light() && !c.isVibrant()) { return undefined }
      const score = 0
      //score -= 1 if c.isVibrant()
      return score
    })
    let i = 0
    while (bases.dark.length < 3 && i < q.length) {
      let color = q[i]
      addDark(synthesizer.dark(color))
      i++
    }
  }
  // add black if needed
  let needsBlack = true
  let baseKeys_dark = bases.dark
  for (let i = 0, len = baseKeys_dark.length; i < len; i++) {
    let base = baseKeys_dark[i]
    if (base.hsl().l <= 5) {
      needsBlack = false
      break
    }
  }

  if (needsBlack) {
    const black = Color('black')
    addDark(black)
  }

  if (bases.light.length < 3) {
    let q = scoredQuery(palette, function(c) {
      if (!c.dark() && !c.isVibrant()) { return undefined }
      const score = 0
      //score -= 1 if c.isVibrant()
      return score
    })
    let i = 0
    while (bases.light.length < 3 && i < q.length) {
      let color = q[i]
      addLight(synthesizer.light(color))
      i++
    }
  }
  // add white if needed
  let needsWhite = true
  let light = bases.light
  let baseKeys_light = light
  for (let i = 0, len = baseKeys_light.length; i < len; i++) {
    let base = baseKeys_light[i]
    if (base.hsl().l >= 97) {
      needsWhite = false
      break
    }
  }

  if (needsWhite) {
    const white = Color('white')
    addLight(white)
  }

  return bases
};

export default basesFromPalette


