import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import {Map, fromJS, Struct} from '@the-grid/immutable'
import Card from '@the-grid/ui-card'
import {SemanticSpace, SemanticDisplay} from '@the-grid/ui-semantics'
import LayoutFlickr from "@the-grid/ui-layout-flickr"
import Media from '@the-grid/ui-media'
import SectionBase from './section-base'
import SectionDual from './section-dual'
import SectionLayout from './section-layout'
import Site from './site'
import SiteLogo from './site-logo'
import Header from '@the-grid/ui-header'
import Icon from '@the-grid/ui-icon'
import Ornament from '@the-grid/ui-ornament'
import GMap from '@the-grid/ui-map'
import {ActionGroup, CTA} from '@the-grid/ui-action-pack'

var el = React.createElement
var Children = React.Children

export default {
  'div': function(props) {return el('div', {...props})},
  'Site': Site,
  'Header': Header,
  'SectionDual': SectionDual,
  'section-base': SectionBase,
  'section-layout': SectionLayout,
  'SectionLayout': SectionLayout,
  'list': function(_props) {
    var {children, className, ...props} = _props
    return el('ul', {...props, className: className + ' list'}, Children.map(children, function(child){
      return el('li', {className: 'ui--li'}, child)
    }))
  },
  'detail': function(_props) {
    var {type, value, label, className, ...props} = _props
    var i, _value
    if (type === 'rating-stars') {
      _value = []
      for (i = 0; i < value; i++) {
        _value.push(el(Icon, {key: 'star-' + i}, 'star'))
      }
      value = _value
    }
    className += ` detail`
    return el('div', {...props, className}, [
      el('span', {className: 'detail_value'}, value),
      el('span', {className: 'detail_label'}, label),
    ])
  },
  'text-group': function(props) {
    return el('div', {...props, className: props.className + ' text-group typ'})
  },
  'title': function(_props) {
    var {children, className, ...props} = _props
    return el('h1', {...props, className: className + ' title typ', dangerouslySetInnerHTML:{__html: children}})
  },
  'sub-title': function(_props) {
    var {children, className, ...props} = _props
    return el('h2', {...props, className: className + ' sub-title typ', dangerouslySetInnerHTML:{__html: children}})
  },
  'quote': function(_props) {
    var {children, ...props} = _props
    return el('blockquote', {...props, className: props.className + ' quote typ', dangerouslySetInnerHTML:{__html: children}})
  },
  'h1': function(props) {return el('h1', {...props, className: props.className + ' typ'})},
  'h2': function(props) {return el('h2', {...props, className: props.className + ' typ'})},
  'h3': function(props) {return el('h3', {...props, className: props.className + ' typ'})},
  'text': function(props) {
    var {children, ...other} = props
    return el('p', {...other, className: props.className + ' text typ', dangerouslySetInnerHTML:{__html: children}})
  },
  'ActionGroup': ActionGroup,
  'CTA': CTA,
  'cta': CTA,
  'img': function(props) {
    return el('img', {...props})
  },
  'media': function(props) {
    return el(Media, {...props})
  },
  'layout-flickr': function(props) {
    return el(LayoutFlickr, {...props, className: props.className + ' layout-flickr'})
  },
  'card': Card,
  'site-logo': SiteLogo,
  'map': GMap,
  'ornament': function(props) {
    return el(Ornament, {...props})
  },
  'separator': function(props) {
    var {className} = props
    className += ' section-separator'
    var style = { backgroundImage: `url(${props.src})` }
    return el('div', {className,  style} )
  }
}



