import React from 'react'
import {Map} from '@the-grid/immutable'
import {installStore} from '@the-grid/cymatics'
import {ItemStruct, SiteDataStruct} from '@the-grid/the-data-modeler'
import {Styleverse, StyleContext, Style} from '@the-grid/styleverse'
import FABContext from '@the-grid/ui-fab'

// TODO
import Component from '../../base-component'
import Styles from '../styles'
import Overlay from '../overlay'
import SiteStore from './site-store'

import {raf} from '@the-grid/deps'

class Site extends Component {

  static propTypes = {
    data: React.PropTypes.object,
    //children: React.PropTypes.object,
    //route: React.PropTypes.object,
  }
  static contextTypes = {
    request: React.PropTypes.func.isRequired,
  }

  siteData = null

  componentWillMount() {
    // TODO - define how we inject initState.

    var {styleverse} = this.props
    if (styleverse) {
      this.context.request('styleverse', 'merge', styleverse)
    }

    this.store.willMount({data: this.props.data})

    // temp hack... so things like CTAs in raw data format can use
    window.storyline = this.context.stores.storyline

    raf(()=>{
    this.context.request('storyline', 'push-story', 'view-site')//'-color-shuffle')
    })
  }

  componentWillUnmount() {
    this.store.willUnmount()
  }

  // componentDidMount() {
  //
  // }

  // TODO: update siteData???
  componentWillReceiveProps(nextProps) {
   this.store.willGetProps({data: nextProps.data})
  }

  render() {
    // <StyleContext id="app-root" perf={false} {...this.getStyleContextProps()}>
    var id = this.props.id || "site"
    var className = this.props.className || "site"
    return (

          <Overlay>
            <div id={id} className={className} style={{visibility: 'hidden', opacity: 0}}>
              <Styles scope="html">
                <Style perf={true} scope={"#"+id}>{`

                  :scope {
                    ovf-x -- hidden
                    ovf-y -- hidden
                    w -- 100%
                    visibility -- visible !important
                    opacity -- 1 !important
                  }

                  * {
                    box-szg -- border-box
                    pad -- 0
                    mar -- 000000
                  }

                  html {
                    bg-clr -- chroma-dark-1-bg
                    min-h -- 540
                  }

                  body {
                    overflow-x -- hidden
                  }

                  section {
                    will-change -- transform, opacity
                    h1, h2, h3, h4, h5, h6, p, picture, iframe, quote, ol, ul {
                      mar-b -- 1.5rem
                      max-w -- 100%
                    }
                  }

                  a {
                    color -- chroma-text-pop
                  }

                  h1:hover, h2:hover, h3:hover {
                    cursor -- pointer
                  }

                  button {
                    bdr -- n
                  }

                  .author > * {
                    whs -- nowrap
                  }

                  section {
                    z-idx -- 0
                    d -- flex
                    overflow -- hidden
                    w -- 100%
                  }
                  .section-layout {
                    d -- block !important
                    tf-origin -- 50% 0%
                    transition -- transform .3s
                  }

                  //.section-layout > * {
                  //  pos -- relative !important
                  //  tf -- scale(1) !important
                  //}

                  ::selection {
                    bg-color -- chroma-selection
                    color -- chroma-selection-text
                    text-shadow -- none
                  }


                `}</Style>
                <style
                  dangerouslySetInnerHTML={{__html: `
                  html body figure {
                    line-height: 0;
                    -webkit-margin-before: 0;
                    -webkit-margin-after: 0;
                    -webkit-margin-start: 0;
                    -webkit-margin-end: 0;
                  }
                  `}} />
              </Styles>

              {this.props.children}

            </div>
          </Overlay>
    )
  }
}

export default installStore(Site, 'site', (component) => {
  return new SiteStore()
})


