import React from 'react'
import {palettes, raf} from '@the-grid/deps'
import {Style} from '@the-grid/styleverse'
import {connectState} from '@the-grid/cymatics'

var allSheet = (
  <Style id="all-palettes-panel-styles" key="all-panel-styles">{`
  html, body {
    overflow--hidden !important
    overflow-y--hidden !important
    #section-layout {
      d -- none!Important
    }
  }
  `}</Style>

)

var sheet = (
  <Style id="color-panel-styles" key="color-panel-styles">{`
    #color-panel {
      
      .current-copy-hack {
        position -- fixed
        color -- hsl(0, 0%, 100%)
        top -- .5em
        right -- 1em
        fnt-sz -- txt-xs
      }
      
      .current-palette {
        pos -- fixed
        r -- 1em
        t -- 72
        b -- 60
        w -- ${48 * 6}
        z-index -- 2001
        overflow-y -- hidden
        display -- flex
        flex-wrap -- wrap
        flex-direction -- row-reverse
        justify-content -- space-between

      }

      .all-palettes {
        pos -- fixed
        r -- 1em
        l -- 1em
        t -- 72
        b -- 0
        z-index -- 2001
        overflow-y -- scroll
        display -- flex
        flex-wrap -- wrap
        flex-direction -- row-reverse
        justify-content -- space-between
      }

      .palette {
        d -- flex
        flx-dir -- row
        box-sh -- m
      }
      .palette {
        h -- 48
        w -- ${48 * 5}
        mar -- 1em
        d -- flex
        flx-dir -- row
        trans -- transform .2s
        will-change -- transform
        &:hover {
          tf -- scale(1.05)
          z-index -- 1
        }
      }
      .palette_color {
        box-shadow -- inset 0 1.2px 3.6px hsla(0,0%,100%,0.1), 1px 2px hsla(0,0%,0%,0.3)
        flex-grow -- 1
        h -- 100%
        trans -- transform .2s
        &:first-child {
          bdr-t-l-rad -- 2px
          bdr-b-l-rad -- 2px
        }
        &:last-child {
          bdr-t-l-rad -- 2px
          bdr-b-l-rad -- 2px
        }
        &:hover {
          tf -- scale(1.05)
        }
      }
    }
  `}</Style>
)

class ColorPanel extends React.Component {
  static displayName = 'ColorPanel'
  static defaultProps = {
    children: null,
  }
  state = {chroma: null, showAll: false}
  componentDidMount() {
    this.targetEl = document.getElementsByClassName('section-layout')[0]
    this.targetEl.addEventListener('mouseenter', this.finish)

    // this.el.addEventListener('mouseleave', this.finish)
  }
  finish = () => {
    this.targetEl.removeEventListener('mouseenter', this.finish)
    this.targetEl = undefined

    // this.el.removeEventListener('mouseleave', this.finish)
  }
  componentWillUnmount() {this.el.removeEventListener('mouseleave', this.finish)}
  render() {
    var currentChroma = this.state.chroma
    if (!currentChroma) return null
    var showAll = this.state.showAll
    var currentPalette = currentChroma.get('colors').toJS()
    var props = this.props
    var ui
    if (showAll) {
      ui = this.renderAllPalettes(currentPalette)
    }
    else {
      ui = this.renderCurrentPalette(currentPalette)
    }
    return (
      <div id="color-panel" ref={(el) => {
        if (this.el) return
        this.el = el
      }}>
        {sheet}
        {this.props.children}
        {ui}
      </div>
    )
  }
  mustShow = false
  renderCurrentPalette(curr) {
    var that = this
    if (that.mustShow ){
      raf(function(){
        if (!that.mustShow) return
        that.mustShow = false
          that.context.request('FAB', 'showActive')
          //that.context.request('FAB', 'update', {show:true})
        //that.context.request('FAB', 'use-prev') // 'update', that._FAB)
        that.context.request('FAF', 'use-prev') // 'update', that._FAF)
        that.context.request('FAN', 'use-prev') // 'update', that._FAN)
      })
    }

    return (
      <div key="curr-pal" className="current-palette">
        {this.renderPalette(curr, 'curr')}
        <div className="current-copy-hack">{"['"+curr.join("', '")+"']"}</div>
      </div>
    )
  }
  renderAllPalettes(curr) {
    var that = this
    raf(function(){
      if (that.mustShow) return
      that.mustShow = true
      that._FAB = that.context.stores.FAB.state
      that._FAF = that.context.stores.FAF.state
      that._FAN = that.context.stores.FAN.state
      that.context.request('FAB', 'hide')
      //that.context.request('FAB', 'update', {show:false})
      that.context.request('FAN', 'update', {
        size: 'm',
        actionList: [
          {
            className: 'is-inset',
            label: 'extract colors from image',
            willActivate() {
              alert(`Ask the owner for Upload permissions or switch to an account with permisssion.`)
            }
          },
        ],
        show:true
      })
      that.context.request('FAF', 'update', {show:false})
    })
    return (
      <div key="all-pal" className="all-palettes">
        {allSheet}
        {this.renderPalette(curr, 'curr')}
        {palettes.getAll().map(this.renderPalette)}
      </div>
    )
  }
  renderPalette = (palette, i) => {
    var that = this
    return (
      <div key={i} className="palette" 
      style={{position: 'relative'}}
      onClick={function(){
        that.setState({showAll: !that.state.showAll})
        that.props.onSelect(palette)
      }}>
        {palette.map( function(color,j) {
          return (<div key={'color' + j} className="palette_color" style={{background: color}} />)
        })}
        
      </div>
    )
  }
}

export default connectState(ColorPanel,['styleverse.styleContext.chroma'],function(chroma){
  return {chroma}
})



