import {Store} from '@the-grid/cymatics'
import {Map} from '@the-grid/immutable'

var EMPTY_MAP = Map()
let defaultState = EMPTY_MAP

let styleStore_pool = []

var count = 0

class StyleStore extends Store {
  static displayName = 'StyleStore'
  static getInstance = (component) => {
    if (styleStore_pool.length) {
      let store = styleStore_pool.shift()
      return store
    }
    return new StyleStore()
  }
  initIndex = count++
  debug = false
  name = 'styleStore'
  deferCommit = false // hmmmmmmm
  state = defaultState
  StyleStoreStruct = undefined
  layoutNodeStore = undefined

  didSetup = false
  gotBase = false
  gotBp = false
  gotLayout = false
  gotTypo = false
  gotChroma = false
  ROOT = false

  shouldCommit(state) {
    if (!this.didSetup) return false
    var asyncState = state.getIn(['meta','asyncState'])
    if (
      !(
        // TODO: FRAGILE?
        // if asyncState not set, then commit for initial render
        !asyncState ||
        // once asyncState is handled, only commit when near rendering
        asyncState === 'visible' || asyncState === 'display'
      )
    ) return false
    return (
      this.gotBase &&
      this.gotBp &&
      this.gotLayout &&
      this.gotTypo &&
      this.gotChroma
    )
  }

  handleContextStores(stores) {
    this.layoutNodeStore = stores['layout-node']
    if (this.layoutNodeStore) {
      this.layoutNodeStore.connectAsyncLifeCycle(this.handleAsyncLifeCycle)
    }
    else {
      this.handleAsyncLifeCycle('visible')
    }
  }

  handleAsyncLifeCycle = (asyncState, prev) => {
    var meta = this.state.get('meta', EMPTY_MAP)
    meta = meta.merge({asyncState})
    this.execute('set', 'meta', meta)
  }

  // TODO make dynamic!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  contextStores = {

    'styleverse-base': (pluginStore) => {
      this.gotBase = true
      // if (!this.didSetup) return this.silently('handle-plugin', 'base', pluginStore)
      this.execute('handle-plugin', 'base', pluginStore)
    },
    'styleverse-breakpoint': (pluginStore) => {
      // console.log("BREAKPOINT",this.count)
      this.gotBp = true
      // if (!this.didSetup) return this.silently('handle-plugin', 'breakpoint', pluginStore)
      this.execute('handle-plugin', 'breakpoint', pluginStore)
    },
    'styleverse-layout': (pluginStore) => {
      // console.log("LAYOUT",this.count)
      this.gotLayout = true
      // if (!this.didSetup) return this.silently('handle-plugin', 'layout', pluginStore)
      // TODO make more elegant
      this.execute('merge', {
        layout: pluginStore.state,
        breakpoint: pluginStore.state.get('breakpoint'),
        typography: pluginStore.state.get('typography'),
      })
    },
    'styleverse-typography': (pluginStore) => {
      // console.log("TYPOGRAPHY",this.count)
      this.gotTypo = true
      // if (!this.didSetup) return this.silently('handle-plugin', 'typography', pluginStore)
      this.execute('handle-plugin', 'typography', pluginStore)
    },
    'styleverse-chroma': (pluginStore) => {
      this.gotChroma = true
      // if (!this.didSetup) return this.silently('handle-plugin', 'chroma', pluginStore)
      this.execute('handle-plugin', 'chroma', pluginStore)
    },
  }

  actions = {

    'setup': (state, styleverse, ROOT) => {
      // if (!styleverse.StyleStoreStruct || this.didSetup) return state
      this.didSetup = true
      this.ROOT = ROOT
      this.StyleStoreStruct = styleverse.StyleStoreStruct
      return styleverse.StyleStoreStruct.initialState.merge(state)
    },

    'handle-plugin': (state, pluginKey, pluginStore) => {
      state = state.set(pluginKey, pluginStore.state)
      // console.log('StyleStore handle-plugin', state.toJS())
      return state
    },

    'teardown': () => {
      this.ROOT = false
      this.didSetup = false
      this.gotBase = false
      this.gotBp = false
      this.gotLayout = false
      this.gotTypo = false
      this.gotChroma = false
      this.StyleStoreStruct = undefined
      if (this.layoutNodeStore) {
        this.layoutNodeStore.disconnectAsyncLifeCycle(this.handleAsyncLifeCycle)
      }
      this.layoutNodeStore = undefined
      this.depCheck = {}
      styleStore_pool.push(this)
      return defaultState
    },
  }
}

export default StyleStore



