import {Struct, Map, List, fromJS} from '@the-grid/immutable'
import SemanticVector from '../semantic-vector'

var EMPTY_LIST = List.EMPTY
var EMPTY_MAP = Map.EMPTY

var SemanticSpace = Struct({

  _vectors: undefined,
  vectors: {
    set(vectors) {
      return {_vectors: SemanticVector.makeList(vectors)}
    },
    cache: 1,
    instanceCache: true,
    deps: ['_vectors'],
    get(_vectors) {
      return _vectors
    },
  },

  components: {
    // default: EMPTY_MAP,
    set(components) {
      return {components: Map(components)}
    },
  },

  rendered: {
    cache: 1,
    instanceCache: true,
    deps: ['_vectors', 'components'],
    get(vectors, components) {
      var content = []
      var vector
      for (var i = 0; i < vectors.size; i++) {
        vector = vectors.get(i)
        content.push(vector.render(components))
      }
      return content
    },
  },
})

SemanticSpace.EMPTY = new SemanticSpace()

export default SemanticSpace


