import Color from "../Color";

function buildPalette(inputColors) {
  let palette = [];

  let brandColors = palette.brandColors = [];
  let vibrantColors = palette.vibrantColors = [];

  let brandHueSpread = -1;

  let dormantHues = [];
  let fallbackHue = 0;

  for (let i = 0; i < inputColors.length; i++) {
    let inputColor = inputColors[i]
    let color = Color(inputColor)
    let {h,s,l} = color.hsl()

    if (color.isBrand()) {
      for (let j = 0; j < brandColors.length; j++)  {
        let bc = brandColors[j]
        let hueDist = color.hueDistance(bc);
        if (hueDist > brandHueSpread)  brandHueSpread = hueDist
      }
      brandColors.push(color)
    }

    //base = color if !base

    if (color.isVibrant()) {
      vibrantColors.push(color)
    }
    else {
      let isDormant = true
      if (h === 0) { isDormant = false; }
      if (s <= 3) { isDormant = false; }
      // dormant hues seem to lay more in dark colors...
      if (l < 10 || l > 85) { isDormant = false; }
      if (isDormant) {
        let chroma = color.chromaDistance()
        for (let k = 0; k < dormantHues.length; k++) {
          let d = dormantHues[k]
          if (Math.abs( h - d.h ) < 9) {
            if (chroma < d.chroma) { // update if more chroma
              d.chroma = chroma;
              d.h = h
            }
            isDormant = false
            break
          }
        }
        if (isDormant) dormantHues.push({h, chroma})
      }
    }

    palette.push(color)
  }


  if (dormantHues.length === 0 && brandColors.length === 0) {
    dormantHues.push(fallbackHue)
  }
  else {
    // sort by dormant chroma
    dormantHues.sort((a,b) => a.chroma - b.chroma)
    // remove the s
    dormantHues = dormantHues.map(dhue => dhue.h)
  }
  palette.dormantHues = dormantHues
  palette.brandHueSpread = brandHueSpread

  // The WCAG luminosity of the color. 0 is black, 1 is white.
  let byLuminosity = palette.slice(0)
  byLuminosity.sort((a,b) => b.luminosity() - a.luminosity())
  palette.byLuminosity = byLuminosity

  let byDarkness = palette.slice(0)
  byDarkness.sort((a,b) => a.luminosity() - b.luminosity())
  palette.byDarkness = byDarkness

  // by percieved color vibrance
  let byChroma = palette.slice(0)
  byChroma.sort((a,b) => a.chromaDistance() - b.chromaDistance())
  palette.byChroma = byChroma

  // by hue
  let byHue = palette.slice(0)
  byHue.sort((a,b) => a.hsl().h - b.hsl().h)
  palette.byHue = byHue

  return palette
}

export default buildPalette


