import React from 'react'
import Sprung from './sprung'
import rebound from 'rebound'
class SprungSheet extends Sprung {
  render() {
    return <style dangerouslySetInnerHTML={{__html: this.props.children(this.springs, this.props)}} />
  }
}
export default SprungSheet



