import React from 'react'
import {fromJS} from '@the-grid/immutable'
import {raf} from '@the-grid/deps'
import {Story} from '@the-grid/ui-story-machine'
import {Style} from '@the-grid/styleverse'

class ContentMedia extends Story {
  storyName = 'content-media'
  renderedMediaVectors = null
  willMount() {
    if (this.focused) return true
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'update', this.UI.FAB)
    this.contextRequest('FAF', 'update', this.UI.FAF)
    this.contextRequest('FAN', 'update', this.UI.FAN)
  }
  display(children) {
    if (!this.renderedMediaVectors && window.VECTORS_MEDIA) this.renderedMediaVectors = (
      <div id="content-media-mount" key="content-media-mount">
        <Style key="content-media-mount-style">{`
          html, body {
            bg -- black
            overflow -- hidden !important
            #section-layout {
              d -- none !important
            }
          }
          #content-media-mount {
            bg -- black
            position -- fixed
            top -- 0
            left -- 0
            right -- 0
            pad-r -- 1goldx
            pad-l -- .5goldx
            bottom -- 0
            z-index -- 200
            overflow-y -- auto
          }
        `}</Style>
        {window.renderVectors(window.VECTORS_MEDIA, {key: 'vectros-media'})}
      </div>
    )
    return (
      <div id="story--content-media">
        {children}
        {this.renderedMediaVectors}
      </div>
    )
  }
  UI = {
    FAB: fromJS({
      show: true,
      active: true,
      chroma: {type: 'dark', typeIndex: 0},
      className: '',
      //icon: '+',
      activeIcon: 'x',
      icon: '+',
      onClick: () => {
        let that = this
        raf(function(){
          that.contextRequest('storyline', 'to-story', 'view-site')
        })
        return false
      },
      appButton: {
        icon: 'arrow_back',
        show: true,
        //activeIcon: 'arrow_back',
        onClick: () => {
          let that = this
          raf(function(){
            that.contextRequest('storyline', 'pop-story')
          })
          return false
        },
      },
      actionList: [
        {
          label: 'Import from...',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          }
        },
        {
          label: 'Upload',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          }
        },
      ],
    }),
    FAF: fromJS({
      show: false,
    }),
    FAN: fromJS({
      hideOnScroll: false,
      show: true,
      chroma: {type: 'dark', typeIndex: 0},
      active: 1,
      actionList2: [
        //{
        //  icon: 'arrow_back',
        //  willActivate: () => {
        //    raf(()=> {
        //      this.req('app','start-story','on-the-grid')
        //    })
        //  },
        //},
        {
          label: 'copywriting',
          onClick: () => {
            let that = this
            raf(function(){
              that.contextRequest('storyline', 'replace-story', 'content')
            })
            return false
          }
        },
        {
          label: 'media',
        },
        {
          label: 'categories',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Categories or switch to an account with permisssion.`)
            return false
          }
        },
        {
          label: 'commerce',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Commerce or switch to an account with permisssion.`)
            return false
          }
        },

      ],
    })
  }
}

export default ContentMedia


