import React from 'react'
import {installStore} from '@the-grid/cymatics'
import SpringsStore from './springs-store'
import SprungSheet from '../sprung-sheet'

class SpringsContext extends React.Component {
  componentWillMount() {
    let id = this.props.id
    if (!id) throw new Error('SpringsContext requires id')
    this.store.execute('setup', {id})
    var springs = this.store.springs
    this.sheet = React.createElement(SprungSheet, {springs}, () => {
      var listeners = this.store.on_style_listeners
      var css = ''
      for (let i = 0; i < listeners.length; i++) {
        css += listener(springs)
      }
      return css
    })
  }
  componentWillUnmount() {
    this.sheet = undefined
    this.store.execute('teardown')
  }
  render() {
    return (
      <div id={'springs-context--' + this.props.id}>
        {this.sheet}
        {this.props.children}
      </div>
    )
  }
}

export default installStore(SpringsContext, 'springs', SpringsStore)



