import {Map} from '@the-grid/immutable'
// import {fromTo} from '@the-grid/multiverse-ui'

let _defaults = {
  show: false,
  active: false,
  render: undefined,
  onClick: undefined,
  actionList: undefined,
  actionList2: undefined,
  actionGroups: undefined,
  className: '',
  onScrollPreset: undefined, // hide, show
  size: 's',
  layoutSelector: '.section-layout',
  chroma: {
    type: 'dark',
  },
}

export default {
  _defaults: {
    cache: false,
    get: function() {
      return _defaults
    },
  },
  show: {
    default: _defaults.show,
  },
  render: undefined,
  onClick: undefined,
  actionList: undefined,
  actionList2: undefined,
  className: {default: _defaults.className},
  active: {default: _defaults.active},
  onScrollPreset: {default: _defaults.onScrollPreset},
  size: {default: _defaults.size},
  layoutSelector: {default: _defaults.layoutSelector},
  chroma: {default: _defaults.chroma},
}



