import {Struct, List, Map, fromJS} from '@the-grid/immutable'
import {BlockStruct, MetadataStruct, ContentUnitStruct} from './'
import linkify from './helpers/linkify'

let count = 0
const titleType = ['h1', 'h2', 'h3']
const subtitleType = ['h4', 'h5', 'h6']
const mediaType = ['video', 'audio']
const textualType = ['text', 'quote', 'list']
const nonContextType = ['interactive', 'location']

let ItemStruct = Struct({
  construct(item) {
    let _item = Object.assign({_title: item.title, index: count}, item)
    count++
    return _item
  },

  id: undefined,
  index: undefined,
  created_by: undefined,
  updated_by: undefined,
  created_at: undefined,
  updated_at: undefined,
  published: undefined,
  score: undefined,
  path: undefined,
  _title: undefined,
  dsClassifier: undefined,

  getMono: {
    deps: ['contextUnit', 'subjectUnits'],
    get: (contextUnit, subjectUnits) => {
      if (contextUnit && !subjectUnits.size) return contextUnit
      if (!contextUnit && subjectUnits.size === 1) return subjectUnits.get(0)
      return false
    },
  },

  getDual: {
    deps: ['contextUnit', 'subjectUnits'],
    get: (contextUnit, subjectUnits) => {
      if (contextUnit && subjectUnits) {
        if (subjectUnits.size > 1) {
          return false
        }
        else if (subjectUnits.size === 1) {
          return new List([contextUnit, subjectUnits.get(0)])
        }
      }

      if (contextUnit) {
        return contextUnit.get('split')
      }

      if (subjectUnits && subjectUnits.size === 2) {
        return subjectUnits
      }

      return false
    },
  },

  getMulti: {
    deps: ['contextUnit', 'subjectUnits'],
    get: (contextUnit, subjectUnits) => {
      if (subjectUnits && subjectUnits.size > 1) {
        if (!contextUnit) {
          return subjectUnits
        } else {
          return subjectUnits.push(contextUnit)
        }
      }
      return false
    },
  },

  contextUnit: {
    deps: ['id', 'starred', 'titlesSB', 'subtitlesSB', 'textualsSB', 'metadata'],
    get(id, blocks, titlesSB, subtitlesSB, textualsSB, metadata) {
      let contextBlocks
      let potentialSimpleCoveredBlocks = []

      let nonTemplateBlocks = blocks.filter((block) => {
        return !block.get('isTemplate')
      })

      if (nonTemplateBlocks.size === 1) {
        contextBlocks = nonTemplateBlocks
      }
      else {
        // all simple
        contextBlocks = blocks.filter((block) => {
          let type = block.get('type')
          if (type === 'cta') return true

          if (nonContextType.includes(type)) return false

          // get the textual blocks
          // TODO: enforce title shit...
          if (titleType.includes(type)
            || subtitleType.includes(type)
            || textualType.includes(type)
            || type === 'code'
          ) {
            if (type === 'text' && block.get('isTemplate')) return false
            return !block.get('metadata.isReshare')
          }

          if (block.get('canBeContextCover')) potentialSimpleCoveredBlocks.push(block)

          return false
        })
      }

      // if no contextBlocks then ignore blocks where canBeContextCover === true
      if (!contextBlocks || !contextBlocks.size) return false

      // only one can be the context cover
      if (potentialSimpleCoveredBlocks.length) contextBlocks = contextBlocks.push(potentialSimpleCoveredBlocks[0])

      return new ContentUnitStruct({
        type: 'context',
        blocks: contextBlocks,
        itemMeta: metadata,
      })
    },
  },

  subjectUnits: {
    deps: ['id', 'starred', 'contextUnit'],
    get(id, blocks, contextUnit) {
      let subjectBlocks
      if (!contextUnit) {
        subjectBlocks = blocks.filter((block) => {
          // remove templates for adding $class
          if (block.get('isTemplate') && block.get('text').indexOf('$class') !== -1) return false
          return true
        })
      }
      else {
        let contextBlocks = contextUnit.get('blocks')
        subjectBlocks = blocks.filter((block) => {
          // remove templates for adding $class
          if (block.get('isTemplate') && block.get('text').indexOf('$class') !== -1) return false
          // ensure blocks are not in context
          return !contextBlocks.includes(block)
        })
      }

      if (!subjectBlocks || !subjectBlocks.size) return false

      return subjectBlocks.map((subjectBlock) => {
        return new ContentUnitStruct({
          type: 'subject',
          blocks: List.of(subjectBlock),
        })
      })
    },
  },

  metadata: {
    init(metadata) {
      metadata = new MetadataStruct(metadata)
      return {metadata}
    },
  },

  url: {
    deps: ['metadata'],
    get(metadata) {
      if (metadata) {
        let url = metadata.get('url')
        return url ? url : metadata.get('isBasedOnUrl')
      }
      return undefined
    },
  },

  isStarred: {
    deps: ['metadata.starred'],
    get(starred) {
      return starred
    },
  },

  inFeed: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('inFeed', false)
    },
  },

  hasPage: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('hasPage', false)
    },
  },

  inNav: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('inNav', false)
    },
  },

  author: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('author')
    },
  },

  publisher: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('publisher', false)
    },
  },

  description: {
    deps: ['metadata'],
    get(metadata) {
      return metadata.get('description', false)
    },
  },

  content: {
    init(content) {
      let blockStructs = content.map((block) => {
        return new BlockStruct(block)
      })
      return {content: new List(blockStructs)}
    },
  },

  title: {
    deps: ['_title', 'metadata', 'titlesSB', 'articlesSB', 'starred'],
    get(_title, metadata, titlesSB, articlesSB, starred) {
      let title = undefined
      if (_title && _title.length > 0) {
        title = _title
      } else if (metadata && metadata.get('title') && metadata.get('title').length > 0) {
        title = metadata.get('title')
      } else if (titlesSB.count() > 0) {
        let titleSB = titlesSB.get(0)
        if (titleSB) {
          title = titleSB.get('title')
        }
      } else if (articlesSB.count() > 0) {
        let articleSB = articlesSB.get(0)
        if (articleSB) {
          title = articleSB.get('title')
        }
      } else {
        let i = 0
        let nbStarred = starred.count()
        while (i < nbStarred) {
          title = starred.get(i).get('title')
          if (title) {
            break
          }
          i++
        }
      }
      return title
    },
  },

  starred: {
    deps: ['content'],
    get(content) {
      return content.filter((block) => {
        return block.get('starred', false)
      })
    },
  },

  starredBlockMap: {
    deps: ['starred'],
    get(starred) {
      let mapPerType = {}
      starred.forEach((block) => {
        let blockType = block.get('type')
        if (titleType.indexOf(blockType) !== -1) blockType = 'title'
        else if (subtitleType.indexOf(blockType) !== -1) blockType = 'subtitle'
        else if (mediaType.indexOf(blockType) !== -1) blockType = 'media'
        else if (textualType.indexOf(blockType) !== -1) blockType = 'textual'

        if (!mapPerType[blockType]) {
          mapPerType[blockType] = []
        }
        mapPerType[blockType].push(block)
      })
      mapPerType = fromJS(mapPerType)
      return Map(mapPerType)
    },
  },

  starredPerType: {
    params: ['type'],
    deps: ['starredBlockMap'],
    get(type, starredBlockMap) {
      return starredBlockMap.get(type, List.EMPTY)
    },
  },

  canBeRendered: {
    params: ['schema'],
    deps: ['starred', 'starredBlockMap'],
    cache: false,
    get(schema, starred, starredBlockMap) {
      let starBlockCount = starred.count()
      let schemaKeys = Object.keys(schema)

      for (let i = 0; i < schemaKeys.length; i++) {
        let key = schemaKeys[i]
        let sblocks = starredBlockMap.get(key, List.EMPTY)
        let def = schema[key]

        if (def instanceof Array) {
          let [min, max] = def
          let sbCount = sblocks.count()
          if (min <= sbCount && sbCount <= max) {
            starBlockCount -= sbCount
          } else {
            return false
          }
        } else {
          if (sblocks.count() === def) {
            starBlockCount -= def
          } else {
            return false
          }
        }
      }
      return starBlockCount === 0
    },
  },

  cover: {
    deps: ['starred'],
    get(starred) {
      for (let block of starred) {
        let cover = block.get('cover')
        if (cover) return cover
      }
      return undefined
    },
  },

  allSBWithCovers: {
    deps: ['mediasSB', 'imagesSB', 'articlesSB'],
    get(mediasSB, imagesSB, articlesSB) {
      return List.EMPTY.concat(mediasSB).concat(imagesSB).concat(articlesSB)
    },
  },

  allCovers: {
    deps: ['allSBWithCovers'],
    get(allSBWithCovers) {
      let covers = []
      let cover
      for (let i = 0; i < allSBWithCovers.count(); i++) {
        let block = allSBWithCovers.get(i)
        cover = block.get('cover')
        if (cover) covers.push(cover)
      }
      if (covers.length === 0) return undefined
      return new List(covers)
    },
  },

  // Starred blocks --- SB = starred blocks
  titlesSB: {
    deps: ['starredPerType'],
    cache: false,
    get(starredPerType) {
      return starredPerType('title')
    },
  },

  subtitlesSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('subtitle')
    },
  },

  mediasSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('media')
    },
  },

  textualsSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('textual')
    },
  },

  interactivesSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('interactive')
    },
  },


  totalTextualsLength: {
    deps: ['textualsSB'],
    get(textuals) {
      if (!textuals) {
        return 0
      }
      return textuals.reduce((totalLength, textBlock) => {
        if (textBlock.get('type') === 'list') {
          let html = textBlock
            .get('html')
            .replace('<ol>', '')
            .replace('</ol>', '')
            .replace('<li>', '')
            .replace('</li>', '')

          return totalLength + html.length
        }
        if (!textBlock.get('text')) {
          return totalLength
        }

        return totalLength + textBlock.get('text').length
      }, 0)
    },
  },

  codesSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('code')
    },
  },

  imagesSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('image')
    },
  },

  ctasSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('cta')
    },
  },

  articlesSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('article')
    },
  },

  placeholdersSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('placeholder')
    },
  },

  locationsSB: {
    deps: ['starredPerType'],
    get(starredPerType) {
      return starredPerType('location')
    },
  },

  allSBWithCover: {
    deps: ['mediasSB', 'imagesSB', 'articlesSB'],
    get(mediasSB, imagesSB, articlesSB) {
      return List.EMPTY.concat(mediasSB).concat(imagesSB).concat(articlesSB)
    },
  },

  isNavigation: {
    deps: ['metadata.@type'],
    get(type) {
      if (type === 'navigation') return true
      return false
    },
  },

  isFooter: {
    deps: ['metadata.@type'],
    get(type) {
      if (type === 'footer') return true
      return false
    },
  },

  isCta: {
    deps: ['metadata.@type'],
    get(type) {
      if (type === 'cta') return true
      return false
    },
  },

  isSiteHeader: {
    deps: ['metadata.@type'],
    get(type) {
      if (type === 'header') return true
      return false
    },
  },

  isNextPage: {
    deps: ['metadata.@type'],
    get(type) {
      return type === 'nextpage'
    },
  },

  isFeed: {
    deps: ['isNavigation', 'isFooter', 'isCta', 'isSiteHeader', 'isNextPage'],
    get(isNavigation, isFooter, isCta, isSiteHeader, isNextPage) {
      return !(isNavigation || isFooter || isCta || isSiteHeader || isNextPage)
    },
  },

  canFilterAllCovers: {
    deps: ['allSBWithCover'],
    get(allSBWithCover) {
      for (let block of allSBWithCover) {
        if (!block.get('canFilter')) return false
      }
      return true
    },
  },

  canCropAllCovers: {
    deps: ['allSBWithCover'],
    get(allSBWithCover) {
      for (let block of allSBWithCover) {
        if (!block.get('canCrop')) return false
      }
      return true
    },
  },

  canOverlayAllCovers: {
    deps: ['allSBWithCover'],
    get(allSBWithCover) {
      for (let block of allSBWithCover) {
        if (!block.get('canOverlay')) return false
      }
      return true
    },
  },

  $class: {
    deps: [
      'author',
      'publisher',
      'canFilterAllCovers',
      'canCropAllCovers',
      'canOverlayAllCovers',
      'allCovers',
      'isNavigation',
      'isFooter',
      'isCta',
      'id',
      'starredPerType',
      'dsClassifier',
    ],
    get(
      author,
      publisher,
      canFilterAllCovers,
      canCropAllCovers,
      canOverlayAllCovers,
      allCovers,
      isNavigation,
      isFooter,
      isCta,
      id,
      starredPerType,
      dsClassifier) {
      let classes = [id]

      if (author) classes.push('item:w-author')
      if (publisher) classes.push('item:w-publisher')
      if (author || publisher) classes.push('item:w-attribution')

      if (canFilterAllCovers) {
        classes.push('item:can-cover-filter')
      } else {
        classes.push('item:dont-cover-filter')
      }

      if (canCropAllCovers) {
        classes.push('item:can-cover-crop')
      } else {
        classes.push('item:dont-cover-crop')
      }

      if (canOverlayAllCovers) {
        classes.push('item:can-cover-overlay')
      } else {
        classes.push('item:dont-cover-overlay')
      }

      if (allCovers) {
        if (allCovers.count() === 0) {
          classes.push('item:wo-image')
        } else {
          for (let i = 0; i < allCovers.count(); i++) {
            let cover = allCovers.get(i)
            if (cover.get('faces') && classes.indexOf('item:w-cover-face') === -1) {
              classes.push('item:w-cover-face')
            }

            if (cover.get('saliency') && classes.indexOf('item:w-cover-saliency') === -1) {
              classes.push('item:w-cover-saliency')
            }

            if (cover.get('orientation')) {
              let orientation = `item:w-cover-${cover.get('orientation')}`
              if (classes.indexOf(orientation) === -1) classes.push(orientation)
            }

            if (cover.get('animated')) {
              let animated = `item:w-cover-${cover.get('animated')}`
              if (classes.indexOf(animated) === -1) classes.push(animated)
            }

            if (cover.get('resolution')) {
              let resolution = `item:w-cover-${cover.get('resolution')}-res`
              if (classes.indexOf(resolution) === -1) classes.push(resolution)
            }

            if (cover.get('filmstrip')) {
              let filmstrip = `item:w-cover-${cover.get('filmstrip')}-res`
              if (classes.indexOf(filmstrip) === -1) classes.push(filmstrip)
            }

            if (cover.get('isFullMobileResolution') && classes.indexOf('item:w-cover-full-mobile-res') === -1) {
              classes.push('item:w-cover-full-mobile-res')
            }

            if (cover.get('isFullDesktopResolution') && classes.indexOf('item:w-cover-full-desktop-res') === -1) {
              classes.push('item:w-cover-full-desktop-res')
            }
          }
        }
      }

      if (starredPerType('interactive').count() > 0) {
        classes.push('item:w-interactive')
      }

      // TODO - remove, temporary classification for the-grid.io
      let textBlocks = starredPerType('textual')
      let classTemplate
      if (textBlocks) {
        textBlocks.forEach((block) => {
          if (block.get('isTemplate')) {
            let template = block.get('text')
            if (template.indexOf('$class') > -1) {
              template = template.slice(1)
              template = template.substr(0, template.length - 1).replace(/'/g, '"').replace(/‘/g, '"').replace(/’/g, '"')
              template = JSON.parse(template)

              if (Array.isArray(template.$class)) {
                classes = classes.concat(template.$class)
              }
            }
          }
        })
      }

      if (isNavigation) classes.push('item:is-navigation')
      if (isFooter) classes.push('item:is-footer')
      if (isCta) classes.push('item:is-cta')
      if (id === 'next-page-item') classes.push('item:w-nextpage')

      let classList = List(classes)
      if (dsClassifier && typeof dsClassifier === 'function') {
        let dsClasses = dsClassifier(this, classList)
        if (dsClasses && dsClasses.length > 0) {
          classList = classList.concat(dsClasses)
        }
      }

      return classList
    },
  },
})

export default ItemStruct



