import React from 'react'
import {installStore} from '@the-grid/cymatics'
import BaseStore from './Store'

class BaseContext extends React.Component {

  static displayName = 'Styleverse_BaseContext'

  componentWillMount() {
    this.store.execute('update-props', this.props)
  }

  componentWillReceiveProps(nextProps) {
    this.store.execute('update-props', nextProps)
  }

  componentWillUnmount() {
    this.store.silently('teardown')
  }

  render() {
    return this.props.children
  }

}

BaseContext = installStore(BaseContext, 'styleverse-base', (componentInstance) => {
  return new BaseStore(componentInstance)
})

export default BaseContext



