import React from 'react'
import {Style} from '@the-grid/styleverse'
import {SprungSheet, Springs} from '@the-grid/ui-sprung'
import {renderer, get_icon_type} from './renderer'

const el = React.createElement

const spring_presets = [
  {friction: 8, tension: 100},
  {friction: 8, tension: 60},
  {friction: 7, tension: 80},
  {friction: 8, tension: 70},
]

const spring_configs = {
  'lines': {
    '+': {
      r_0: 0,    o_0: 1,
      r_1: 90,   o_1: 1,
      r_2: 0,    o_2: 0,
      r_svg: -180, o_svg: 0,
      r_svgurl: -180, o_svgurl: 0,
      r_f: -180, o_f: 0,
    },
    '*': {
      r_0: 60,   o_0: 1,
      r_1: 0,    o_1: 1,
      r_2: -60,  o_2: 1,
      r_svg: -180, o_svg: 0,
      r_svgurl: -180, o_svgurl: 0,
      r_f: -180, o_f: 0,
    },
    'x': {
      r_0: -45,  o_0: 1,
      r_1: 45,   o_1: 1,
      r_2: 0,    o_2: 0,
      r_svg: -180, o_svg: 0,
      r_svgurl: -180, o_svgurl: 0,
      r_f: -180, o_f: 0,
    },
    '-': {
      r_0: -90,  o_0: 1,
      r_1: 0,    o_1: 0,
      r_2: 0,    o_2: 0,
      r_svg: -180, o_svg: 0,
      r_svgurl: -180, o_svgurl: 0,
      r_f: -180, o_f: 0,
    },
  },
  'svg': {
    r_0: 90,   o_0: 0,
    r_2: -90,  o_1: 0,
    r_2: 90,   o_2: 0,
    r_svg: 0,    o_svg: 1,
    r_svgurl: -180, o_svgurl: 0,
    r_f: -360, o_f: 0,
  },
  'svgurl': {
    r_0: 90,   o_0: 0,
    r_2: -90,  o_1: 0,
    r_2: 90,   o_2: 0,
    r_svg: -180,    o_svg: 0,
    r_svgurl: 0,    o_svgurl: 1,
    r_f: -360, o_f: 0,
  },
  'font': {
    r_0: 90,   o_0: 0,
    r_1: -90,  o_1: 0,
    r_2: 90,   o_2: 0,
    r_svg: -360, o_svg: 0,
    r_svgurl: -360, o_svgurl: 0,
    r_f: 0,    o_f: 1,
  },
}

let count = 0
const rootElStyle = {position: 'relative'}
const _className = 'ornament ornament-morphable'

class IconMorphable extends React.Component {
  static displayName = 'IconMorphable'
  static propTypes = {
    children: React.PropTypes.string.isRequired, // '+' '*' 'x'
    iconWeight: React.PropTypes.number.isRequired,
    iconSize: React.PropTypes.number.isRequired,
  }
  static defaultProps = {
    children: null,
    iconWeight: 2,
    iconSize: 14,
  }
  id = 'icon-morphable-' + count++
  getId() {
    return this.props.id || this.id
  }
  springs = undefined
  springs_update(icon) {
    let config_key = get_icon_type(icon)
    let config = (config_key === 'lines')
      ? spring_configs[config_key][icon]
      : spring_configs[config_key]
    if (!config) throw new Error('morphable icon not recognized! ' + icon)
    let springs = this.springs
    // if (this.last_lines_icon) {
    springs.set('r_0', config['r_0'], spring_presets[0])
    springs.set('r_1', config['r_1'], spring_presets[1])
    springs.set('r_2', config['r_2'], spring_presets[2])
    springs.set('o_0', config['o_0'], spring_presets[0])
    springs.set('o_1', config['o_1'], spring_presets[1])
    springs.set('o_2', config['o_2'], spring_presets[2])
      // }
    if (this.last_svg_icon) {
      springs.set('r_svg', config['r_svg'], spring_presets[3])
      springs.set('o_svg', config['o_svg'], spring_presets[3])
    }
    if (this.last_svgurl_icon) {
      springs.set('r_svgurl', config['r_svgurl'], spring_presets[3])
      springs.set('o_svgurl', config['o_svgurl'], spring_presets[3])
    }
    if (this.last_font_icon) {
      springs.set('r_f', config['r_f'], spring_presets[3])
      springs.set('o_f', config['o_f'], spring_presets[3])
    }
    springs.commit()
  }
  componentWillMount() {
    let id = this.getId()
    this.springs = new Springs({id})
    this.springs_sheet = (
      <SprungSheet key="SprungSheet-icon-morphable" springs={this.springs}>{ ({vals}) => {
        let font_scaleX = (this.last_font_icon === 'share')
          ? 'scaleX(-1)'
          : ''
        let {
          r_0,  o_0,
          r_1,  o_1,
          r_2,  o_2,
          r_svg,  o_svg,
          r_svgurl, o_svgurl,
          r_f,  o_f,
        } = vals
        var css = ``
        if (this.last_lines_icon) {
          css += `
            #${id} .icon-line-0 {
              opacity: ${o_0};
              transform: rotate(${r_0}deg) scale(${o_0});
            }
            #${id} .icon-line-1 {
              opacity: ${o_1};
              transform: rotate(${r_1}deg) scale(${o_1});
            }
            #${id} .icon-line-2 {
              opacity: ${o_2};
              transform: rotate(${r_2}deg) scale(${o_2});
            }`
        }
        if (this.last_svg_icon) {
          css += `
            #${id} .icon-svg {
              opacity: ${o_svg};
              transform: rotate(${r_svg}deg) scale(${o_svg});
            }`
        }
        if (this.last_svgurl_icon) {
          css += `
            #${id} .icon-svgurl {
              opacity: ${o_svg};
              transform: rotate(${r_svgurl}deg) scale(${o_svgurl});
            }`
        }
        if (this.last_font_icon) {
          css += `
            #${id} .icon-font {
              opacity: ${o_f};
              transform: rotate(${r_f}deg) scale(${o_f}) ${font_scaleX};
            }`
        }
        return css
      }}</SprungSheet>
    )
  }
  componentDidMount() {
    this.springs_update(this.props.children)
  }
  componentWillReceiveProps(nextProps) {
    this.springs_update(nextProps.children)
  }
  componentWillUnmount() {
    this.last_lines_icon = undefined
    this.last_svg_icon = undefined
    this.last_svgurl_icon = undefined
    this.last_font_icon = undefined
    this.springs_sheet = undefined
    this.springs.destroy()
    this.springs = undefined
  }

  last_lines_icon = undefined
  last_svg_icon = undefined
  last_svgurl_icon = undefined
  last_font_icon = undefined
  render() {
    let id = this.getId()
    let icon = this.props.children
    let icon_type = get_icon_type(icon)
    if (icon_type === 'lines') {
      this.last_lines_icon = icon
    }
    else if (icon_type === 'svg') {
      this.last_svg_icon = icon
    }
    else if (icon_type === 'svgurl') {
      this.last_svgurl_icon = icon
    }
    else {
      this.last_font_icon = icon
    }
    let className = this.props.className
      ? this.props.className + ' ' + _className
      : _className
    let children = [this.springs_sheet]
    if (this.last_svg_icon) children.push(renderer(id, this.last_svg_icon, this.props))
    if (this.last_svgurl_icon) children.push(renderer(id, this.last_svgurl_icon, this.props))
    if (this.last_font_icon) children.push(renderer(id, this.last_font_icon, this.props))
    if (this.last_lines_icon) children.push(renderer(id, this.last_lines_icon, this.props))
    return (
      <div id={id} className={className} style={rootElStyle}>
        {children}
      </div>
    )
  }
}

export default IconMorphable



