//

// internally offset to account for -1 index
//
// based on:
//
// - An Elegant Pairing Function by Matthew Szudzik @ Wolfram Research, Inc.
// - https://codepen.io/sachmata/post/elegant-pairing

//https://jsperf.com/pairing-function-showdown-with-unpair
// https://jsperf.com/pairing-function-showdown

function pair(x, y) {
  x++
  y++
  return (x >= y) ? (x * x + x + y) : (y * y + x);
}

function unpair(z) {
  var sqrtz = Math.floor(Math.sqrt(z))
  var sqz = sqrtz * sqrtz
  return ((z - sqz) >= sqrtz) ? [sqrtz - 1, z - sqz - sqrtz - 1] : [z - sqz - 1, sqrtz - 1];
}

export default {
  pair,
  unpair,
}


