import {Struct, List, Map, fromJS} from '@the-grid/immutable'
import {ItemStruct} from './'

let SiteConfigStruct = Struct({

  name: undefined,
  title: undefined,
  description: undefined,
  style: undefined,
  favicon: undefined,
  logo: undefined,

  cta: {
    init(cta) {
      cta = fromJS(cta)
      return {cta}
    },
  },

  profiles: {
    init(profiles) {
      if (profiles && profiles.length > 0) {
        profiles = fromJS(profiles)
        return {profiles}
      }
      return undefined
    },
  },

  contact_details: {
    init(contact_details) {
      if (contact_details && contact_details.length > 0) {
        contact_details = fromJS(contact_details)
        return {contact_details}
      }
      return undefined
    },
  },

  social_links: {
    init(social_links) {
      if (social_links && Object.keys(social_links).length > 0) {
        social_links = fromJS(social_links)
        return {social_links}
      }
      return undefined
    },
  },

  secondary_navigation: {
    init(secondary_navigation) {
      if (secondary_navigation && secondary_navigation.length > 0) {
        secondary_navigation = fromJS(secondary_navigation)
        return {secondary_navigation}
      }
      return undefined
    },
  },

  purpose: {
    init(purpose) {
      if (purpose && purpose.length > 0) {
        purpose = fromJS(purpose)
        return {purpose}
      }
      return {purpose: undefined}
    },
  },

  analytics: {
    init(analytics) {
      analytics = fromJS(analytics)
      return {analytics}
    },
  },

  image_filters: {
    init(image_filters) {
      image_filters = fromJS(image_filters)
      return {image_filters}
    },
  },

  opengraph: {
    init(opengraph) {
      opengraph = fromJS(opengraph)
      return {opengraph}
    },
  },

  content_rating: {
    init(content_rating) {
      content_rating = fromJS(content_rating)
      return {content_rating}
    },
  },

  collections: {
    init(collections) {
      collections = fromJS(collections)
      return {collections}
    },
  },

  color: {
    init(color) {
      if (color && color.brandColors && color.brandColors.length === 0) {
        color.brandColors = undefined
      }
      color = fromJS(color)
      return {color}
    },
  },

  layout_spectrum: undefined,

  typography_spectrum: undefined,

  site_lang: undefined,

  content: {
    init(content) {
      content = fromJS(content)
      return {content}
    },
  },

  author: {
    init(author) {
      author = fromJS(author)
      return {author}
    },
  },

  profiles: {
    init(profiles) {
      profiles = fromJS(profiles)
      return {profiles}
    },
  },

  auto_approve: undefined,

  coverPrefs: {
    init(coverPrefs) {
      coverPrefs = fromJS(coverPrefs)
      return {coverPrefs}
    },
  },

  site_media: {
    init(site_media) {
      if (site_media && site_media.length > 0) {
        site_media = fromJS(site_media)
        return {site_media}
      }
      return undefined
    },
  },
})

export default SiteConfigStruct



