import React from 'react'
import {Struct, List} from '@the-grid/immutable'

class Selectable extends React.Component {

  constructor(...args) {
    super(...args)

    //this.perfCheckState = false

    let that = this

    let setState = this.setState
    this.setState = (...args) => {
      return setState.apply(that, args)
    }

    const selectAll = () => {
      let struct = that.state.struct
      let items = struct.get('items')
      if(!struct.get('multi')) return
      return that.setState({
        struct: struct.set( 'selectedItems', items )
      })
    }

    const deselectAll = () => {
      let struct = that.state.struct
      return that.setState({
        struct: struct.set( 'selectedItems', List() ),
      })
    }

    const isItemSelected = (item) => {
      let struct = that.state.struct
      let selectedItems = struct.get('selectedItems')
      return selectedItems.includes(item)
    }

    const isItemDeselected = (item) => {
      let struct = that.state.struct
      let selectedItems = struct.get('selectedItems')
      let multi = struct.get('multi')
      return ( multi && selectedItems.size > 0 && !selectedItems.includes(item))
    }

    const toggleItem = (item) => {
      let struct = this.state.struct

      let itemIsSelected = isItemSelected(item)
      let required = struct.get('required')

      if (!struct.get('multi')) {

        if (itemIsSelected && required) return null

        return that.setState({
          struct: (itemIsSelected)
            ? struct.set('selectedItems', List())
            : struct.set('selectedItems', List([item]))
        })
      }
      else {
        let selectedItems = struct.get('selectedItems')

        if (itemIsSelected && required && selectedItems.size === 1) return null

        return that.setState({
          struct: (itemIsSelected)
            ? struct.deleteIn( [ 'selectedItems', selectedItems.indexOf(item) ] )
            : struct.set( 'selectedItems', selectedItems.push(item) )
        })
      }

    }

    const itemToggler = (item) => {
      return () => {
        toggleItem(item)
      }
    }

    const itemTogglerIfHasSelected = (item) => {
      return () => {
        let struct = that.state.struct
        let selectedItems = struct.get('selectedItems')
        if (selectedItems.size) toggleItem(item)
      }
    }


    const SelectableStruct = Struct({

      construct: function (vals) {
        let {
          required,
          //multi,
          initial,
          selectedItems,
          items,
        } = vals

        if (required) {
          if (!selectedItems && !initial) {
            vals = Object.assign( {selectedItems: List.of(items.get(0))}, vals)
          }
        }
        return vals
      },

      items: undefined,

      multi: undefined,

      required: undefined,

      selectedItems: {
        default: List.EMPTY,
      },

      selectedItem: {
        cache: false,
        deps: ['selectedItems'],
        get: function (selectedItems) {
          return selectedItems.get(0)
        },
      },

      selectedIndex: {
        cache: false,
        deps: ['selectedItems','items'],
        get: function (selectedItems,items) {
          return items.indexOf( selectedItems.get(0) )
        },
      },

      selectedIndices: {
        cache: false,
        deps: ['selectedItems','items'],
        get: function (selectedItems,items) {
          return selectedItems.map( (item) => {
            return items.indexOf( item )
          })
        },
      },

      selectAll: {
        params: ['IGNORE'],
        cache: false,
        get: function() {
          return selectAll()
        }
      },

      deselectAll: {
        params: ['IGNORE'],
        cache: false,
        get: function() {
          return deselectAll()
        }
      },

      isItemSelected: {
        params: ['IGNORE'],
        cache: false,
        get: function(item) {
          return isItemSelected(item)
        }
      },

      isItemDeselected: {
        params: ['IGNORE'],
        cache: false,
        get: function(item) {
          return isItemDeselected(item)
        }
      },

      toggleItem: {
        params: ['IGNORE'],
        cache: false,
        get: function(item) {
          return toggleItem(item)
        }
      },

      itemToggler: {
        params: ['IGNORE'],
        cache: false,
        get: function(item) {
          return itemToggler(item)
        }
      },

      itemTogglerIfHasSelected: {
        params: ['IGNORE'],
        cache: false,
        get: function(item) {
          return itemTogglerIfHasSelected(item)
        }
      },
    })

    this.state = {
      struct: new SelectableStruct(this.props),
    }

  }

  componentWillReceiveProps(nextProps) {
    let struct = this.state.struct
    let nextStruct = struct.merge(nextProps)
    if (nextStruct.equals(this.state.struct)) return
    this.setState({struct: nextStruct})
  }

  componentWillUpdate(nextProps, nextState) {
    let struct = this.state.struct
    let nextStruct = nextState.struct
    if (struct.equals(nextStruct)) return
    if (this.props.onChange) this.props.onChange(nextStruct)
  }

  render() {
    return this.props.children(this.state.struct)
  }

}


let {PropTypes} = React
Selectable.propTypes = {

  // immutable List or other keyed iterable
  // items: Immutable.PropTypes.list.isRequired,

  // children must be function
  children: PropTypes.func.isRequired,

  onChange: PropTypes.func,

  multi: PropTypes.bool,

  required: PropTypes.bool,

  initial: PropTypes.any, // TODO: not supported
}

Selectable.defaultProps = {
  multi: true,
  required: false,
}

export default Selectable



