import React from 'react'
import ContentResizer from './content-resizer'
import {fromJS} from '@the-grid/immutable'
import {Story} from '@the-grid/ui-story-machine'
import {raf} from '@the-grid/deps'

class Content extends Story {
  storyName = 'content'
  willMount() {
    if (this.focused) return
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'update', this.UI.FAB)
    this.contextRequest('FAF', 'update', this.UI.FAF)
    this.contextRequest('FAN', 'update', this.UI.FAN)
  }
  display(children) {
    return React.createElement(ContentResizer, {key: 'content-resizer'}, children)
  }
  UI = {
    FAB: fromJS({
      show: true,
      active: true,
      icon: '+',
      chroma: {type: 'light', typeIndex: 0},
      onClick: () => {
        let that = this
        raf(function(){
          that.contextRequest('storyline', 'to-story', 'view-site')
        })
        return false
      },
      appButton: {
        icon: 'arrow_back',
        show: true,
        //activeIcon: 'arrow_back',
        onClick: () => {
          let that = this
          raf(function(){
            that.contextRequest('storyline', 'pop-story')
          })
          return false
        },
      },
      actionList: [
        {
          label: 'Write something...',
          //icon: 'color_lens',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          }
        },
        {
          label: 'Import from...',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          }
        },
        {
          label: 'Upload',
          //icon: 'text_format',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          }
        },
      ]
    }),
    FAF: fromJS({
      show: false,
      //hideOnScroll: true,
      //chroma: {type: 'dark', typeIndex: 0},
      //active: 2,
      //actionList: [
      //  // {
      //  //   //label: 'newsfeed',
      //  //   icon: 'weekend',
      //  //   key: 'newsfeed',
      //  //   willActivate: () => {
      //  //     alert('show newsfeed')
      //  //   }
      //  // },
      //  //{
      //  //  //label: 'sites',
      //  //  icon: 'apps',
      //  //  key: 'sites',
      //  //  willActivate: () => {
      //  //    this.contextRequest('storyline', 'to-story','create')
      //  //  }
      //  //},
      //  {
      //    //label: 'thegrid.io',
      //    icon: 'home',
      //    //key: 'current-site',
      //    willActivate: () => {
      //      this.contextRequest('storyline', 'to-story','on-the-grid')
      //    }
      //  },
      //],
    }),
    FAN: fromJS({
      hideOnScroll: false,
      show: true,
      chroma: {type: 'light', typeIndex: 0},
      active: 0,
      actionList2: [
        //{
        //  icon: 'arrow_back',
        //  willActivate: () => {
        //    raf(()=> {
        //      this.req('app','start-story','on-the-grid')
        //    })
        //  },
        //},
        {
          label: 'copywriting',
          //willActivate: () => {
          //  alert(`Ask the owner for access to edit or view Content or switch to an account with permisssion.`)
          //  return false
          //}
        },
        {
          label: 'media',
          onClick: () => {
            let that = this
            raf(function(){
              that.contextRequest('storyline', 'replace-story', 'content-media')
            })
            return false
          }
        },
        {
          label: 'categories',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Categories or switch to an account with permisssion.`)
            return false
          }
        },
        {
          label: 'commerce',
          onClick: () => {
            alert(`Ask the owner for access to edit or view Commerce or switch to an account with permisssion.`)
            return false
          }
        },

        //{
        //  icon: 'zoom_out',
        //  willActivate: () => {
        //    var key = 'design/zoom_browser'
        //    this.AppBody.request('merge',{key, render: () => {
        //      return React.createElement(DesignBody, {key:'DesignBody', deviceType: 'full'})
        //    }})
        //  }
        //}
      ],
      //actionList2: [
      //  {
      //    icon: 'color_lens',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //  {
      //    icon: 'shuffle',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //  {
      //    icon: 'settings',
      //    willActivate: () => {
      //      this.execute('start-story','socialize')
      //    }
      //  },
      //],
    })
  }
}

export default Content


