import {Store} from '@the-grid/cymatics'
import PluginStruct from './Struct'
let initialState = new PluginStruct()

class TypographyStore extends Store {

  name = 'styleverse-typography'
  deferCommit = false
  state = initialState

  gotDep = false
  gotProps = false

  shouldCommit() {
    return this.gotDep && this.gotProps
  }

  contextStores = {
    'styleverse-breakpoint': (bpStore) => {
      this.gotDep = true
      this.execute('handle-parent-breakpoint', bpStore)
    },
  }

  actions = {

    'update-props': (state, props) => {
      this.gotProps = true
      var {fonts} = props
      if (fonts) {
        state = state.set('fonts',fonts)
      }
      // NO PROPS!
      //return state.merge(props)
      return state
    },

    'handle-parent-breakpoint': (state, bpStore) => {
      return state.merge({
        breakpoint: bpStore.state,
      })
    },

    'teardown': () => {
      this.gotProps = false
      this.gotDep = false
      return initialState
    },
  }
}

export default TypographyStore


