  nextFrom(collection, callback) {
    for (let i = 0; i < collection.length; i++) {
      let color = collection[i]
      let valid = callback(color)
      if (valid) return color
    }
    return undefined
  },

  query(collection, callback) {
    let results = []
    for (let i = 0; i < collection.length; i++) {
      let color = collection[i]
      let valid = callback(color)
      if (valid) { return results.push(color) }
    }
    return results
  },

  scoredQuery(collection, callback, options = {}) {
    let {max, raw, clone} = options
    if (!raw) raw = false
    let results = []
    for (let i = 0; i < collection.length; i++) {
      let data = collection[i]
      let score = callback(data)
      if (clone) data = data.clone()
      if (typeof score === 'number') results.push({score,data})
    }
    results.sort((a,b) => b.score - a.score)
    if (max) {
      results = results.slice(0,max)
    }
    if (raw) {
      return results
    }
    return results.map(r => r.data)
  },
