import {Store} from '@the-grid/cymatics'
import PluginStruct from './Struct'
let initialState = new PluginStruct()

class LayoutStore extends Store {

  name = 'styleverse-layout'
  deferCommit = false
  state = initialState
  got_props = false
  got_bp = false
  got_typo = false

  shouldCommit() {
    return this.got_bp && this.got_props && this.got_typo
  }

  contextStores = {
    'styleverse-breakpoint': (bpStore) => {
      this.got_bp = true
      this.execute('handle-parent-breakpoint', bpStore)
    },
    'styleverse-typography': (typoStore) => {
      this.got_typo = true
      this.execute('set', 'typography', typoStore.state)
    },
  }

  actions = {

    'update-props': (state, props) => {
      this.got_props = true
      // DOES NOT HAVE PROPS RIGHT NOW!
      // return state.merge(props)
      return state
    },

    'handle-parent-breakpoint': (state, bpStore) => {
      return state.merge({
        breakpoint: bpStore.state,
        viewport: bpStore.state.get('viewport'),
      })
    },

    'teardown': () => {
      this.got_props = false
      this.got_bp = false
      this.got_typo = false
      return initialState
    },
  }
}

export default LayoutStore



