import React from 'react'
import Immutable from '@the-grid/immutable'
import ActionButton from '../action-button'
import ActionLink from '../action-link'
// import ActionListStyle from './action-list-style'
import ActionListStaticStyle from './action-list-static-style'
let typeConigs = {
  'button': {
    View: ActionButton,
    viewProps: {
      // pillable: false,
    },
  },
  'link': {
    View: ActionLink,
    viewProps: {
      pillable: false,
    },
  },
}

var types = Object.keys(typeConigs)
var count = 0

class ActionList extends React.Component {
  static displayName = 'ActionList'
  static types = types
  /*
  static propTypes = {
    list: Immutable.PropTypes.list.isRequired,
    title: React.PropTypes.string,
    type: React.PropTypes.string.isRequired,
    active: React.PropTypes.bool.isRequired,
    x: React.PropTypes.number.isRequired,
    y: React.PropTypes.number.isRequired,
    fabSize: React.PropTypes.number.isRequired,
    viewportWidth: React.PropTypes.number.isRequired,
    viewportHeight: React.PropTypes.number.isRequired,
  }
  */
  static defaultProps = {
    list: Immutable.List(),
    type: 'button',
  }
  id = 'ActionList-' + count++
  hasActivated = false
  componentWillMount() {
    this.hasActivated = (this.props.active) ? true : false
  }
  // shouldComponentUpdate(nextProps) {
  //  let {
  //    show
  //  } = nextProps
  //  return show
  // }
  componentWillUpdate(nextProps) {
    this.hasActivated = (nextProps.active || this.hasActivated) ? true : false
  }
  render() {
    let {
      list,
      type,
      active,
      title,
      id,
      className,
      ...attrs
    } = this.props
    id = id || this.id
    if (!list) return null
    let length = list.size || list.length || 0
    if (!length) return null
    let hasActivated = this.hasActivated
    let scope = '#' + id

    className = className || ''
    className += ' action-list'
    if (active) className += ' active'

    let typeConfig = typeConigs[type]
    let {View, viewProps} = typeConfig
    var actionListStyle = null

    // if (!active && !hasActivated) {
    //  if (!attrs.style) {
    //    attrs.style = {visibility: 'hidden'}
    //  } else {
    //    attrs.style.visibility = 'hidden'
    //  }
    // }
    // else {
    let actionListStyleProps = {
      list,
      type, // TODO
      scope,
      active,
    }
    // actionListStyle = <ActionListStyle key="ActionListStyle" styleProps={actionListStyleProps} />
    if (!attrs.style) {
      attrs.style = {visibility: 'visible'}
    } else {
      attrs.style.visibility = 'visible'
    }
    // }

    let actionRenders = []
    let actionProps
    for (let i = 0; i < length; i++) {
      actionProps = list.get ? list.get(i) : list[i]
      if (actionProps.toJS) actionProps = actionProps.toJS()
      if (!actionProps.id) actionProps.id = id + '-action-button-' + i
      if (!actionProps.key) {
        actionProps.key = actionProps.id
      }
      actionRenders.push(<View {...actionProps} {...viewProps}/>)
    }
    return (
      <div id={id} className={className} {...attrs}>
        {ActionListStaticStyle}
        {this.renderTitle(title)}
        {actionRenders}
      </div>
    )
  }

  renderTitle(title) {
    if (!title) return null
    return (
      <h2 key="action-list-title" className="action-list-title">{title}</h2>
    )
  }
}
export default ActionList



