import {Struct, fromJS} from '@the-grid/immutable'

let CoverStruct = Struct({

  metadata: undefined,

  src: undefined,
  original: undefined,
  altsrc: undefined,
  imgflosrc: undefined,
  blurredUrl: undefined,

  resized: undefined,
  ratio: undefined,
  aspect: undefined,
  orientation: undefined,

  width: undefined,
  height: undefined,
  rotation: undefined,

  animated: undefined,
  unsalvageable: undefined,

  scene: {
    init(scene) {
      if (scene) {
        scene = fromJS(scene)
        return {scene}
      }
      return undefined
    },
  },

  textregions: {
    init(textregions) {
      if (textregions && textregions.length > 0) {
        textregions = fromJS(textregions)
        return {textregions}
      }
      return undefined
    },
  },

  hasTextTop: {
    init(hasTextTop) {
      hasTextTop = fromJS(hasTextTop)
      return {hasTextTop}
    },
  },

  hasTextBottom: {
    init(hasTextBottom) {
      hasTextBottom = fromJS(hasTextBottom)
      return {hasTextBottom}
    },
  },

  hasTextLeft: {
    init(hasTextLeft) {
      hasTextLeft = fromJS(hasTextLeft)
      return {hasTextLeft}
    },
  },

  hasTextRight: {
    init(hasTextRight) {
      hasTextRight = fromJS(hasTextRight)
      return {hasTextRight}
    },
  },

  hasTextXMiddle: {
    init(hasTextXMiddle) {
      hasTextXMiddle = fromJS(hasTextXMiddle)
      return {hasTextXMiddle}
    },
  },

  hasTextYMiddle: {
    init(hasTextYMiddle) {
      hasTextYMiddle = fromJS(hasTextYMiddle)
      return {hasTextYMiddle}
    },
  },

  easyTextRegions: {
    init(easyTextRegions) {
      if (easyTextRegions && easyTextRegions.length > 0) {
        easyTextRegions = fromJS(easyTextRegions)
        return {easyTextRegions}
      }
      return undefined
    },
  },

  all_faces: {
    init(all_faces) {
      all_faces = fromJS(all_faces)
      return {all_faces}
    },
  },

  lines: {
    init(lines) {
      lines = fromJS(lines)
      return {lines}
    },
  },

  closest_aspect: {
    init(closest_aspect) {
      closest_aspect = fromJS(closest_aspect)
      return {closest_aspect}
    },
  },

  transparent: {
    init(transparent) {
      transparent = fromJS(transparent)
      return {transparent}
    },
  },

  lightness: {
    init(lightness) {
      lightness = fromJS(lightness)
      return {lightness}
    },
  },

  saturation: {
    init(saturation) {
      saturation = fromJS(saturation)
      return {saturation}
    },
  },

  pictureImgfloUrls: {
    init(pictureImgfloUrls) {
      if (pictureImgfloUrls && pictureImgfloUrls.length > 0) {
        pictureImgfloUrls = fromJS(pictureImgfloUrls)
        return {pictureImgfloUrls}
      }
      return undefined
    },
  },

  faces: {
    init(faces) {
      if (faces && faces.length > 0) {
        faces = fromJS(faces)
        return {faces}
      }
      return undefined
    },
  },

  colors: {
    init(colors) {
      colors = fromJS(colors)
      return {colors}
    },
  },

  saliency: {
    init(saliency) {
      saliency = fromJS(saliency)
      return {saliency}
    },
  },

  exif: {
    init(exif) {
      exif = fromJS(exif)
      return {exif}
    },
  },

  resolution: {
    deps: ['width', 'height'],
    get(width, height) {
      if (width && height) {
        let surface = height * width

        if (surface <= 66000) return 'icon'
        if (surface > 66000 && surface <= 150000) return 'low'
        if (surface > 150000 && surface <= 600000) return 'medium'
        if (surface > 600000) return 'high'
      }
      return undefined
    },
  },

  filmstrip: {
    deps: ['width', 'height'],
    get(width, height) {
      if (width && height) {
        let aspectRatio = width / height

        if (aspectRatio > 2.5) return 'panorama'
        if (aspectRatio < .4) return 'tower'
      }
      return undefined
    },
  },

  isHeroCover: {
    deps: ['isFullMobileResolution', 'isFullDesktopResolution', 'orientation'],
    get(isFullMobileResolution, isFullDesktopResolution, orientation) {
      return isFullMobileResolution && isFullDesktopResolution && orientation === 'landscape'
    },
  },

  isFullMobileResolution: {
    deps: ['width', 'height'],
    get(width, height) {
      if (width && height) {
        if (width >= 320 && height >= 640) return true
        return false
      }
      return undefined
    },
  },

  isFullDesktopResolution: {
    deps: ['width', 'height'],
    get(width, height) {
      if (width && height) {
        if (width >= 1280 && height >= 640) return true
        return false
      }
      return undefined
    },
  },

  focalPoint: {
    deps: ['faces', 'saliency', 'width', 'height', 'scene'],
    get(faces, saliency, width, height, scene) {
      let focalPoint = ['50%', '50%']

      // Give faces-only priority, followed by scene
      if (faces) {
        let face = faces.get(0)
        focalPoint = [
          Math.min(1, ((face.get('x') + (face.get('width') / 2)) / width)) * 100 + '%',
          Math.min(1, ((face.get('y') + (face.get('height') / 2)) / height)) * 100 + '%',
        ]
      } else if (scene) {
        let center = scene.get('center')
        if (center) {
          focalPoint = [
            Math.min(1, center.get('x') / width) * 100 + '%',
            Math.min(1, center.get('y') / height) * 100 + '%',
          ]
        }
      } else if (saliency) {
        let bbox = saliency.get('bbox')
        if (bbox) {
          focalPoint = [
            Math.min(1, ((bbox.get('x') + bbox.get('width') / 2) / width)) * 100 + '%',
            Math.min(1, ((bbox.get('y') + bbox.get('height') / 2) / height)) * 100 + '%',
          ]
        }
      }
      return focalPoint
    },
  },
})

export default CoverStruct



