import {Struct, Map} from '@the-grid/immutable'

let TypographyStruct = Struct({

  // inputs

  breakpoint: undefined,

  isTypography: {
    default: true
  },

  //
  fonts: {
    default: {
      'd': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': '"TradeGothicNextLight", "Trade Gothic Next LT Pro Light"',
          'font-weight': '300 !important',
          'font-style': 'normal !important',
          'letter-spacing': 0,
        },
      },

      'd3': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': '"TradeGothicNextBold", "Trade Gothic Next LT Pro Bold", "HelveticaNeue-CondensedBold", -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto", "Segoe UI", "Helvetica Neue", "Lucida Grande", sans-serif',
          'font-weight': '700 !important',
          'font-style': 'normal !important',
          'letter-spacing': 0,
        },
      },

      'd2': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': '"TradeGothicNextBoldCondensed", "Trade Gothic Next LT Pro Bold Condensed", "HelveticaNeue-CondensedBold", "HelveticaNeue-CondensedBlack", -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto", "Segoe UI", "Helvetica Neue", "Lucida Grande", sans-serif',
          'font-weight': '700 !important',
          'font-style': 'normal !important',
          'letter-spacing': '-0.03em',
        },
      },

      'txt': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': '"GeorgiaProItalic", "Trade Gothic Next LT Pro Light"',
          'font-weight': '400 !important',
          'font-style': 'italic !important',
          'letter-spacing': 0,
        },
      },

      'txt2': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': ' -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto", "Segoe UI", "Helvetica Neue", "Lucida Grande", sans-serif',
          'letter-spacing': 0,
        },
      },

      'num': {
        style: {
          '-webkit-font-smoothing': 'antialiased',
          'font-family': '"GeorgiaProItalic", "Trade Gothic Next LT Pro Light"',
          'font-weight': '400 !important',
          'font-style': 'italic !important',
          'letter-spacing': 0,
        },
      },
    }
  },

  // computed

  'fnt-sz': {
    deps: ['breakpoint.isSizeGTE'],
    get: function (isSizeGTE) {

      let sz

      if (isSizeGTE('media-xl')) {
        sz = {
          'd-xxl': 166,
          'd-xl': 112,
          'd-l': 80,
          'd': 56,
          'd-m': 56,
          'd-s': 36,

          'txt-l': 24,
          'txt': 20,
          'txt-s': 16,
          'txt-xs': 14,

          'icon': 24,
        }
      }

      else if (isSizeGTE('media-l')) {
        sz = {
          'd-xxl': 130,
          'd-xl': 96,
          'd-l': 72,
          'd': 48,
          'd-m': 48,
          'd-s': 32,

          'txt-l': 24,
          'txt': 20,
          'txt-s': 16,
          'txt-xs': 14,

          'icon': 24,
        }
      }

      else if (isSizeGTE('media-s')) {
        sz = {
          'd-xxl': 104,
          'd-xl': 88,
          'd-l': 64,
          'd': 40,
          'd-m': 40,
          'd-s': 32,

          'txt-l': 20,
          'txt': 16,
          'txt-s': 16,
          'txt-xs': 14,

          'icon': 24,
        }
      }

      else if (isSizeGTE('media-xs')) {
        sz = {
          'd-xxl': 96,
          'd-xl': 80,
          'd-l': 56,
          'd': 36,
          'd-m': 36,
          'd-s': 24,

          'txt-l': 20,
          'txt': 16,
          'txt-s': 14,
          'txt-xs': 12,

          'icon': 24,
        }
      }

      else if (isSizeGTE('el-xl')) {
        sz = {
          'd-xxl': 72,
          'd-xl': 56,
          'd-l': 40,
          'd': 32,
          'd-m': 32,
          'd-s': 24,

          'txt-l': 20,
          'txt': 16,
          'txt-s': 14,
          'txt-xs': 10,

          'icon': 24,
        }
      }

      else if (isSizeGTE('el-l')) {
        sz = {
          'd-xxl': 64,
          'd-xl': 48,
          'd-l': 32,
          'd': 24,
          'd-m': 24,
          'd-s': 20,

          'txt-l': 16,
          'txt': 14,
          'txt-s': 12,
          'txt-xs': 10,

          'icon': 24,
        }
      }

      else if (isSizeGTE('el-m')) {
        sz = {
          'd-xxl': 50,
          'd-xl': 32,
          'd-l': 24,
          'd': 20,
          'd-m': 20,
          'd-s': 20,

          'txt-l': 16,
          'txt': 14,
          'txt-s': 12,
          'txt-xs': 10,

          'icon': 24,
        }
      }

      else if (isSizeGTE('el-s')) {
        sz = {
          'd-xxl': 40,
          'd-xl': 20,
          'd-l': 16,
          'd': 16,
          'd-m': 16,
          'd-s': 14,

          'txt-l': 14,
          'txt': 12,
          'txt-s': 10,
          'txt-xs': 10,

          'icon': 20,
        }
      }

      else {
        sz = {
          'd-xxl': 40,
          'd-xl': 20,
          'd-l': 16,
          'd': 16,
          'd-m': 16,
          'd-s': 14,

          'txt-l': 14,
          'txt': 12,
          'txt-s': 10,
          'txt-xs': 0,

          'icon': 16,
        }
      }


      return Map(sz)

    }
  }
})

export default TypographyStruct



