import React from 'react'
import {Style} from '@the-grid/styleverse'
import Icon from '@the-grid/ui-icon'
import stylePillable from './style-pillable'
import styleStatic from './style-static'

var count = 0
class ActionButton extends React.Component {
  static displayName = 'ActionButton'
  /*
  static propTypes = {
    label: PropTypes.string,
    icon: PropTypes.string,
    layout: PropTypes.string.isRequired, // 'v-icon-label', 'v-label-icon', 'h-icon-label', 'h-label-icon'
    active: PropTypes.bool.isRequired,
    show: PropTypes.bool.isRequired,
    pillable: PropTypes.bool.isRequired, // 'pillable', 'v-icon-label', 'v-label-icon', 'h-icon-label'
    size: PropTypes.string, // 's' 'm' 'l'
    onClick: PropTypes.func,
    onMouseDown: PropTypes.func,
  }
  */
  static defaultProps = {
    layout: 'h-label-icon',
    active: false,
    show: true,
    pillable: false,
    size: 'm',

    // @TODO:
    // togglableChildren: false,
    // children: false,
  }
  id = 'ActionButton-' + count++
  prevIcon = undefined

  componentWillMount() {
    let {icon} = this.props
    this.prevIcon = icon
  }
  // componentWillReceiveProps(nextProps) {
  //  let {icon} = nextProps
  // }
  render() {
    let {
      icon,
      label,
      pillable,
      onMouseDown,
      layout,
      className,
      id,
      style,
      labelAsTip,
      ...attrs
    } = this.props
    
    id = id || this.id
    let scope = '#' + id

    className = className || ' '
    className += ' action-button'
    className += ' layout--' + layout
    className += (icon) ? ' with-icon' : ' without-icon'
    className += (label) ? ' with-label' : ' without-label'
    if (pillable) className += ' pillable'

    style = style
      ? Object.assign({backfaceVisibility: 'hidden'}, style)
      : {backfaceVisibility: 'hidden'}

    // @TODO: position direction here as well @see fab/index
    if (labelAsTip) {
      var tooltip = {
        ['data-rh']: label,
        ['data-rh-at']: 'left',
      }
      attrs = {...attrs, ...tooltip}
    }

    let rendered = (
      <div id={id} className={className} onMouseDown={onMouseDown} style={style} {...attrs}>
        {this.renderStyle(scope)}
        {this.renderIcon(this.props)}
        {this.renderLabel(this.props)}
      </div>
    )

    this.prevIcon = icon

    return rendered
  }

  renderStyle(scope) {
    let {
      pillable,
    } = this.props

    return (
      <span>
        {styleStatic(scope)}
        {pillable && (stylePillable(scope))}
      </span>
    )
  }

  renderIcon({icon, id}) {
    if (id === 'fab') debugger
    if (!icon) return null
    return <Icon className="action-icon icon" morphable={true}>{icon}</Icon>
  }

  renderLabel({label, labelAsTip}) {
    if (!label || labelAsTip) return null
    return (<div className="action-label"><span>{label}</span></div>)
  }
}
export default ActionButton



