import React from 'react'
import {Struct, List} from '@the-grid/immutable'
import {chromatose, Color} from  '@the-grid/deps'

const grayScaleMatrix = '0.2126 0.7152 0.0722 0 0 0.2126 0.7152 0.0722 0 0 0.2126 0.7152 0.0722 0 0 0 0 0 1 0'

let GradientMapStruct = Struct({

  id: undefined,

  colors: {
    set(colorArray) {
      // already set from a struct, from a merge for example
      var isImmutable = (colorArray.toJS && colorArray.size)
      var l = isImmutable ? colorArray.size : colorArray.length

      // cast colors as List of rgbaLists
      var colors = [], c
      for (var i = 0; i < l; i++) {
        c = isImmutable ? colorArray.get(i) : colorArray[i]
        if (typeof c !== 'string') throw new Error('SVGGradientMap: props.colors must be List || Array of Strings')
        colors.push(List(Color(c).rgbArray()))
      }
      return {colors: List(colors)}
    },
  },

  rgbaTable: {
    deps: ['colors'],
    get(colors) {
      var r = ''
      var g = ''
      var b = ''
      var a = ''
      for (var i = 0; i < colors.size; i++) {
        var rgbaList = colors.get(i)
        r += (rgbaList.get(0) / 255.0 + ' ')
        g += (rgbaList.get(1) / 255.0 + ' ')
        b += (rgbaList.get(2) / 255.0 + ' ')
        a += (rgbaList.get(3, '1') + ' ')
      }
      return {r, g, b, a}
    },

  },

  render: {
    deps: ['id','rgbaTable'],
    get(id, {r, g, b, a}) {
      return (<svg version="1.1" width="0" height="0" dangerouslySetInnerHTML={{__html: `
        <filter id="${id}" filterUnits="objectBoundingBox" primitiveUnits="objectBoundingBox" x="0" y="0" width="100%" height="100%">
          <feColorMatrix type="matrix"values="${grayScaleMatrix}" />
          <feComponentTransfer color-interpolation-filters="sRGB">
            <feFuncA type="table" tableValues="${a}"/>
            <feFuncR type="table" tableValues="${r}"/>
            <feFuncG type="table" tableValues="${g}"/>
            <feFuncB type="table" tableValues="${b}"/>
          </feComponentTransfer>
       </filter>
      `}} />)
    },

  },

  /* TODO: gradient map steps

  _steps: undefined,

  steps: {

    // stores set value on internal `_steps` so getter can dynamically handle when not set
    set: function(_steps) {
      return { _steps }
    },

    cache: false,

    deps: ['_steps'],

    get: function(_steps, colors) {
      if (_steps) return _steps
      throw new Error("Gradient Map TODO")
      return
    }
  },

  */

})

export default GradientMapStruct



