import React from 'react'
import {Store} from '@the-grid/cymatics'
import {Struct, Map} from '@the-grid/immutable'
import {Springs, SprungSheet} from '@the-grid/ui-sprung'

let springs = new Springs({id: 'overlay-springs'})
springs.set('show', 0)
springs.set('scale', 2)
springs.set('y', -64)
springs.commit()
let sprungSheet = (
  <SprungSheet key="sprung-sheet" springs={springs}>{function({vals, _}) {
    let {show, scale, y} = vals
    let scope = "#overlay-renderer"
    return `
      ${scope} {
        visibility: ${show <= 0 ? 'hidden' : 'inherit'} !important;
        opacity: ${show};
        transform: scale(${scale}) translate3d(0,${y}px,0);
      }
      body {
        overflow-y: ${show === 0 ? 'auto' : 'hidden'} !important;
      }
    `
  }}</SprungSheet>
)

let OverlayState = Struct({
  renderers: {
    default: Map()
  },
  show: {
    default: false,
  },
  current: undefined,

  // computed

  render: {
    cache: false,
    deps: ['current','renderers'],
    get: function (current, renderers) {
      return renderers.get(current)
    }
  },

  sprungSheet: {
    cache: false,
    get: function() {
      return sprungSheet
    }
  }
})

let initialState = new OverlayState()

class OverlayStore extends Store {
  state = initialState
  name = 'overlay'
  actions = {
    'show': function(state, name) {
      if (!name) return state.set('show', true)
      springs.set('show', 1)
      springs.set('scale', 1)
      springs.set('y', 0, {start: -320})
      springs.commit()
      return state.merge({
        'show': true,
        'current': name,
      })
    },
    'hide': function(state) {
      springs.set('show', 0)
      springs.set('scale', 2)
      springs.set('y', -64)
      springs.commit()
      return state.set('show', false)
    },
    'toggle': function(state, name) {
      if (this.state.get('show')) return this.actions.hide(state)
      return this.actions.show(state, name)
    },
    'setRenderer': function(state, name, renderer) {
      return state.setIn(['renderers', name], renderer)
    },
  }
}

export default OverlayStore


