import React from 'react'
import {fromJS} from '@the-grid/immutable'
import {raf} from '@the-grid/deps'
import {Story} from '@the-grid/ui-story-machine'
import {Style} from '@the-grid/styleverse'

class Sites extends Story {
  storyName = 'sites'
  renderedSitesVectors = null
  willMount() {
    if (this.focused) return true
    this.updateUI()
  }
  focus() {
    this.updateUI()
  }
  updateUI() {
    this.contextRequest('FAB', 'update', this.UI.FAB)
    this.contextRequest('FAF', 'update', this.UI.FAF)
    this.contextRequest('FAN', 'update', this.UI.FAN)
  }
  display(children) {
    if (!this.renderedSitesVectors && window.VECTORS_SITES) this.renderedSitesVectors = (
      <div id="sites-mount" key="sites-mount">
        <Style key="sites-mount-style">{`
          html, body {
            overflow -- hidden !important
            #section-layout {
              d -- none !important
            }
          }
          #sites-mount {
            position -- fixed
            top -- 0
            left -- 0
            right -- 0
            bottom -- 0
            z-index -- 200
            overflow-y -- auto
          }
        `}</Style>
        {window.renderVectors(window.VECTORS_SITES, {key: 'vectros-sites'})}
      </div>
    )
    return (
      <div id="story--sites">
        {children}
        {this.renderedSitesVectors}
      </div>
    )
  }
  UI = {
    FAB: fromJS({
      show: true,
      active: true,
      chroma: {type: 'light', typeIndex: 0},
      className: '',
      //icon: '+',
      activeIcon: 'x',
      icon: '+',
      onClick: () => {
        let that = this
        raf(function(){
          that.contextRequest('storyline', 'to-story', 'view-site')
        })
        return false
      },
      appButton: {
        icon: 'arrow_back',
        show: true,
        //activeIcon: 'arrow_back',
        onClick: () => {
          let that = this
          raf(function(){
            that.contextRequest('storyline', 'pop-story')
          })
          return false
        },
      },
      actionList: [

        {
          label: 'import existing site',
          // key: 'create',
          onClick: () => {
            alert('This account does not have the permission to create new sites')
          }
        },
        {
          label: 'create new site',
          // key: 'create',
          onClick: () => {
            alert('This account does not have the permission to create new sites')
          }
        },
      ],
    }),
    FAF: fromJS({
      show: false,
    }),
    FAN: {
      className: 'align--l',
      show: false,
      hideOnScroll: false
    }
  }
}

export default Sites


