import {Struct} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
// import {Store} from '@the-grid/cymatics'
import {raf} from '@the-grid/deps'
import elementResizeDetectorMaker from 'element-resize-detector'

let resizeDetector = undefined
const getResizeDetector = () => {
  if (!resizeDetector) {
    resizeDetector = elementResizeDetectorMaker({
      strategy: 'scroll', // <- For ultra performance.
    })
  }
  return resizeDetector
}

let ResizeState = Struct({

  target: undefined, // string
  resizeTarget: undefined, // string
  didMeasure: {
    cache: false,
    deps: ['width'],
    get: function(width) {
      return width !== undefined
    },
  },
  bottom: undefined,
  height: undefined,
  left: undefined,
  right: undefined,
  top: undefined,
  width: undefined,
  windowWidth: undefined,
  windowHeight: undefined,

}, {
  defaultCache: false,
})

let initialState = new ResizeState({})

// TODO!!!

let USER_AGENT
let iOS
let webkit
let iOSSafari

// ensuring window measurement happens only once when needed
let prev_windowWidth
let prev_windowHeight
let windowWidth
let windowHeight
let isListeningToWindow = false
let windowListeners = []
function measureWindow() {
  prev_windowWidth = windowWidth
  prev_windowHeight = windowHeight
  windowWidth = window.innerWidth
  windowHeight = window.innerHeight
}
function _onWindowResize() {
  measureWindow()
  if (iOSSafari) {
    if (prev_windowWidth === windowWidth || prev_windowHeight === windowHeight) {
      windowWidth = prev_windowWidth
      windowHeight = prev_windowHeight
      return
    }
  }
  if (!USER_AGENT) {
    USER_AGENT = window.navigator.userAgent
    iOS = !!USER_AGENT.match(/iPad/i) || !!USER_AGENT.match(/iPhone/i)
    webkit = !!USER_AGENT.match(/WebKit/i)
    iOSSafari = iOS && webkit && !USER_AGENT.match(/CriOS/i)
  }
  for (let i = 0; i < windowListeners.length; i++) {
    windowListeners[i]()
  }
}

// TODO decrease long trailing throttle time with a requestStyleFrame or something...
let onWindowResize = raf.throttle(_onWindowResize, {leading: true, delay: 22})
// let onWindowResize = debounce(_onWindowResize, 240)
function listenToWindowIfNeeded() {
  if (isListeningToWindow) return
  isListeningToWindow = true
  window.addEventListener('resize', onWindowResize)
  measureWindow()
}


class ResizeStore extends Store {

  // debug = true
  name = 'resize'
  state = initialState
  deferCommit = false
  props = undefined
  isListening = false
  isSearchingTarget = false
  targetElement = undefined

  didCommit() {
    let handler = this.props.handler
    if (handler) handler(this)
    let onResize = this.props.onResize
    if (onResize) onResize(this.state)
  }

  shouldCommit(state) {
    if (!this.targetElement) return false
    return state.get('width') !== undefined
  }

  actions = {

    'update-props': (state, props) => {

      let {
        id,
        target,
        resizeTarget,
        shouldUpdateProps,
        shouldSearchTarget,
        handler,
      } = props

      if (target === 'self') target = 'auto'
      if (target === 'auto') {
        if (!id) throw new Error('ResizeContext with target: auto requires an id')
        target = '#' + id
      }
      if (resizeTarget === 'auto') resizeTarget = target

      props = {
        id,
        target,
        resizeTarget,
        shouldUpdateProps,
        shouldSearchTarget,
        handler,
      }

      if (this.props) {
        let currentTarget = this.props.target
        let target = props.target
        let currentResizeTarget = this.props.resizeTarget
        let resizeTarget = props.resizeTarget
        if (currentTarget !== target || currentResizeTarget !== resizeTarget) state = this.actions.unlistenIfNeeded(state)
      }
      this.props = props
      // return state
      return state.merge(props)
    },

    'listenIfNeeded': (state) => {
      if (this.isListening || this.isSearchingTarget || !this.props) return state
      return this.actions.listen(state)
    },

    'listen': (state) => {

      this.isSearchingTarget = false

      if (!this.props) return state

      let {target, resizeTarget} = this.props

      if (target === 'window') {
        this.targetElement = window
        // window.addEventListener('resize', listener)
        windowListeners.push(this.listener)
        this.isListening = true
      }
      else if (target.indexOf('#') === 0) {

        this.targetElement = document.getElementById(target.substr(1))

        // TODO?
        if (!this.targetElement && this.props.shouldSearchTarget) {
          this.isSearchingTarget = true
          raf(() => {
            return this.actions.listen(state, target, resizeTarget)
          })
          return state
        }

        if (resizeTarget) {
          if (resizeTarget === 'window') {
            // window.addEventListener('resize', listener)
            windowListeners.push(this.listener)
            this.isListening = true
          }
          else if (resizeTarget.indexOf('#') === 0) {
            getResizeDetector().listenTo(this.targetElement, this.listener)
            this.isListening = true
          }
          else {
            throw new Error(`ResizeContext resizeTarget must be undefined, 'window' or an id selector`)
          }
        }

      }
      else {
        throw new Error(`ResizeContext target must be 'window' or id selector`)
      }

      this.willMeasure()

      return state
      // return actions.onScrollStart(state, true)
    },

    'unlistenIfNeeded': (state) => {
      if (!this.isListening || !this.props) return state
      return this.actions.unlisten(state)
    },

    'unlisten': (state) => {

      if (!this.props) return state

      if (!this.isListening) return state

      let {target, resizeTarget} = this.props

      if (target === 'window' || resizeTarget === 'window') {
        // window.removeEventListener('resize', listener)
        windowListeners.splice( windowListeners.indexOf(this.listener) , 1 )
      }
      else if (resizeTarget) {
        getResizeDetector().removeListener(this.targetElement, this.update)
        // TODO!!!!
        // throw new Error("RECT CONTEXT NOT READY")
        // scrollingElement.removeEventListener('scroll', listener)
      }

      this.isListening = false
      this.targetElement = undefined
      return state
    },

    'measure': (state) => {
      let toMerge = this.measureRect()
      if (!toMerge) return state
      state = state.merge(toMerge)
      return state
    },

    'teardown': (state) => {
      this.actions.unlistenIfNeeded(state)
      this.props = undefined
      this.isListening = false
      this.isSearchingTarget = false
      this.targetElement = undefined
      return initialState
    },

  }

  constructor() {
    super()

    listenToWindowIfNeeded()

    let request = this.request

    this.willMeasure = () => {
      raf.cancel(this._measure)
      raf(this._measure)
    }
    this._measure = () => {
      request( 'measure' )
      // raf.cancel(this.__measure)
      // raf(this.__measure)
    }
    // this.__measure = () => {
    //  raf(this.___measure)
    // }
    // this.___measure = () => {
    //  request( 'measure' )
    // }

    this.listener =  this.willMeasure
    // let listener = throttle(this.willMeasure, 16, {leading: true, trailing: true})
    // let listener = debounce(this.willMeasure, 16)

    this.measureRect = () => {
      if (this.targetElement === window) {
        return {
          width: windowWidth,
          height: windowHeight,
          right: windowWidth,
          bottom: windowHeight,
          top: 0,
          left: 0,
          windowWidth,
          windowHeight,
          target: this.props.target,
          resizeTarget: this.props.resizeTarget,
        }
      }
      else if (this.targetElement) {
        let {
          bottom,
          height,
          left,
          right,
          top,
          width,
        } = this.targetElement.getBoundingClientRect()
        return {
          bottom,
          height,
          left,
          right,
          top,
          width,
          windowWidth,
          windowHeight,
          target: this.props.target,
          resizeTarget: this.props.resizeTarget,
        }
      }
      else {
        return undefined
      }
    }

  }

}

export default ResizeStore


