const operators = ['===', '==', '<=', '!==', '!=', '>=', '<', '>']
const stringOperators = ['===', '==', '!==', '!=']

// TODO!!! ties plugin logic too tightly?? get keys from stylestore
const getDepPath = (e) => {
  if (
    e.indexOf('props.') !== 0 &&
    e.indexOf('context.') !== 0
  ) {
    return 'styleStore.' + e
  }
  return e
}

const selectorParser = function(selector) {
  const idx = selector.indexOf('@?')
  if (idx > -1) {
    // if (idx !== 0) throw new Error('@breakpoint selector must be at begining of selector')
    const expression = selector.substr('@?'.length)
    let e1
    let e2
    let e1DepIndex
    let e2DepIndex
    const deps = []
    let operator

    for (let i = 0, len = operators.length; i < len; i++) {
      const op = operators[i]
      if (expression.includes(op)) {
        operator = op
        break
      }
    }

    if (operator) {
      const split = expression.split(operator)
      if (!split.length > 2) throw new Error('@? selector doesnt support: ' + selector)

      e1 = split[0]
      e2 = split[1]
    } else {
      e1 = expression
    }

    e1 = e1.trim()
    if (e1.includes('.')) {
      e1DepIndex = deps.length
      deps.push(getDepPath(e1))
    }

    if (e2) {
      e2 = e2.trim()
      if (e2.includes('.')) {
        e2DepIndex = deps.length
        deps.push(getDepPath(e2))
      }
    }

    return {
      deps,
      get(...args) {
        let v1 = args[e1DepIndex]
        let v2 = args[e2DepIndex]
        if (v1 === undefined) v1 = e1
        if (v2 === undefined) v2 = e2

        if (!operator) {
          if (e1 === '') v1 = true
          return {
            selector: !!v1 ? '&' : false,
            selectorCondGroup: 'conditional',
          }
        }


        if (isNaN(v1)) {
          if (v1[0] !== '\'' && v1[0] !== '"') v1 = '"' + v1 + '"'
          if (!stringOperators.includes(operator)) throw new Error('@? cant compare string types: ' + v1 + operator + v2)
        }
        if (isNaN(v2)) {
          if (v2[0] !== '\'' && v2[0] !== '"') v2 = '"' + v2 + '"'
          if (!stringOperators.includes(operator)) throw new Error('@? cant compare string types: ' + v1 + operator + v2)
        }

        return {
          selector: eval(v1 + ' ' + operator + ' ' + v2) ? '&' : false,
          selectorCondGroup: 'conditional',
        }
      },
    }
  }

  return undefined
}

export default selectorParser



