import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import {raf} from '@the-grid/deps'
import DesignBody from './design-body'

function NOOP() {}
let count = 0

let globalStyles = (
  <Style perf={true} key="design-globalStyles" id="design-globalStyles">
  {`

    //.fan_content {
    //  pad-l -- 30vw
    //}

    .component--layout-node {
      d -- flex !important
    }

  `}
  </Style>
)

let readyStyles = (
  <Style perf={true} key="design-readyStyles" id="design-readyStyles">
  {`

    .section-layout {
      tf-origin -- 50% 0%
      tf -- scale(.4)
      transition -- transform .3s
      box-sh -- l

      //section:hover {
      //  .next-section-layout-button {
      //    border-rad -- 50%
      //    bg -- chroma-box-pop
      //    color -- chroma-inverse-box
      //    d -- flex
      //    flx-aln -- mid
      //    flx-jst -- mid
      //    w -- 100
      //    h -- 100
      //    r -- 0
      //    t -- 50%
      //  }
      //}

      &:hover {
        .component--layout-node:not(:hover) section {
          o -- .25
        }
      }
      .section-layout-node {
        :first-child {
          tf-origin -- 50% 0%
        }
        > section {
          transition -- transform .3s, box-shadow .3s, opacity .3s
        }
        &:hover {
          z-index -- 1001 !important
          > section {
            tf -- scale(1.3)
            box-sh -- l
          }
        }

      }
    }

  `}
  </Style>
)

class DesignResizer extends React.Component {
  static displayName = 'DesignResizer'
  static contextTypes = {
    request: React.PropTypes.func.isRequired,
  }
  static defaultProps = {
    children: null,
  }
  state = {
    ready: false,
    resizeState: undefined,
  }
  currentPath = null
  // state = {
  //  // ['iphone','ipad','macbook','full']
  //  deviceType: 'macbook'
  // }

  componentWillMount() {
    this.context.request('story', 'connect', this.handleStory)
    this.context.request('FAF', 'connect', this.handleFAF)
  }
  handleStory = (story) => {
    let resizeState = story.get('resizeState')
    this.currentPath = story.get('path')
    if (resizeState === this.state.resizeState) return
    this.setState({resizeState})
  }
  FAF = null
  handleFAF = (FAF) => {
    this.FAF = FAF
  }
  componentDidMount() {
    this.targetEl = document.getElementsByClassName('section-layout')[0]
    setTimeout(this.onReady, 300)


  }
  onReady = () => {
    raf(() => {
      this.targetEl.addEventListener('mouseleave', this.onTargetMouseLeave)
      this.targetEl.addEventListener('mouseenter', this.onTargetMouseEnter)
      this.setState({ready: true}, NOOP)
    })
  }

  showOnLeave = false
  onTargetMouseEnter = () => {
    this.context.request('FAB', 'deactivate')
    if (this.FAF.get('show')) {
      this.showOnLeave = true
      //this.context.request('FAF', 'hide')
    }
    else {
      this.showOnLeave = false
    }
    // this.context.request('FAB', 'hide')
  }
  onTargetMouseLeave = () => {
    this.context.request('FAB', 'activate')
    if (this.showOnLeave) {
      this.showOnLeave = false
      //this.context.request('FAF', 'show')
    }
  }

  undone = false
  componentWillUnmount() {
    this.undone = true
    raf(()=>{
      window.layout.execute('zoom',1)
    })
    this.context.request('story', 'disconnect', this.handleStory)
    this.targetEl.removeEventListener('mouseenter', this.onTargetMouseEnter)
    this.targetEl.removeEventListener('mouseleave', this.onTargetMouseLeave)
  }

  render() {
    if (this.state.ready) {
      setTimeout(()=>{
      raf(()=>{
        if (this.undone) return
        window.layout.execute('zoom',.4)
      })
      }, 300)
    }
    return (
        <div id="design-resizer" key="design-resizer">
          {globalStyles}
          {this.state.ready ? readyStyles : null}
          {this.props.children}
          {this.renderDevice()}
        </div>
    )
  }

  renderDevice() {
    if (this.state.resizeState === 'zoom_out') return null
    return <DesignBody key="design-body" deviceType={this.state.resizeState} />
  }
}

export default DesignResizer



