import React, {PropTypes} from 'react'
import ResizeContext from '@the-grid/ui-resize-context'

//let mountCount = 0
let count = 0

export default class AsyncLayoutNode extends ResizeContext {
  static displayName = 'AsyncLayoutNode'

  state = {
    resize: undefined,
  }

  onResize = (store) => {
    this.context.request('async-layout', 'handle-node-size', this.props.index, store)
  }

  componentWillMount() {
    if (super.componentWillMount) super.componentWillMount()
    if (count === 0) {
      this.context.request('styleverse','add-css','component--AsyncLayoutNode', `
        .async-layout-node {
          display: none !important;
        }
      `)
    }
    //mountCount++
    count++
  }


  render() {
    let {
      target,
      tag,
      resizeTarget,
      children,
      shouldUpdateProps,
      shouldSearchTarget,
      ...p
    } = this.props

    if (!p.className) p.className = ''
    p.className += ' async-layout-node '

    if (target === 'auto') {
      if (!p.id) p.id = this.id
      return React.createElement(tag, p, this.renderChildren(children))
    }
    return children
  }

  renderChildren(children) {
    return children
  }
}


