import {Store} from '@the-grid/cymatics'
import {Struct, Map} from '@the-grid/immutable'
var StyleverseState = Struct({
  styleContext: undefined,
  cssById: {
    default: Map()
  },
  renderedCSS: {
    deps: ['cssById'],
    get(cssById) {
      if (!cssById.size) return null
      var cssString = cssById.toArray().join('\n')
      return <style id="root-styleverse-added-css" key={'root-styleverse-added-css'} dangerouslySetInnerHTML={{__html: cssString}} />
    }
  }
})
var initialState = new StyleverseState()
class StyleverseStore extends Store {
  displayName = 'StyleverseStore'
  name = 'styleverse'
  state = initialState
  deferCommit = true
  actions = {
    'add-css': (state, id, cssString) => {
      if (!id && !cssString) throw new Error(`styleverse action add-css requires (id, cssString)`)
      return state.setIn(['cssById', id], cssString)
    },
    'remove-css': (state, id) => {
      return state.deleteIn(['cssById', id])
    },
  }
}
export default StyleverseStore



