import {Struct} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
import {
  raf,
  debounce,
} from '@the-grid/deps'

// for browsers that dont' support document.body or document.scrolllingElement, ugh

let rootScrollingElement
let getRootScrollingElement = function() {
  if (!rootScrollingElement) {
    rootScrollingElement = (document.scrollingElement) ? document.scrollingElement : document.body
    if (!rootScrollingElement) rootScrollingElement = document.getElementsByTagName('body')[0]
  }

  // TODO: D4 hacky!!!!!!!!!!!!!
  // window.addEventListener('resize',function(){
  //   rootScrollingElement.scrollTop = 0
  // })

  return rootScrollingElement
}


let storesById = {}

let ScrollState = Struct({
  __store_id: undefined,
  target: undefined, // string
  count: 0,
  isScrolling: false,
  isRoot: {
    deps: ['target'],
    get: function(taget) {
      return (target === 'window' || target === 'document')
    },
  },

  // PERF: scrollTop, scrollHeight
  // ensuring DOM reads are only when needed & cached per scroll

  scrollTop: {
    cache: 1,
    instanceCache: true,
    deps: ['count'],
    get: function() {
      let id = this.get('__store_id')
      let el = storesById[id].scrollingElement
      if (!el) return undefined
      return el.scrollTop
    },
  },

  scrollHeight: {
    cache: 1,
    instanceCache: true,
    deps: ['count'],
    get: function() {
      let id = this.get('__store_id')
      let el = storesById[id].scrollingElement
      if (!el) return undefined
      return el.scrollHeight
    },
  },

}, {
  defaultCache: false,
})

let initialState = new ScrollState({})

let storeCount = 0



class ScrollStore extends Store {

  name = 'scroll'
  state = initialState
  props = undefined
  deferCommit = false
  isListening = false
  scrollingElement = undefined
  shouldCommit(state) {
    return state.get('__store_id') !== undefined
  }
  constructor() {
    super()

    this.__id = 'scroll-store-' + storeCount
    storesById[this.__id] = this
    storeCount++

    let request = this.request

    const scrollEndDebounce = debounce( (target, data) => {
      request( 'onScrollEnd' )
    }, 333)

    let _listener =  (isInitial) => {
      request( 'onScrollStart' )
    }

    let listener = raf.throttle(_listener, {leading: true})

    // let listener = throttle(_listener, 16, {leading: true, trailing: true})
    // let listener = debounce(listener, 16)

    let actions = this.actions = {

      'update-props': (state, props) => {
        if (this.props) {
          let currentTarget = this.props.target
          let target = props.target
          if (currentTarget !== target) state = actions.unlistenIfNeeded(state)
        }
        this.props = props
        // return state
        return state.merge(props)
      },

      'listenIfNeeded': (state) => {
        if (this.isListening || !this.props) return state
        return actions.listen(state)
      },

      'listen': (state) => {

        if (!this.props) return state

        let {target} = this.props

        if (target === 'window' || target === 'document') {
          this.scrollingElement = getRootScrollingElement()
          window.addEventListener('scroll', listener)
        }
        else if (target.indexOf('#') === 0) {
          this.scrollingElement = document.getElementById(target.substr(1))
          // TODO: searchTarget? shit...
          if (!this.scrollingElement) return state
          this.scrollingElement.addEventListener('scroll', listener)
        }
        else {
          throw new Error(`scroll context target must be 'document', 'window' or an id selector `)
        }
        listener()
        // raf( () => {
        //   request( 'onScrollStart', true )
        // })
        this.isListening = true
        // state = state.set('target', target) // hmmmm...
        return state
        // return actions.onScrollStart(state, true)
      },

      'unlistenIfNeeded': function(state) {
        if (!this.isListening) return state
        return actions.unlisten(state)
      },

      'unlisten': function(state) {

        if (!this.props) return state

        let {target} = this.props

        if (target === 'window' || target === 'document') {
          window.removeEventListener('scroll', listener)
        }
        else if (this.scrollingElement) {
          this.scrollingElement.removeEventListener('scroll', listener)
        }

        this.isListening = false
        this.scrollingElement = undefined
        return state
      },

      'onScrollStart': function(state) {
        scrollEndDebounce.cancel()
        let count = this.state.get('count')
        let isInitial = count === 0
        count++
        let isScrolling = isInitial ? false : true
        let toMerge = {
          count,
          __store_id: this.__id,
          // scrollTop,
          // scrollHeight,
          // topDelta,
          isScrolling,
        }

        state = state.merge(toMerge)

        if (isScrolling) scrollEndDebounce()
        return state
      },

      'onScrollEnd': function(state) {
        // let topDelta = 0
        let isScrolling = false

        state = state.merge({
          // topDelta,
          isScrolling,
        })

        return state
      },

      'teardown': (state) => {
        delete storesById[this.__id]
        actions.unlistenIfNeeded(state)
        this.props = undefined
        this.isListening = false
        this.scrollingElement = undefined
        return initialState
      },

    }
  }

}

export default ScrollStore


