import {Store} from '@the-grid/cymatics'
import PluginStruct from './Struct'
let initialState = new PluginStruct()

class BreakpointStore extends Store {
  //debug = true
  name = 'styleverse-breakpoint'
  deferCommit = false
  state = initialState
  got_props = false
  got_vp = false
  use_vp = false

  shouldCommit(state) {
    return (state.get('width') >= 0 && this.got_props && this.got_vp)
  }

  contextStores = {

    'viewport': (viewportStore) => {
      this.got_vp = true
      let viewport = viewportStore.state
      if (this.use_vp) return this.execute('merge', viewport, {viewport})
      return this.execute('set', 'viewport', viewport)
    },

    'resize': (store) => {
      let width = store.state.get('width')
      let height = store.state.get('height')
      this.execute('update-size', {width, height})
    },

    // TODO !!!!!!!!!!!!!!!!!!!!
    //'styleverse-breakpoint:root':
  }

  actions = {

    'update-props': (state, props) => {
      this.got_props = true
      let target = props.target
      let toMerge
      if (target === 'window' || target === 'viewport') {
        this.use_vp = true
        if (this.got_vp) return state.merge(state.get('viewport'), props)
      }
      return state.merge(props)
    },

    'update-size': (state, size) => {
      let constrain = state.get('constrain')
      if (constrain) size = constrain(size)
      return state.merge(size)
    },

    'teardown': () => {
      this.got_props = false
      this.got_vp = false
      this.use_vp = false
      return initialState
    }
  }
}

export default BreakpointStore


