import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import Device from '@the-grid/ui-device'

let count = 0

class DesignBody extends React.Component {
  static defaultProps = {
    deviceType: 'macbook',
  }
  // state = {
  //  // ['iphone','ipad','macbook','full']
  //  deviceType: 'macbook'
  // }

  // componentWillUnmount() {
  // }

  render() {
    return (
      <StyleContext key="StyleContext" chroma={{randomIfNeeded: true, type: 'light'}}>
        <div id="device-demo" key="device-demo">
          <Style perf={true} scope={'#device-demo'} id="device-styles">
          {`
              html, body {
                overflow-y -- hidden !important

              }
              :scope {
                  w -- 100vw
                  h -- 100vh
                  min-h -- 640
                  pos -- absolute
                  t -- 0
                  bg-clr -- chroma-dark-1-bg

                .device {
                  pos -- a
                  t -- 1goldy
                  b -- 1goldy
                  w -- initial
                  h -- initial
                  l -- 0
                  r -- 0
                }

                #selectable-iframe {
                  pos -- a
                  t -- 0
                  l -- 1goldx
                  r -- 1goldx
                  b -- 0
                  d -- flx
                  flx-jst -- mid
                  flx-aln -- mid
                  font-size -- txt-l
                }
              }
          `}
          </Style>

          <div id="selectable-iframe">
            <Device.IFrame
              className={'device'}
              type={this.props.deviceType} url={window.location.href} />
          </div>
        </div>
      </StyleContext>
    )
  }
}

export default DesignBody



