import React from 'react'
import {installStore} from '@the-grid/cymatics'
import AsyncLayoutStore from './async-layout-store'

function NOOP(){}

class AsyncLayout extends React.Component {

  dirty = false
  state = {displayCount: 0}

  componentWillMount() {
    this.store.willMount(this)
    this.store.execute('connect', this.handleStore)
  }

  componentDidMount() {
    this.store.didMount(this)
  }

  componentWillUnmount() {
    this.store.willUnmount(this)
    this.store.execute('disconnect', this.handleStore)
  }

  handleStore = (store) => {
    //console.log("HANDLE ASYNC LAYOUT STORE +++++++", this.dirty)
    if (this.dirty) return
    this.dirty = true
    this.setState({displayCount: ++this.state.displayCount}, NOOP)
  }

  render() {
    this.dirty = false
    return this.store.render()
  }
}

AsyncLayout = installStore(AsyncLayout, 'async-layout', function(component) {
  return new AsyncLayoutStore(component)
})

export default AsyncLayout



