import React from 'react'
import IconMorphable from './icon-morphable'
import renderer from './renderer'
function Icon(props) {
  if (!props.children) throw new Error('Icon requires children as lookup to icon renderer')
  if (props.morphable) return React.createElement(IconMorphable, props)
  var className = props.className || ''
  className += ' ornament'
  var id = props.id
  var style = props.style || {}
  //style.width = style.height = 24
  //style.height = '1em'
  style.width = '100%'
  style.display = 'inline-block'
  style.position = 'relative'
  return React.createElement('div',{id, className, style}, renderer(id, props.children, props))
}
export default Icon



