import React from 'react'
import ReactPlayer from 'react-player'
import designable from '@the-grid/ui-designable'
import Icon from '@the-grid/ui-icon'
import {raf} from '@the-grid/deps'
import ResizeRenderer from '@the-grid/ui-resize-renderer'

let el = React.createElement

var designConfig = {
  style: `
    .icon {
      clr -- chroma-text-pop
    }
    :scope {
      bg-clr -- chroma-box
    }
  `,
  designverse: {

    'filter--gmap': {
      style: `
        :scope {
          chroma-filter--gradient-map
        }
      `
    },
    'layout--bg-fill': {
      style: `
        :scope {
          pos -- absolute !important
          mar -- 0 !important
          z-idx -- -1
          l -- 0
          r -- 0
          t -- 0
          b -- 0
        }
      `
    },
    'layout--absolute-fill': {
      style: `
        :scope {
          pos -- absolute !important
          l -- 0
          r -- 0
          t -- 0
          b -- 0
        }
      `
    }
  },
  sharedCSS: `
    /*
      9/16*100 = 56.25
      16/9*100 = 177.77
    */
    .component--media {
      width: 100%;
      height: 100%;
    }
      .component--media .video_src {
        position: absolute;
        opacity: 1;
        left: 0;
        right: 0;
        top: 0;
        width: 100% !important;
        height: 0 !important;
        padding-bottom: 56.25% !important;
        transform: translate3d(0px,0px,0px);
        backface-visibility: hidden;
        background-size: cover;
        background-position: 50% 50%;
        overflow: hidden;
        pointer-events: none;
        /*
          //for svg-filters
          z-index: -1;

          bottom: 0;
          height: 100%;
          min-height: 100vh;
          min-width: 177.77vh;
        */
      }
        .component--media .video_src iframe, .component--media .video_src video {
          /*
            width -- 100vw !important
            height -- 56.25vw !important
            min-height -- 100vh
            min-width -- 177.77vh
          */
          width: 100% !important;
          height: 100% !important;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      .component--media .image_src {
        position: absolute;
        opacity: 1;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        height: 100%;
        backface-visibility: hidden;
        transform: translate3d(0px,0px,0px);
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        overflow: hidden;
        pointer-events: none;
        /*
          // for svg-filters
          z-index: -1;
        */
      }
  `
}

