function src_is_video(src) {
  if (src.includes('vimeo.com')) return true
  if (src.includes('youtube.com')) return true
  return false
}

const vimeoConfig_autoplay = {
  background: 1,
  iframeParams: {background: 1},
  preload: true,
}


class Media extends React.Component {

  static displayName = 'Media'

  static defaultProps = {
    autoplay: true,
    volume: .8,
  }

  player = null
  hasPlayed = false

  state = {
    playing: false,
    videoIsReady: false,
    //REMOVE: false,
  }

  componentDidUpdate() {
    if (!this.hasPlayed && this.state.playing) this.hasPlayed = true
  }

  get_id() {
    if (this.props.id) return this.props.id
    return this._id
  }

  get_className() {
    let {className} = this.props
    if (!className) className = ''
    className += ' media component--media'
    return className
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.videoSrc !== nextProps.videoSrc) return true
    if (this.props.imageSrc !== nextProps.imageSrc) return true
    if (this.props.className !== nextProps.className) return true
    if (this.props.style !== nextProps.style) return true

    if (this.state.playing !== nextState.playing) return true
    if (this.state.videoIsReady !== nextState.videoIsReady) return true
    return false
  }

  hasVideo() {
    return !!this.props.videoSrc
  }

  canToggle() {
    return this.props.videoSrc && this.props.playIcon
  }

  toggleVideo() {
    if (!this.hasVideo()) return
    if (!this.canToggle) return
    if (!this.hasPlayed) {
     if (this.player) this.player.seekTo(0)
    }
    this.setState({playing: !this.state.playing})
  }

  onClick = () => {
    this.toggleVideo()
  }


  render() {

    // getId('Header-bg')
    this.id = this.get_id()
    this.className = this.get_className()
    var style = this.props.style

    var props = {
      id: this.id,
      key: this.id,
      className: this.className,
      style,
      onClick: this.onClick,
    }

    if (this.hasVideo()) {
     return (
       <ResizeRenderer {...props}>{ (size) => {
         this.measured_width = size.get('width')
         this.measured_height = size.get('height')
         if (this.measured_width === undefined) return null
         return this.renderChildren()
       }}</ResizeRenderer>
     )
    }

    //this.measured_width = 1440
    //this.measured_height = 900

    return (
      <div {...props}>
        {this.renderChildren()}
      </div>
    )
  }

  renderChildren() {
    let children = []

    if (this.hasVideo()) {
      children.push(this.render_media_video_style())
      children.push(this.render_media_video(this.props))
    }
    else {
      children.push(this.render_media_image(this.props))
    }

   children = this.injectPlayIconIfNeeded(children)

    return children
  }

  injectPlayIconIfNeeded(children) {
    let {playIcon} = this.props
    if (!playIcon) return children
    let {pauseIcon} = this.props
    if (!this.state.playing) {
      children.push( el(Icon, {key: 'play-icon', style:{
        fontSize: '16vw',
        textAlign: 'center',
        width: '100%',
        height: '100%',
        opacity: 0.7,
        //color: 'white',
      }}, (playIcon )) )
    }
    // else {
    //   if (!pauseIcon) pauseIcon = null
    //   children.push( el(Icon, {key: 'play-icon'}, pauseIcon) )
    // }
    return children
  }

  render_media_video_style() {
    let m_w = this.measured_width
    let m_h = this.measured_height
    let m_ratio = m_w/m_h
    let ratio = 16/9
    let h, w, t, l

    if (ratio >= m_ratio) {
      h = m_h
      w = m_h * ratio
      t = 0
      l = 0 - (w - m_w) / 2
    }
    else {
      w = m_w
      h = m_w / ratio
      t = 0 - (h - m_h) / 2
      l = 0
    }

    let scope = `#${this.id} .video_src`
    let css = `
      ${scope} {
        width: ${w}px !important;
        height: ${h}px !important;
        min-height: 100%;
        min-width: 100%;
        left: ${0}px !important;
        top: ${0}px !important;
        -webkit-transform: translate3d(${l}px, ${t}px, 0px) !important;
        transform: translate3d(${l}px, ${t}px, 0px) !important;
      }
    `
    //if (this.state.playing && !this.props.autoplay) {
    //  css += `
    //    ${scope} {
    //
    //    }
    //  `
    //}
    return (<style key="video-placement-style" dangerouslySetInnerHTML={{__html: css}} />)
  }

  player = null
  render_media_video(props) {
    let src = props.videoSrc
    let autoplay = props.autoplay
    let volume
    if (!this.state.playing) {
      volume = 0
    }
    else {
      volume = props.volume ? props.volume : 0
    }

    //let volume = autoplay ? 0 : props.volume
    //let playing = autoplay ? true : this.state.playing
    let playing = this.hasPlayed ? this.state.playing : autoplay

    //let vimeoConfig = autoplay  ? vimeoConfig_autoplay  : undefined
    let vimeoConfig = (src.includes('vimeo.') && autoplay)
      ? vimeoConfig_autoplay  : undefined
    //let vimeoConfig = autoplay
    //  ? vimeoConfig_autoplay
    //  : !playing
    //    ? vimeoConfig_autoplay
    //    : {}

    let hidden = autoplay && !this.state.videoIsReady
    hidden = false

    //if (!this.onVideoStart) {
    //  this.onVideoStart = () => {
    //    if (this.state.videoIsReady) return
    //    raf(()=>{
    //      this.setState({videoIsReady: true})
    //    })
    //  }
    //}
    return (
      <ReactPlayer
        ref={(player) => {
          this.player = player
        }}
        key="reactplayer"
        id={this.get_id() + '_src'}
        className={'media_src video_src'}
        url={src}
        hidden={hidden}
        playing={playing}
        loop={autoplay}
        volume={volume}
        vimeoConfig={vimeoConfig}
        width="100%"
        height="100%"
        onStart={this.onVideoStart}
        playsinline={autoplay}
      />
    )
    //style={inlineStyles.video_src}
  }

  render_media_image(props) {
    let src = props.imageSrc
    return (
      <div
        key="image"
        id={this.get_id() + '_src'}
        className={'media_src image_src'}
        style={{backgroundImage: `url(${src})`}}
      />
    )
    // id={getId('Header-bg-media')}
  }
}
export default designable(Media, designConfig)



