import React from 'inferno-compat'
import {installStore} from '@the-grid/cymatics'
import StorylineStore from './storyline-store'
import Content from './storyline-content'

class Storyline extends React.Component {
  static displayName = 'Storyline'
  componentWillMount() {
    this.store.willMount(this)
  }
  componentWillUnmount() {
    this.store.willUnmount(this)
  }
  render() {
    if (!this.content) this.content = <Content />
    return (
      <div id="storyline-context" key="storyline-context">
        {this.props.children}
        {this.content}
      </div>
    )
  }
}

const InstalledStoryline = installStore(Storyline, 'storyline', function(component) {
  return new StorylineStore(component)
})

export default InstalledStoryline
export {
  Content as StorylineContent,
  Storyline as AbstractStoryline,
  InstalledStoryline as Storyline,
  StorylineStore as AbstractStorylineStore,
}



