import React, {PropTypes} from 'inferno-compat'
import {raf} from '@the-grid/deps'
import {installStore} from '@the-grid/cymatics'
// import {installStore} from '@the-grid/cymatics'
import ResizeStore from './ResizeStore'

let count = 0

class ResizeContext extends React.Component {

  /*

  state = {
    resize: undefined,
  }

  onResize = (store) => {
    this.setState({resize: store.state})
  }

  */

  static displayName = 'ResizeContext'

  static propTypes = {
    id: PropTypes.string,
    tag: PropTypes.any,
    target: PropTypes.string.isRequired,
    resizeTarget: PropTypes.string,
    children: PropTypes.any.isRequired,
    shouldUpdateProps: PropTypes.bool.isRequired,
    shouldSearchTarget: PropTypes.bool.isRequired,
    handler: PropTypes.func,
    onResize: PropTypes.func,
    // silentWidth ?
    // silentHeight ?
  }

  static defaultProps = {
    target: 'auto',
    resizeTarget: 'auto',
    tag: 'div',
    shouldUpdateProps: true,
    shouldSearchTarget: true,
  }

  id = 'resize-context-' + count++

  componentWillMount() {
    this.store.silently( 'update-props', this.props )
    this.store.execute( 'listenIfNeeded' )

    //
    if (this.onResize) this.store.execute( 'connect', this.onResize )
  }

  componentDidMount() {
    let {shouldUpdateProps, shouldSearchTarget} = this.props
    if (!shouldSearchTarget) {
      raf( () => {
        this.store.execute( 'listenIfNeeded' )
      })
    }
  }

  componentWillReceiveProps(props) {
    if (props.shouldUpdateProps) this.store.silently( 'update-props', props )
  }

  componentDidUpdate() {
    let {shouldUpdateProps, shouldSearchTarget} = this.props
    if (shouldUpdateProps || !shouldSearchTarget) { this.store.execute( 'listenIfNeeded' ) }
  }

  componentWillUnmount() {
    //
    if (this.onResize) this.store.execute( 'disconnect', this.onResize )

    this.store.silently( 'teardown' )

  }

  render() {
    let {
      target,
      tag,
      resizeTarget,
      children,
      shouldUpdateProps,
      shouldSearchTarget,
      ...componentProps
    } = this.props

    if (target === 'auto') {
      if (!componentProps.id) componentProps.id = this.id
      return React.createElement(tag, componentProps, this.renderChildren(children))
    }
    return children
  }

  renderChildren(children) {
    return children
  }
}

export default installStore( ResizeContext, 'resize', (component) => {
  return new ResizeStore(component)
})


