import React from 'react'
import ClassName from '@the-grid/util-classname'
import designable from '@the-grid/ui-designable'

class ActionGroup extends React.Component {
  static displayName = 'ActionGroup'

  render() {
    var {id, className, style, children} = this.props
    this.className = ClassName.add(className, 'ActionGroup')
    this.id = id || this._id
    return (
      <div key="ActionGroup" id={this.id} className={this.className} style={style}>
        {this.renderChildren()}
      </div>
    )
  }

  renderChildren() {
    return React.Children.toArray(this.props.children).slice()
  }

}

export default designable(ActionGroup,
  {
    sharedCSS: `
      .ActionGroup {
        width: 100%;
              display: -webkit-flex;
        display: flex;
              -webkit-flex-direction: row;
        flex-direction: row;
              -webkit-align-items: flex-start;
        align-items: flex-start;
              -webkit-justify-content: center;
        justify-content: center;
      }
    `,
    style: `
      :scope {
        .cta {
          d -- flex
          cursor -- pointer
          pad-t--.75em
          pad-b--1em
          pad-l--1.25em
          pad-r--1.25em
          mar -- 0 1em
          box-szg -- content-box

          .icon, .label {
            mar-l -- .25em
            mar-r -- .25em
          }
        }
        .cta {
          txt-sh--n
          trans-- color .3s, background-color .3s

          & {
            bg-clr -- chroma-box-pop

            > * {
              clr -- chroma-box-pop-text
            }
          }

          &:nth-child(even) {
            bg-clr -- chroma-box-pop2

            > * {
              clr -- chroma-box-pop2-text
            }
          }

          &:hover {
            bg-clr -- chroma-text-pop

            > * {
              clr -- chroma-box
            }

          }

          &:focus {
            otln -- n
          }

          &.as-detail {
            box-sh -- none !important
            bg-clr -- transparent !important
            > * {
              clr -- chroma-text-pop2
            }

          }
        }
      }
    `
  }
)


