import {Struct} from '@the-grid/immutable'

/*
TODO

.bp-lte-media-m .d--n-at-bp-lte-media-m { display: none; }
.bp-lte-el-l .d--n-at-bp-lte-el-l { display: none; }
.bp-lte-el-m .d--n-at-bp-lte-el-m { display: none; }
.bp-lte-el-s .d--n-at-bp-lte-el-s { display: none; }
.bp-lte-el-xs .d--n-at-bp-lte-el-xs { display: none; }

*/

let LayoutStruct = Struct({

  // inputs
  typography: undefined,
  breakpoint: undefined,
  goldDivsMin: 3,
  goldDivsMax: 21,

  viewport: undefined,
  node: undefined,

  // computed

  'header-h': {
    deps: ['goldY'],
    get(goldY) {
      let h = goldY(-1)
      return Math.max(h, 540)
    },
  },

  vw: {
    params: ['n', 'divs'],
    deps: ['viewport.width'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  vh: {
    params: ['n', 'divs'],
    deps: ['viewport.height'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  nw: {
    params: ['n', 'divs'],
    deps: ['node.width'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  nh: {
    params: ['n', 'divs'],
    deps: ['node.height'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  npw: {
    params: ['n', 'divs'],
    deps: ['node.parent.width'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  nph: {
    params: ['n', 'divs'],
    deps: ['node.parent.height'],
    get(n, divs, size) {
      if (!divs) divs = 100
      // if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  goldX: {
    params: ['n','divs'],
    deps: ['goldDivsAuto', 'breakpoint.width'],
    get(n, divs, divsAuto, size) {
      if (!divs) divs = divsAuto
      if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  goldY: {
    params: ['n','divs'],
    deps: ['goldDivsAuto', 'breakpoint.height'],
    get(n, divs, divsAuto, size) {
      if (!divs) divs = divsAuto
      if (n < 0) return size * (n + divs) / divs
      return size * n / divs
    },
  },

  goldDivsAuto: {
    deps: ['breakpoint.size','goldDivsMin','goldDivsMax'],
    get(bp, min, max) {
      let auto = 9

      if (bp === 'media-xl') {
        auto = 6
      }
      else if (bp === 'media-l') {
        auto = 8
      }
      else if (bp === 'media-m') {
        auto = 9
      }
      else if (bp === 'media-s') {
        auto = 12
      }
      else if (bp === 'media-xs') {
        auto = 15
      }

      else if (bp === 'el-xl') {
        auto = 9
      }
      else if (bp === 'el-l') {
        auto = 9
      }
      else if (bp === 'el-m') {
        auto = 12
      }
      else if (bp === 'el-s') {
        auto = 15
      }
      else if (bp === 'el-xs') {
        auto = 18
      }

      return Math.max(Math.min(max, auto), min)
    },
  },

})

export default LayoutStruct



