import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
export default (
  <Style id={'fan-static-style'} key="fan-static-style">
    {`
      // TODO
      #fan {
        t -- 0
        fnt-sz -- txt
        .icon {
          fnt-sz -- txt-l
        }

        .fan_content {
          flx-jst -- btwn
        }

        .fan_action-list {
          min-w --  0
          flx-jst -- btwn
          mar-l -- 1.5em
          mar-r -- 1.5em
        }
        &.align--l {
          .fan_action-list:first-child {
            mar-r -- auto
          }
        }
        &.align--r {
          .fan_action-list:first-child {
            mar-l -- auto
          }
        }
        &.align--c {
          .fan_action-list:first-child {
            mar-l -- auto
            mar-r -- auto
          }
        }

        .fan_action-item {
          &:after {
            b -- 0
          }
        }
      }
      #fan {
        o -- 1

        d -- block !important
        pos -- fixed
        &.size--s {
          h -- 3em-txt-s
        }
        &.size--m {
          h -- 4em-txt-s
        }
        &.size--l {
          h -- 5em-txt-s
        }


        l -- 0
        r -- 0
        bf-viz -- hidden
        tf -- translate3d(0,0,0)
        z-idx -- 2000
        box-szg -- border-box

        .fan_bg {
          pos -- a
          t -- 0
          b -- 0
          l -- 0
          r -- 0
        }

        .fan_content {
          d -- flex
          flx-dir -- row
          flx-jst -- mid
          flx-aln -- mid
          pos -- a
          t -- 0
          b -- 0
          l -- 0
          r -- 0
        }

        .fan_action-list {
          d -- flex
          flx-dir -- row
          flx-aln -- mid
          h -- 100%
        }

        .fan_action-item {
          pos -- relative
          d -- flex
          flx-dir -- row
          flx-aln -- mid
          flx-jst -- mid
          flx-grow -- 1
          pad-l -- 1em-txt
          pad-r -- 1em-txt
          h -- 100%
          min-w -- 6em-txt

          &.is-inset {
            h -- 60%
            bdr-rad -- 2
            box-sh -- m
          }

          .fan_action-item_src {
            bdr-rad -- 50%
            w -- 32
            h -- 32
          }

          &:after {
            d -- block
            content -- ''
            pos -- a
            l -- 0
            r -- 0
            h -- 4
          }
        }


        // TYPOGRAPHY ****************************

        fnt -- d3
        fnt-sz -- txt-s

      }
    `}
  </Style>
)


