import RenderListStruct from './RenderListStruct'
import {Map, List} from '@the-grid/immutable'

let parseCache = {}
let compileCache = {}
let EMPTY_ARRAY = []

// window.linecount = 0
const RULESET_CLOSE_ITEM = {
  type: 'RULESET_CLOSE',
}

class Compiler {
  constructor(plugins) {
    this.renderList = undefined
    this.compile = this.compile.bind(this)

    // build parsers from plugins...

    let {
      namesWithParser,
      parsers,
    } = plugins

    if (!namesWithParser || !parsers) throw new Error('Compiler requires plugins with parsers')

    let keyValParsers = []
    let keyParsers = []
    let valParsers = []
    let renderParsers = []
    let selectorParsers = []
    let rulesetFilters = []

    namesWithParser.forEach((name) => {
      let {selector, keyVal, key, val, ruleset} = parsers[name]
      // inverse order of parsers! later plugins higher parse priority
      if (keyVal) keyValParsers.unshift(keyVal)
      if (key) keyParsers.unshift(key)
      if (val) valParsers.unshift(val)
      if (selector) selectorParsers.unshift(selector)
      if (ruleset) rulesetFilters.unshift(ruleset)
    })

    let parser = (_parsers, requiredMessage = false, ...args) => {
      let result
      for (let i = 0, len = _parsers.length; i < len; i++) {
        let _parser = _parsers[i]
        result = _parser(...args)
        if (result !== undefined) break
      }
      if (result === undefined && requiredMessage) throw new Error(requiredMessage + ' ' + JSON.stringify(args))
      return result
    }

    let _parseKeyVal = parser.bind(this, keyValParsers, false)
    let _parseKey = parser.bind(this, keyParsers, 'Styleverse could not parse key!')
    let _parseVal = parser.bind(this, valParsers, false)
    let _parseSelector = parser.bind(this, selectorParsers, false)

    this.parseSelector = (selector) => {
      let path = 'SEL ' + selector
      let cached = parseCache[path]
      if (!parseCache.hasOwnProperty(path)) {
        cached = _parseSelector(selector)
        parseCache[path] = cached
      }
      return cached
    }
    this.parseKeyVal = (keyVal) => {
      let path = 'KV ' + keyVal
      let cached = parseCache[path]
      if (!parseCache.hasOwnProperty(path)) {
        cached = _parseKeyVal(keyVal)
        parseCache[path] = cached
      }
      return cached
    }
    this.parseKey = (key, val) => {
      let path = 'K ' + key + ':' + val
      let cached = parseCache[path]
      if (!parseCache.hasOwnProperty(path)) {
        cached = _parseKey(key, val)
        parseCache[path] = cached
      }
      return cached
    }
    this.parseVal = (key, val) => {
      let path = 'V ' + key + ':' + val
      let cached = parseCache[path]
      if (!parseCache.hasOwnProperty(path)) {
        cached = _parseVal(key, val)
        parseCache[path] = cached
      }
      return cached
    }

    // ruleset filtering & rendering

    this.filterRuleset = (selector, rulesObj, prerender, struct) => {
      for (let i = 0; i < rulesetFilters.length; i++) {
        let rulesetFilter = rulesetFilters[i]
        rulesObj = rulesetFilter(selector, rulesObj, prerender, struct)
        if (!rulesObj) break
      }
      return rulesObj
    }

    this.renderRuleset = (selector, rulesObj, prerender, struct) => {
      rulesObj = this.filterRuleset(selector, rulesObj, prerender, struct)
      // console.log("struct.get('renderToString')",struct.get('renderToString'))
      if (struct.get('renderToString') === false) return rulesObj
      if (!rulesObj) return ''
      let rulesetString = '\n'
      rulesetString += selector
      rulesetString += ' {'
      const keyKeys = Object.keys(rulesObj)
      for (let i = 0, len = keyKeys.length; i < len; i++) {
        const key = keyKeys[i]
        const val = rulesObj[key]

        rulesetString += '\n  '
        rulesetString += key
        rulesetString += ':'
        rulesetString += ' '
        rulesetString += val
        rulesetString += ';'
      }
      rulesetString += '\n}'
      return rulesetString
    }

    return this
  }

  compile(str) {
    let ostr = str
    if (compileCache[str]) return compileCache[str]
    // console.time("compile")

    this.expressions = {}
    this.deps = []
    this.list = []
    this.renderList = undefined

    // window.linecount += str.split('\n').length
    // /
    // if (
    //  typeof str !== 'string' ||
    //
    // ) {
    //
    // }
    // TODO
    // let stringNeedsParsing = true
    // if (!stringNeedsParsing) return str

    // remove comments `//` and `/* ... */`
    str = str.replace(/(\/\*([^*]|(\*+[^*\/]))*\*+\/)|(\/\/.*)/g, '')

    // remove extra padding whitespace
    str = str.trim()

    // remove all but one line break
    str = str.replace(/\n{2,}/g, '\n')
    // str = str.replace(/(\r\n?|\n){2,}/g, '$1');

    const lines = str.split('\n')
    for (let i = 0, len = lines.length; i < len; i++) {
      let line = lines[i]
      this.compileLine(line)
    }

    let compiled = new RenderListStruct({
      deps: this.deps.sort(function(a,b) {return a > b}).join(','),
      expressions: Map(this.expressions),
      list: List(this.list),
      renderRuleset: this.renderRuleset,
    })

    compileCache[ostr] = compiled
    // console.timeEnd("compile")
    return compiled
  }

  compileLine(line) {
    // string = string.trim()
    if (!line) return

    // } RULESET_CLOSE
    if (line.includes('}')) {
      // if strictMode ???
      // if (line.trim().length !== 1) throw new Error('Styleverse Compiler: rulesets close `}` must be on new line : ' + line)
      this.addRenderItem(RULESET_CLOSE_ITEM)
      return
    }

    // remove all but 1 inner whitespace
    line = line.replace(/\s{2,}/g,' ')

    // selector {
    if (line.includes('{')) {
      let selector = line.split('{')[0].trim()

      // TODO parseSelector

      let selectorRenderItem = this.parseSelector(selector)
      if (selectorRenderItem) {
        this.addRenderItem(selectorRenderItem)
        return
      }

      this.addRenderItem({
        selector,
      })

      return
    }

    line = line.trim()
    if (line.length <= 2) return
    // if (string === '') throw new Error('Styleverse Compiler: Empty Line!')
    // parse key-val pairs to RulesetStruct schema

    this.compileRule(line)
    // return renderList
  }


  compileRule(keyVal) {
    // clean key val
    let [key,val] = keyVal.split('--')

    if (!val) {
      val = ''
      throw new Error('Styleverse Ruleset expects {key} -- {val}, no -- found... keyVals:' + keyVal)
      // TODO: throw?
    }

    key = key.trim()
    val = val.trim()
    keyVal = key + '--' + val

    let keyValObj = this.parseKeyVal(keyVal)
    if (keyValObj) {
      this.addRenderItem(keyValObj)
      return
    }

    key = this.parseKey(key, val)

    // TODO: remove? this if statement likely uneeded
    if (typeof key === 'object') {
      this.addRenderItem(key)
      // Object.assign( obj, key )
      return
    }


    // let listSize = this.list.length
    // let expressionsSize = Object.keys(this.expressions).length

    let compiledVal = this.compileVal(val, key)

    if (!compiledVal.__ASAP) {
      this.addRenderItem({
        style: {
          [key]: compiledVal,
        },
      })
    }
    else {
      this.addRenderItem({
        deps: EMPTY_ARRAY,
        get() {
          let renderList = this
          return {
            style: {
              [key]: compiledVal(renderList),
            },
          }
        },
      })
    }

    // // val has only compilte-time resolved expressions
    // if ( listSize === this.list.length && expressionsSize === Object.keys(this.expressions).length ) {
    //
    //  this.addRenderItem({
    //    style: {
    //      [key]: compiledValOrDepKey,
    //    }
    //  })
    // }
    //
    // // val has render-time resolved expressions
    // else {
    //  this.addRenderItem({
    //    deps: [compiledValOrDepKey],
    //    get: function (computedVal) {
    //      let renderList = this
    //      return {
    //        style: {
    //          [key]: (typeof computedVal === 'function') ? computedVal(renderList) : computedVal
    //        }
    //      }
    //    }
    //  })
    // }
  }

  compileVal(val, key) {
    let parseVal = this.parseVal
    let parsedVals = []
    let deps = []
    let currentStringChain = ''

    let valParts = val.split(' ')
    let valPartCount = valParts.length
    for (let i = 0, len = valParts.length; i < len; i++) {
      const valPart = valParts[i]
      let parsedResult = parseVal(key, valPart)
      if (parsedResult === undefined) parsedResult = valPart

      // if (typeof parsedResult === 'string') {
      //  return this.addExpression( valPart, parsedResult )
      //  // TODO?
      //  // return parsedResult
      // }

      const typeofResult = typeof parsedResult

      // compile-time val
      if (typeofResult === 'string' || typeofResult === 'number') {
        if (currentStringChain) currentStringChain += ' '
        currentStringChain += parsedResult

        // finish last currentString
        if (i === valPartCount - 1) parsedVals.push(currentStringChain)
      }

      // render-time val
      else {
        // finish currentString
        if (currentStringChain) parsedVals.push(currentStringChain)
        currentStringChain = ''

        // if (!hasRenderTimeExpression) staticParsedResults.push( parsedResult )
        const expKey = this.addExpression(valPart, parsedResult)
        deps.push(expKey)
        parsedVals.push(expKey)
      }
    }


    // TODO?
    // if (!hasRenderTimeExpression) {
    //  return deps.join(' ')
    // }

    if (parsedVals.length === 0) {
      return currentStringChain
    }

    // if val doesn't have any spaces, return the only dep
    if (parsedVals.length === 1) {
      return parsedVals[0]
    }

    let callback = function(renderList) {
      return parsedVals.map((parsedVal) => {
        let depIndex = deps.indexOf(parsedVal)
        if (depIndex >= 0) {
          return deps[depIndex].call(renderList, renderList)
        } else {
          return parsedVal
        }
      }).join(' ')
    }
    callback.__ASAP = true

    return callback

    // else {
    //  let parsedResult = parseVal( key, val )
    //  if (!parsedResult) parsedResult = valPart
    //
    //  if (typeof parsedResult === 'string') {
    //    return parsedResult
    //  }
    //  else {
    //    this.addExpression( val, parsedResult )
    //    return val
    //  }
    // }
  }

  // render({styleStore, props}) {
  //  if (!styleStore || !props) throw new Error('Compiler render({styleStore, props}) params required')
  //
  // }

  addRenderItem(renderItem) {
    let {deps, get, ...staticRenderItem} = renderItem

    // add dynamic item
    if (deps && get) {
      // add to this.deps
      for (let i = 0, len = deps.length; i < len; i++) {
        if (!this.deps.includes(deps[i])) this.deps.push(deps[i])
      }

      // wrap getter w/ deps as params
      // TODO: cache this? .... hmmmm.....
      let getWrap = function(renderList) {
        let params = deps.map(function(path) {
          // key:EEK
          let firstDot = path.indexOf('.')
          let type = path.substr(0,firstDot)
          let subPath = path.substr(type.length + 1, path.length)
          let v = renderList.get(type).get(subPath)

          // make sure expressions getters are called
          // TODO....
          if (
            typeof v === 'function' &&
            path.indexOf('expression.') === 0
          ) {
            return v()
          }
          return v
        })

        // add renderList to params
        // params.push(renderList)

        let computedItem = get.apply(renderList, params)
        return computedItem

        // return get.apply(renderList, params)
        // return new RenderItemStruct(get.apply(renderList, params))
      }
      getWrap.__ASAP = true

      // add to this.list
      this.list.push(getWrap)
    }

    // add static item

    let {
      type,
      selector,
      style,
      html,
      component,
      css,
    } = staticRenderItem

    if (style || html || component || css || type || selector) {
      // add to this.list
      this.list.push(staticRenderItem)
      // newList = newList.push( new RenderItemStruct(staticRenderItem) )
    }
  }

  addExpression(string, expressionItem) {
    let expressionKey

    // if expressionItem is simple string, use that part of key!
    if (typeof expressionItem === 'string') {
      expressionKey = expressionItem
    } else {
      expressionKey = string
    }

    if (expressionItem === undefined) throw new Error('addExpression requires expressionItem: ' + JSON.stringify({expressionKey, expressionItem}))

    // compile-time expression
    // let typeofExpItem = typeof expressionItem
    // if (typeofExpItem === 'string' || typeofExpItem === 'number') {
    //  throw new Error("!!!!!!!!!")
    //
    //  // add to this.expressions if not already there...
    //  let existing = this.expressions[expressionKey]
    //  if (existing) {
    //    if (existing !== expressionItem) throw new Error("Styleverse compiler expressions dont match?? WHAT???")
    //    return this
    //  }
    //
    //  this.expressions[expressionKey] = expressionItem
    //
    // }

    // render-time expression

    const {deps, get} = expressionItem

    // add to this.deps
    for (let i = 0, len = deps.length; i < len; i++) {
      if (!this.deps.includes(deps[i])) this.deps.push(deps[i])
    }

    // wrap getter w/ deps as params
    const getWrap = function(renderList) {
      let params = deps.map(function(path) {
        // key:EEK
        let firstDot = path.indexOf('.')
        let type = path.substr(0,firstDot)
        let subPath = path.substr(type.length + 1, path.length)
        let v = renderList.get(type).get(subPath)

        // let v = renderList.get(path)
        // make sure expressions getters are called
        if (
          typeof v === 'function' &&
          path.indexOf('expression.') === 0
        ) {
          return v()
        }
        return v
      })
      // add renderList to params
      // params.push(renderList)
      return get.apply(renderList, params)
    }
    getWrap.__ASAP = true

    // add to this.expressions if not already there...
    // if (newExpressions.get(expressionKey)) return this

    this.expressions[expressionKey] = getWrap

    return getWrap

    // return expression deps key!!!
    // TODO!!!!!  can be better
    // return "expressions." + expressionKey
  }
}

export default Compiler



