import {Map, Struct, List, fromJS} from '@the-grid/immutable'
import {CoverStruct, MetadataStruct, CtaStruct} from './'
import linkify from './helpers/linkify'

const titleBlockType = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6']
const mediaBlockType = ['video', 'audio']
const frameType = mediaBlockType.concat(['interactive'])
const textuals = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'text', 'quote', 'list', 'code']

let BlockStruct = Struct({

  construct(block) {
    return Object.assign({
      _title: block.title,
      _caption: block.caption,
      _text: block.text,
      _html: block.html,
      _cover: block.cover,
    }, block)
  },

  id: undefined,
  item: undefined,
  type: undefined,
  src: undefined,
  url: undefined,
  created_at: undefined,
  updated_at: undefined,
  ctaType: undefined,
  label: undefined,
  _title: undefined,
  _caption: undefined,
  _text: undefined,
  _html: undefined,
  _cover: undefined,

  metadata: {
    init(metadata) {
      metadata = new MetadataStruct(metadata)
      return {metadata}
    },
  },


  // TODO: metadata?
  cover: {
    deps: ['_cover','metadata'],
    get(cover, metadata) {
      if (cover) {
        return new CoverStruct(Object.assign({}, cover, {metadata}))
      }
      return false
    },
  },

  canBeContextCover: {
    deps: ['title','description','text','cover'],
    get: (title, description, text, cover) => {
      // only if simple cover
      // can have attribution or caption
      if (cover && !title && !description && !text) return true
      return false
    },
  },


  html: {
    deps: ['_html', 'metadata.publisherName'],
    get(html, publisherName) {
      if (html) {
        html = linkify(html, publisherName)
        return html
      }
      return undefined
    },
  },

  starred: {
    deps: ['metadata'],
    get(metadata) {
      if (metadata) return metadata.get('starred', false)
      return false
    },
  },

  title: {
    deps: ['_title', 'type', '_text', 'metadata'],
    get(_title, type, text, metadata) {
      let title = undefined
      if (_title && _title.length > 0) {
        title = _title
      }
      else if (titleBlockType.indexOf(type) > -1) {
        if (text && text.length > 0) {
          title = text
        }
      }
      else if (metadata) {
        let metatitle = metadata.get('title')
        if (metatitle && metatitle.length > 0) {
          title = metatitle
        }
      }
      return title
    },
  },

  description: {
    deps: ['metadata'],
    get(metadata) {
      if (metadata) return metadata.get('description')
      return false
    },
  },

  caption: {
    deps: ['_caption', 'metadata'],
    get(_caption, metadata) {
      if (_caption && _caption.length > 0) {
        return _caption
      }
      return metadata ? metadata.get('caption') : false
    },
  },

  text: {
    deps: ['_text', 'type', 'metadata.publisherName', 'isTemplate'],
    get(text, type, publisherName, isTemplate) {
      if (!text || text.length === 0) {
        return null
      }
      if (type === 'cta' || isTemplate) return text
      return linkify(text, publisherName)
    },
  },

  isTemplate: {
    deps: ['_text'],
    get(text) {
      if (!text) return false
      return (text.startsWith('{{') && text.endsWith('}}'))
    },
  },

  ctaStructs: {
    deps: ['type', 'url', 'ctaType', 'text', 'metadata.openAsModal'],
    get(type, url, ctaType, text, openAsModal) {
      if (type === 'cta') {
        return new CtaStruct({
          type: ctaType,
          href: url,
          label: text,
          openAsModal,
        })
      }

      if (mediaBlockType.includes(type)) {
        return new CtaStruct({
          type: 'iframe',
          icon: 'play',
        })
      }
    },
  },

  frame: {
    deps: [
      'type',
      'metadata.isBasedOnUrl',
      'html', // video & audio types set this
    ],
    get(type, href, html) {
      if (frameType.includes(type) && (html || href)) {
        return Map({href, html})
      }
      return false
    },
  },

  textStructs: {
    deps: ['title', 'text', 'description', 'type', 'html', 'id', 'item', 'isTemplate'],
    get(title, text, description, type, html, id, item, isTemplate) {
      if (type === 'cta' || isTemplate) return []
      if (textuals.includes(type)) return [this]

      let texts = []

      if (title) {
        texts.push(new BlockStruct({
          id: id + '-title',
          item,
          title,
          type: 'h2',
        }))
      }

      if (text && text !== title) {
        texts.push(new BlockStruct({
          id: id + '-text',
          text,
          item,
          type: 'text',
        }))
      }

      if (description && description !== text) {
        texts.push(new BlockStruct({
          id: id + '-description',
          text: description,
          item,
          type: 'text',
        }))
      }

      return texts
    },
  },

  templateStruct: {
    deps: ['_text', 'isTemplate'],
    get(text, isTemplate) {
      if (text && isTemplate && text.indexOf('$class') === -1) {
        return this
      }
      return false
    },
  },

  textLongestWordLength: {
    deps: ['_text'],
    get(text) {
      if (text) {
        let words = text.split(' ')
        let length = 0
        for (let word of words) {
          if (word.length > length) length = word.length
        }
        return length
      }
      return 0
    },
  },

  getHtmlTextuals: {
    params: ['nbTextuals'],
    deps: ['html'],
    get(nbTextuals, html) {
      let textuals = []
      let textsRgx = html.match(/<p>(.*?)<\/p>/g)
      if (textsRgx) {
        let texts = textsRgx.map(function(tag) {
          return tag
        })
        if (texts) textuals = textuals.concat(texts)
      }

      if (textuals.length < nbTextuals) {
        let quotesRgx = html.match(/<quote>(.*?)<\/quote>/g)
        if (quotesRgx) {
          let quotes = quotesRgx.map(function(tag) {
            return tag
          })
          if (quotes) textuals = textuals.concat(quotes)
        }
      }

      if (textuals.length < nbTextuals) {
        let listsRgx = html.match(/<ul>(.*?)<\/ul>/g)
        if (listsRgx) {
          let lists = listsRgx.map(function(tag) {
            return tag
          })
          if (lists) textuals = textuals.concat(lists)
        }
      }

      if (textuals.length > 0) {
        return List(textuals.slice(0, nbTextuals))
      }

      return undefined
    },
  },

  canFilter: {
    deps: ['metadata.coverPrefs'],
    get(coverPrefs) {
      if (coverPrefs) return coverPrefs.get('filter', true)
      return true
    },
  },

  canCrop: {
    deps: ['metadata.coverPrefs'],
    get(coverPrefs) {
      if (coverPrefs) return coverPrefs.get('crop', true)
      return true
    },
  },

  canOverlay: {
    deps: ['metadata.coverPrefs'],
    get(coverPrefs) {
      if (coverPrefs) return coverPrefs.get('overlay', true)
      return true
    },
  },

  navigation: {
    init(navigation) {
      if (navigation && navigation.length > 0) {
        // transform .name to .title
        for (let i = 0; i < navigation.length; i++) {
          let navItem = navigation[i]
          if (navItem.name) {
            navItem.title = navItem.name
            delete navItem.name
          }
          navigation[i] = navItem
        }
      }

      navigation = fromJS(navigation)
      return {navigation}
    },
  },

  footer: {
    init(footer) {
      footer = fromJS(footer)
      return {footer}
    },
  },

  link: {
    init(link) {
      link = fromJS(link)
      return {link}
    },
  },

})

export default BlockStruct



