import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import designable from '@the-grid/ui-designable'


class SectionBase extends React.Component {
  static displayName = 'SectionBase'
  render() {
    let {id, className, ...props} = this.props
    this.id = id
    this.className = className
    return React.createElement('section', {...props, id, className}, this.renderChildren())
  }
  renderChildren() {
    return this.props.children
  }
}

