import React from 'react'
import {installStore} from '@the-grid/cymatics'
import PluginStore from './Store'

class LayoutContext extends React.Component {

  static displayName = 'Styleverse_LayoutContext'

  static propTypes = {
    children: React.PropTypes.any.isRequired,
  }

  componentWillMount() {
    this.store.execute( 'update-props', this.props )
  }

  componentWillReceiveProps(nextProps) {
    this.store.execute( 'update-props', nextProps )
  }

  componentWillUnmount() {
    this.store.silently( 'teardown' )
  }

  render() {
    return this.props.children
  }
}

LayoutContext = installStore(LayoutContext, 'styleverse-layout', (componentInstance) => {
  return new PluginStore(componentInstance)
})

export default LayoutContext


