import {Map, Struct, List, fromJS} from '@the-grid/immutable'

let siteMediaCount = 0

function getRandomInt() {
  let max = 1000000
  return Math.floor(Math.random() * max)
}

let itemBuilder = {
  buildNavigation(navigation) {
    let navTransformed = navigation
    if (navTransformed && typeof navTransformed.toJS() === 'function') {
      navTransformed = navTransformed.toJS()
      if (navTransformed && navTransformed.length > 0) {
        // transform .name to .title
        for (let i = 0; i < navigation.length; i++) {
          let navItem = navigation[i]
          if (navItem.name) {
            navItem.title = navItem.name
            delete navItem.name
          }
          navigation[i] = navItem
        }
      }
    }

    return {
      id: 'site-navigation-item',
      metadata: {
        starred: true,
        '@type': 'navigation',
      },
      content: [
        {
          id: 'site-navigation-block',
          type: 'navigation',
          html: '',
          metadata: {
            starred: true,
          },
          navigation,
        },
      ],
    }
  },

  buildFooter(rssLink) {
    return {
      id: 'site-footer-item',
      metadata: {
        starred: true,
        '@type': 'footer',
      },
      content: [
        {
          id: 'site-footer-block',
          type: 'footer',
          html: '',
          metadata: {
            starred: true,
          },
          footer: {
            rss: rssLink,
          },
        },
      ],
    }
  },

  buildNextPage(nextPageLink) {
    return {
      id: `next-page-item-${getRandomInt()}`,
      metadata: {
        starred: true,
        '@type': 'nextpage',
      },
      content: [
        {
          id: 'next-page-block',
          type: 'nextpage',
          html: '',
          metadata: {
            starred: true,
          },
          link: nextPageLink,
        },
      ],
    }
  },
  // .media, .type {phone|email|internal-link|external-link}?
  // .data.type {video|follow|contact|purchase}?
  // @TODO: remove .id?
  buildCtaItem(siteConfig, siteHeader = false) {
    let title = siteConfig.get('title')
    let description = siteConfig.get('description')
    let ctas = siteConfig.get('purpose')
    let item

    let itemType
    if (siteHeader) {
      itemType = 'header'
    } else {
      itemType = 'cta'
    }

    if (title || description || ctas) {
      item = {
        id: `header-item-${getRandomInt()}`,
        metadata: {
          starred: true,
          '@type': itemType,
        },
        content: [],
      }

      if (!title && !description && ctas) title = siteConfig.get('name')

      if (title) {
        item.content.push({
          id: `cta-title-${getRandomInt()}`,
          type: 'h1',
          html: `<h1>${title}</h1>`,
          metadata: {
            starred: true,
          },
          text: title,
        })
      }

      if (description) {
        item.content.push({
          id: `cta-subtitle-${getRandomInt()}`,
          type: 'h2',
          html: `<h2>${description}</h2>`,
          metadata: {
            starred: true,
          },
          text: description,
        })
      }

      if (ctas) {
        for (let i = 0; i < ctas.count(); i++) {
          let cta = ctas.get(i)
          let ctaType = cta.get('type')
          let ctaUrl = cta.get('url') || cta.get('value')

          if (ctaType && ctaUrl) {
            let label = cta.get('label')
            let html

            if (ctaType === 'external') {
              if (!(ctaUrl.indexOf('http://') === 0 || ctaUrl.indexOf('https://') === 0)) {
                ctaUrl = 'http://' + ctaUrl
              }
              html = `<a href="${ctaUrl}">${label}</a>`
            } else if (ctaType === 'internal') {
              if (ctaUrl.indexOf('/index.html') === -1) {
                ctaUrl += '/index.html'
              }
              html = `<a href="/${ctaUrl}">${label}</a>`
            } else if (ctaType === 'contact') {
              html = `<a href="${ctaUrl}">${label}</a>`
            } else {
              html = ctaUrl
            }

            item.content.push({
              id: `cta-block-${getRandomInt()}`,
              type: 'cta',
              metadata: {
                starred: true,
                '@type': 'cta',
              },
              html,
              text: label,
              label,
              ctaType,
              url: ctaUrl,
            })
          }
        }
      }

      let siteMedias = siteConfig.get('site_media')
      if (siteMedias && siteMedias.count() > 0) {
        if (siteMediaCount >= siteMedias.count()) siteMediaCount = 0
        item.content.push(siteMedias.get(siteMediaCount).toJS())
        siteMediaCount++
      }
    }

    return item
  },
}

export default itemBuilder



