import React from 'react'
import ribbonStyle from './style'

var mountCount = 0

class Ribbon extends React.Component {
  static displayName = 'Ribbon'
  static contextTypes = {
    request: React.PropTypes.func.isRequired,
  }
  static propTypes = {
    children: React.PropTypes.any,
    //shouldStyle: React.PropTypes.bool,
  }

  componentWillMount() {
    if (mountCount === 0) {
      this.context.request('styleverse','add-css','component--Ribbon', ribbonStyle.shared)
    }
    mountCount++
    this._id = 'ribbon-' + mountCount
    this.id = this.props.id || this._id
    this.scopedStyle = ribbonStyle.renderScoped('#' + this.id)
  }
  componentWillUnmount() {
    mountCount--
    // if (mountCount === 0) {
    //
    // }
  }

  shouldComponentUpdate() {
    return false
  }

  render() {
    const {children} = this.props
    // for ds-app compat until styles fixed
    // var styles = null
    //if (shouldStyle) styles = style
    return (
      <div id={this.id} className='Ribbon ribbon'>
        {this.scopedStyle}
        <div className="Ribbon-text">{children}</div>
      </div>
    )
  }
}

export default Ribbon



