import {Struct, List, fromJS} from '@the-grid/immutable'

let types = ['context', 'subject']
const iframeType = ['audio', 'video', 'interactive', 'location']
const textuals = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'text', 'quote', 'list', 'code']


let ContentUnitStruct = Struct({


  // input
  // ============================

  blocks: undefined,
  type: undefined, // context | subject
  itemMeta: undefined, // only if type context
  computeText: {
    default: true,
  },
  computeOthers: {
    default: true,
  },

  // computed
  // ============================
  className: {
    cache: 1,
    deps: ['type', 'has-cover', 'has-cta', 'has-template', 'has-text', 'has-title', 'has-frame', 'has-meta'],
    get(type, has_cover, has_cta, has_template, has_text, has_title, has_frame, has_meta) {
      let className = `content-unit ${type}-unit`
      if (has_cover) className += ' has-cover'
      if (has_cta) className += ' has-cta'
      if (has_template) className += ' has-template'
      if (has_text) className += ' has-text'
      if (has_title) className += ' has-title'
      if (has_frame) className += ' has-frame'
      if (has_meta) className += ' has-meta'
      return className
    },
  },

  'has-cover': {
    deps: ['cover'],
    get(cover) { return !!cover },
  },
  'has-cta': {
    deps: ['cta'],
    get(cta) { return !!cta },
  },
  'has-template': {
    deps: ['template'],
    get(template) { return !!template },
  },
  'has-text': {
    deps: ['text'],
    get(text) { return !!text },
  },
  'has-frame': {
    deps: ['frame'],
    get(frame) { return !!frame },
  },
  'has-meta': {
    deps: ['meta'],
    get(meta) { return !!meta },
  },
  'has-title': {
    deps: ['blocks', 'computeText'],
    get(blocks, computeText) {
      if (computeText) {
        for (let i = 0; i < blocks.size; i++) {
          let block = blocks.get(i)
          if (block.get('title')) return true
        }
      }
      return false
    },
  },

  cover: {
    deps: ['blocks', 'computeOthers'],
    get(blocks, computeOthers) {
      if (computeOthers) {
        for (let i = 0; i < blocks.size; i++) {
          let block = blocks.get(i)
          let cover = block.get('cover')
          if (cover) return cover
        }
      }
      return false
    },
  },

  text: {
    deps: ['blocks', 'computeText'],
    get(blocks, computeText) {
      if (computeText) {
        let texts = []
        for (let i = 0; i < blocks.size; i++) {
          let block = blocks.get(i)
          texts = texts.concat(block.get('textStructs'))
        }
        if (texts.length) return List(texts)
      }
      return false
    },
  },

  template: {
    deps: ['blocks'],
    get(blocks) {
      let templates = []
      for (let i = 0; i < blocks.size; i++) {
        let block = blocks.get(i)
        templates = templates.concat(block.get('templateStruct'))
      }
      if (templates.length) return List(templates)
      return false
    },
  },

  cta: {
    deps: ['blocks', 'computeText'],
    get(blocks, computeText) {
      if (computeText) {
        let ctas = []
        for (let i = 0; i < blocks.size; i++) {
          let block = blocks.get(i)
          let cta = block.get('ctaStructs')
          if (cta) ctas.push(cta)
        }
        if (ctas.length) return List(ctas)
      }
      return false
    },
  },

  frame: {
    deps: ['blocks', 'computeOthers'],
    get(blocks, computeOthers) {
      if (computeOthers) {
        let frame
        for (let i = 0; i < blocks.size; i++) {
          let block = blocks.get(i)
          frame = block.get('frame')
          if (frame) return frame
        }
      }
      return false
    },
  },

  meta: {
    deps: ['itemMeta', 'blocks'],
    get: (itemMeta, blocks) => {
      if (itemMeta) return itemMeta
      for (let i = 0; i < blocks.size; i++) {
        let meta = blocks.get(i).get('metadata')
        if (meta) return meta
      }
      return false
    },
  },

  split: {
    deps: ['blocks', 'type', 'itemMeta', 'text', 'cover', 'cta', 'frame'],
    get(blocks, type, itemMeta, text, cover, cta, frame) {
      if (text && (cover || cta || frame)) {
        return List([
          new ContentUnitStruct({type: 'context', blocks, computeOthers: false, itemMeta}),
          new ContentUnitStruct({type: 'subject', blocks, computeText: false}),
        ])
      }
      return false
    },
  },
})

export default ContentUnitStruct



