import React, {PropTypes} from 'react'
import raf from 'raf'
import Component from '@the-grid/ui-component-deprecated' // TODO import {Component} from '@the-grid/ui'

// import {requestPerfFrame} from 'perf' // TODO!

import {installStore} from '@the-grid/cymatics'
import StyleStore from './StyleStore'


let _id_count = 0

class Style extends Component {
  static displayName = 'Style'

  static contextTypes = {
    styleverse: PropTypes.object.isRequired,
  }

  static propTypes = {
    children: PropTypes.string.isRequired,
    sheet: PropTypes.string,
    perf: PropTypes.bool,
    perfFrame: PropTypes.bool,
    parseObject: PropTypes.bool,
  }

  static defaultProps = {
    // TODO: set true by default?
    perf: true,
    perfFrame: false,
  }

  _id = 'style-' + _id_count++

  perfCheckFunctions = false

  componentWillMount() {
    this.isDead = false
    let id = this.props.id || this._id
    this.store.willMount(id, this.props, this.context)
    // if (!this.props.perfFrame) this.store.willMount(id, this.props, this.context)
    // raf(()=>{
    //    if (this.isDead) return
    //    this.isAwake = true
    //    this.store.willMount(id, this.props, this.context)
    // })
  }
  componentWillReceiveProps(nextProps) {
    // if (!this.isAwake) return
    this.store.willGetProps(nextProps)
  }
  componentWillUnmount() {
    this.isDead = true
    this.isAwake = false
    this.store.willUnmount()
  }
  render() {
    var {children, sheet} = this.props
    if (sheet && children && typeof children !== 'string') return children
    return null
  }
}

Style = installStore(Style, 'style', (componentInstance) => {
  return StyleStore.getInstance(componentInstance)
})

export default Style



