import React, {PropTypes} from 'react'
import {SprungSheet, Springs} from '@the-grid/ui-sprung'
import {Style, StyleContext} from '@the-grid/styleverse'
import {calculator} from '../../helpers/calculator'
import {gooeyStyleGenerator, gooeyStyleGenerator2} from '../gooey'

/* eslint 'camelcase': 0 */

// @TODO: move calculations out of this
class FABSprings extends Springs {
  stateWillUpdate = (state) => {
    let ready = (state.gotFAB && state.gotViewport && state.gotScroll)
    if (!ready) return false

    var {
      prevActionsCount,
      actionsCount,
      appButton,
      fab_size,
      fab_x,
      fab_y,
      isInTopY,
      isInRightX,
    } = calculator.call(this, state)

    var that = this
    prevActionsCount = prevActionsCount || 0
    actionsCount = actionsCount || 0
    // if (actionsCount !== prevActionsCount) {
    if (actionsCount) {
      let action_y_setter = function(val, config) {
        if (!config) return val
        let {i, isAppButton} = config.meta
        if (!that.state.active && !isAppButton) return val
        val = val + (fab_size * -1.5)
        return val
      }
      let prevId = 'fab'
      for (let i = 0; i < actionsCount; i++) {
        let isAppButton = (i === actionsCount - 1) && !!appButton
        let id
        if (isAppButton) id = 'app-button'
        else id = 'actions_' + i

        this.set(id + '_x', prevId + '_x', {start: fab_x})
        this.set(id + '_y', prevId + '_y', {set: action_y_setter, meta: {i, isAppButton}, start: fab_y})
        prevId = id
      }
    }
    if (actionsCount < prevActionsCount) {
      for (let i = prevActionsCount - 1; i >= actionsCount; i--) {
        if ((i === 0) && !!appButton) continue
        let id = 'actions_' + i
        this.delete(id + '_x')
        this.delete(id + '_y')
      }
    }
    this.setState('prevActionsCount',actionsCount)

    return true
  }
}

