import React from 'react'
import Component from '@the-grid/ui-component-deprecated'
import ResizeRenderer from '@the-grid/ui-resize-renderer'
import {computeLayout, renderStyle} from './style'
import {List} from '@the-grid/immutable'
import Icons from './icons'
import IFrame from './iframe'
import Selectable from './selectable'

let {PropTypes} = React
let types = ['iphone','ipad','macbook','full']

class Device extends Component {

  static displayName = "Device"
  static types = types
  static typesList = List(types)
  static propTypes = {
    type: PropTypes.string.isRequired,
  }
  static defaultProps = {
    type: 'macbook',
  }

  // Attach sub components
  static Icons = Icons
  static IFrame = IFrame
  static Selectable = Selectable

  constructor(...args) {
    super(...args)
    this.state = {
      lastTypeResolved: undefined,
    }
    this.isResolved = true
  }

  render() {
    return (
      <ResizeRenderer {...this.elProps()}>{ (size) => {
        return this.renderDevice(size)
      }}</ResizeRenderer>
    )
  }

  renderDevice(size) {
    let width = size.get('width')
    let height = size.get('height')
    let props = this.props
    let {children, type} = props
    let renderedChildren = null

    let layout = computeLayout(type, width, height)

    let getId = this.getId
    if (typeof children === 'function') {
      renderedChildren = children( layout )
    } else if (children) {
      renderedChildren = children
    }

    if (type !== this.lastType && this.lastType) {
      if (type !== this.state.lastTypeResolved) {
        //renderedChildren = null
        this.isResolved = false
        let that = this
        setTimeout( () => {
          if (type !== that.state.lastTypeResolved) {
            that.isResolved = true
            that.setState({ lastTypeResolved: type })
          }
        }, 333)
      }
    }

    this.lastType = type

    return (
      <div id={getId('pca-hold')} className='pca-hold'>
        { renderStyle(this, layout) }
        <div id={getId('pca-main')} className='pca-main'>
          <div id={getId('pca-inner')} className='pca-inner' style={{display: (this.isResolved) ? 'block' : 'none'}}>
            {renderedChildren}
          </div>
        </div>
        <div id={getId('pca-sub')} className='pca-sub'>
          <div id={getId('pca-top')} className='pca-top'></div>
          <div id={getId('pca-mid')} className='pca-mid'>
            <div id={getId('pca-part')} className='pca-part'></div>
          </div>
          <div id={getId('pca-bot')} className='pca-bot'></div>
        </div>
      </div>
    )
  }
}
export default Device



