import React from 'react'
import {Style} from '@the-grid/styleverse'

export default (scope) => (
  <Style key="action-button-pillable-style" scope={scope}>{`
    :scope {
      trans -- width .4s cubic-bezier(0.215, 0.61, 0.355, 1), background-color .4s
      trans-delay -- .1s
      ovf--hidden
    }
    :scope:hover {
      // TODO!!!!
      w--10em
    }

    :scope .action-icon {
      tf--translate3d(0px,0px,0px)
      trans-- opacity .33s, transform .33s cubic-bezier(0.215, 0.61, 0.355, 1)
      trans-delay -- .1s
    }
    :scope:hover .action-icon {
      tf--translate3d(6em,0px,0px)
      o--0
    }

    :scope .action-label {
      pos--a
      t--0
      r--0
      l--0
      b--0

      tf--translate3d(-6em,0px,0px)

      o--0
      trans-- opacity 1s, transform .25s cubic-bezier(0.215, 0.61, 0.355, 1)
      trans-delay -- .1s
    }
    :scope:hover .action-label {
      o--1
      tf--translate3d(0px,0px,0px)
    }

`}</Style>)



