import React from 'react'
import {Style} from '@the-grid/styleverse'

export default (scope) => (<Style key="default-style" scope={scope}>{`
  :scope {
    d--flx
    flx-aln--mid
    flx-jst--mid
    cur -- pointer
    .icon {
      fnt-sz -- 24
    }
    .icon-sz--l {
      fnt-sz -- d-s
    }

    .
  }

  :scope .action-label {
    txt-aln--center
    d--flex
    flx-jst--mid
    flx-aln--mid
    whs--nowrap

    pad-l -- 1.5em
    pad-r -- 1.5em
  }

  :scope:not(.pillable).with-icon.with-label {
    &.layout--h-label-icon {
      .action-icon {
        order -- 1
      }
      .action-label {
        pad-r -- 0
      }
    }
    &.layout--h-icon-label {
      .action-icon {
        order -- 0
      }
      .action-label {
        pad-l -- 0
      }
    }
  }
`}</Style>)



