import React from 'react'
import {installStore} from '@the-grid/cymatics'
import ResizeContext from '@the-grid/ui-resize-context'
import PluginStore from './Store'
// TODO import {ResizeContext} from '@the-grid/ui'

let el = React.createElement

class BreakpointContextInstaller extends React.Component {
  static displayName = 'Styleverse_BreakpointContextInstaller'

  static propTypes = {
    constrain: React.PropTypes.func,
    target: React.PropTypes.string,
    resizeTarget: React.PropTypes.string,
    width: React.PropTypes.number,
    height: React.PropTypes.number,
  }

  static defaultProps = {
    target: '',
    resizeTarget: 'window',
  }

  render() {
    let {target, width, height} = this.props
    let render = el(BreakpointContext, this.props)
    if (!target) {
      if (!width || !height) throw new Error('Styleverse Breakpoint without {target} requires {width,height}')
      return render
    }
    if (target === 'window' || target === 'viewport') return render
    return el(ResizeContext, this.props,
      render
    )
  }
}

class BreakpointContext extends React.Component {

  static displayName = 'Styleverse_BreakpointContext'

  updateProps(p) {
    let {
      constrain,
      target,
      resizeTarget,
      width,
      height,
    } = this.props
    this.store.execute( 'update-props', {
      constrain,
      target,
      resizeTarget,
      width,
      height,
    } )
  }

  updateProps = (p) => {
    let props = {}
    if (p.constrain) props.constrain = p.constrain
    if (p.target) props.target = p.target
    if (p.resizeTarget) props.resizeTarget = p.resizeTarget
    if (p.width !== undefined) props.width = p.width
    if (p.height !== undefined) props.height = p.height
    this.store.execute( 'update-props',props)
  }

  componentWillMount() {
    this.updateProps(this.props)
  }

  componentWillReceiveProps(nextProps) {
    this.updateProps(nextProps)
  }

  componentWillUnmount() {
    this.store.silently( 'teardown' )
  }

  render() {
    return this.props.children
  }
}

BreakpointContext = installStore(BreakpointContext, 'styleverse-breakpoint', (componentInstance) => {
  return new PluginStore(componentInstance)
})

export default BreakpointContextInstaller



