import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
import {raf} from '@the-grid/deps'
function NOOP() {}
let count = 0
let SCALE = .8
let styles = (
  <Style perf={true} key="global-styles" id="global-styles">
  {`

    body, html {
      bg-clr -- chroma-bg
      #section-layout {

        cursor -- text !important

        * {
          pointer-events -- none !important
        }

        .media, img, video, svg {
          d -- n !important
        }
        section {
          min-h -- 0 !important
        }
        section.no-typography {
          d -- n !important
        }
        *:not(button) {

          bg-clr -- white !important
          box-sh -- none !important
          text-sh -- none !important
        }
        *:before, *:after {
          d -- n !important
        }
        h1 {
          clr -- chroma-title !important
        }
        h2, h3, h4, h5 {
          clr -- chroma-title2 !important
        }
        p {
          clr -- chroma-text !important
        }
        .meta {
          clr -- chroma-meta !important
        }
        a {
          clr -- chroma-text-pop !important
        }
        section {
          mar-t -- 2em !important
          mar-b -- 2em !important
        }
        button:nth-child(odd) {
          &, * {
            bg-clr -- chroma-box-pop !important
          }
          clr -- chroma-box-pop-text !important
        }
        button:nth-child(even) {
          &, * {
            bg-clr -- chroma-box-pop2 !important
          }
          clr -- chroma-box-pop2-text !important
        }
      }

    }

    //.fan_content {
    //  pad-l -- 10vw
    //}


  `}
  </Style>
)
let readyStyles = (
  <Style perf={true} key="readyStyles" id="readyStyles">
  {`

    .section-layout {
      will-change -- transform, opacity
      tf-origin -- 50% 0%
      tf -- scale(${SCALE})
      transition -- transform .3s
      box-sh -- l

      //section:hover {
      //  .next-section-layout-button {
      //    border-rad -- 50%
      //    bg -- chroma-box-pop
      //    color -- chroma-inverse-box
      //    d -- flex
      //    flx-aln -- mid
      //    flx-jst -- mid
      //    w -- 100
      //    h -- 100
      //    r -- 0
      //    t -- 50%
      //  }
      //}

      // &:hover {
      //   .component--layout-node:not(:hover) section {
      //     o -- .25
      //   }
      // }
      .component--layout-node {
        :first-child {
          tf-origin -- 50% 0%
        }
        > div > section {
          transition -- transform .3s, box-shadow .3s, opacity .3s
        }
        &:hover {
          z-index -- 1001 !important
          > div > section {
           tf -- scale(1.03)
           box-sh -- l
          }
        }

      }
    }

  `}
  </Style>
)

class ContentResizer extends React.Component {
  static displayName = 'ContentResizer'
  static contextTypes = {
    request: React.PropTypes.func.isRequired,
  }
  static defaultProps = {
    children: null,
  }
  state = {
    ready: false,
  }

  componentDidMount() {
    this.targetEl = document.getElementsByClassName('section-layout')[0]
    setTimeout(this.onReady, 300)
  }
  onReady = () => {
    raf(() => {
      this.targetEl.addEventListener('mouseleave', this.onTargetMouseLeave)
      this.targetEl.addEventListener('mouseenter', this.onTargetMouseEnter)
      this.setState({ready: true}, NOOP)
    })
  }
  onTargetMouseEnter = () => {
    this.context.request('FAB', 'deactivate')
    //this.context.request('FAF', 'hide')
    // this.context.request('FAB', 'hide')
  }
  onTargetMouseLeave = () => {
    this.context.request('FAB', 'activate')
    //this.context.request('FAF', 'show')
  }
  componentWillUnmount() {
    this.targetEl.removeEventListener('mouseenter', this.onTargetMouseEnter)
    this.targetEl.removeEventListener('mouseleave', this.onTargetMouseLeave)
    raf(()=>{
      window.layout.execute('zoom',1)
    })
  }

  render() {
    if (this.state.ready) {
      setTimeout(()=>{
      raf(()=>{
        if (this.undone) return
        window.layout.execute('zoom',SCALE)
      })
      }, 300)
    }
    return (
      <StyleContext chroma={{type: 'light'}}>
        <div id="design-resizer" key="design-resizer">
          {styles}
          {this.state.ready ? readyStyles : null}
          {this.props.children}
        </div>
      </StyleContext>
    )
  }
}

export default ContentResizer



