import React from 'react'
import {raf, debounce} from '@the-grid/deps'
import {Struct, Map} from '@the-grid/immutable'
import {Store} from '@the-grid/cymatics'
import {RowVirtualizer} from '@the-grid/ui-async-row-virtualizer'
import AsyncLayoutNode from './async-layout-node'
import encoder_indexDouble from '@the-grid/_/encoder_index-double'

let el = React.createElement


class AsyncLayoutStore extends Store {

  deferCommit = false // 'raf'
  contentMeasurer = null
  ignoredIndices = []
  unignoredIndices = []
  wasVisibleIndices = []
  invalidIndices = []
  measurerByIndex = []
  renderHash = -1.111111111111111111
  renderCache = null

  shouldDisplayIndexForMeasurement(i, measuredHeights) {
    if (this.invalidIndices.includes(i)) return true
    return this.unignoredIndices[i] && !measuredHeights.get(i)
  }
  unignoreIndex(i){
    this.wasVisibleIndices[i] = true
    this.unignoredIndices[i] = true
    this.ignoredIndices[i] = false
  }
  ignoreIndex(i){
    this.unignoredIndices[i] = false
    this.ignoredIndices[i] = true
  }

  shouldCommit(state) {
    if (!state.get('hasFrame')) return false
    if (!state.get('hasScroll'))return false
    let renderHash = this.rangeToRender()
    if (renderHash === this.renderHash) return false
    this.renderHash = renderHash
    this._render()
    return true
  }

  rangeToRender() {
    if (!this.get('hasContent')) return -1
    let frameTop = this.get('frameTop') /// frameScale
    let frameHeight = this.get('frameHeight')
    let contentTop = this.get('contentTop') /// frameScale
    let frameBottom = frameTop + frameHeight
    let renderHash = this.virtualizer.rangeToRender(frameTop, frameBottom, contentTop)
    return renderHash
  }

  _render() {
    this.renderCache = this.state.render( this.renderHash )
    this.cleanInvalidsIfNeeded()
  }

  cleanInvalidsIfNeeded = () => {
    if (!this.invalidIndices.length) return
    raf.cancel(this._cleanInvalids)
    raf(this._cleanInvalids)
  }

  _cleanInvalids = () => {
    if (!this.invalidIndices.length) return
    let index = this.invalidIndices.pop()
    let h = this.getIn(['measuredHeights', index])
    if (!h) {
      // TODO ERRRRRRRRR
      //console.log("FORCE MEASURE", index)
      this.measurerByIndex[index].request('measure')
    }
    this.cleanInvalidsIfNeeded()
  }

  render() {
    return this.renderCache
  }

  init() {

    window.layout = this

    let that = this

    // UGH, need better separation of state & stores... ECS!!!
    var AsyncLayoutState = Struct({
      //
      isReady: {
        cache: false,
        deps:['hasFrame','hasScroll','hasContent'],
        get(a,b,c) { return a && b && c; }
      },

      //
      count: undefined,
      _defaultRowHeight: undefined,
      defaultRowHeight: {
        cache: false,
        deps: ['_defaultRowHeight', 'frameHeight'],
        get(_defaultRowHeight, frameHeight) {
          if (_defaultRowHeight) return _defaultRowHeight
          // TODO FIX BUG! HAD TO ADD .5 so:  h !== defaultRowHeight
          return frameHeight + .5
        },
        set(_defaultRowHeight) {
          return {_defaultRowHeight}
        }
      },

      //
      hasFrame: false,
      frameScale: 1,
      frameTop: undefined,
      frameWidth: undefined,
      frameHeight: undefined,

      //
      hasScroll: false,
      didScroll: false,
      isScrolling: false,

      //
      measuredHeights: {default: Map.EMPTY},

      //
      hasContent: false,
      contentTop: undefined,
      contentWidth: undefined,

      //
      rangeKeyGroup: {
        cache: false,
        deps: ['didScroll'],
        get(didScroll) {
          if (!didScroll) return 'd' // visible = min, render least
          return 'r' // retained = max, render most
        }
      },

      // render inputs
      tag: 'div',
      id: undefined,
      className: undefined,
      renderer: undefined,

      //
      render: {
        instanceCache: true,
        cache: 1,
        params: ['rangeHash'],
        deps: ['tag','id','className','renderRowAtIndex','rangeKeyGroup', 'frameScale','measuredHeights'],
        get: function(
          rangeHash,
          tag, id, className, renderRowAtIndex, rangeKeyGroup, frameScale, measuredHeights
        ) {
          let props = {
            tag, id, key: id,
            className,
            index: 'content',
            style: {height: that.virtualizer.get('totalHeight') * frameScale}
          }

          if (rangeHash === -1) return el(AsyncLayoutNode, props)
          let children = []
          let rangeGet = that.virtualizer.rangeGet
          let i = rangeGet(rangeHash, rangeKeyGroup+'0')
          let end = rangeGet(rangeHash, rangeKeyGroup+'1')
          if (rangeKeyGroup === 'd') {
            if (end >= i + 1) end = i + 1
          }


          let css = '', asyncCSS
          let t, child, childId, asyncState
          let
            v0 = rangeGet(rangeHash, 'v0'),
            v1 = rangeGet(rangeHash, 'v1'),
            d0 = rangeGet(rangeHash, 'd0'),
            d1 = rangeGet(rangeHash, 'd1'),
            p0 = rangeGet(rangeHash, 'p0'),
            p1 = rangeGet(rangeHash, 'p1')

          while (i <= end) {
            child = renderRowAtIndex(i)
            children.push(child)
            childId = child.props.id
            if (!childId) throw new Error("AsyncLayout renderer requires all items have props.id")
            t = that.virtualizer.topAtRowIndex(i)

            if (rangeKeyGroup === 'v') {
              that.unignoreIndex(i)
              asyncCSS = `#${childId}{ display: -webkit-flex !important;  display: flex !important; }`
            }
            else {
              if (v0 <= i && i <= v1) {
                that.unignoreIndex(i)
                asyncCSS = `#${childId}{ display: -webkit-flex !important;  display: flex !important; }` // 'v'
              }
              else if (d0 <= i && i <= d1) {
                that.unignoreIndex(i)
                asyncCSS = `
                  #${childId}{ display: -webkit-flex !important;  display: flex !important; }
                `
               //   #${childId} .media { filter: none !important; -webkit-filter: none !important; }
               //   #${childId} .media .media_src {display: none !important;}
               //   #${childId} * {box-shadow: none !important; border-radius: none !important;}
               //s `
              }

              // keep index displayed until measured
              else if ((that.shouldDisplayIndexForMeasurement(i, measuredHeights))) {
                if (!that.invalidIndices.includes(i)) that.invalidIndices.push(i)
                that.ignoreIndex(i)
                asyncCSS = `
                  #${childId}{ display: -webkit-flex !important;  display: flex !important; visibility: hidden;}
                `//
              }


              else if (p0 <= i && i <= p1) {
                that.ignoreIndex(i)
                asyncCSS = `
                  #${childId}{ display: none; }
                `
              }
              else {
                that.ignoreIndex(i)
                asyncCSS = `
                  #${childId}{ display: none; }
                `
              }
            }

            css += `
              ${asyncCSS}
              #${childId}{
                -webkit-transform: translate3d(0px, ${t}px, 0px);
                transform: translate3d(0px, ${t}px, 0px);
                -webkit-backface-visibility: hidden;
                -moz-backface-visibility: hidden;
                backface-visibility: hidden;
                will-change: transform;
              }
            `
            i++
          }
          children.push(<style id="layout-node-transforms" key="layout-node-transforms">{css}</style>)
          return el(AsyncLayoutNode, props, children)
        }
      },
      renderRowAtIndex: {
        cache: true, // TODO: improve for reordering & stuff
        params: ['index'],
        deps: ['renderer'],
        get: function(index, renderer) {
          let rendered = renderer(index, AsyncLayoutNode)
          return rendered
        }
      },

    })
    this.initialState = new AsyncLayoutState()
    this.state = this.initialState

    // this.scrollThrottle = raf.throttle((store)=>{
    //   this.execute('update-scroll',store)
    // }, {leading: true})

    this.scrollThrottle = (store) => {
      this.execute('update-scroll',store)
    }

    this.viewportHandle = debounce((store)=>{
      this.execute('update-frame',store)
    }, 16)
  }

  contextStores = {

    // TODO: frame context?
    'viewport': this.viewportHandle,

    'scroll': this.scrollThrottle,

    //'rect': (rectStore) => {
    //  if (!this.measureRect) this.measureRect = rectStore.measureRect
    //  this.contentElement = rectStore.targetElement
    //  this.execute('update-rect', rectStore.state)
    //  // TODO?
    //  //if (!this.hasHandledScroll) this.execute('update-scroll', false)
    //},
  }


  virtualize_config(state, v_config) {

    // TODO: HACKY!!!!!!!!!!!!!!!!!!!
    if (!v_config.defaultRowHeight) {
      v_config.defaultRowHeight = state.get('defaultRowHeight')
    }

    this.virtualizer.configure(v_config)
    return state
  }

  virtualize_frameHeight(state, frameHeight) {

  }

  virtualize_contentWidth(state, width) {
    // TODO: defer running configure?
    let curr = this.virtualizer.get('width')
    if (width !== curr) {
      this.virtualizer.configure({width})
      if (typeof curr === 'number') {
        state = state.set('measuredHeights', Map.EMPTY)
      }
    }
    return state
  }

  virtualize_heightAtIndex(state, index, height) {
    // TODO: defer running configure?
    let curr = this.virtualizer.measuredHeightAtIndex(index)
    if (height !== curr) {
      this.virtualizer.registerHeights({[index]: height})
    }
    // TODO...
    curr = state.getIn(['measuredHeights', index])
    if (height !== curr) {
      state = state.setIn(['measuredHeights', index], height)
    }
    return state
  }

  measure = () => {
    this.execute('measure')
    // which in turn fires 'handle-node-size'
  }

  actions = {

    'update-config': (state, v_config) => {
      state = state.merge(v_config)
      this.virtualize_config(state, v_config)
      return state
    },

    'update-frame': (state, store) => {
      let scale = state.get('frameScale')
      state = state.merge({
        hasFrame: true,
        frameTop: store.get('y') / scale,
        frameWidth: store.get('width'),
        frameHeight: store.get('height') / scale,
      })

      // TODO: HACKY!!!!!!!!!!!!!!!!!!!
      if (this.virtualizer && !this.get('_defaultRowHeight')) {
        state = this.virtualize_config(state, {})
      }

      return state
    },

    'zoom': (state, scale) => {
      let currScale = state.get('frameScale')
      if (scale === currScale) return state
      //scale = scale / currScale
      // WARNING ASYNC FUCK YOUR HEAD SHIT
      raf(this.measure,1)
      let measuredHeights = state.get('measuredHeights')
      let that = this
      measuredHeights.forEach(function(v,k) {
        state = that.actions['handle-node-size'](state, k, Map({height: v * currScale}))
      })
      return state.merge({
        'frameScale': scale,
        'frameTop': state.get('frameTop') / scale,
        'frameHeight': state.get('frameHeight') / scale,
      })
    },
    'zoom-out': (state) => {
      let currScale = state.get('frameScale')
      if (scale === 1) return state
      return this.actions['zoom'](state, 1/ currScale)
    },

    'update-scroll': (state, store) => {
      let isScrolling = store.get('isScrolling')
      let obj = {isScrolling}

      if (!state.get('hasScroll')) obj.hasScroll = true
      if (isScrolling && !state.get('didScroll')) obj.didScroll = true

      state = this.actions['measure'](state, obj)
      //state = state.set('content-top', store.get)

      // WARNING ASYNC FUCK YOUR HEAD SHIT
      //setTimeout(this.measure,1)

      return state
    },

    'measure': (state, obj = null) => {
      if (this.contentMeasurer) {
        let scale = state.get('frameScale')
        let rect = this.contentMeasurer.measureRect()
        if (!obj) obj = {}
        obj.contentTop = rect.top / scale
      }
      if (obj) state = state.merge(obj)
      return state
    },

    'handle-node-size': (state, index, measurer) => {
      let scale = state.get('frameScale')
      if (index === 'content') {
        let obj = null
        if (!this.contentMeasurer) {
          if (!obj) obj = {}
          obj.hasContent = true
          this.contentMeasurer = measurer
        }
        let contentWidth = measurer.get('width') / scale
        if (contentWidth !== state.get('contentWidth')) {
          if (!obj) obj = {}
          obj.contentWidth = contentWidth
        }
        let contentTop = measurer.get('top') / scale
        if (contentTop !== state.get('contentTop')) {
          if (!obj) obj = {}
          obj.contentTop = contentTop
        }
        if (!obj) return state
        state = state.merge(obj)
        state = this.virtualize_contentWidth( state, contentWidth )
      }
      else {
        this.measurerByIndex[index] = measurer
        //if (this.ignoredIndices[index]) return state
        let h = measurer.get('height') / scale
        //if (!this.validateIndexHeight(index, h)) {
        //  return state
        //}
        if (h === 0) {
          //if (!this.wasVisibleIndices[index]) throw new Error("h === 0???? index:" + index)
          return state
        }
        let j = this.invalidIndices.indexOf(index)
        if (j > -1) this.invalidIndices.splice(j, 1)
        state = this.virtualize_heightAtIndex(state, index, h)
        //state = state.setIn(['measuredHeights', index], measurer.get('height'))
      }
      return state
    },

  }



  willMount({props}) {
    let {
      id,
      className,
      tag,
      renderer,
      //type, // TODO
      //virtual, // TODO
      //draggableOrder, // TODO

      //width,

      count,
      defaultRowHeight,
      //overscan,
      trailingPreloadScreenfuls,
      trailingDisplayScreenfuls,
      leadingDisplayScreenfuls,
      leadingPreloadScreenfuls,
      retainRendered,
    } = props

    if (!id) throw new Error("AsyncLayout requires props.id, " + props)

    //this.renderer = new this.RendererStruct({
    //  id,
    //  className,
    //  renderer,
    //})

    let v_config = {

      // TODO: does not belong here
      id,
      className,
      renderer,

      trailingPreloadScreenfuls,
      trailingDisplayScreenfuls,
      leadingDisplayScreenfuls,
      leadingPreloadScreenfuls,
      retainRendered,
    }
    if (count !== undefined) v_config.count = count
    if (defaultRowHeight !== undefined) v_config.defaultRowHeight = defaultRowHeight

    this.virtualizer = new RowVirtualizer()

    this.execute('update-config', v_config)

  }

  willGetProps() {

  }

  willUnmount() {
    // TODO
    // - virtualizer?
    this.AsyncLayoutState.cacheReset()
  }

  didMount() {

  }



}

export default AsyncLayoutStore


