import { React, Component } from '../../../deps'
import Isvg from '@the-grid/ui-inline-svg'
import {Style} from '@the-grid/styleverse'

let displayName = 'SiteLogo'

class SiteLogo extends Component {

  constructor(...args) {
    super(...args)
    this.state = {
      src: undefined,
      isSVG: undefined,
    }
  }

  componentWillMount() {
    this.context.request('site','connect',this.handleSiteStore)
  }
  componentWillUnmount() {
    this.context.request('site','disconnect',this.handleSiteStore)
  }

  handleSiteStore = (store) =>  {
    let src = store.get('data.config.logo')
    if (!src) return
    src = src.trim()
    let isSVG = (src.substr(-3) === 'svg')
    this.setState({
      src,
      isSVG
    })
  }

  render() {
    let {src, isSVG} = this.state
    if (!src) return null

    let ref = ''
    if (typeof window !== 'undefined' && window.theGridSiteType === 'permalink') {
      ref = '../'
    }

    let children = <img src={src} />

    if (isSVG) {
      //children = null
      children = (
       <Isvg key="isvg" src={src} cacheGetRequests={true}>
         {children}
       </Isvg>
      )
    }

    // TODO: handle SVG!
    let p = this.elProps()
    let {id} = p
    //if (!className) className = ''
    return (
      <a {...p} href={`${ref}index.html`}>
        <Style key='style' scope={"#" + id}>{`
          :scope svg * {
            //fill -- chroma-text-pop !important
            stroke -- chroma-text-pop !important
          }
          svg {
            max-width -- 100% !important
          }
        `}</Style>
        {children}
      </a>
    )
  }
}

SiteLogo.contextTypes = {
  request: React.PropTypes.func.isRequired,
}

SiteLogo.displayName = displayName

export default SiteLogo



