import React from 'react'
import {Style, StyleContext} from '@the-grid/styleverse'
export default (
  <Style id="faf-chroma-style" key="faf-chroma-style"  perfFrame={true}>
  {`
    #faf {

      bg-clr -- chroma-box
      box-sh -- 0 20px 0 chroma-bg
      trans -- background-color .5s

      ::selection {
        background -- none !important
      }

      .faf_action-item {
        clr -- chroma-inverse-bg
        .label {
          o -- .6
        }
        &:hover {
          clr -- chroma-text
          &:after {
            bg -- chroma-meta
          }
          .label {
            o -- 1
          }
        }
        &.active {
          clr -- chroma-text-pop
          &:after {
            bg -- chroma-box-pop
          }
          .label {
            o -- 1
          }
        }
      }

    }
  `}
  </Style>
)


