import polator from './polator'
// polator(input, input1, input2, output1, output2, pennerType='linear', boundType='extend')

import Color from 'color'

/*

// For backwards API compat with Color v1.0.3

Color.prototype.hexString = function() {
  return this.hex()
}

Color.prototype.hslString = function() {
  return this.hsl().string()
}

Color.prototype.rgbString = function() {
  return this.rgb().string()
}

Color.prototype.rgbaString = function() {
  return this.rgba().string()
}

Color.prototype.clone = function() {
  return Color(this.hsl().object())
}

Color.prototype.rgbArray = function() {
  return this.rgb().array()
}

Color.prototype.rgbaArray = function() {
  // see https://github.com/Qix-/color/issues/104
  return this.rgb().array()
}

*/

Color.prototype.whiteOrBlack = function() {
  if (this.light()) return Color('black')
  return Color('white')
}

Color.prototype.isReddish = function() {
  let {h} = this.hsl()
  if (9 >= h || Math.abs(360 - h) <= 40) return true
  return false
}

Color.prototype.whiteOrBlack = function() {
  if (this.light()) return Color('black')
  return Color('white')
}

Color.prototype.isReddish = function() {
  let {h} = this.hsl()
  if (9 >= h || Math.abs(360 - h) <= 40) return true
  return false
}

Color.prototype.isYellowish = function() {
  let {h} = this.hsl()
  if (9 <= h && h <= 65) return true
  return false
}

Color.prototype.chromaDistance = function() {
  let {h,s,l} = this.hsl()
  let {b,w} = this.hwb()
  let sChromaDistance = Math.abs(100 - s)
  let lChromaDistance = Math.abs(50 - l)

  let vibranceScaler
  // dampen warm hues b/c look less vibrant
  if (this.isYellowish()) {
    vibranceScaler = 1.1
  // boost green-turquoise hues b/c look more vibrant
  }
  else if (
    100 <= h &&
    h <= 199 &&
    sChromaDistance < 50 &&
     lChromaDistance < 25
  ) {
    // boost when saturated known
    if (sChromaDistance < 10 && lChromaDistance < 10) {
      vibranceScaler = .5
    } else {
      vibranceScaler = .9
    }
  } else {
    vibranceScaler = 1
  }
  // dampen light and dark colors
  if (l <= 20) {
    vibranceScaler *= polator(l, 0, 20, 2, 1, 'linear')
  } else if (l >= 85) {
    vibranceScaler *= polator(l, 100, 85, 2, 1, 'linear')
  }
  // dampen desaturated colors
  if (s <= 15) {
    vibranceScaler *= polator(s, 0, 15, 2, 1, 'linear')
  }
    // vibranceScaler *= 1.5
  let chromaDistance = b + w // Math.sqrt(Math.pow(b,2) + Math.pow(w,2))
  // color.chromaDistance = Math.max(color.chromaDistance,.01)
  chromaDistance *= vibranceScaler // if color.chromaDistance <= vibranceThreshold
  return chromaDistance
}

Color.prototype.vibranceThreshold = 50

Color.prototype.isVibrant = function() {
  // reddish colors are dominant
  // if color.isReddish()
  //  if color.chromaDistance() <= 69
  //    return true
  if (this.chromaDistance() <= this.vibranceThreshold) return true
  return false
}

Color.prototype.isLight = function() {
  return this.light()
}

Color.prototype.isDark = function() {
  return this.dark()
}

Color.prototype.isBrand = function() {
  let color = this
  let {h,s,l} = this.hsl()
  if (l <= 10 || l >= 95) {
    return false
  } else if (s <= 3) {
    return false
  } else if (s <= 9 && (Math.abs(l - 50) > 15)) {
    return false
  } else if ((s <= 15) && (l <= 20 || l >= 87)) {
    return false
  }
  return true
}

Color.prototype.hueDistance = function(targetColor) {
  let {h} = this.hsl()
  let targetH = targetColor.hsl().h
  let distance = targetH - h
  let absDistance = Math.abs(distance)
  if (absDistance <= 180) return absDistance
  if (distance > 0) {
    h += 360
    return h - targetH
  } else {
    targetH += 360
    return targetH - h
  }
}

Color.prototype.oppositeLightness = function(targetColor) {
  if (targetColor.dark()) return this.light()
  return targetColor.light()
}

Color.prototype.oppositeChroma = function(targetColor) {
  if (!targetColor.isVibrant()) return this.isVibrant()
  return targetColor.isVibrant()
}

Color.prototype.darkMutedClone = function() {
  let {h,s,l} = this.hsl()
  l = Math.min(l, 12)
  s = Math.min(s, 30)
  return Color({h,s,l})
}

Color.prototype.lightMutedClone = function() {
  let {h,s,l} = this.hsl()
  l = Math.max(l, 95)
  s = Math.min(s, 40)
  return Color({h,s,l})
}

Color.prototype.lightVibrantClone = function() {
  if (this.isVibrant()) return this.clone()
  let {h,s,l} = this.hsl()
  s += 0
  l += 40
  return Color({h,s,l})
}

Color.prototype.darkVibrantClone = function() {
  if (this.isVibrant()) return this.clone()
  let {h,s,l} = this.hsl()
  s += 30
  l -= 20
  return Color({h,s,l})
}


export default Color



