import React from 'react'
import Device from '../Device'
import Selectable from '@the-grid/ui-selectable-deprecated'

let DeviceSelectable = function (props) {

  return (
    <Selectable
      multi={false}
      required={true}
      items={ Device.typesList }
    >
      {props.children}
    </Selectable>
  )

}

let {PropTypes} = React

DeviceSelectable.propTypes = {
  children: PropTypes.func.isRequired,
}

export default DeviceSelectable


