import {Struct, List, fromJS} from '@the-grid/immutable'
import {ItemStruct} from './'
import {SiteConfigStruct} from './'
import itemsBuilder from './items-builder'

let SiteDataStruct = Struct({

  construct(siteData) {
    return Object.assign({
      _items: siteData.items,
      _config: siteData.config,
    }, siteData)
  },

  id: undefined,
  number: undefined,
  ordered: undefined,
  site: undefined,
  paginate: undefined,
  title: undefined,
  navigable: undefined,
  type: undefined,
  siteUrl: undefined,
  siteId: undefined,
  user: undefined,
  dsClassifier: undefined,

  _config: undefined,

  config: {
    deps: ['_config', '_items'],
    get(config, items) {
      if (!config.site_media || config.site_media.length === 0) {
        let siteMediaCovers = []

        let addHeroCover = function(item) {
          let blocksWCover = item.get('allSBWithCovers')
          for (let i = 0; i < blocksWCover.count(); i++) {
            if (siteMediaCovers.length > 5) return
            let block = blocksWCover.get(i)
            let cover = block.get('cover')
            if (cover && cover.get('isHeroCover')) {
              siteMediaCovers.unshift(block)
            }
          }
        }

        for (let i = 1; i < items.count(); i++) {
          addHeroCover(items.get(i))
        }

        config.site_media = siteMediaCovers
      }
      return new SiteConfigStruct(config)
    },
  },

  navigation: {
    init(navigation) {
      if (navigation && navigation.length > 0) {
        // transform .name to .title
        for (let i = 0; i < navigation.length; i++) {
          let navItem = navigation[i]
          if (navItem.name) {
            navItem.title = navItem.name
            delete navItem.name
          }
          navigation[i] = navItem
        }

        navigation = fromJS(navigation)
        return {navigation}
      }
      return undefined
    },
  },

  links: {
    init(links) {
      if (links && links.length > 0) {
        links = fromJS(links)
        return {links}
      }
      return undefined
    },
  },

  _items: {
    init(_items) {
      let itemStructs = _items.map(function(item) {
        return new ItemStruct(item)
      })
      return {_items: List(itemStructs)}
    },
  },

  items: {
    deps: ['_items', 'navigation', 'links', 'config', 'type'],
    get(_items, navigation, links, config, type) {
      let __items = _items.toJS()
      if (type === 'index' || type === 'permalink') {
        let siteHeaderItem = itemsBuilder.buildCtaItem(config, true)
        if (siteHeaderItem) __items.unshift(siteHeaderItem)
      } else if (__items.length > 8 && config.get('purpose')) {
        let ctaItem = itemsBuilder.buildCtaItem(config)
        if (ctaItem) __items.unshift(ctaItem)
      }

      if (navigation) __items.unshift(itemsBuilder.buildNavigation(navigation))

      let rssLink
      if (links && links.count() > 0) {
        let nextPageLink
        for (let link of links) {
          if (link.get('rel') === 'next') {
            nextPageLink = link
          } else if (link.get('href') === 'rss.xml') {
            rssLink = link
          }
        }
        if (nextPageLink) __items.push(itemsBuilder.buildNextPage(nextPageLink))
      }

      if (rssLink) __items.push(itemsBuilder.buildFooter(rssLink))

      let items = __items.map(function(item) {
        return new ItemStruct(item)
      })

      return List(items)
    },
  },

  $class: {
    deps: ['number', 'config', 'dsClassifier'],
    get(nbItems, config, dsClassifier) {
      let classes = []

      if (nbItems === 1) {
        classes.push('site:w-single-item')
      } else {
        classes.push('site:w-multiple-item')
      }

      if (config) {
        let coverPrefs = config.get('coverPrefs')

        if (coverPrefs) {
          if (!coverPrefs.get('filter', true)) classes.push('site:dont-cover-filter')
          if (!coverPrefs.get('crop', true)) classes.push('site:dont-cover-crop')
          if (!coverPrefs.get('overlay', true)) classes.push('site:dont-cover-overlay')
        }


        if (config.get('title') || config.get('description')) {
          classes.push('site:w-header')
        }

        let siteHeaderContent = config.get('content')
        if (siteHeaderContent && siteHeaderContent.size) {
          classes.push('site:w-site-media')
          let containVideo = false
          siteHeaderContent.forEach((media) => {
            if (media.get('type') === 'video') containVideo = true
          })

          if (containVideo) classes.push('site:w-site-media--video')
        }
      }

      let classList = List(classes)
      if (dsClassifier && typeof dsClassifier === 'function') {
        let dsClasses = dsClassifier(this, classList)
        if (dsClasses && dsClasses.length > 0) {
          classList = classList.concat(dsClasses)
        }
      }

      return classList
    },
  },
})

export default SiteDataStruct



