import {Struct, List, Map, fromJS} from '@the-grid/immutable'
import linkify from './helpers/linkify'

let MetadataStruct = Struct({

  construct(metadata) {
    return Object.assign({
      authors: metadata.author,
      _description: metadata.description,
      _caption: metadata.caption,
    }, metadata)
  },

  '@type': undefined,
  isBasedOnUrl: undefined,
  permalinkUrl: undefined,
  url: undefined,
  inLanguage: undefined,
  starred: undefined,
  inFeed: undefined,
  hasPage: undefined,
  height: undefined,
  inNav: undefined,
  text: undefined,
  openAsModal: undefined,

  _description: undefined,
  _caption: undefined,

  title: {
    init(title) {
      if (title && title.length > 0) {
        return {title}
      }
      return undefined
    },
  },

  keywords: {
    init(keywords) {
      keywords = fromJS(keywords)
      return {keywords}
    },
  },

  via: {
    init(via) {
      via = fromJS(via)
      return {via}
    },
  },

  isReshare: {
    deps: ['author', 'publisher'],
    get(author, publisher) {
      if (author || publisher) return true
      return false
    },
  },

  description: {
    deps: ['_description', 'publisherName'],
    get(description, publisherName) {
      if (description && description.length > 0) {
        description = linkify(description, publisherName)
        return description
      }
      return undefined
    },
  },

  caption: {
    deps: ['_caption', 'publisherName'],
    get(caption, publisherName) {
      if (caption && caption.length > 0) {
        caption = linkify(caption, publisherName)
        return caption
      }
      return undefined
    },
  },

  author: {
    deps: ['authors'],
    get(authors) {
      if (authors) {
        let author = authors.get(0)
        let name = author.get('name')
        if (name && name.length > 0) {
          return author
        }
        return undefined
      }
    },
  },

  avatarSrc: {
    deps: ['author'],
    get(author) {
      if (author) {
        let avatar = author.get('avatar')
        if (avatar) {
          return avatar.get('src')
        }
      }
      return undefined
    },
  },

  authors: {
    init(authors) {
      if (authors && authors.length > 0) {
        authors = fromJS(authors)
        return {authors}
      }
      return undefined
    },
  },

  publisher: {
    init(publisher) {
      if (publisher &&
          ((publisher.name && publisher.name.length > 0) ||
           (publisher.url && publisher.url.length > 0))) {
        publisher = fromJS(publisher)
        return {publisher}
      }
      return undefined
    },
  },

  publisherName: {
    deps: ['publisher'],
    get(publisher) {
      if (publisher) return publisher.get('name')
      return undefined
    },
  },

  htmlAuthor: {
    deps: ['author'],
    get(author) {
      if (author) {
        let url = author.get('url')
        let name = author.get('name')
        if (url) {
          return `<a href="${url}">${name}</a>`
        } else {
          return `<span>${name}</span>`
        }
      }
      return undefined
    },
  },

  htmlPublisher: {
    deps: ['publisher'],
    get(publisher) {
      if (publisher) {
        let url = publisher.get('url')
        let domain = publisher.get('domain')
        let name = publisher.get('name')

        if (url) {
          return `<a href="${url}">${name}</a>`
        } else if (domain) {
          let domainUrl = `http://${domain}`
          return `<a href="${domainUrl}">${name}</a>`
        } else {
          return `<span>${name}</span>`
        }
      }
      return undefined
    },
  },

  htmlAttributions: {
    deps: ['htmlAuthor', 'htmlPublisher'],
    get(htmlAuthor, htmlPublisher) {
      if (htmlAuthor && htmlPublisher) {
        return htmlAuthor + ' / ' + htmlPublisher
      }
      if (htmlAuthor) return htmlAuthor
      if (htmlPublisher) return htmlPublisher
      return undefined
    },
  },

  coverPrefs: {
    init(coverPrefs) {
      coverPrefs = fromJS(coverPrefs)
      return {coverPrefs}
    },
  },

  geo: {
    init(geo) {
      geo = fromJS(geo)
      return {geo}
    },
  },

  address: {
    init(address) {
      address = fromJS(address)
      return {address}
    },
  },

  hasMap: {
    init(hasMap) {
      hasMap = fromJS(hasMap)
      return {hasMap}
    },
  },

})

export default MetadataStruct



