class FAF extends React.Component {
  static displayName = 'FAF'
  static contextTypes = {
    request: PropTypes.func.isRequired,
  }
  /*
  static propTypes = {
    name: string
    icon: PropTypes.string,
    label: PropTypes.string,
    chromaType: PropTypes.string,
    actionList: Immutable.PropTypes.list.isRequired,
    onClick: PropTypes.func,
    active: PropTypes.bool.isRequired,
    show: PropTypes.bool.isRequired,
    pop: PropTypes.number.isRequired,
  }
  */
  state = {
    // mouseDown: false,
    FAFState: undefined,
  }
  // prevMouseDownIndex = -1
  // mouseDownIndex = -1
  hasShown = false
  // onLeave = (targetIndex) => {
  //  this.mouseDownIndex = targetIndex
  //  this.setState({mouseDown: true})
  // }
  // captureClick = (e) => {
  //  e.stopPropagation()
  // }
  get(key, fallback) {
    let FAFState = this.state.FAFState
    if (!FAFState) return fallback
    let val = FAFState.get(key)
    if (val === undefined) return fallback
    return val
  }
  updateFAFState = (store) => {
    let FAFState = store.state
    let prev = this.state.FAFState
    //console.log('updateFAFState -------- ', {equals: FAFState.equals(prev), actionList: FAFState.toJS().actionList})
    if (prev) {
      if (FAFState.equals(prev)) return
    }
    this.setState({FAFState})
  }
  mountIndex = -1
  componentWillMount() {
    this.mountIndex = mountCount
    mountCount++
    // TODO: auto unmount lower index?
    if (mountCount > 1) throw new Error('Only one FAF can be mounted at a time')
    this.hasShown = false
    this.context.request('FAF', 'connect', this.updateFAFState)
    var that = this
    // this.onMouseUp = function(){
    //  onMouseUp(that)
    // }
    // document.addEventListener('mouseup',this.onMouseUp)
  }
  componentWillUnmount() {
    mountCount--
    this.hasShown = false
    this.context.request('FAF', 'disconnect', this.updateFAFState)
    // document.removeEventListener('mouseup',this.onMouseUp)
    // this.onMouseUp = undefined
  }
  componentDidMount() { this.didRender() }
  componentDidUpdate() { this.didRender() }
  didRender() {
    // this.prevMouseDownIndex = -1
  }
  render() {
    // let {
    //  mouseDownIndex,
    //  prevMouseDownIndex,
    // } = this
    if (!this.hasShown && this.state.FAFState === undefined) return null
    var show = this.get('show')
    if (this.get('show')) this.hasShown = true
    if (!this.hasShown) return null

    let id = 'faf'
    let scope = '#' + id
    var active = this.get('active')
    var chroma = this.get('chroma')
    var pop = this.get('pop')
    var render = this.get('render')
    let className = this.props.className || ''
    className += ` pop-` + pop
    if (active !== false) className += ' is-active'
    let content = []
    let child
    child = this.renderActionList('actionList', id, active, show)
    if (child) content.push(child)
    child = this.renderActionList('actionList2', id, active, show)
    if (child) content.push(child)
    child = render ? render() : null
    if (child) content.push(child)

    return (
      <div id={id} className={className} style={{display: 'none'}}>
        <div id={id + '-bg'} className={'faf_bg'} />
        <StyleContext key="chroma-context" chroma={chroma.toJS()}>
          {FAFChromaStyle}
        </StyleContext>
        {renderSprungStyle()}
        {FAFStaticStyle}
        <div className="faf_content" key="faf_content">
          {content}
        </div>
      </div>
    )
  }
  // renderOverlay(id) {
  //  let overlayProps = this.get('overlayProps')
  //  if (!overlayProps) return null
  //  return null
  //  return (
  //    <div key="overlay" id={id+'-active-bg-mask'}>
  //      <div id={id+'-active-bg'} onClick={linkEvent(this, onClick)} />
  //    </div>
  //  )
  // }

  onActionItemClick = (key) => {
    this.context.request('FAF', 'activate', key)
  }
  renderActionList(path, id, active, show) {
    var actionList = this.get(path)
    if (!actionList) return null
    var l = actionList.size
    if (!l) return null
    var className = 'faf_action-list action-list'
    if (path === 'actionList2') {
      className += ' secondary'
    }
    else if (path === 'actionList') {
      className += ' primary'
    }
    var children = [], action, id, key, label, isActive, _className, onItemClick, icon, src
    for (var i = 0; i < l; i++) {
      action = actionList.get(i)
      id = 'faf_action-item-' + i
      key = action.get('key', i)
      isActive = active === key
      _className = 'faf_action-item'
      onItemClick = action.get('onClick')
      if (!onItemClick) {
        if (isActive) {
          _className += ' active'
        }
        else {
          onItemClick = this.onActionItemClick.bind(this, key)
        }
      }

      label = action.get('label', null)
      if (label) label = <div className="faf_action-item_label label">{label}</div>
      icon = action.get('icon', null)
      if (icon) icon = <Icon className="faf_action-item_icon">{icon}</Icon>
      src = action.get('src', null)
      if (src) src = <div className="faf_action-item_src" style={{background: `url(${src})`, backgroundSize: 'cover'}} />

      children.push(
        <div id={id} key={'action-item-' + key} className={_className} onClick={onItemClick}>
          {icon}
          {label}
          {src}
        </div>
      )
    }
    return (
      <div className={className}>
        {children}
      </div>
    )
    // let actionListProps = {
    //  key: 'action-list',
    //  id: id+'-action-list',
    //  list: actionList,
    //  show,
    //  active,
    //  //type, // TODO
    // }
    // return (
    //  <ActionList {...actionListProps} />
    // )
  }
}


export default FAF



