import React from 'react'
import Component from '@the-grid/ui-component-deprecated'
import {Style} from '@the-grid/styleverse'

let count = 0

export default
class LogoLoader extends Component {
  static displayName = "LogoLoader"

  constructor(props) {
    super(props)
    this.id = 'LogoLoader-' + count++
  }

  render() {
    let strokeLength = 192
    let delay = 0// Math.random() * 1.2

    let sel = this.sel
    return (
      <div {...this.elProps({
        className: 'LogoLoader'
      })}>
        <style dangerouslySetInnerHTML={{
          __html: `
            @keyframes loader-fill-1 {
              from {
                stroke-dashoffset: -${strokeLength}px;
              }
              to {
                stroke-dashoffset: -${strokeLength * 3}px;
              }
            }
            @keyframes loader-fill-2 {
              from {
                stroke-dashoffset: -${strokeLength*1.5}px;
              }
              to {
                stroke-dashoffset: -${strokeLength * 3}px;
              }
            }
          `
        }} />
        <Style key="styleverse-styles" perf={true}>
        {`
            ${sel()} {
              ovf -- hidden
              stroke -- chroma-text-pop
            }
            ${{sel}} svg {
              chroma--loading-bg
            }
            ${{sel}} .path1 {
              stroke -- chroma-line
            }
            ${{sel}} .path2 {
              stroke -- chroma-text-pop
            }
            ${{sel}} .path3 {
              stroke -- chroma-text-pop2
            }
            ${{sel}} .path4 {
              stroke -- chroma-selection
            }
            ${{sel}} .path5 {
              stroke -- chroma-box-pop
            }
            ${{sel}} .path6 {
              stroke -- chroma-inverse-box-pop
            }
        `}
        </Style>
        <Style key="scoped-styles" scope={sel()} perf={true}>
          {`
            :scope {
              width -- 56
              height -- 56
              svg {
                position -- absolute
                box-shadow -- 0 1px 2px hsla(0,0%,0%,.5)
                left -- calc(50% - 56px/2)
                top -- calc(50% - 56px/2)
                //transform -- rotateZ(45deg)
              }
              path {
                opacity -- .4
              }
              .path1 {
                //stroke-dashoffset -- ${strokeLength * -1}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                //animation_name -- loader-fill-1
                animation-delay -- ${delay + .2}s
                animation-duration -- 3s
                animation-iteration-count -- infinite
                animation-timing-function -- ease-in
              }
              .path2 {
                stroke-dashoffset -- -${strokeLength}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                animation-name -- loader-fill-1
                animation-delay -- ${delay}s
                animation-duration -- 8s
                animation-iteration-count -- infinite
                animation-timing-function -- ease
              }
              .path3 {
                stroke-dashoffset -- -${strokeLength}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                animation-name -- loader-fill-1
                animation-delay -- ${delay + 2}s
                animation-duration -- 8s
                animation-iteration-count -- infinite
                animation-timing-function -- ease
              }
              .path4 {
                stroke-dashoffset -- -${strokeLength}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                animation-name -- loader-fill-1
                animation-delay -- ${delay + 4}s
                animation-duration -- 8s
                animation-iteration-count -- infinite
                animation-timing-function -- ease
              }
              .path5 {
                stroke-dashoffset -- -${strokeLength}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                animation-name -- loader-fill-1
                animation-delay -- ${delay + 6}s
                animation-duration -- 8s
                animation-iteration-count -- infinite
                animation-timing-function -- ease
              }
              .path6 {
                stroke-dashoffset -- -${strokeLength}px
                stroke-dasharray -- ${strokeLength} ${strokeLength}
                animation-name -- loader-fill-1
                animation-delay -- ${delay + 8}s
                animation-duration -- 8s
                animation-iteration-count -- infinite
                animation-timing-function -- ease
              }
            }
          `}
        </Style>
        <div
          dangerouslySetInnerHTML={{
            __html: `
            <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            	 width="56px" height="56px" viewBox="396 1317 56 56" enable-background="new 396 1317 56 56" xml:space="preserve">
            <g>
            	<rect class="LogoLoader-bg" x="0" y="0" width="56" height="56"/>
            	<path class="path1" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            	<path class="path2" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            	<path class="path3" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            	<path class="path4" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            	<path class="path5" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            	<path class="path6" fill="none"  stroke-width="2" stroke-linecap="square" stroke-miterlimit="10"
                d="M424,1345h8v8h-16v-16h24
                		v24h-32v-32h32"/>
            </g>
            </svg>
            `
          }}
        ></div>
      </div>
    )
  }
}



