import React, {PropTypes} from 'react'
import {raf} from '@the-grid/deps'
import ScrollContext from '@the-grid/ui-scroll-context'
import ViewportContext from '@the-grid/ui-viewport-context'
import {installStore} from '@the-grid/cymatics'
import defaultPlugins from './plugins/defaultPlugins'
import makeStyleStoreStruct from './structs/makeStyleStoreStruct'
import Compiler from './compiler'
import StyleContext from './style-context'
import resetStyles from './resetStyles'
import StyleverseStore from './StyleverseStore'
function NOOP() {}

let count = 0

let styleContextPropsDefault = {
  base: {},
  breakpoint: {target: 'window', resizeTarget: 'window'},
  layout: {},
  typography: {},
  chroma: {randomIfNeeded: true, type: 'light'},
}

class Styleverse extends React.Component {

  static displayName = 'Styleverse'

  static childContextTypes = {
    styleverse: PropTypes.object.isRequired,
  }

  getChildContext() {
    return {styleverse: this}
  }

  state = {
    styleContext: undefined,
  }

  plugins = undefined
  StyleStoreStruct = undefined
  compiler = undefined

  constructor(props, ...args) {
    super(props, ...args)

    this.id = 'Styleverse-' + count
    count++

    let plugins = (this.props && this.props.plugins)
      ? this.props.plugins
      : defaultPlugins

    this.installPlugins(plugins)
  }

  componentWillMount() {
    this.store.request('connect', this.handleStore)
  }
  handleStore = (store) => {
    var styleContext = store.get('styleContext')
    if (!styleContext) return
    this.setState({styleContext}, NOOP)
  }

  // componentDidMount() {
  //   raf(()=>{
  //     this.setState({visibility: ''})
  //   })
  // }

  render() {
    var styleContextProps = this.state.styleContext
    if (!styleContextProps) styleContextProps = this.props.styleContext || {}
    if (styleContextProps.toJS) styleContextProps = styleContextProps.toJS()
    styleContextProps = Object.assign({},styleContextPropsDefault,styleContextProps)

    return (
      <div id={this.id} key="styleverse" className="styleverse" style={{visibility: 'hidden'}}>
        <style id="reset-styles" key="reset-styles" dangerouslySetInnerHTML={{__html: resetStyles}} />
        <RootSheet key="root-sheet" />
        <ViewportContext key="vpctx">
          <ScrollContext key="scrollctx" target="document">
              <StyleContext key="stylectx" {...styleContextProps} ROOT={true}>
                {this.props.children}
              </StyleContext>
          </ScrollContext>
        </ViewportContext>
      </div>
    )
  }

  installPlugins = (_plugins) => {
    if (this.plugins) throw new Error('Cannot set Styleverse plugins more than once or after initial <StyleContext> component instantiation')

    const names = Object.keys(_plugins)
    const namesWithComponent = []
    const namesWithParser = []

    const components = {}
    const parsers = {}
    const structs = {}

    for (let i = 0, len = names.length; i < len; i++) {
      const pluginName = names[i]

      const plugin = _plugins[pluginName]
      if (plugin.Component) {
        components[pluginName] = plugin.Component
        namesWithComponent.push(pluginName)
      }
      if (plugin.Parser) {
        parsers[pluginName] = plugin.Parser
        namesWithParser.push(pluginName)
      }
      if (plugin.Struct) {
        structs[pluginName] = plugin.Struct
      }
    }

    const componentsToRenderFromProps = function(props) {
      const toRender = []

      // looping length to 0 so plugin component parent-child nesting is correct order
      for (let i = namesWithComponent.length - 1; i >= 0; i--) {
        const pluginName = namesWithComponent[i]
        if (props[pluginName]) toRender.push(pluginName)
      }

      return toRender
    }

    let plugins = {
      names,
      namesWithParser,
      namesWithComponent,
      parsers,
      components,
      structs,
      componentsToRenderFromProps,
    }

    this.StyleStoreStruct = makeStyleStoreStruct(plugins)
    this.plugins = plugins
    this.compiler = new Compiler(plugins)
  }

  compile(string) {
    return this.compiler.compile(string)
  }
}

Styleverse = installStore(Styleverse, 'styleverse', function() {
  return new StyleverseStore()
})

export default Styleverse



class RootSheet extends React.Component {
  static contextTypes = {
    styleverse: PropTypes.object.isRequired,
  }
  state = {
    storeHashCode: undefined,
    sheet_ids: [],
  }
  shouldUpdate = false
  sheets = []
  sheet_needsRender = (id, sheet) => {
    let sheet_ids = this.state.sheet_ids
    let index = sheet_ids.indexOf(id)
    if (index === -1) {
      sheet_ids.push(id)
      this.sheets.push(sheet)
      // sheet.setRootSheetIndex(index)
      // this.setState({sheet_ids})
    } else {
      this.sheets.splice(index,1,sheet)
    }
    this.shouldUpdate = true
    this.setState({sheet_ids}, NOOP)
  }
  sheet_willUnmount = (id) => {
    let sheet_ids = this.state.sheet_ids
    let index = sheet_ids.indexOf(id)
    if (index > -1) {
      sheet_ids.splice(index,1)
      this.sheets.splice(index,1)
      // sheet.setRootSheetIndex(index)
      // this.setState({sheet_ids})
    }
    this.shouldUpdate = true
    this.setState({sheet_ids}, NOOP)
  }
  componentWillMount() {
    this.readyStyles = <style key="root-ready-styles" dangerouslySetInnerHTML={{__html: `.styleverse {visibility: inherit !important;}`}} />
    let styleverse = this.context.styleverse
    styleverse.rootSheet = this
    styleverse.sheet_needsRender = this.sheet_needsRender
    styleverse.sheet_willUnmount = this.sheet_willUnmount
    styleverse.store.request('connect', this.handleStyleverse)
  }
  componentWillUnmount() {
    let styleverse = this.context.styleverse
    styleverse.store.request('disconnect', this.handleStyleverse)
  }
  handleStyleverse = (store) => {
    var storeHashCode = store.state.hashCode()
    if (this.state.storeHashCode === storeHashCode) return
    this.shouldUpdate = true
    this.setState({storeHashCode})
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (!nextState) return false
    if (!this.shouldUpdate) return false
    return true
  }
  render() {
    let styleverseStore = this.context.styleverse.store
    let renderedCSS = styleverseStore.get('renderedCSS')

    this.shouldUpdate = false
    if (!this.sheets.length && !renderedCSS) return null
    let children = [this.readyStyles]
    if (renderedCSS) children.push(renderedCSS)
    for (let i = 0; i < this.sheets.length; i++) {
      let sheet = this.sheets[i]
      children = children.concat(sheet)
    }
    return (
      <span key={'styleverse-root-sheet'} id={'styleverse-root-sheet'} style={{display: 'block', height: 0, width: 0, position: 'absolute'}}>
        {children}
      </span>
    )
  }
}



